import React from "react";
import styled from "styled-components";
import {
    BREAKPOINT_LARGE,
    BREAKPOINT_MIDDLE,
    BREAKPOINT_SMALL,
} from "../../constants/constants";
import { DisplayFlex, SpoqaHansSansNeo } from "../../css/css";

export function NewBlankButton({
    width = 100,
    height = 100,
    onClick,
    children,
    style,
}) {
    return (
        <Container
            width={width}
            height={height}
            onClick={onClick}
            style={style}
        >
            <Text>{children}</Text>
        </Container>
    );
}

const Text = styled.div`
    ${DisplayFlex("center", "center")}
    position:relative;
    

    @media screen and (min-width: ${BREAKPOINT_LARGE}px) {
        ${SpoqaHansSansNeo(20, 24, 400)};
    }
    @media screen and (min-width: ${BREAKPOINT_MIDDLE}px) and (max-width: ${BREAKPOINT_LARGE}px) {
        ${SpoqaHansSansNeo(20, 24, 400)};
    }
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) and (max-width: ${BREAKPOINT_MIDDLE}px) {
        ${SpoqaHansSansNeo(16, 20, 400)};
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        padding: 0px 0px 20px 0px;
    }
`;
const Container = styled.div`
    @media screen and (min-width: ${BREAKPOINT_LARGE}px) {
        width: 275px;
        height: 45px;
    }
    @media screen and (min-width: ${BREAKPOINT_MIDDLE}px) and (max-width: ${BREAKPOINT_LARGE}px) {
        width: 260px;
        height: 45px;
    }
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) and (max-width: ${BREAKPOINT_MIDDLE}px) {
        width: 220px;
        height: 40px;
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        padding: 0px 0px 20px 0px;
    }

    transition: all 0.3s;
    cursor: pointer;
    ${DisplayFlex("center", "center")}
    border-radius: 100px;
    border: 3px solid #bd03fa;
    text-align: center;
    color: #f7f7f7;

    &:hover {
        background-color: #bd03fa;
        border-color: #bd03fa;
        color: ${props => (props.activate ? "#F7F7F7" : "#F7F7F7")};
    }
`;
