import styled from "styled-components";
import { SpoqaHansSansNeo } from "../../css/css";
import file from "../../images/file.svg";
import x from "../../images/x.svg";

export function File({ name, size, deleteFile }) {
    const sizeString = size.toString();
    const frontNum = sizeString.substr(0, sizeString.length - 3);
    const backNum = sizeString.substr(sizeString.length - 3, 2);
    const printFrontNum = () => {
        if (frontNum.length === 0) {
            return "00";
        } else if (frontNum.length === 1) {
            return "0" + frontNum;
        } else {
            return frontNum;
        }
    };

    return (
        <Container>
            <img src={file} />
            <Text>{name}</Text>
            <Right>
                <img
                    onClick={() => deleteFile(name, size)}
                    src={x}
                    style={{ cursor: "pointer", marginLeft: "26px" }}
                />
            </Right>
        </Container>
    );
}
const Right = styled.div`
    display: flex;
    margin-left: auto;
`;
const Text = styled.div`
    ${SpoqaHansSansNeo(16, 24)};
    color: #f7f7f7;
    margin-left: 22px;
    top: 2px;
    position: relative;
`;
const Container = styled.div`
    margin-top: 4px;
    width: 274px;
    height: 40px;
    background: #4a4a4a;
    border: 1px solid #4a4a4a;
    border-radius: 4px;
    display: flex;
    align-items: center;
    padding: 0px 22px;
`;
