import { useState } from "react";
import { BREAKPOINT_SMALL } from "src/ts/constants/common";
import { IImage } from "src/ts/interfaces/common";
import { IDropdownStateNode } from "src/ts/models";
import styled from "styled-components";
import chevron from "../../images/chevron.svg";
import { makeCenter, SpoqaHansSansNeo } from "../../styles/css";
import { DropDownRow } from "./dropDownRow";

interface IDropDown {
    position?: string;
    border?: boolean;
    checkBox?: boolean;
    error: boolean;
    pass: boolean;
    header: string;
    contents: Array<IDropdownStateNode>;
    width: number;
    rightAlign?: boolean;
    onCreate?: Function;
    onRemove?: Function;
    onToggle?: Function;
}
interface IHeader {
    border: boolean;
    error: boolean;
    pass: boolean;
    onClick: () => void;
    isOpen: boolean;
    width: number;
    rightAlign?: boolean;
}
interface IDropdownList {
    isOpen: boolean;
    contentsNum: number;
    width: number;
    position: string;
}
interface IChevronImage extends IImage {
    isOpen: boolean;
}
export function DropDown({
    position = "relative",
    error,
    pass,
    header,
    contents,
    rightAlign,
    width,
    checkBox = false,
    onCreate,
    onRemove,
    onToggle,
    border = true,
}: IDropDown) {
    const [isOpen, setIsOpen] = useState(false);
    const toggling = () => setIsOpen(!isOpen);

    return (
        <Container>
            <Header
                border={border}
                rightAlign={rightAlign}
                width={width}
                error={error}
                pass={pass}
                onClick={toggling}
                isOpen={isOpen}
            >
                <Text>{header}</Text>
                <IMG isOpen={isOpen} src={chevron} />
            </Header>
            <DropDownList
                position={position}
                isOpen={isOpen}
                contentsNum={contents.length}
                width={width}
            >
                <Inner width={width} isOpen={isOpen}>
                    {contents.length > 0 &&
                        contents.map((content, i) => {
                            return (
                                <DropDownRow
                                    content={content}
                                    onCreate={onCreate}
                                    onRemove={onRemove}
                                    onToggle={onToggle}
                                    checkBox={checkBox}
                                    isOpen={isOpen}
                                    key={i}
                                />
                            );
                        })}
                </Inner>
            </DropDownList>
        </Container>
    );
}
interface IInner {
    width: number;
    isOpen: boolean;
}
const Inner = styled.div<IInner>`
    transition: all 0.3s;
    margin-top: 8px;
    width: ${props => props.width}px;
    z-index: ${props => props.isOpen && 4};
    visibility: ${props => !props.isOpen && "hidden"};
    overflow-y: auto;
    vertical-align: top;
    max-height: 384px;
    &::-webkit-scrollbar {
        display: none;
    }
    -ms-overflow-style: none; /* IE and Edge */
    scrollbar-width: none; /* Firefox */
`;
const Container = styled("div")`
    height: fit-content;
`;
const Header = styled.div<IHeader>`
    background: #0e0e10;
    border: ${props => props.border && "1px solid"};
    box-sizing: border-box;
    border-radius: 4px;
    z-index: 3;
    &:hover {
        border-color: ${props => (props.error ? "#FF4E45" : "#BD03FA")};
    }
    border-color: ${props =>
        props.error
            ? "#FF4E45"
            : props =>
                  props.isOpen
                      ? "#BD03FA"
                      : props.pass
                      ? "#06BE34"
                      : "#828282"};

    position: relative;
    cursor: pointer;
    ${makeCenter(0)}
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
        height: 40px;
        padding: 8px 12px;
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        height: 32px;
        width: 100px;
        padding: 4px 12px;
    }
    width: ${props => props.width}px;
    margin-left: ${props => props.rightAlign && "auto"};
`;
const IMG = styled.img<IChevronImage>`
    z-index: 16;
    position: relative;
    -ms-user-select: none;
    -moz-user-select: -moz-none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    user-select: none;
    margin-left: auto;
    transition: all ease 0.5s;
    transform: ${props => props.isOpen && "rotate( -180deg )"};
`;
const DropDownList = styled.div<IDropdownList>`
    //padding: 8px 0px;
    width: ${props => props.width}px;
    z-index: ${props => props.isOpen && 2};
    position: ${props => props.position};
    background: #0e0e10;
    transition: all 0.4s;
    height: ${props =>
        props.position !== "absolute"
            ? props.isOpen
                ? props => props.contentsNum * 40 + "px"
                : "0px"
            : "auto"};
    opacity: ${props => (props.isOpen ? "1" : "0")};
    visibility: ${props => !props.isOpen && "hidden"};
`;

const Text = styled.div`
    z-index: 2;
    -ms-user-select: none;
    -moz-user-select: -moz-none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    user-select: none;
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
        ${SpoqaHansSansNeo(16, 24, 700)}
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        ${SpoqaHansSansNeo(14, 16, 400)}
    }
`;
