import { useState } from "react";
import { Button } from "src/ts/components/button/button";
import { SpoqaHansSansNeo } from "src/ts/styles/css";
import styled from "styled-components";
import { File } from "./file";




export function AttachFiles2({
    files,
    addFile,
    deleteFile,
    setOverSizeModalOpen,
    setOverSize,
    currentSize,
}) {
    const [totalSize, setTotalSize] = useState(0);
    const [frontNum, setFrontNum] = useState(0);
    const [backNum, setBackNum] = useState(0);
    const handleFileInput = e => {
        if (e.target.files.length === 0) {
            return;
        } else if (totalSize + e.target.files[0].size > 1024 * 1024 * 10) {
            setOverSizeModalOpen(true);
            setOverSize(
                Math.round((e.target.files[0].size / 1024 / 1024) * 100) / 100 +
                "MB",
            );
            return;
        }
        addFile(files.concat(e.target.files[0]));
        setTotalSize(totalSize + e.target.files[0].size);
        const sizeString = (totalSize + e.target.files[0].size).toString();
        setFrontNum(sizeString.substr(0, sizeString.length - 3));
        setBackNum(sizeString.substr(sizeString.length - 3, 2));
    };
    const deleteFileAndResizing = (name, size) => {
        deleteFile(name);
        setTotalSize(totalSize - size);
        const sizeString = (totalSize - size).toString();
        setFrontNum(sizeString.substr(0, sizeString.length - 3));
        setBackNum(sizeString.substr(sizeString.length - 3, 2));
    };
    const printFrontNum = () => {
        if (frontNum.length === 0) {
            return "00";
        } else if (frontNum.length === 1) {
            return "0" + frontNum;
        } else {
            return frontNum;
        }
    };
    return (
        <Container>
            <Button
                accept="audio/*"
                onClick={() => { }}
                style={{}}
                marginTop={20}
                label={true}
                round={false}
                width={274}
                height={currentSize !== "MOBILE" ? 48 : 32}
            >
                음원 업로드
                <input
                    style={{ display: "none" }}
                    type="file"
                    name="file"
                    onChange={e => handleFileInput(e)}
                />
            </Button>
            <DataLimit>
                {totalSize === 0
                    ? "(00.00 KB / 10.00 MB 사용)"
                    : "( " +
                    printFrontNum() +
                    "." +
                    backNum +
                    "KB / 10.00 MB 사용)"}
            </DataLimit>
            <List file={files.length > 0}>
                {files.map((file, i) => (
                    <File
                        deleteFile={deleteFileAndResizing}
                        key={i}
                        name={file.name}
                        size={file.size}
                    />
                ))}
            </List>
        </Container>
    );
}
const List = styled.div`
    margin-top: ${props => props.file && "16px"};
`;
const DataLimit = styled.div`
    margin-left: 4px;
    margin-top: 4px;
    ${SpoqaHansSansNeo(12, 14, 400, "#F7F7F7", "0em")};
`;
const Container = styled.div``;