import React, { useState } from "react";
import styled from "styled-components";
import { DisplayFlex, SpoqaHansSansNeo } from "../../css/css";

export function BlankButton4({ style, onClick, onClick2, text1, text2, followed, isMobile = false }) {
    const [activate, setActivate] = useState(followed);
    const buttonClick = () => {
        if (!activate) {
            onClick();
        } else {
            onClick2();
        }
        setActivate(!activate)
    }
    return (
        <Container style={style} onClick={buttonClick} activate={activate} isMobile={isMobile}>
            <Text isMobile={isMobile}>
                {!activate ? text1 : text2}
            </Text>
        </Container>
    );
}
const Text = styled.div`
    ${props=> !props.isMobile ? SpoqaHansSansNeo(16,16,700) : SpoqaHansSansNeo(14,18,700)};
    position: relative;
    top:${props=> !props.isMobile ? '2px' : '1px'};
`
const Container = styled.div`
    transition: all 0.3s;
    cursor: pointer;
    ${DisplayFlex('center', 'center')}
    border-radius: 4px;
    border: 1px solid;
    text-align: center;
    color: ${props=>props.activate ? '#BD03FA' : '#F7F7F7'};
    border-color: #F7F7F7;
    &:hover{
        background-color: #212121;
        border-color: #F7F7F7;
        color: ${props=>props.activate ? '#F7F7F7' : '#F7F7F7'};
    }
`