import React, { useEffect, useState } from "react";
import styled from "styled-components";
import { API_SERVER2, BREAKPOINT_LARGE, BREAKPOINT_MIDDLE, BREAKPOINT_SMALL } from "../../../../constants/constants";
import axios from "axios";
import useAsync from "../../../../hook/useAsync";
import { ArtistPagination } from "../../4.BrowseArtist/3.ArtistPagination/artistPagination";
import { PortfolioCards } from '../01.PortfolioCards/0.PortfolioCards/portfolioCards'
import { DisplayFlex, SpoqaHansSansNeo } from "../../../../css/css";
import arrowRight from '../../../../images/arrow-right.svg'
import { useHistory } from "react-router";
export function Portfolio(props) {
  const [currentSize, setCurrentSize] = useState(props.currentSize);
  const history = useHistory();
  useEffect(() => {
    if (props.currentSize !== currentSize) {
      setCurrentSize(props.currentSize)
      refetch(data)
    }
  })
  const culLimitNum = () => {
    if (props.currentSize === "LARGE" || props.currentSize === "MIDDLE") {
      return 8;
    } else if (props.currentSize === "SMALL") {
      return 9;
    } else {
      return 4;
    }
  }
  const getPortfolios = async () => {
    let params = new URLSearchParams();
    params.append("page", page)
    params.append("limit", culLimitNum())

    const url = API_SERVER2 + `/portfolios/recommand`;
    const response = await axios.get(
      url, {
        withCredentials: true,
      params: params
    }
    );
    return response.data;
  }
  const [state, refetch] = useAsync(getPortfolios, [], true);
  const [page, setPage] = useState(1);
  const { loading, data, error } = state;
  useEffect(() => {
    refetch(data)
  }, [page]);

  return (
    <Container>
      <InnerContainer>
        <Row1>
          <Text>추천 포트폴리오</Text>
          <SendRequest
            onClick={() => {
              if (window.localStorage.getItem('userData')) {
                history.push('/stage8/request');
                window.scrollTo(0, 0);
              } else {
                history.push('/login');
              }
            }}>
            <Row2>
              <Text2>이런 작품 나도 받아보기</Text2>
              <ArrowImage src={arrowRight} />
            </Row2>
          </SendRequest>
        </Row1>
        {(loading && !data) ? null : (error ? null :
          <>
            <PortfolioCards data={data.data} />
            {(data.pagination.lastPage > 1) &&
              <ArtistPagination pagination={data.pagination} setPage={setPage} />}
          </>)
        }
      </InnerContainer>
    </Container>
  );
}
const ArrowImage = styled.img`
  width: 24;
  height: 24;
`
const Row2 = styled.div`
  display: flex;
`
const Row1 = styled.div`
  width: 100%;
  @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
    display: flex;
  }
`
const Text2 = styled.div`
  @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
    ${SpoqaHansSansNeo(16, 24, 700)};   
  } 
  @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
    ${SpoqaHansSansNeo(16, 24, 400)}; 
  }
`
const SendRequest = styled.div`
  @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
    ${DisplayFlex('center', 'center')}
    margin-left: auto;
    height: 40px;
    width: 231px;
  }
  cursor: pointer;
  border-radius: 4px;
`
const Container = styled.div`
    background-color: #01010B;
    width: 100%;
    ${DisplayFlex('center', '')}
`
const InnerContainer = styled.div`
  padding: 40px 20px 80px 20px;
  
  @media screen and (min-width: ${BREAKPOINT_LARGE}px) {
    width: 1196px;
  }
  @media screen and (min-width: ${BREAKPOINT_MIDDLE}px) and (max-width: ${BREAKPOINT_LARGE}px) {
    width: 1140px;
  }
  @media screen and (min-width: ${BREAKPOINT_SMALL}px) and (max-width: ${BREAKPOINT_MIDDLE}px){
    width: 754px;
  }
  @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
    width: 376px;
  }
`
const Text = styled.div`
  @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
    ${SpoqaHansSansNeo(24, 30, 700)};  
  }
  @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
    ${SpoqaHansSansNeo(20, 24, 700)};    
  }
`
