import axios from "axios";
import React, { useEffect, useState } from "react";
import { Route } from "react-router";
import styled from "styled-components";
import { API_SERVER2, BREAKPOINT_MIDDLE, BREAKPOINT_SMALL } from "../../constants/constants";
import { NavBar } from "../main/1.NavBar/0.NavBar/navBar";
import { Footer } from "../main/6.Footer/0.Footer/footer";
import { MyArtists } from "./myArtists";
import { MyProfile } from "./myProfile";
import { MyProjects } from "./myProjects";
import { Settings } from "./settings";
import { UpperSection } from "./upperSection";

export function MyPage({ currentSize }) {
    const [smallProfileURI, setSmallProfileURI] = useState(null);
    const [data, setData] = useState(null);
    const [sort, setSort] = useState(null);
    useEffect(() => {
        let params = new URLSearchParams();
        params.append("page", 1)
        params.append("sortBy", sort)

        const url = API_SERVER2 + `/artists/following`;
        axios.get(url, { withCredentials: true, params: params })
            .then((data) => {
                setData(data.data)
            });
    }, [sort])

    let artists = null;
    if (data) {
        artists = data.data.artists
    }

    return (
        <Container>
            <NavBar
                currentSize={currentSize}
                smallProfileURI={smallProfileURI} />
            <InnerContainer>
                <UpperSection setSort={setSort} currentSize={currentSize} />
                <Route path="/mypage/myartists" exact render={() =>
                    <MyArtists data={data} artists={artists} />} />
                <Route path="/mypage/myprojects" exact render={() =>
                    <MyProjects currentSize={currentSize} />} />
                <Route path="/mypage/myprofile" render={() =>
                    <MyProfile currentSize={currentSize} />} />
                <Route path="/mypage/settings" exact render={() =>
                    <Settings currentSize={currentSize} setSmallProfileURI={setSmallProfileURI} />} />
            </InnerContainer>
            <Footer currentSize={currentSize} />
        </Container>
    );
}
const InnerContainer = styled.div`
    @media screen and (min-width: ${BREAKPOINT_MIDDLE}px) { 
        padding-top: 80px;
    }
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) and (max-width: ${BREAKPOINT_MIDDLE}px) {
        padding-top: 56px;
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        padding-top: 56px;
    }
`

const Container = styled.div`
    display: flex;
    flex-direction: column;
    background-color:#01010B;
    -ms-user-select: none; 
    -moz-user-select: -moz-none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    user-select: none;
`