import React from "react";
import styled from "styled-components";
import profile from '../../../../../images/profile.svg'
import arrow from '../../../../../images/arrow-down.svg'
import { API_SERVER2, BREAKPOINT_MIDDLE, BREAKPOINT_SMALL } from "../../../../../constants/constants";
import axios from "axios";
import { DisplayFlex, SpoqaHansSansNeo } from "../../../../../css/css";
import { useHistory } from "react-router";

export function Profile({smallProfileURI,isOpen, setIsOpen}) {
  const userData = JSON.parse(window.localStorage.getItem('userData'));
  const history = useHistory();
  const onOptionClicked = async (value) => {
    setIsOpen(false);
    switch (value) {
      case "My Page":
        history.push('/mypage/myartists');
        window.scrollTo(0, 0);
        break;
      case "Logout":{
        const url = API_SERVER2 + `/auth/logout`;
        await axios.post(
          url,{},{ withCredentials: true }
        ).then(()=>{
          window.localStorage.removeItem("isLogin");
          window.localStorage.removeItem("userData");
          history.push("/")
          window.scrollTo(0, 0);
        })
        break;}
    }
  };
  const toggling = () => setIsOpen(!isOpen);
  const options = ["My Page", "Logout"]
  return (
    <Container onClick={toggling}>
      <ProfileIMG src={smallProfileURI || userData.smallProfileURI || profile} />
      <Arrow src={arrow} isOpen={isOpen} />
      <DropDownList isOpen={isOpen} >
        <Inner>
          {options.map((option, i) => (
            <ListItem isOpen={isOpen} key={i} onClick={() => onOptionClicked(option)}>
              <Text>{option}</Text>
            </ListItem>
          ))}
        </Inner>
      </DropDownList>
    </Container>
  );
}
const ProfileIMG = styled.img`
  border-radius: 50em;
  margin-right:10px;
  @media screen and (min-width: ${BREAKPOINT_MIDDLE}px) {
    width:32px;
    height:32px;
  }
  @media screen and (max-width: ${BREAKPOINT_MIDDLE}px) {
    width:24px;
    height:24px;
  }
`
const Arrow = styled.img`
    transition: all ease 0.5s;
    transform: ${props => props.isOpen && 'rotate( -180deg )'};
`
const Container = styled.div`
  -ms-user-select: none; 
  -moz-user-select: -moz-none;
  -khtml-user-select: none;
  -webkit-user-select: none;
  position:relative;
  float:left;
  justify-content: center;
  align-items: center;
  display: flex;
  cursor: pointer;
  @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
    height: 40px;
    width: 78px;
  }
  @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
    width: 52px;
    height: 16px;
    padding: 0px 4px;
  }
`
const Inner = styled.div`
    z-index:2;
    overflow-y: auto;
    max-height:384px;
    &::-webkit-scrollbar {
    display: none; 
    }
    -ms-overflow-style: none; /* IE and Edge */
    scrollbar-width: none; /* Firefox */
`
const DropDownList = styled.div`
  height:${props=>props.isOpen ? '96px' : '0px' };
  border:${props=>props.isOpen ? '1px solid #4A4A4A' : '1px solid #0E0E10' };
  max-height:384px;
  box-shadow: 0px 4px 4px rgba(1, 1, 11, 0.25);
  border-radius: 4px;
  position: absolute;
  visibility: ${props => (!props.isOpen && "hidden")};
  //opacity: ${props => (props.isOpen ? "1" : "0")};
  top: 40px;
  right: 5px;
  background-color:#0E0E10;
  padding: 8px 0px;
  z-index:2;
  transition: all .3s;
`;
const ListItem = styled.li`
    padding: ${props=>props.isOpen ? '8px 12px' : '0px 12px' };
    opacity: ${props => (props.isOpen ? "1" : "0")};
    cursor: pointer;
    transition: all .3s;
    &:hover {
        background: ${props => (props.selected ? '#BD03FA' : '#4A4A4A')};
    }
    background: ${props => (props.selected ? '#BD03FA' : '#0E0E10')};
    &:active{
        background: #BD03FA;
    }
    ${DisplayFlex("", "center")}
    height: ${props => (props.isOpen ? '40px' : '0px')};
    opacity: ${props=>props.isOpen ? 1 : 0 };
    width: 226px;
`;
const Text = styled.div`
    z-index:2;
    -ms-user-select: none; 
    -moz-user-select: -moz-none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    user-select: none;
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
        ${SpoqaHansSansNeo(16, 24, 700)}
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        ${SpoqaHansSansNeo(14, 16, 400)}
    }
`