import { ClientContentBig } from "./clientContentBig";

export function HowWorks() {
  const [status, setStatus] = useState(0);
  const compareAndSet = (compare, setNum) => () => {
    if (status === compare)
      setStatus(setNum)
  }

  return (
    <Container>
      <InnerContainer>
        <Title>How STAGE8 works:</Title>
        <Text>서로 잘 몰라도 괜찮아요, 스테이지에이트가 함께할게요!</Text>
        <SelectionRow>
          <Client status={status}>
            <BBARtist onMouseEnter={compareAndSet(0, 1)} />
            <Text3 >CLIENT</Text3>
            <TextColumn>
              <Text1>클라이언트</Text1>
              <Text2>CLIENT</Text2>
            </TextColumn>
          </Client>
          <Artist status={status}  >
            <BBARtist onMouseEnter={compareAndSet(0, 2)} />
            <TextColumn2>
              <Text5>아티스트</Text5>
              <Text4>ARTIST</Text4>
            </TextColumn2>
            <TextBig>ARTIST</TextBig>
          </Artist>
        </SelectionRow>
        <ArtistContentBig status={status} compareAndSet={compareAndSet} />
        <ClientContentBig status={status} compareAndSet={compareAndSet} />
      </InnerContainer>
    </Container>
  );
}
