import axios from "axios";
import React, { useState } from "react";
import { useHistory, useParams } from "react-router";
import styled from "styled-components";
import { BlankButton3 } from "../../components/0.Button/blankButton3";
import { API_SERVER2, BREAKPOINT_LARGE, BREAKPOINT_MIDDLE, BREAKPOINT_SMALL } from "../../constants/constants";
import { DisplayFlex, SpoqaHansSansNeo } from "../../css/css";
import useAsync from "../../hook/useAsync";
import { NavBar } from "../main/1.NavBar/0.NavBar/navBar";
import { Footer } from "../main/6.Footer/0.Footer/footer";
import projects from '../../images/projects.svg'
import { PurpleTextLink } from "../../components/1.PurpleTextLink/purpleTextLink";
import { Inquiry } from "../userTypeSelect/1.TypeSelect/2.Note/2.Inquiry/inquiry";
import { EstimateCard } from "./estimateCard";

export function ProjectDetail({ currentSize }) {
    const history = useHistory();
    const [modalOpen, setModalOpen] = useState(false);
    const openModal = () => {
        setModalOpen(true);
    }
    const closeModal = () => {
        setModalOpen(false);
    }
    const printCreatedAt = () => {
        let date = project.createdAt
        const index = date.indexOf('T');
        let newDate = date.slice(0, index);

        return "등록일자 " + newDate;
    }
    const projectID = useParams().projectID
    const getProject = async () => {
        const url = API_SERVER2 + `/projects/${projectID}`;
        const response = await axios.get(
            url, { withCredentials: true });

        return response.data;
    }
    const getEstimates = async () => {
        const userType = JSON.parse(window.localStorage.getItem('userData')).userType;
        const url = API_SERVER2 + `/estimates/project/${projectID}/${userType}`;
        const response = await axios.get(
            url, { withCredentials: true });

        return response.data;
    }
    const [state] = useAsync(getProject, []);
    const [state2, refetch2] = useAsync(getEstimates, []);
    const data2 = state2.data;
    const { data } = state;
    let project, estimates = null;
    if (data) {
        project = data.data.project
    }
    if (data2) {
        estimates = data2.data.estimates
    }
    const converter = () => {
        if (project.progress === '의뢰' ||
            project.progress === '견적 확인 및 선택') {
            return '오픈 준비'
        }
        else if (
            project.progress === '세부 사항 협의' ||
            project.progress === '계약' ||
            project.progress === '결제') {
            return '진행 중'
        }
        else if (project.progress === '결과물 수령') {
            return '완료'
        }
    }
    const printGenre = () => {
        const a = [];
        project.genre.map((v, i) => {

            a.push(v);
            if (project.genre.length - 1 !== i)
                a.push(" · ")
        })
        return a;
    }
    const printCategory = () => {
        const a = [];
        project.category.map((v, i) => {
            const index = v.indexOf('-');
            const newThing = v.slice(0, index).
                concat(' > ').concat(v.slice(index + 1, v.length));

            a.push(newThing);
            if (project.category.length - 1 !== i) {
                a.push(" ")
                a.push("  ·  ")
                a.push(" ")
            }

        })
        return a;
    }
    return (
        <Container>
            <NavBar currentSize={currentSize} />
            <InnerContainer>
                <InnerContainer2>
                    {(data && data2) ? (
                        <>
                            <Row0>
                                <State><Text12>{converter()}</Text12></State>
                                <CreatedAt>{printCreatedAt()}</CreatedAt>
                            </Row0>
                            <Name>{project.name}</Name>
                            <Row1>
                                <Category>{printCategory()}</Category>
                                <Genre>{printGenre()}</Genre>
                            </Row1>
                            <BlankButton3
                                onClick={() => history.push(`/requestdetail/${project._id}`)}
                                style={
                                    {
                                        marginTop: 20,
                                        borderRadius: 0,
                                        marginBottm: 20
                                    }
                                }
                                width={182}
                                height={48}
                                text1={"의뢰서 보기"}></BlankButton3>
                            {
                                estimates.length ?
                                    <Estimates>
                                        <Text4>{estimates.length}개의 아티스트 견적서가 도착했습니다!</Text4>
                                        <Text5>아래 견적서에 대해 질문이 있으신가요? STAGE8에게 바로 <PurpleTextLink onClick={openModal}> 문의주세요</PurpleTextLink>.</Text5>
                                        <EstimateContainer>{
                                            estimates.map((estimate, i) => {
                                                return (
                                                    <EstimateCard
                                                        refetch2={refetch2}
                                                        style={{
                                                            marginBottom: (i !== (estimates.length - 1)) && 20
                                                        }}
                                                        key={i}
                                                        data={estimate} />
                                                )
                                            })}
                                        </EstimateContainer>
                                    </Estimates>
                                    :
                                    <EmptyProject>
                                        <img src={projects} style={{ marginTop: 200 }} />
                                        <Text3>도착한 견적서가 없습니다.</Text3>
                                        <Text1>
                                            프로젝트 등록 후 영업일 2일 안에 평균 2-3개의 견적을 받아볼 수 있습니다. <br></br>
                                            조금만 기다려주세요!
                                        </Text1>
                                    </EmptyProject>
                            }
                        </>
                    ) :
                        (
                            <EmptySpace />
                        )
                    }


                </InnerContainer2>
            </InnerContainer>
            <Footer />
            <Inquiry open={modalOpen} close={closeModal} />
        </Container>
    );
}
const EmptySpace = styled.div`
    height: 900px;
`
const EstimateContainer = styled.div`
    margin-top: 20px;
`
const Text5 = styled.div`
    margin-top:20px;
    ${SpoqaHansSansNeo(16, 20)};
`
const Estimates = styled.div`
    margin-top: 20px;
    border-top: 1px solid #828282;

`
const Text4 = styled.div`
    margin-top:20px;
    ${SpoqaHansSansNeo(24, 28, 700)};
`
const Text3 = styled.div`
    margin-top: 32px;
    ${SpoqaHansSansNeo(34, 40, 700, '#DBDBDB')};
    height: 80px;
    ${DisplayFlex('', 'center')}
`
const Name = styled.div`
    ${SpoqaHansSansNeo(48, 88, 700)};
    margin-top: 20px;
`
const Text12 = styled.div`
    position: relative;
`
const InnerContainer = styled.div`
    @media screen and (min-width: ${BREAKPOINT_MIDDLE}px) { 
        padding-top: 80px;
    }
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) and (max-width: ${BREAKPOINT_MIDDLE}px) {
        padding-top: 56px;
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        padding-top: 56px;
    }
    ${DisplayFlex("center", "")}
`
const InnerContainer2 = styled.div`
    padding: 80px 20px;
    @media screen and (min-width: ${BREAKPOINT_LARGE}px) {
        width: 1196px;
    }
    @media screen and (min-width: ${BREAKPOINT_MIDDLE}px) and (max-width: ${BREAKPOINT_LARGE}px) {
        width: 1140px;
    }
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) and (max-width: ${BREAKPOINT_MIDDLE}px){
        width: 100%;
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        width: 375px;
    }
`
const Container = styled.div`
    background-color:#01010B;
    -ms-user-select: none; 
    -moz-user-select: -moz-none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    user-select: none;
`
const State = styled.div`
    ${DisplayFlex('center', 'center')};
    padding: 8px 20px;
    height: 40px;
    background: #4A4A4A;
    border-radius: 50px;
    ${SpoqaHansSansNeo(16, 20, 700)};
`
const Genre = styled.div`
    white-space: pre-wrap;
    width: fit-content;
    ${SpoqaHansSansNeo(12, 14, 700, '#D0B1FF', '0em')};
    padding-left: 8px;
`
