import { useState } from "react";
import styled from "styled-components";
import { SpoqaHansSansNeo } from "../../css/css";
import question from '../../images/question.svg'

export function Tooltip({ children, left, width }) {
    const [hover, setHover] = useState(false);
    return (
        <Container
            onMouseOver={() => { setHover(true) }}
            onMouseOut={() => { setHover(false) }}>
            <IMG src={question}>
            </IMG>
            {hover && <Message left={left} width={width}>{children}</Message>}
        </Container>
    );
}
const Container = styled.div`
    position:relative;
    display:inline-block;
`
const Message = styled.div`
    z-index:2;
    left:${props => props.left || '0px'};
    width:${props => props.width || '280px'};
    height:44px;
    background-color:#0E0E10;
    position:absolute;
    border-radius: 4px;
    padding: 8px;
    ${SpoqaHansSansNeo(12, 14, 400, '#F7F7F7', '0em')};
    display: flex;
    align-items: center;
`

const IMG = styled.img`
    position:relative;
    top:1px;
    height:16px;
    width:16px;
`