import React, { useState } from "react";
import {Slide} from "./Slider/slide";
import styled from "styled-components";
import axios from 'axios'
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import Slider from "react-slick";
import { API_SERVER2, BREAKPOINT_LARGE, BREAKPOINT_MIDDLE, BREAKPOINT_SMALL } from "../../../constants/constants";
import useAsync from "../../../hook/useAsync";
import SlideNavBar from "./slideNavBar";
import { DisplayFlex } from "../../../css/css";

export function Banner( {currentSize} ) {
    const getBanners = async () => {
        const response = await axios.get(
            API_SERVER2 + '/banners',{ withCredentials: true });
        SetTotalSlideNum(response.data.data.banners.length)
        return response.data.data.banners;
    }
    const [sliderRef, setSliderRef] = useState(null);
    const [state] = useAsync(getBanners, []);
    const [currentSlide, SetCurrentSlide] = useState(1);
    const [totalSlideNum, SetTotalSlideNum] = useState(0);
    const { loading, data: banners, error } = state;
    const settings = {
        beforeChange: (current, next) => SetCurrentSlide(next + 1),
        infinite: true,
        speed: 500,
        slidesToShow: 1,
        slidesToScroll: 1,
        touchMove: true
    };
    
    return (
        <Container>
            <InnerContainer>
                <Slider {...settings} ref={slider => (setSliderRef(slider))}>
                    {loading ? null : (error ? null : (!banners ? null :
                        banners.map((banner, i) => (
                            <Slide
                                nameForURI={banner.nameForURI}
                                currentSize={currentSize}
                                id={banner.artistID}
                                key={i}
                                bannerURI={banner.bannerURI} 
                                name={banner.nameEN} />
                        ))))
                    }
                </Slider>
                <SlideNavBar 
                    currentSize={currentSize}
                    sliderRef={sliderRef}
                    totalSlideNum={totalSlideNum}
                    currentSlide={currentSlide} />
            </InnerContainer>
        </Container>
    );
}
const InnerContainer = styled.div`
    position: relative;
    overflow: hidden;
    @media screen and (min-width: ${BREAKPOINT_LARGE}px) { 
        width: 100%;
        height: 571px;
    }
    @media screen and (min-width: ${BREAKPOINT_MIDDLE}px) and (max-width: ${BREAKPOINT_LARGE}px) {
        width: 100%;
        height: 416px;
    }
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) and (max-width: ${BREAKPOINT_MIDDLE}px) {
        width: 100%;
        height: 278px;
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        width: 100%;
        height: 278px;
    }
`;
const Container = styled.div`
    ${DisplayFlex('center','')};
    width: 100%;
    background: #01010B;
`;