import React from "react";
import styled from "styled-components";
import { BREAKPOINT_SMALL } from "../../../../../constants/constants";

export function SocialMediaLink({ src, link, right }) {
  return (
    <Container href={link} target='_blank' right={right}>
      <IMG src={src} />
    </Container>
  );
}
const Container = styled.a`
  text-decoration:none;
  height: 24px;
  width: 24px;
  margin-left: ${props=>props.right && '20px'};
  @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
    margin-left:20px;
  }
`
const IMG = styled.img`
  height: 24px;
  width: 24px;
`