import axios from "axios";
import React from "react";
import styled from "styled-components";
import { API_SERVER2 } from "../../constants/constants";
import useAsync from "../../hook/useAsync";
import { NavBar } from "../main/1.NavBar/0.NavBar/navBar";
import { Footer } from "../main/6.Footer/0.Footer/footer";
import { RequestForm } from "./requestForm";

export function RequestToStage8({ currentSize }) {
    const getArtistData = async () => {
        const url = API_SERVER2 + `/artists/by-name-uri/stage8`;
        const response = await axios.get(
            url, { withCredentials: true }
        );

        return response.data;
    }
    const [state] = useAsync(getArtistData, []);
    const { data } = state;

    let artistData = null;
    let src, nameKO, profileURI, nameForURI = null;
    if (data) {
        artistData = data.artist
        src = artistData.bannerURI
        nameKO = artistData.nameKO
        nameForURI = artistData.nameForURI
        profileURI = artistData.profileURI
    }

    return (
        <Container>
            <NavBar currentSize={currentSize} />
            <InnerContainer>
                {
                    data ?
                        <>
                            <Banner src={src}></Banner>
                            <RequestForm
                                profileURI={profileURI}
                                nameForURI={nameForURI}
                                nameKO={nameKO} />
                        </>
                        :
                        <EmptySpace />
                }
            </InnerContainer>
            <Footer />
        </Container>
    );
}
const EmptySpace = styled.div`
    height:900px;
    width: 100%;
`
const InnerContainer = styled.div`
    
`
const Banner = styled.div`
    display:flex;
    flex-direction:column;
    height: 572px;
    background: linear-gradient(
        to bottom,
        rgba(0, 0, 0, 0) 10%,
        rgba(0, 0, 0, 0.25) 25%,
        rgba(0, 0, 0, 0.5) 50%,
        rgba(0, 0, 0, 0.75) 75%,
        rgba(0, 0, 0, 1) 100%
    ), url(${props => props.src});
    background-size: cover;
`
const Container = styled.div`
    display: flex;
    flex-direction: column;
    background-color:#01010B;
    -ms-user-select: none; 
    -moz-user-select: -moz-none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    user-select: none;
`