import React from "react";
import { useLocation } from "react-router";
import styled from "styled-components";
import { BREAKPOINT_MIDDLE, BREAKPOINT_SMALL } from "../../constants/constants";
import { NoResults } from "../0.common/noResults";
import {NavBar} from "../main/1.NavBar/0.NavBar/navBar";
import { BrowseArtist } from "../main/4.BrowseArtist/0.BrowseArtist/browseArtist";
import { Footer } from "../main/6.Footer/0.Footer/footer";

export function SearchResults({currentSize}) {
    const location = useLocation();
    let search = location.state.search || null;
    
    return (
        <Container>
            <NavBar currentSize={currentSize}/>
            <InnerContainer>
                {!search ? <NoResults currentSize={currentSize} search={search} /> :
                    <>
                        <UpperSection />
                        <BrowseArtist search={search} />
                    </>}
            </InnerContainer>
            <Footer currentSize={currentSize}/>
        </Container>
    );
}
const InnerContainer = styled.div`
    @media screen and (min-width: ${BREAKPOINT_MIDDLE}px) { 
        padding-top: 80px;
    }
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) and (max-width: ${BREAKPOINT_MIDDLE}px) {
        padding-top: 56px;
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        padding-top: 56px;
    }
`
const UpperSection = styled.div`
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
        height:80px;
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        height:20px;
    }
`
const Container = styled.div`
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
        min-height:912px;
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        min-height:520px;
    }
    display: flex;
    flex-direction: column;
    background-color:#01010B;
    -ms-user-select: none; 
    -moz-user-select: -moz-none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    user-select: none;
`