import { IImage } from "src/ts/interfaces/common";
import styled from "styled-components";
import activeImage from "../../images/activate.svg";
import inactivateImage from "../../images/inactivate.svg";

interface ICheckBox {
    check: boolean;
    onClick: () => void | null;
    clickable?: boolean;
    blur?: boolean;
    hoverEffect?: boolean;
    requirement?: boolean;
    style?: object;
}
interface IContainer {
    check: boolean;
    clickable?: boolean;
    blur?: boolean;
    hoverEffect?: boolean;
    requirement?: boolean;
    style?: object;
}

export function CheckBox({
    check,
    onClick,
    clickable = true,
    blur = false,
    hoverEffect = false,
    requirement = false,
    style = {},
}: ICheckBox) {
    return (
        <Container
            clickable={clickable}
            hoverEffect={hoverEffect}
            requirement={requirement}
            style={style}
            check={check}
            onClick={onClick}
        >
            {check && <Image src={blur ? inactivateImage : activeImage} />}
        </Container>
    );
}

const Container = styled.div<IContainer>`
    transition: all 0.5s;
    cursor: ${props => !props.blur && "pointer"};
    border: ${props =>
        !props.check
            ? props.requirement
                ? "1px solid #FF4E45"
                : "1px solid #828282"
            : null};
    &:hover {
        border-color: ${props => props.hoverEffect && "#8700C5"};
    }
    border-color: ${props => props.blur && "#4A4A4A"};
    box-sizing: ${props => !props.check && "border-box"};
    border-radius: ${props => !props.check && "4px"};
    height: 20px;
    width: 20px;
    margin-right: 8px;
    -ms-user-select: none;
    -moz-user-select: -moz-none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    user-select: none;
`;
const Image = styled.img<IImage>`
    height: 20px;
    width: 20px;
`;
