import React from "react";
import styled from "styled-components";
import { BREAKPOINT_SMALL } from "../../constants/constants";
import { DisplayFlex, SpoqaHansSansNeo } from "../../css/css";

export function BlankButton3({ style, width=0, height=0, onClick, text1, isOpen, inner }) {
    const buttonClick = () => {
        if(inner && !isOpen){
            return;
        }
        onClick();
    }
    return (
        <Container width={width} height={height} style={style} onClick={buttonClick} >
            <Text>
                {text1}
            </Text>
        </Container>
    );
}
const Text = styled.div`
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) { 
        ${SpoqaHansSansNeo(16,32,700)};
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        ${SpoqaHansSansNeo(14,16,700)};
    }
`
const Container = styled.div`
    width:${props=>props.width}px;
    height:${props=>props.height}px;
    transition: all 0.3s;
    &:hover{
        background-color: #212121;
        border-color: #F7F7F7;
    }
    cursor: pointer;
    ${DisplayFlex('center', 'center')}
    border-radius: 4px;
    border-color: #F7F7F7;
    border: 1px solid;
    text-align: center;
    color: #F7F7F7;
`