import React, { useState } from "react";
import {Logo} from "../1.Logo/logo";
import {SearchBar} from "../2.SearchBar/searchBar";
import styled from "styled-components";
import { RightBox } from "../3.RightBox/0.RightBox/rightBox";
import { DisplayFlex } from "../../../../css/css";
import { BREAKPOINT_LARGE, BREAKPOINT_MIDDLE, BREAKPOINT_SMALL } from "../../../../constants/constants";
import { MobileSearchBar } from "../2.SearchBar/mobileSearchBar";

export function NavBar({ currentSize, smallProfileURI }) {
  const [mobileSearch, setMobileSearch] = useState(false);
  return (
    <Container >
      <InnerContainer mobileSearch={mobileSearch}>
        <Logo />
        <SearchBar currentSize={currentSize} setMobileSearch={setMobileSearch} />
        <RightBox smallProfileURI={smallProfileURI} />
      </InnerContainer>
      <MobileSearchBar mobileSearch={mobileSearch} setMobileSearch={setMobileSearch} />
    </Container>
  );
}
const Container = styled.div`
  z-index: 9999;
  position: fixed;
  width: 100%;
  background-color: #0E0E10;
  ${DisplayFlex('center','')};
  @media screen and (min-width: ${BREAKPOINT_LARGE}px) {
    height: 80px;
  }
  @media screen and (min-width: ${BREAKPOINT_MIDDLE}px) and (max-width: ${BREAKPOINT_LARGE}px) {
    height: 80px;
  }
  @media screen and (min-width: ${BREAKPOINT_SMALL}px) and (max-width: ${BREAKPOINT_MIDDLE}px){
    height: 56px;
  }
  @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
    height: 56px;
  }
`
const InnerContainer = styled.div`
  transition: all .5s;
  visibility: ${props=>props.mobileSearch && 'hidden'};
  opacity: ${props=>!props.mobileSearch ? 1 : 0};
  ${DisplayFlex('center','center')};
  padding: 0px 20px;
  @media screen and (min-width: ${BREAKPOINT_LARGE}px) {
    width: 1196px;
  }
  @media screen and (min-width: ${BREAKPOINT_MIDDLE}px) and (max-width: ${BREAKPOINT_LARGE}px) {
    width: 1140px;
  }
  @media screen and (min-width: ${BREAKPOINT_SMALL}px) and (max-width: ${BREAKPOINT_MIDDLE}px){
    width: 100%;
  }
  @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
    width: 100%;
  }
`;
