import React from "react";
import styled, { css, keyframes } from "styled-components";
import { DisplayFlex, SpoqaHansSansNeo } from "../../../../../css/css";
import newX from '../../../../../images/newX.svg'

export function Keyword({ keyword, onRemove, onToggle, deleteQuery, style }) {
  const onAnimationEnd = () => {
    if (!keyword.activate) {
      onRemove(keyword.id)
      if (deleteQuery)
        deleteQuery(keyword.query)
    }
  }
  const onClick = () => {
    onToggle(keyword.id)
  }

  return (
    <Container style={style} activate={keyword.activate} onAnimationEnd={onAnimationEnd}>
      <Text>{keyword.text}</Text>
      <IMG src={newX} onClick={onClick} />
    </Container>
  );
}
const fadeIn = keyframes`
  from {
    transform: scale(.25);
    opacity: 0;
  }

  to {
    transform: scale(1);
    opacity: 1;
  }
`;
const fadeOut = keyframes`
  from {
    transform: scale(1);
    opacity: 1;
  }

  to {
    transform: scale(.0);
    opacity: 0.25;
  }
`;
const Container = styled.div`
    -ms-user-select: none; 
    -moz-user-select: -moz-none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    user-select: none;
    margin-right:8px;
    margin-bottom:8px;
    transition: all 0.3s;
    animation-name: ${fadeIn};
    animation-duration: 0.3s;
    ${props =>
    !props.activate &&
    css`
        animation-name: ${fadeOut};
        `
  }
    &:hover {
        background: rgba(208, 177, 255, 0.7);
        cursor: pointer;
    }
    height: 40px;
    border-radius: 50px;
    padding: 10px 15px 10px 20px;
    background: #D0B1FF;
    border-radius: 50px;
    ${DisplayFlex("", "center")}
`
const Text = styled.div`
  position: relative;
  top: 2px;
  ${SpoqaHansSansNeo(16, 24, 700, '#121212')}
`
const IMG = styled.img`
    cursor: pointer;
    margin-left:4px;
`