import { useState } from "react";
import { useRecoilValueLoadable, useSetRecoilState } from "recoil";
import { Button } from "src/ts/components/button/button";
import { artistProfileStateSelector, artistProfileStateTrigger, usePostArtistProfileState } from "src/ts/models/artistProfile/about";
import { FontNormal } from "src/ts/styles/css";
import { ArtistProfile } from "src/ts/views/artistProfile/artistProfile";
import styled from "styled-components";
import {
    BREAKPOINT_SMALL
} from "../../constants/constants";
import { SpoqaHansSansNeo } from "../../css/css";
import projects from "../../images/projects.svg";
export function MyProfile() {
    const state = useRecoilValueLoadable(artistProfileStateSelector);
    const setTrigger = useSetRecoilState(artistProfileStateTrigger);
    const [isEditing, setIsEditing] = useState(false);
    const [setter] = usePostArtistProfileState();

    switch (state.state) {
        case "hasValue":
            switch (state.contents.artistProfileState) {
                case "complete":
                    return (<Container>
                        <ArtistProfile />
                    </Container>)
                    
                case "none":
                    if (isEditing) {
                        return (<Container>
                            <ArtistProfile />
                        </Container>)
                    } else {
                        return (
                            <Container>
                                <EmptyProject>
                                    <ProjectImage src={projects} />
                                    <Text1>등록된 프로필이 없습니다.</Text1>
                                    <div
                                        style={{
                                            marginTop: 4,
                                            marginBottom: 32,
                                            display: "flex",
                                            flexDirection: "column",
                                            alignItems: "center",
                                        }}
                                    >
                                        <TextBox>
                                            <Text3>
                                                스테이지에이트의 아티스트가 되어주세요!
                                            </Text3>
                                            <Text3>
                                                나만의 페이지 내용을 상세하게 작성해보세요.
                                            </Text3>
                                        </TextBox>
                                    </div>
                                    <Button onClick={() => { setIsEditing(true); setTrigger(Date.now()) }}>
                                        아티스트 등록하기
                                    </Button>
                                </EmptyProject></Container>)
                    }


                case "checking":
                    return (
                        <Container>
                            <EmptyProject>
                                <ProjectImage src={projects} />
                                <Text1>프로필 검토중입니다.</Text1>
                                <div
                                    style={{
                                        marginTop: 4,
                                        marginBottom: 32,
                                        display: "flex",
                                        flexDirection: "column",
                                        alignItems: "center",
                                    }}
                                >
                                    <TextBox>
                                        <Text3>
                                            등록하신 프로필의 검토완료까지 2-3일 정도 소요될 수 있습니다.                                </Text3>
                                    </TextBox>
                                </div>
                            </EmptyProject></Container>)
                case "request":
                    if (isEditing) {
                        return (<Container>
                            <ArtistProfile />
                        </Container>)
                    } else {

                        return (
                            <Container>
                                <EmptyProject>
                                    <ProjectImage src={projects} />
                                    <Text1>보완요청</Text1>
                                    <div
                                        style={{
                                            marginTop: 4,
                                            marginBottom: 32,
                                            display: "flex",
                                            flexDirection: "column",
                                            alignItems: "center",
                                        }}
                                    >
                                        <TextBox>
                                            <Text3>
                                                등록하기 과정 중 보완사항이 발견되었습니다.                               </Text3>
                                            <Text3>
                                                다시 한번 확인 후 작성해주세요.
                                            </Text3>
                                        </TextBox>
                                        <Button onClick={() => { setIsEditing(true); setTrigger(Date.now()) }}>
                                            프로필 보완하기
                                        </Button>
                                        <RequestMemoBox>
                                            <FontNormal>{state.contents.requestMemo}</FontNormal>
                                        </RequestMemoBox>
                                    </div>
                                </EmptyProject></Container>)
                    }
                case "pass":
                    return (
                        <Container>
                            <EmptyProject>
                                <ProjectImage src={projects} />
                                <Text1>아티스트 등록 완료</Text1>
                                <div
                                    style={{
                                        marginTop: 4,
                                        marginBottom: 32,
                                        display: "flex",
                                        flexDirection: "column",
                                        alignItems: "center",
                                    }}
                                >
                                    <TextBox>
                                        <Text3>
                                            스테이지에이트 아티스트로 등록 완료되었습니다.                                            </Text3>
                                        <Text3>
                                            아래 버튼을 클릭하여 추가 포트폴리오를 작성하여 나만의 페이지를 완성해보세요!                                            </Text3>
                                    </TextBox>
                                    <Button onClick={() => { setter("complete"); setTrigger(Date.now()) }}>
                                        포트폴리오 작성하기
                                    </Button>
                                </div>
                            </EmptyProject></Container>)
                default:
                    return (<Container>
                        <ArtistProfile />
                    </Container>)
            }
        case "loading":
            return <></>;
        case "hasError":
            throw state.contents;
        default:
            return <></>
    }



}
const RequestMemoBox = styled.div`
    margin:40px 0px;
    width:1000px;
    height:400px;
    background: rgba(255, 255, 255, 0.3);
    opacity: 0.5;
`
const TextBox = styled.div`
    
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        flex-direction: column;
        align-items: center;
        margin-top: 20px;
    }
`;
const Text3 = styled.div`
    text-align: center;
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
        ${SpoqaHansSansNeo(16, 20, 400, "#DBDBDB")};
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        ${SpoqaHansSansNeo(14, 16, 400, "#DBDBDB", "0em")};
    }
`;
const Text1 = styled.div`
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
        margin-top: 32px;
        ${SpoqaHansSansNeo(34, 40, 700, "#DBDBDB")};
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        margin-top: 20px;
        ${SpoqaHansSansNeo(20, 24, 700, "#DBDBDB")};
    }
`;
const EmptyProject = styled.div`
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
`;
const Container = styled.div`
    width: 100%;
    min-height: 900px;
`;
const ProjectImage = styled.img`
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
        margin-top: 200px;
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        width: 96px;
        height: 91px;
        margin-top: 75px;
    }
`;
