import { ReactNode } from "react";
import { BREAKPOINT_SMALL } from "src/ts/constants/common";
import { makeCenter, SpoqaHansSansNeo } from "src/ts/styles/css";
import styled from "styled-components";

interface IBlankButton {
    width: number;
    height: number;
    onClick: () => void;
    style?: object;
    children?: ReactNode;
    marginLeft?: number;
    marginRight?: number;
    marginTop?: number;
    marginBottom?: number;
}

interface IContainer {
    style?: object;
    width: number;
    height: number;
    marginLeft?: number;
    marginRight?: number;
    marginTop?: number;
    marginBottom?: number;
}

export function BlankButton({
    style,
    width = 0,
    height = 0,
    onClick,
    children = <></>,
    marginLeft = 0,
    marginRight = 0,
    marginTop = 0,
    marginBottom = 0,
}: IBlankButton) {
    return (
        <Container
            width={width}
            height={height}
            style={style}
            marginLeft={marginLeft}
            marginRight={marginRight}
            marginTop={marginTop}
            marginBottom={marginBottom}
            onClick={onClick}
        >
            <Text>{children}</Text>
        </Container>
    );
}
const Text = styled.div`
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
        ${SpoqaHansSansNeo(16, 32, 700)};
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        ${SpoqaHansSansNeo(14, 16, 700)};
    }
`;
const Container = styled.div<IContainer>`
    width: ${props => props.width}px;
    height: ${props => props.height}px;
    margin-right: ${props => props.marginRight}px;
    margin-left: ${props => props.marginLeft}px;
    transition: all 0.3s;
    &:hover {
        background-color: #8700c5;
        border-color: #8700c5;
    }
    cursor: pointer;
    ${makeCenter()}
    border-radius: 4px;
    border-color: #f7f7f7;
    border: 1px solid;
    text-align: center;
    color: #f7f7f7;
`;
