import React from "react";
import styled from "styled-components";
import { BREAKPOINT_LARGE, BREAKPOINT_MIDDLE, BREAKPOINT_SMALL } from "../../../../constants/constants";
import { ArtistDataContainer } from "../1.ArtistDataContainer/artistDataContainer";

export function BannerSection({ refetch, src, currentSize, nameEN, followCount, onRequest, followed, id }) {
  return (
    <Container src={src}>
      <ArtistDataContainer
        refetch={refetch}
        currentSize={currentSize}
        id={id}
        followed={followed}
        nameEN={nameEN} followCount={followCount} onRequest={onRequest} />
    </Container>
  );
}

const Container = styled.div`  
  background: linear-gradient(rgba(0, 0, 0, 0) 25%, 75%, #000000), url(${props => props.src});
  background-repeat: no-repeat;
  background-position: top;
  background-size: cover;
  width: 100%;
  display:flex;
  flex-direction:column;
  @media screen and (min-width: ${BREAKPOINT_LARGE}px) {
    height: 572px;
  }
  @media screen and (min-width: ${BREAKPOINT_MIDDLE}px) and (max-width: ${BREAKPOINT_LARGE}px) {
    height: 416px;
  }
  @media screen and (min-width: ${BREAKPOINT_SMALL}px) and (max-width: ${BREAKPOINT_MIDDLE}px){
    height: 278px;
  }
  @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
    height: 258px;
  }
`