import { atom, selector } from "recoil";
import {
    BREAKPOINT_LARGE,
    BREAKPOINT_MIDDLE,
    BREAKPOINT_SMALL,
} from "src/ts/constants";

export const currentSizeState = atom<string>({
    key: "currentSizeState",
    default: "",
});
const culCurrentSize = () => {
    if (window.innerWidth >= BREAKPOINT_LARGE) {
        return "LARGE";
    } else if (window.innerWidth >= BREAKPOINT_MIDDLE) {
        return "MIDDLE";
    } else if (window.innerWidth >= BREAKPOINT_SMALL) {
        return "SMALL";
    } else {
        return "MOBILE";
    }
};
export const currentSizeSelector = selector({
    key: "currentSizeSelector",
    get: ({ get }) => get(currentSizeState),
    set: ({ set }) => {
        set(currentSizeState, culCurrentSize);
    },
});
