import axios from "axios";
import { useState } from "react";
import { useRecoilValue, useSetRecoilState } from "recoil";
import { Button } from "src/ts/components/button/button";
import { CheckBox } from "src/ts/components/button/checkBox";
import { InputForm } from "src/ts/components/input/inputForm";
import { API_SERVER2, CATEGORIES2, GENRES2 } from "src/ts/constants";
import { cloneObj, onChange } from "src/ts/lib/lib";
import { IDropdownState, IDropdownStateNode } from "src/ts/models";
import { ArtistProfileScrollState } from "src/ts/models/artistProfile/about";
import { artistPortFolioProfileState } from "src/ts/models/artistProfile/artistPortFolio";
import { usePostPortfolios } from "src/ts/models/artistProfile/profileBanner";
import { modalScrollState } from "src/ts/models/modal";
import {
    FontHeader,
    FontNormal,
    FontRedStar,
    makeCenter,
    SpoqaHansSansNeo,
} from "src/ts/styles/css";
import styled from "styled-components";
import { BlankButton } from "../../components/button/blankButton";
import {
    Modal,
    ModalFooter,
    ModalHeader,
    ModalMain,
} from "../../components/modal/modal";
import plus from "../../images/plus.svg";
import { getNameForURIFromURI } from "../../lib/lib";
import { ChooseGenre } from "./chooseGenre";
import { ChooseService } from "./chooseService";
import { ProjectProfile } from "./projectProfile";
export const AddArtistProject = () => {
    const [isOpen, setIsOpen] = useState(false);
    const [isOpen2, setIsOpen2] = useState(false);
    const aritstProfile = useRecoilValue<any>(artistPortFolioProfileState);
    const [projectName, setProjectName] = useState("");
    const [videos, setVideos] = useState("");
    const [withStage8, setWithStage8] = useState(false);
    const [projectStart, setProjectStart] = useState("");
    const [projectEnd, setProjectEnd] = useState("");
    const [clientName, setClientName] = useState("");
    const [servicesKeywords, setServicesKeywords] = useState<
        IDropdownStateNode[]
    >(new Array<IDropdownStateNode>());
    const [services, setServices] = useState<IDropdownState>(
        cloneObj(CATEGORIES2),
    );
    const [genreKeywords, setGenreKeywords] = useState<IDropdownStateNode[]>(
        new Array<IDropdownStateNode>(),
    );
    const [genres, setGenres] = useState<IDropdownState>(cloneObj(GENRES2));

    const [description, setDescription] = useState("");
    const [videoData, setVideoData] = useState({
        pass: false,
        message: "",
        requirement: false,
    });
    const [setUpdate] = usePostPortfolios();
    const validateVideo = e => {
        const value = e.target.value;
        if (value === "") {
            setVideoData({
                pass: false,
                message: "",
                requirement: false,
            });
            return;
        }
        const duplicate = async () => {
            let form = new FormData();
            form.append("videoUrl", value);
            const url = API_SERVER2 + `/auth/check-videourl`;

            const response = await axios.post(url, form, {
                withCredentials: true,
            });
            return response;
        };
        duplicate()
            .then(function (data) {
                setVideoData({
                    pass: true,
                    message: "사용가능한 Youtube url입니다.",
                    requirement: false,
                });
            })
            .catch(er => {
                setVideoData({
                    pass: false,
                    message: "유효하지 않은 Youtube url입니다.",
                    requirement: true,
                });
            });
    };
    const checkValidation = () => {
        if (aritstProfile === null) return false;
        else if (projectName === "") return false;
        else if (servicesKeywords.length === 0) return false;
        else if (projectStart === "") return false;
        else if (projectEnd === "") return false;
        else if (clientName === "") return false;
        else if (genreKeywords.length === 0) return false;
        else if (description === "") return false;

        return true;
    };

    const result = () => {
        let _category = new Array<string>();
        servicesKeywords.map(category => {
            _category.push(category.query);
        });
        let _genre = new Array<string>();
        genreKeywords.map(category => {
            _genre.push(category.query);
        });
        const data = new FormData();

        data.append("name", projectName);
        data.append("withStage8", JSON.stringify(withStage8));
        data.append("category", JSON.stringify(_category));
        data.append("year", JSON.stringify([projectStart, projectEnd]));
        data.append("client", clientName);
        data.append("genre", JSON.stringify(_genre));
        data.append("description", description);
        data.append("files", aritstProfile);
        data.append("nameForURI", getNameForURIFromURI() || "");
        const video: string[] = [];
        video.push(videos);
        data.append("videos", JSON.stringify(video));

        return data;
    };

    const setBGscroll = useSetRecoilState(ArtistProfileScrollState);
    const setModalcroll = useSetRecoilState(modalScrollState);
    const ModalOn = () => {
        setIsOpen(true);
        setBGscroll(false);
        setModalcroll(true);
    };
    const ModalOff = () => {
        setIsOpen(false);
        setBGscroll(true);
        setModalcroll(false);
    };
    return (
        <>
            <AddButton onClick={ModalOn}>
                <PlusImage src={plus}></PlusImage>
                <Text2>포트폴리오 추가</Text2>
            </AddButton>
            <Modal isOpen={isOpen} empty={false} fullScreen={true}>
                {isOpen && (
                    <>
                        <ModalHeader />
                        <ModalMain
                            style={{
                                display: "flex",
                                justifyContent: "center",
                            }}
                        >
                            <Container>
                                <UpperRow>
                                    <ProjectProfile
                                        existedProfile={undefined}
                                    />
                                    <RightColumn>
                                        <InputForm
                                            requirement
                                            header="프로젝트명"
                                            value={projectName}
                                            width={590}
                                            onChange={onChange(setProjectName)}
                                            explanation="최종 프로젝트 이름을 입력해주세요."
                                            placeholder="아사히 플레이리스트"
                                        />
                                        <Stage8Row>
                                            <CheckBox
                                                onClick={() =>
                                                    setWithStage8(!withStage8)
                                                }
                                                check={withStage8}
                                            />
                                            STAGE8을 통해 진행한 프로젝트인가요?
                                        </Stage8Row>
                                        <FontHeader style={{ marginTop: 40 }}>
                                            어느 서비스에 해당되나요?
                                            <FontRedStar>*</FontRedStar>
                                        </FontHeader>
                                        <FontNormal>
                                            이 프로젝트는 어느 서비스에
                                            해당되는지 알려주세요.
                                        </FontNormal>
                                        <ChooseService
                                            keywords={servicesKeywords}
                                            setKeywords={setServicesKeywords}
                                            options={services}
                                            setOptions={setServices}
                                        />
                                        <Row>
                                            <FontHeader>
                                                수행연도
                                                <FontRedStar>*</FontRedStar>
                                            </FontHeader>
                                            <FontNormal
                                                style={{ marginBottom: 16 }}
                                            >
                                                언제 이 프로젝트가 진행되었는지
                                                수행연도(YYYY)를 입력해주세요.
                                            </FontNormal>
                                            <YearsRow>
                                                <InputForm
                                                    width={200}
                                                    value={projectStart}
                                                    onChange={onChange(
                                                        setProjectStart,
                                                    )}
                                                    placeholder="프로젝트 시작"
                                                ></InputForm>
                                                <Dash>-</Dash>
                                                <InputForm
                                                    width={200}
                                                    value={projectEnd}
                                                    onChange={onChange(
                                                        setProjectEnd,
                                                    )}
                                                    placeholder="프로젝트 완료"
                                                ></InputForm>
                                            </YearsRow>
                                        </Row>
                                        <Row>
                                            <FontHeader>
                                                의뢰자(단체)명
                                                <FontRedStar>*</FontRedStar>
                                            </FontHeader>
                                            <FontNormal
                                                style={{ marginBottom: 16 }}
                                            >
                                                프로젝트를 의뢰한 개인 또는 단체
                                                이름을 입력해주세요.
                                            </FontNormal>
                                            <YearsRow>
                                                <InputForm
                                                    width={590}
                                                    value={clientName}
                                                    onChange={onChange(
                                                        setClientName,
                                                    )}
                                                    placeholder="아사히 맥주"
                                                ></InputForm>
                                            </YearsRow>
                                        </Row>
                                        <Row>
                                            <ChooseGenre
                                                genreKeywords={genreKeywords}
                                                setGenreKeywords={
                                                    setGenreKeywords
                                                }
                                                genres={genres}
                                                setGenres={setGenres}
                                            />
                                        </Row>
                                        <Row>
                                            <FontHeader>
                                                프로젝트 소개
                                                <FontRedStar>*</FontRedStar>
                                            </FontHeader>
                                            <FontNormal
                                                style={{ marginBottom: 16 }}
                                            >
                                                어떤 프로젝트인지 설명해주세요.
                                                설명이 더 상세할 수록, 미래의
                                                의뢰자에게 더 많은 도움을 줄 수
                                                있습니다.
                                            </FontNormal>
                                            <InputForm
                                                placeholder="깔끔하게 다 잊고 음악에 파묻히고 싶은,나를 위한 1:1 라이브 콘서트 '아사히 플레이리스트' 참여"
                                                height={160}
                                                textarea={true}
                                                width={590}
                                                value={description}
                                                onChange={onChange(
                                                    setDescription,
                                                )}
                                            ></InputForm>
                                        </Row>
                                        <InputForm
                                            marginTop={40}
                                            marginBottom={140}
                                            error={videoData.requirement}
                                            errorMessage={videoData.message}
                                            header="관련동영상링크"
                                            value={videos}
                                            width={590}
                                            onChange={onChange(
                                                setVideos,
                                                validateVideo,
                                            )}
                                            explanation="관련 동영상 링크를 입력해주세요."
                                            placeholder="https://www.youtube.com/watch?v=y5pqNsGlEio"
                                        />
                                    </RightColumn>
                                </UpperRow>
                            </Container>
                        </ModalMain>
                        <ModalFooter
                            style={{
                                display: "flex",
                                justifyContent: "center",
                            }}
                        >
                            <Container2>
                                <BlankButton
                                    height={40}
                                    width={96}
                                    onClick={() => ModalOff()}
                                >
                                    취소
                                </BlankButton>
                                <Button
                                    rightAlign={true}
                                    height={40}
                                    width={96}
                                    onClick={() => {
                                        if (checkValidation()) {
                                        } else {
                                            setIsOpen2(true);
                                            return;
                                        }
                                        ModalOff();
                                        setUpdate(result());
                                    }}
                                >
                                    {"확인"}
                                </Button>
                            </Container2>
                        </ModalFooter>
                    </>
                )}
            </Modal>

            {isOpen2 && (
                <BG>
                    <Form>
                        <ModalHeader>{"필수항목 입력 필요"}</ModalHeader>
                        <ModalMain>
                            <FontNormal>
                                모든 필수항목을 입력해 주세요.
                            </FontNormal>
                        </ModalMain>
                        <ModalFooter>
                            <Button
                                rightAlign={true}
                                height={40}
                                width={96}
                                onClick={() => {
                                    setIsOpen2(false);
                                }}
                            >
                                {"확인"}
                            </Button>
                        </ModalFooter>
                    </Form>
                </BG>
            )}
        </>
    );
};
const Container = styled.div``;
const Container2 = styled.div`
    width: 55%;
    display: flex;
`;
const BG = styled.div``;
const Form = styled.div`
    width: 300px;
    height: 300px;
    left: 40%;
    top: 40%;
    position: fixed;
    border-radius: 8px;
    background: #01010b;
    z-index: 9999;
`;
const UpperRow = styled.div`
    display: flex;
`;
const Dash = styled.div`
    width: 40px;
    ${makeCenter()}
`;
const YearsRow = styled.div`
    display: flex;
`;
const Row = styled.div`
    margin-top: 40px;
`;
const RightColumn = styled.div`
    width: 590px;
`;
const PlusImage = styled.img``;
const AddButton = styled.div`
    cursor: pointer;
    margin-left: auto;
    display: flex;
`;
const Text2 = styled.div`
    ${makeCenter(0, 1)}
    ${SpoqaHansSansNeo(16, 20, 700, "#BD03FA")}
margin-left: 8px;
`;
const Stage8Row = styled.div`
    display: flex;
    margin-top: 16px;
    margin-bottom: 40px;
`;
