import React from "react";
import styled from "styled-components";
import { BREAKPOINT_SMALL } from "../../../../../constants/constants";
import { DisplayFlex, DragNone, SpoqaHansSansNeo } from "../../../../../css/css";
import filterImage from '../../../../../images/filter.svg'

export function Filter(props) {
  return (
    <Container onClick={props.onClick} isOpen={props.isOpen}>
      <IMG src={filterImage} />
      <Text>필터</Text>
    </Container>
  );
}

const Container = styled.div`
  &:hover {
    opacity: 1;
      background-color: #4A4A4A;
      border-color: #E0E0E0;
  }
  margin-right: 3px;
  transition: all 0.2s;
  background-color: ${props => props.isOpen && '#4A4A4A'};
  cursor: pointer;
  margin-left: 20px;
  border: 1px solid;
  border-color: var(--gravel);
  border-radius: 4px;
  ${DisplayFlex("", "center")}
  @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
    height: 40px;
    width: 100px;
    padding: 12px 17px;
  }
  @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
    height: 32px;
    width: 78px;
    padding: 4px 12px;
  }
`
const Text = styled.div`
  ${DragNone}
  position: relative;
  margin-left:auto;
  ${DisplayFlex("center", "center")}
  @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
    ${SpoqaHansSansNeo(16, 24, 700)}
  }
  @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
    ${SpoqaHansSansNeo(14, 16, 400, '#F7F7F7', '0em')};
  }
`
const IMG = styled.img`
  width: 24px;
  height:  24px;
  ${DisplayFlex("center", "center")}
`
