import React, { useState } from "react";
import styled from "styled-components";
import { BREAKPOINT_SMALL } from "../../constants/constants";
import { DisplayFlex, SpoqaHansSansNeo } from "../../css/css";
import chevron from "../../images/chevron.svg"
import { DropDownListRow2 } from "./dropDownRow2";

export function DropDown3({ requirement, pass, option, onClick2, choose, onCreate, onRemove, onToggle, setCategory, deleteCategory }) {
    const [isOpen, setIsOpen] = useState(false);
    const [selected, setSelected] = useState(null);
    const { header, contents } = option;
    const toggling = () => setIsOpen(!isOpen);
    const onClick = (text) => {
        choose(text.data)
        setSelected(text.text)
        setIsOpen(false)
        onClick2();
    }
    return (
        <Container>
            <Header
                requirement={requirement}
                pass={pass}
                onClick={toggling}
                isOpen={isOpen}>
                <Text>{selected || header}</Text>
                <IMG isOpen={isOpen} src={chevron} />
            </Header>
            <DropDownList 
                isOpen={isOpen} 
                contentsNum={contents.length}>
                {
                    contents.map((content, i) => (
                        <DropDownListRow2
                            onClick={onClick}
                            isOpen={isOpen}
                            key={i}
                            content={content}
                            onCreate={onCreate}
                            onRemove={onRemove}
                            onToggle={onToggle}
                            addQuery={setCategory}
                            deleteQuery={deleteCategory} />
                    ))
                }
            </DropDownList>
        </Container>
    );
}
const Container = styled("div")`
    height:40px;
`
const Header = styled.div`
    background: #0E0E10;
    border: 1px solid ;
    box-sizing: border-box;
    border-radius: 4px;
    z-index:3;
    &:hover{
        border-color:${props=>props.requirement ? '#FF4E45' : '#BD03FA'};
    }
    border-color: ${props => props.requirement ? '#FF4E45' : props=>props.isOpen ? '#BD03FA' : props.pass ? '#06BE34' : '#828282'};
    position:relative;
    cursor: pointer;
    ${DisplayFlex("", "center")}
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
        height: 40px;
        width:626px;
        padding: 8px 12px;
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        height: 32px;
        width: 100px;
        padding: 4px 12px;
    }
`;
const IMG = styled.img`
    z-index: 16;
    position:relative;
    -ms-user-select: none; 
    -moz-user-select: -moz-none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    user-select: none;
    margin-left: auto;
    transition: all ease 0.5s;
    transform: ${props => props.isOpen && 'rotate( -180deg )'};
`
const DropDownList = styled.div`
    margin-top:8px;
    width:626px;
    padding: 8px 0px;
    z-index:4;
    position: relative;
    background: #0E0E10;
    transition: all .3s;
    height: ${props => (props.isOpen ? props => ((props.contentsNum * 40)+16) + 'px' : "0px")};
    visibility: ${props => (!props.isOpen && "hidden")};
    opacity: ${props => (props.isOpen ? "1" : "0")};
`;

const Text = styled.div`
    z-index:2;
    -ms-user-select: none; 
    -moz-user-select: -moz-none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    user-select: none;
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
        ${SpoqaHansSansNeo(16, 24, 700)}
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        ${SpoqaHansSansNeo(14, 16, 400)}
    }
`
