import { Newsletter } from "../newsletter";
function isPC() {
  // 디바이스 종류 설정
  var pc_device = "win16|win32|win64|mac|macintel";

  // 접속한 디바이스 환경
  var this_device = navigator.platform;

  if (this_device) {

    if (pc_device.indexOf(navigator.platform.toLowerCase()) < 0) {
      return false;
    } else {
      return true;
    }

  }
}


export function Main(props) {
  useEffect(() => {
    if (!isPC()) {
      window.alert("COMING SOON\n\n아직 모바일 웹사이트 준비 중입니다!\n원활한 STAGE8 경험을 위해 PC로 접속해 주세요.")
    }
  }, [])
  const {
    currentSize
  } = props;



  return (
    <Container>
      <NavBar currentSize={currentSize} />
      <InnerContainer>
        <Banner currentSize={currentSize} />
        <TextSection currentSize={currentSize} />
        <BrowseArtist currentSize={currentSize} />
        <Portfolio currentSize={currentSize} />
        <HowWorks currentSize={currentSize} />
        <Newsletter currentSize={currentSize} />
        <Footer currentSize={currentSize} />
      </InnerContainer>
    </Container>
  );
}
const InnerContainer = styled.div`
  @media screen and (min-width: ${BREAKPOINT_MIDDLE}px) { 
    padding-top: 80px;
  }
  @media screen and (min-width: ${BREAKPOINT_SMALL}px) and (max-width: ${BREAKPOINT_MIDDLE}px) {
    padding-top: 56px;
  }
  @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
    padding-top: 56px;
  }
`
const Container = styled.div`
  background-color: #01010B;
  display: flex;
  flex-direction: column;
  position: relative;
`