import React, { useEffect, useState } from "react";
import styled from "styled-components";
import { ArtistPagination } from "../3.ArtistPagination/artistPagination";
import axios from "axios";
import useAsync from "../../../../hook/useAsync";
import { API_SERVER2, BREAKPOINT_LARGE, BREAKPOINT_MIDDLE, BREAKPOINT_SMALL } from "../../../../constants/constants";
import { SearchOption } from "../1.SearchOption/0.SearchOption/searchOption";
import { ArtistCards } from "../2.ArtistCards/artistCards";
import { DisplayFlex } from "../../../../css/css";
import { isEmptyObject } from "../../../../lib/lib";
import { NoResults2 } from "../../../0.common/noResults2";

export function BrowseArtist(props) {
  const culLimitNum = () =>{
    if(props.currentSize === "LARGE" || props.currentSize === "MIDDLE"){
      return 8;
    } else if(props.currentSize === "SMALL"){
      return 9;
    } else {
      return 4;
    }
  }
  const getArtistCards = async () => {    
    let params = new URLSearchParams();
    if(category.length !== 0)
      params.append("category", '['+category+']')
    if(genre.length !== 0)
      params.append("genre", '['+genre+']')
    if(artistType.length !== 0)
      params.append("artistType", '['+artistType+']')
    params.append("page", page)
    if(props.search === undefined)
      params.append("limit", culLimitNum())
    params.append("sortBy", sort)
    let url = null;
    
    if (props.search === undefined) {
      url = API_SERVER2 + `/artists`;
    } else {
      url = API_SERVER2 + `/artists/text-search?keyword=${props.search}`;
    }
    
    const response = await axios.get(
      url, {
      withCredentials: true,
      params: params}
    );
    
    return response.data;
  }

  const [state, refetch] = useAsync(getArtistCards, []);
  const [page, setPage] = useState(1); 
  const [currentSize, setCurrentSize] = useState(props.currentSize);
  const [sort, setSort] = useState(null);
  const [artistType, setArtistType] = useState([]);
  const [category, setCategory] = useState([]);
  const [genre, setGenre] = useState([]);
  const addGenre = (newCategory) => {
    setGenre(genre.concat(newCategory))
  }
  const deleteGenre = (text) => {
    setGenre(genre.filter(keyword => keyword !== text));
  }
  const addCategory = (newCategory) => {
    setCategory(category.concat(newCategory))
  }
  const deleteCategory = (text) => {
    setCategory(category.filter(keyword => keyword !== text));
  }
  const { data } = state;
  useEffect(() => {
    if (props.currentSize !== currentSize) {
      setCurrentSize(props.currentSize)
      refetch(data)
    }
  })
  useEffect(() => {
    refetch(data)
  }, [page, sort, artistType, category, genre, props.search]);

  let artists, pagination = null;
  
  if(data){
    if(props.search === undefined){
      artists = data.data.artists
      pagination = data.pagination
    } else {
      artists = data.data.artists
    }
  }

  return (
    <Container>
      <InnerContainer>
        <SearchOption
          setSort={setSort}
          setArtistType={setArtistType}
          setCategory={addCategory}
          deleteCategory={deleteCategory}
          addGenre={addGenre}
          deleteGenre={deleteGenre}
        />
        {data && (!isEmptyObject(data.data) ?
          <>
            {artists && <ArtistCards artists={artists} />}
            {(pagination) && (pagination.lastPage > 1)&&
                    <ArtistPagination pagination={pagination} setPage={setPage} />}
          </> :
          <NoResults2 />)
        }
      </InnerContainer>
    </Container>
  );
}
const Container = styled.div`
    background-color: #01010B;
    width: 100%;
    ${DisplayFlex('center','')}
`
const InnerContainer = styled.div`
  padding: 0px 20px;
  padding-bottom: 40px;
  @media screen and (min-width: ${BREAKPOINT_LARGE}px) {
    width: 1196px;
  }
  @media screen and (min-width: ${BREAKPOINT_MIDDLE}px) and (max-width: ${BREAKPOINT_LARGE}px) {
    width: 1140px;
  }
  @media screen and (min-width: ${BREAKPOINT_SMALL}px) and (max-width: ${BREAKPOINT_MIDDLE}px){
    width: 754px;
  }
  @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
    width: 376px;
  }
`