import React, { useState } from "react";
import styled from "styled-components";
import phone from "../../images/phone.svg";
import mail from "../../images/mail.svg";
import { Button } from "../../components/0.Button/button";
import x from "../../images/x.svg";
import bigCheck from "../../images/bigCheck.svg";
import { Input } from "../../components/4.Input/input";
import { useInputs } from "../../hook/useInputs";
import { BlankButton3 } from "../../components/0.Button/blankButton3";
import { API_SERVER2 } from "../../constants/constants";
import axios from "axios";
import { DisplayFlex, SpoqaHansSansNeo } from "../../css/css";
import { Modal4 } from "../../components/3.Modal/modal4";

export function InqueryToArtist({
    open,
    close,
    src,
    name,
    projectID,
    estimateID,
    questionTo,
}) {
    const [sended, setSended] = useState(false);
    const sendQNA = async () => {
        let form = new FormData();
        form.append("questionTo", questionTo);
        form.append("projectID", projectID);
        form.append("estimateID", estimateID);
        form.append("question", explanation);

        const url = API_SERVER2 + `/qna`;
        const options = {
            method: "POST",
            url: url,
            data: form,
            withCredentials: true,
        };
        axios(options).then(res => {
            if (res.status === 200) {
                setSended(true);
            }
        });
    };
    const [{ explanation }, onChange] = useInputs({
        explanation: "",
    });
    const [focus, setFocus] = useState(false);

    const [explanationData, setExplanationData] = useState({
        pass: false,
        message: "",
        count: 0,
    });
    const validateExplanation = e => {
        const value = e.target.value;
        var long = value.length > 500 ? true : false;
        if (long) {
            setExplanationData({
                pass: false,
                message: "*500자 이내로 입력해주세요.",
                requirement: true,
                count: value.length,
            });
        } else {
            if (e.target.value.length > 0) {
                setExplanationData({
                    pass: true,
                    message: "",
                    requirement: false,
                    count: value.length,
                });
            } else {
                setExplanationData({
                    pass: false,
                    message: "",
                    requirement: false,
                    count: value.length,
                });
            }
        }
    };
    return (
        <Modal4 open={open} close={close}>
            <Container sended={sended}>
                {!sended ? (
                    <>
                        <Header>
                            <img
                                src={src}
                                style={{
                                    width: 32,
                                    height: 32,
                                    borderRadius: "50em",
                                    marginRight: 16,
                                }}
                            />
                            {name}
                            <X src={x} onClick={close} />
                        </Header>
                        <Main>
                            <Text1>
                                STAGE8을 통해 아티스트에게 연락해 보세요.
                            </Text1>
                            <Text2>
                                아티스트와 처음 이야기를 하게 되어 긴장되시나요?
                                걱정마세요!<br></br>
                                <Text3>STAGE8</Text3>을 통해 아티스트와 원활한
                                커뮤니케이션을 언제든지 진행할 수 있습니다.{" "}
                                <br></br>
                                <br></br>
                                아래 경로 중 하나를 선택하시어 바로 문의 사항을
                                남겨 보세요.
                                <br />
                            </Text2>
                            <br />
                            <Contact>
                                <IMG src={phone} />
                                032-322-4364
                            </Contact>
                            <Contact>
                                <IMG src={mail} />
                                info@stage8.co
                            </Contact>
                            <br></br>
                            <Text2>또는</Text2>
                            <Text440 focus={focus}>
                                직접 입력{focus && <Pink></Pink>}
                            </Text440>
                            <Input
                                onBlur={() => setFocus(false)}
                                onFocus={() => setFocus(true)}
                                textarea={true}
                                name="explanation"
                                style={{ marginTop: 8, width: 640, height: 88 }}
                                value={explanation}
                                requirement={explanationData.requirement}
                                pass={explanationData.pass}
                                onChange={e => {
                                    validateExplanation(e);
                                    onChange(e);
                                }}
                                placeholder={`이 아티스트에게 문의할 내용을 자유롭게 작성해주세요. (500자 이내) `}
                            />
                            <Row1>
                                <ErrorMessage pass={false}>
                                    {explanationData.message}
                                </ErrorMessage>
                                <Count>{explanationData.count + "/500"}</Count>
                            </Row1>
                        </Main>
                        <Row3>
                            <BlankButton3
                                onClick={() => close()}
                                style={{ height: 40, width: 96 }}
                                text1={"취소"}
                            />
                            <Button
                                onClick={() => {
                                    sendQNA();
                                }}
                                style={{
                                    marginLeft: "auto",
                                    height: 40,
                                    width: 96,
                                }}
                            >
                                제출
                            </Button>
                        </Row3>{" "}
                    </>
                ) : (
                    <OK>
                        <img src={bigCheck} style={{ marginTop: 115 }}></img>
                        <Text400>문의 사항이 제출되었습니다!</Text400>
                        <Text401>
                            담당자 확인 후 바로 답변 드리도록 하겠습니다.
                        </Text401>
                        <Button
                            onClick={() => close()}
                            style={{ width: 372, height: 48, marginTop: 8 }}
                        >
                            확인
                        </Button>
                    </OK>
                )}
            </Container>
        </Modal4>
    );
}
const Text400 = styled.div`
    margin-top: 63px;
    ${SpoqaHansSansNeo(24, 28, 700)};
`;
const Text401 = styled.div`
    margin-top: 8px;
    ${SpoqaHansSansNeo(16, 20)};
`;
const OK = styled.div`
    ${DisplayFlex("", "center")}
    flex-direction: column;
`;
const Row3 = styled.div`
    border-top: 1px solid #828282;

    padding: 24px;
    display: flex;
`;
const Pink = styled.div`
    left: -24px;
    top: -2px;
    width: 8px;
    height: 24px;
    background: #bd03fa;
    position: absolute;
`;
const Count = styled.div`
    margin-top: 4px;
    margin-left: auto;
    ${SpoqaHansSansNeo(16, 24)};
    text-align: right;
    color: ${props => (props.activate ? "#F7F7F7" : "#828282")};
`;
const ErrorMessage = styled.div`
    color: ${props => (props.pass ? "#06BE34" : "#FF4E45")};
    position: absolute;
    margin-top: 4px;
    ${SpoqaHansSansNeo(16, 24)};
    text-align: left;
`;
const Row1 = styled.div`
    width: 640px;
`;
const Container = styled.div`
    background: #0e0e10;
    width: 688px;
    height: ${props => (props.sended ? "504" : "588")}px;
`;
const Text440 = styled.div`
    position: relative;
    ${SpoqaHansSansNeo(20, 24, 700, "#F7F7F7")};
    color: ${props => (props.focus ? "#F7F7F7" : "#828282")};
    margin-top: 20px;
`;
const Text3 = styled.span`
    ${SpoqaHansSansNeo(16, 20, 700)};
`;
const Text1 = styled.div`
    margin-bottom: 20px;
    ${SpoqaHansSansNeo(20, 24, 700)};
`;
const IMG = styled.img`
    width: 20px;
    height: 20px;
    top: 4px;
    position: relative;
    margin-right: 4px;
`;
const Contact = styled.div`
    width: fit-content;
    cursor: pointer;
    display: flex;
    align-items: baseline;
    ${SpoqaHansSansNeo(16, 20)};
    text-decoration-line: underline;
`;
const Text2 = styled.div`
    ${SpoqaHansSansNeo(16, 20)};
`;
const Header = styled.div`
    position: relative;
    height: 72px;
    padding: 24px;
    ${SpoqaHansSansNeo(20, 24, 700)};
    display: flex;
    align-items: center;
`;
const X = styled.img`
    cursor: pointer;
    width: 24px;
    height: 24px;
    margin-left: auto;
`;
const Main = styled.div`
    padding: 24px 24px 0px 24px;
    border-top: 1px solid #828282;
`;
