import axios from "axios";
import React, { useContext } from "react";
import { withRouter } from "react-router";
import styled from "styled-components";
import { Button } from "../../../../components/0.Button/button";
import { PurpleTextLink } from "../../../../components/1.PurpleTextLink/purpleTextLink";
import { API_SERVER2 } from "../../../../constants/constants";
import { DisplayFlex, SpoqaHansSansNeo } from "../../../../css/css";
import { signIn } from "../../../login/1.LoginSection/loginSection";
import { SignUpDispatch } from "../../0.signUp/signUp";
import { InputForm } from "../1.InputForm/inputForm";
import { Agree } from "../2.Agree/agree";
import { LoginLink } from "../3.LoginLink/loginLink";

export const SignUpSection = withRouter(SignUpSectionFunction);
function SignUpSectionFunction({ requirements, password, email, onChange, agreeMarketing, agreeTerms, location, history }) {
    const singUp = async () => {
        let params = new FormData();
        const url = API_SERVER2 + `/auth/signup/local`;
        params.append('email', email)
        params.append('password', password)
        params.append('userType', location.state.userType)
        if (location.state.userType === 'artist')
            params.append('artistType', '[' + location.state.artistType + ']')

        const options = {
            method: 'POST',
            headers: { 'content-type': 'application/x-www-form-urlencoded' },
            data: params,
            url,
            withCredentials: true
        };
        axios(options)
            .then(res => {
                if (res.status === 204) {
                    signIn(history, email, password)
                }
            })
    }
    const dispatch = useContext(SignUpDispatch)
    const validateEmail = (e) => {
        const value = e.target.value
        var regExp = /^[0-9a-zA-Z]([-_]?[0-9a-zA-Z])*@[0-9a-zA-Z]([-_]?[0-9a-zA-Z])*\.[a-zA-Z]{2,3}$/i
        let duplicated;
        if (regExp.test(value)) {
            const duplicate = async () => {
                let form = new FormData()
                form.append('email', value)
                const url = API_SERVER2 + `/auth/check-email-duplicate`;

                const response = await axios.post(
                    url, form, { withCredentials: true }
                );
                return response;
            }
            duplicate().then(function (data) {

                duplicated = data.data.data.duplicated
                if (duplicated) {
                    dispatch({
                        type: 'CHANGE_REQUIREMENT',
                        id: 'email',
                        activate: true,
                        pass: false,
                        message: '이미 등록된 메일입니다.'
                    })
                } else {
                    dispatch({
                        type: 'CHANGE_REQUIREMENT',
                        id: 'email',
                        activate: false,
                        pass: true,
                        message: '사용 가능한 메일입니다.'
                    })
                }

            });

        } else {
            dispatch({ type: 'CHANGE_REQUIREMENT', id: 'email', activate: true, message: '*규칙에 맞는 이메일 주소를 입력해주세요.' })
        }
    }

    const validatePassword = (e) => {
        const value = e.target.value
        var re = {
            lower: /[a-z]/g,
            upper: /[A-Z]/g,
            alpha: /[A-Z]/gi,
            numeric: /[0-9]/g,
            special: /[\W_]/g
        }
        var alpha = (value.match(re['alpha']) || []).length > 0 ? true : false;
        var numeric = (value.match(re['numeric']) || []).length > 0 ? true : false;
        var special = (value.match(re['special']) || []).length > 0 ? true : false;
        var length = (value.length > 7) ? true : false;

        if (!length) {
            dispatch({
                type: 'CHANGE_REQUIREMENT',
                id: 'password',
                activate: true,
                pass: false,
                message: '*비밀번호가 너무 짧습니다(8자 이상).'
            })
        } else if (!alpha) {
            dispatch({
                type: 'CHANGE_REQUIREMENT',
                id: 'password',
                activate: true,
                pass: false,
                message: '*비밀번호에 문자가 포함되어있지 않습니다.'
            })
        } else if (!numeric) {
            dispatch({
                type: 'CHANGE_REQUIREMENT',
                id: 'password',
                activate: true,
                pass: false,
                message: '*비밀번호에 숫자가 포함되어있지 않습니다.'
            })
        } else if (!special) {
            dispatch({
                type: 'CHANGE_REQUIREMENT',
                id: 'password',
                activate: true,
                pass: false,
                message: '*비밀번호에 특수문자가 포함되어있지 않습니다.'
            })
        } else {
            dispatch({
                type: 'CHANGE_REQUIREMENT',
                id: 'password',
                activate: false,
                pass: true,
                message: '사용 가능한 비밀번호 입니다.'
            })
        }
    }
    const checkButtonActivate = () => {
        if (!requirements[0].activate && (email !== '') &&
            !requirements[1].activate && (password !== '') &&
            agreeTerms) {
            return true;
        } else {
            return false;
        }
    }
    return (
        <Container>
            <InnerContainer>
                <InputForm
                    requirement={requirements[0].activate}
                    message={requirements[0].message}
                    name={requirements[0].id}
                    onChange={(e) => {
                        validateEmail(e);
                        onChange(e);
                    }}
                    value={email}
                    pass={requirements[0].pass}
                    placeholder="example@example.com">Email Address<Star>*</Star></InputForm>
                <InputForm
                    requirement={requirements[1].activate}
                    message={requirements[1].message}
                    name={requirements[1].id}
                    pass={requirements[1].pass}
                    onChange={(e) => {
                        validatePassword(e);
                        onChange(e);
                    }}
                    value={password}
                    placeholder="영문, 숫자, 특수문자 포함 8자 이상">Password<Star>*</Star></InputForm>
                <Agree
                    onClick={() => dispatch({ type: "TOGGLE_AGREEMARKETING" })}
                    style={{ marginBottom: 16 }}
                    agree={agreeMarketing}>
                    STAGE8 마케팅 수신 (주요 혜택, 프로모션 등)에 동의합니다 <Gray>(선택)</Gray>
                </Agree>
                <Agree
                    onClick={() => dispatch({ type: "TOGGLE_AGREETERMS" })}
                    style={{ marginBottom: 48 }}
                    agree={agreeTerms}>
                    STAGE8의 <PurpleTextLink href="https://www.notion.so/stage8/47771798abe54c0888cbf7be799192de">서비스 이용 약관</PurpleTextLink>과 <PurpleTextLink href="https://www.notion.so/stage8/d69758b4c8d648958ade544dfb046570">개인정보 취급방침</PurpleTextLink>에 동의합니다 <Star>*</Star></Agree>
                <Button
                    onClick={singUp}
                    activate={checkButtonActivate()}
                    style={{ width: 372, height: 48 }}>SIGN UP</Button>
                <LoginLink>이미 회원이신가요?<PurpleTextLink onClick={() => history.push('/login')}> 로그인</PurpleTextLink></LoginLink>
            </InnerContainer>
        </Container >
    );
}
const Container = styled.div`
    ${DisplayFlex("center", "")}
`
const InnerContainer = styled.div`
    margin-top:80px;
    padding: 80px 195px;
    height: 622px;
    width: 764px;
    border: 1px solid #F7F7F7;
    border-radius: 8px;
`
const Star = styled.div`
    color:#E41376;
    display:inline;
`
const Gray = styled.span`
    ${SpoqaHansSansNeo(12, 14, 400, '#DBDBDB', '0em')};
`