import React, { useState } from "react";
import styled from "styled-components";
import { SpoqaHansSansNeo } from "../../../../css/css";
import { Album } from "./album";
import { PlayBtn } from "./playBtn";

export function TrackRow({ song, onClickMusic, selectedMusic, isPlay }) {
    const [isHover, setIsHover] = useState(false);

    return (
        <Container
            onClick={onClickMusic(song)}
            onMouseEnter={() => setIsHover(true)}
            onMouseLeave={() => setIsHover(false)}>
            <Album albumArt={song.albumArt} />
            { isHover && <PlayBtn isPlay={(song === selectedMusic) && isPlay} />}
            <Title>{song.title}</Title>
            <ArtistName>{song.artistName}</ArtistName>
            <AlbumName>{song.albumName}</AlbumName>
        </Container>
    );
}

const AlbumName = styled.div`
    ${SpoqaHansSansNeo(16,20,400,'#DBDBDB')};
    margin-left:40px;
    width:23%;
`
const ArtistName = styled.div`
    ${SpoqaHansSansNeo(16,20,400,'#DBDBDB')};
    margin-left:40px;
    width:22%;
`
const Title = styled.div`
    width:35%;
    margin-left:40px;
    ${SpoqaHansSansNeo(16,20,700)};
`
const Container = styled.div`
    position: relative;
    &:hover{
        cursor: pointer;
        background: #4A4A4A;   
    }
    display:flex;
    align-items:center;
    height:80px;
    margin-bottom:4px;
`