import React, { useState } from "react";
import styled from "styled-components";
import grayTriangle from "../../images/grayTriangle.svg";
import x from "../../images/x.svg";
import { Button } from "../../components/0.Button/button";
import { CheckBox } from "../../components/0.Button/checkBox";
import { Input } from "../../components/4.Input/input";
import { BlankButton } from "../../components/0.Button/blankButton";
import { ExpendedTerms } from "./expendedTerms";
import { useInputs } from "../../hook/useInputs";
import { API_SERVER2 } from "../../constants/constants";
import axios from "axios";
import { useHistory } from "react-router";
import { Modal4 } from "../../components/3.Modal/modal4";
import { SpoqaHansSansNeo } from "../../css/css";
export function SecessionModal({ nickName, open, close }) {
    const history = useHistory();
    const withdrawal = async () => {
        let form = new FormData();
        let newReasons = [];
        if (reason1) newReasons.push(1);
        if (reason2) newReasons.push(2);
        if (reason3) newReasons.push(3);
        if (reason4) newReasons.push(4);
        if (reason5) newReasons.push(5);
        if (reason6) newReasons.push(6);

        form.append("reasons", newReasons);
        form.append("feedback", feedback);
        const url = API_SERVER2 + `/users/withdrawal`;
        const options = {
            method: "POST",
            url: url,
            data: form,
            withCredentials: true,
        };
        axios(options).then(res => {
            if (res.status === 200) {
                window.localStorage.removeItem("isLogin");
                window.localStorage.removeItem("userData");
                document.cookie =
                    "access_token" +
                    "=; expires=Thu, 01 Jan 1999 00:00:10 GMT;";
                history.push("/");
                window.scrollTo(0, 0);
            }
        });
    };
    const [feedbackData, setFeedbackData] = useState({
        pass: false,
        message: "",
        count: 0,
    });
    const validateExplanation = e => {
        const value = e.target.value;
        var long = value.length > 250 ? true : false;
        if (long) {
            setFeedbackData({
                pass: false,
                message: "*250자 이내로 입력해주세요.",
                requirement: true,
                count: value.length,
            });
        } else {
            if (e.target.value.length > 0) {
                setFeedbackData({
                    pass: true,
                    message: "",
                    requirement: false,
                    count: value.length,
                });
            } else {
                setFeedbackData({
                    pass: false,
                    message: "",
                    requirement: false,
                    count: value.length,
                });
            }
        }
    };
    const [onTerms, setOnTerms] = useState(false);
    const [reason1, setReason1] = useState(false);
    const [reason2, setReason2] = useState(false);
    const [reason3, setReason3] = useState(false);
    const [reason4, setReason4] = useState(false);
    const [reason5, setReason5] = useState(false);
    const [reason6, setReason6] = useState(false);
    const [{ feedback }, onChange] = useInputs({
        feedback: "",
    });
    return (
        <Modal4 open={open} close={close} style={{ width: 714 }}>
            <Header>
                <HeaderText>회원탈퇴</HeaderText>
                <X src={x} onClick={close} />
            </Header>
            <Main>
                <Title>{nickName}님이 탈퇴하신다니 아쉽네요.</Title>
                <Text1>
                    탈퇴하시면 팔로워/팔로잉, 프로젝트 등 모든 데이터가 삭제되며
                    복구되지 않습니다.
                </Text1>
                <div style={{ display: "flex" }}>
                    <Text2>
                        단, 관계법령에 따라 회사가 보관하여야 하는 정보는 일정
                        기간 보관 처리합니다.
                    </Text2>
                    <TermsButton onMouseEnter={() => setOnTerms(true)}>
                        자세한 내용 보기
                    </TermsButton>
                </div>
                {onTerms && (
                    <div style={{ position: "absolute" }} className="dddd">
                        <img
                            src={grayTriangle}
                            style={{
                                left: 418,
                                top: -1,
                                zIndex: 9999,
                                position: "absolute",
                            }}
                            onMouseEnter={() => setOnTerms(true)}
                        />
                    </div>
                )}
                <ExpendedTerms
                    isOpen={onTerms}
                    onMouseLeave={() => setOnTerms(false)}
                />
                <div
                    style={{
                        zIndex: 999,
                        background: "#0E0E10",
                        position: "relative",
                    }}
                >
                    <Text3>탈퇴 사유를 알려주세요 (복수 선택 가능)</Text3>
                    <Row1>
                        <CheckBox
                            hover={true}
                            activate={reason1}
                            onClick={() => setReason1(!reason1)}
                        />
                        <CheckBoxText>중복 계정이 있음</CheckBoxText>
                    </Row1>
                    <Row1>
                        <CheckBox
                            hover={true}
                            activate={reason2}
                            onClick={() => setReason2(!reason2)}
                        />
                        <CheckBoxText>이메일을 너무 많이 받음</CheckBoxText>
                    </Row1>
                    <Row1>
                        <CheckBox
                            hover={true}
                            activate={reason3}
                            onClick={() => setReason3(!reason3)}
                        />
                        <CheckBoxText>멤버십의 혜택이 별로 없음</CheckBoxText>
                    </Row1>
                    <Row1>
                        <CheckBox
                            hover={true}
                            activate={reason4}
                            onClick={() => setReason4(!reason4)}
                        />
                        <CheckBoxText>개인정보를 보호하고 싶음</CheckBoxText>
                    </Row1>
                    <Row1>
                        <CheckBox
                            hover={true}
                            activate={reason5}
                            onClick={() => setReason5(!reason5)}
                        />
                        <CheckBoxText>
                            모르는 사람의 연락을 받고 싶지 않음
                        </CheckBoxText>
                    </Row1>
                    <Row1>
                        <CheckBox
                            hover={true}
                            activate={reason6}
                            onClick={() => setReason6(!reason6)}
                        />
                        <CheckBoxText>기타</CheckBoxText>
                    </Row1>
                </div>
                <Text4>피드백을 남겨주세요 (선택)</Text4>
                <Text5>
                    회원님의 피드백은 매우 소중합니다. 다만, 개별 답변을 드리기
                    어려운 점에 대해 양해 부탁드립니다.
                </Text5>
                <Input
                    textarea={true}
                    name="feedback"
                    style={{ height: 88, width: 666, marginBottom: 4 }}
                    value={feedback}
                    requirement={feedbackData.requirement}
                    pass={feedbackData.pass}
                    onChange={e => {
                        validateExplanation(e);
                        onChange(e);
                    }}
                    placeholder={`STAGE8에게 남길 피드백을 자유롭게 서술해주세요 (최대 250자)`}
                />
                <Row44>
                    <ErrorMessage pass={false}>
                        {feedbackData.message}
                    </ErrorMessage>
                    <Count activate={feedbackData.length > 0}>
                        {feedbackData.count + "/250"}
                    </Count>
                </Row44>
            </Main>
            <Footer>
                <Row2>
                    <BlankButton
                        onClick={close}
                        width={90}
                        height={40}
                        text="취소"
                    />
                    <Button
                        style={{ marginLeft: "auto", width: 90, height: 40 }}
                        onClick={withdrawal}
                    >
                        회원 탈퇴
                    </Button>
                </Row2>
            </Footer>
        </Modal4>
    );
}
const Row44 = styled.div`
    width: 666px;
`;
const CheckBoxText = styled.div`
    position: relative;
    top: 2px;
`;
const Footer = styled.div`
    border-top: 1px solid #828282;
    padding: 24px;
`;
const ErrorMessage = styled.div`
    color: ${props => (props.pass ? "#06BE34" : "#FF4E45")};
    position: absolute;
    margin-top: 4px;
    ${SpoqaHansSansNeo(16, 24)};
`;
const Count = styled.div`
    margin-top: 4px;
    margin-left: auto;
    ${SpoqaHansSansNeo(16, 24)};
    text-align: right;
    color: ${props => (props.activate ? "#F7F7F7" : "#828282")};
`;
const TermsButton = styled.div`
    &:hover {
        text-decoration: underline;
    }
    cursor: pointer;
    margin-left: 4px;
    ${SpoqaHansSansNeo(12, 14, 700, "#F7F7F7", "0em")};
`;
const Row2 = styled.div`
    display: flex;
`;
const Text5 = styled.div`
    margin-bottom: 16px;
    ${SpoqaHansSansNeo(16, 20)};
`;
const Text4 = styled.div`
    ${SpoqaHansSansNeo(16, 20, 700)};
    margin-top: 24px;
    margin-bottom: 20px;
`;
const Row1 = styled.div`
    height: 40px;
    ${SpoqaHansSansNeo(16, 24)};
    display: flex;
    align-items: center;
    padding-left: 12px;
`;
const Text3 = styled.div`
    padding-top: 24px;
    margin-bottom: 8px;
    ${SpoqaHansSansNeo(16, 20, 700)};
`;
const Text2 = styled.div`
    ${SpoqaHansSansNeo(12, 14, 400, "#F7F7F7", "0em")};
`;
const Title = styled.div`
    margin-bottom: 20px;
    ${SpoqaHansSansNeo(20, 24, 700)};
`;
const Text1 = styled.div`
    ${SpoqaHansSansNeo(16, 20)};
    margin-bottom: 8px;
`;
const Header = styled.div`
    height: 72px;
    padding: 24px;
    display: flex;
`;
const HeaderText = styled.div`
    ${SpoqaHansSansNeo(20, 24, 700)};
`;
const X = styled.img`
    cursor: pointer;
    width: 24px;
    height: 24px;
    margin-left: auto;
`;
const Main = styled.div`
    padding: 24px;
    border-top: 1px solid #828282;
    background: #0e0e10;
    position: relative;
`;
