import { useRecoilValueLoadable, useSetRecoilState } from "recoil";
import { VPagination } from "src/ts/components/pagination/pagination";
import {
    testimonialPage,
    testimonialSelector,
} from "src/ts/models/artistProfile/about";
import { VTestimonial } from "./vTestimonial";

export const Testimonial = () => {
    const data = useRecoilValueLoadable(testimonialSelector);
    const setTestimonialPage = useSetRecoilState(testimonialPage);

    switch (data.state) {
        case "hasValue":
            if (data.contents !== undefined) {
                return (
                    <>
                        <VTestimonial contents={data.contents.data} />
                        <VPagination
                            setPage={setTestimonialPage}
                            data={data.contents.pagination}
                        />
                    </>
                );
            } else {
                return <></>;
            }

        case "loading":
            return <></>;
        case "hasError":
            throw data.contents;
    }
};
