import React from "react";
import styled from "styled-components";
import line from "../../images/line.svg";
import { MiniCircle } from "./miniCircle";
export function Progress({ data }) {
    const converter = () => {
        if (data === "의뢰") {
            return 1;
        } else if (data === "견적 확인 및 선택") {
            return 2;
        } else if (data === "세부 사항 협의") {
            return 3;
        } else if (data === "계약") {
            return 4;
        } else if (data === "결제") {
            return 5;
        } else if (data === "결과물 수령") {
            return 6;
        }
    };
    const status = converter();
    const selectStatus = num => {
        if (status === num) {
            if (num === 6) {
                return "complete";
            } else return "current";
        } else if (status > num) {
            return "complete";
        } else {
            return "default";
        }
    };
    return (
        <Container>
            <img style={{ marginTop: 26 }} src={line}></img>
            <Current>
                <MiniCircle
                    style={{ width: 195, left: -90 }}
                    number={1}
                    state={selectStatus(1)}
                    status="의뢰"
                />
                <MiniCircle
                    style={{ width: 195, left: -90 }}
                    number={2}
                    state={selectStatus(2)}
                    status="견적 확인 및 선택"
                />
                <MiniCircle
                    style={{ width: 195, left: -90 }}
                    number={3}
                    state={selectStatus(3)}
                    status="세부 사항 협의"
                />
                <MiniCircle
                    style={{ width: 195, left: -90 }}
                    number={4}
                    state={selectStatus(4)}
                    status="계약"
                />
                <MiniCircle
                    style={{ width: 195, left: -90 }}
                    number={5}
                    state={selectStatus(5)}
                    status="결제"
                />
                <MiniCircle
                    style={{ width: 90, left: -35 }}
                    number={6}
                    state={selectStatus(6)}
                    status="결과물 수령"
                />
            </Current>
        </Container>
    );
}

const Container = styled.div``;
const Current = styled.div`
    display: flex;
    position: relative;
    top: -15px;
`;
