import { BREAKPOINT_SMALL } from "src/ts/constants/common";
import { IDropdownStateNode } from "src/ts/models";
import styled from "styled-components";
import { makeCenter, SpoqaHansSansNeo } from "../../styles/css";
import { CheckBox } from "../button/checkBox";

interface IDropDownRow {
    isOpen: boolean;
    content: IDropdownStateNode;
    checkBox?: boolean;
    activate?: boolean;
    onCreate?: Function;
    onRemove?: Function;
    onToggle?: Function;
}
interface IContainer {
    isOpen: boolean;
    check: boolean;
}
export function DropDownRow({
    isOpen,
    content,
    checkBox = false,
    onCreate,
    onRemove,
    onToggle,
}: IDropDownRow) {
    const { id, activate, text } = content;
    const onClick = () => {
        if (isOpen) {
            if (onToggle) onToggle(id);
            if (activate) {
                if (onRemove) onRemove(id);
            } else {
                if (onCreate) onCreate(content);
            }
        }
    };
    return (
        <Container check={activate} onClick={onClick} isOpen={isOpen}>
            {checkBox && <CheckBox onClick={() => {}} check={activate} />}
            <Text>{text}</Text>
        </Container>
    );
}
const Container = styled.div<IContainer>`
    visibility: ${props => !props.isOpen && "hidden"};
    display: ${props => !props.isOpen && "none"};
    box-sizing: border-box;
    cursor: ${props => props.isOpen && "pointer"};
    transition: all 0.4s;
    &:hover {
        opacity: 1;
        background: ${props => (props.check ? "#BD03FA" : "#4A4A4A")};
    }
    background: ${props => (props.check ? "#BD03FA" : "#0E0E10")};
    &:active {
        opacity: 1;
        background: #bd03fa;
    }
    ${makeCenter(0)}
    height:40px;
    padding: 8px 12px;
`;
const Text = styled.div`
    box-sizing: border-box;
    z-index: 5;
    -ms-user-select: none;
    -moz-user-select: -moz-none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    user-select: none;
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
        ${SpoqaHansSansNeo(16, 24, 700)}
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        ${SpoqaHansSansNeo(14, 16, 400)}
    }
`;
