import React, { useState } from "react";
import styled from "styled-components";
import { BREAKPOINT_SMALL } from "../../constants/constants";
import { DisplayFlex, DragNone, SpoqaHansSansNeo } from "../../css/css";
import chevron from '../../images/chevron.svg'
const options = ["팔로워순", "가나다순"];

export function SortMyArtists({ setSort }) {
    const [isOpen, setIsOpen] = useState(false);
    const [selectedOption, setSelectedOption] = useState('정렬');
    const toggling = () => { setIsOpen(!isOpen); }
    const onOptionClicked = value => () => {
        setSelectedOption(value);
        let sortKey = null;
        switch (value) {
            case "팔로워순":
                sortKey = 'followCount';
                break;
            case "가나다순":
                sortKey = 'nameKO';
                break;
        }
        setIsOpen(false)
        setSort(sortKey)
    };

    return (
        <Container>
            <Header onClick={toggling} isOpen={isOpen}>
                <Text>{selectedOption}</Text>
                <IMG isOpen={isOpen} src={chevron} />
            </Header>
            <DropDownList isOpen={isOpen} >
                {options.map(option => (
                    <ListItem
                        selected={(selectedOption === option) ? true : false}
                        onClick={onOptionClicked(option)} key={Math.random()}>
                        <Text>{option}</Text>
                    </ListItem>
                ))}
            </DropDownList>
        </Container>
    );
}
const Container = styled.div`
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
        height: 40px;
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        height: 32px;
    }
    margin-left: auto;
    margin-top:20px;
`
const Header = styled.div`
    z-index:5;
    &:hover {
        opacity: 1;
        border-color: #BD03FA;
    }
    transition: all 0.2s;
    background-color: #01010B;
    position:relative;
    cursor: pointer;
    border: 1px solid ;
    border-radius: 4px;
    ${DisplayFlex("", "center")}
    border-color: ${props => props.isOpen ? '#BD03FA' : 'var(--gravel)'};
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
        height: 40px;
        width: 248px;
        padding: 8px 12px;
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        height: 32px;
        width: 100px;
        padding: 4px 12px;
    }
`;
const IMG = styled.img`
    width: 24px;
    height: 24px;
    -ms-user-select: none; 
    -moz-user-select: -moz-none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    user-select: none;
    margin-left: auto;
    transition: all ease 0.5s;
    transform: ${props => props.isOpen && 'rotate( -180deg )'};
`
const DropDownList = styled.ul`
    pointer-events: ${props => !props.isOpen && 'none'};
    visibility: ${props => (!props.isOpen && "hidden")};
    z-index:4;
    position: relative;
    background: #0E0E10;
    border-radius: 4px;
    transition: all 0.4s;
    opacity: ${props => (props.isOpen ? "1" : "0")};
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
        width: 226px;
        padding: 8px 0px;
        top: ${props => (props.isOpen ? '8px' : '-48px')};
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        height: 112px;
        width: 100px;
        padding: 8px 0px;
        top: ${props => (props.isOpen ? '8px' : '-32px')};
    }
`;
const ListItem = styled.li`
    cursor: pointer;
    transition: all 0.3s;
    &:hover {
        opacity: 1;
        background: ${props => (props.selected ? '#BD03FA' : '#4A4A4A')};
    }
    background: ${props => (props.selected ? '#BD03FA' : '#0E0E10')};
    &:active{
        opacity: 1;
        background: #BD03FA;
    }
    ${DisplayFlex("", "center")}
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
        padding: 8px 12px;
        height: 40px;
        width: 226px;
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        padding: 8px 12px;
        height: 32px;
        width: 100px;
    }
`;
const Text = styled.div`
    z-index:2;
    position: relative;
    ${DragNone}
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
        ${SpoqaHansSansNeo(16, 24, 700)}
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        ${SpoqaHansSansNeo(14, 16, 400, '#F7F7F7', '0em')}
    }
`