import React, { useState } from "react";
import styled from "styled-components";
import { BREAKPOINT_SMALL } from "../../../../../constants/constants";
import { DisplayFlex, SpoqaHansSansNeo } from '../../../../../css/css';
import chevron from '../../../../../images/chevron.svg'

const options = ["모든 아티스트", "퍼포머", "프로듀서"];

export function ArtistType({ setArtistType }) {
    const [isOpen, setIsOpen] = useState(false);
    const [selectedOption, setSelectedOption] = useState("모든 아티스트");

    const toggling = () => setIsOpen(!isOpen);

    const onOptionClicked = value => () => {
        setSelectedOption(value);
        setIsOpen(false);
        let sortKey = [];
        switch (value) {
            case "모든 아티스트":
                break;
            case "퍼포머":
                sortKey.push('"performer"');
                break;
            case "프로듀서":
                sortKey.push('"producer"');
                break;
        }
        setArtistType(sortKey)
    };

    return (
        <Container>
            <Header onClick={toggling} isOpen={isOpen}>
                <Text>{selectedOption || "모든 아티스트"}</Text>
                <IMG isOpen={isOpen} src={chevron} />
            </Header>
            <DropDownList isOpen={isOpen} >
                {options.map(option => (
                        <ListItem 
                            selected={(selectedOption === option) ? true : false}
                            onClick={onOptionClicked(option)} key={Math.random()}>
                            <Text>{option}</Text>
                        </ListItem>
                ))}
            </DropDownList>
        </Container>
    );
}
const Container = styled("div")`
    margin-right: 20px;
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
        height: 40px;
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        height: 32px;
        margin-bottom: 8px;
    }
`
const Header = styled.div`
    z-index: 3;
    &:hover {
        opacity: 1;
        border-color: #BD03FA;
    }
    transition: all 0.2s;
    background-color: #01010B;
    position:relative;
    cursor: pointer;
    border: 1px solid ;
    border-radius: 4px;
    ${DisplayFlex("", "center")};
    border-color: ${props => props.isOpen ? '#BD03FA' : 'var(--gravel)'};
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
        height: 40px;
        width: 248px;
        padding: 8px 12px;
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        height: 32px;
        width: 333px;
        padding: 4px 12px;
    }
`;
const IMG = styled.img`
    -ms-user-select: none; 
    -moz-user-select: -moz-none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    user-select: none;
    margin-left: auto;
    transition: all ease 0.5s;
    transform: ${props => props.isOpen && 'rotate( -180deg )'};
`
const DropDownList = styled.ul`
    visibility: ${props => (!props.isOpen &&  "hidden")};
    z-index:2;
    padding: 8px 0px;
    position: relative;
    top: 8px;
    height: 136px;
    width: 248px;
    background: #0E0E10;
    border-radius: 4px;
    transition: all 0.4s;
    opacity: ${props => (props.isOpen ? "1" : "0")};
    top: ${props => (props.isOpen ? `8px` : `-48px`)};
`;
const ListItem = styled.li`
    cursor: pointer;
    transition: all 0.3s;
    &:hover {
        opacity: 1;
        background: ${props => (props.selected ? '#BD03FA' : '#4A4A4A')};
    }
    background: ${props => (props.selected ? '#BD03FA' : '#0E0E10')};
    &:active{
        opacity: 1;
        background: #BD03FA;
    }
    ${DisplayFlex("", "center")}
    padding: 8px 12px;
    height: 40px;
    width: 248px;
`;
const Text = styled.div`
    z-index:2;
    -ms-user-select: none; 
    -moz-user-select: -moz-none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    user-select: none;
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
        ${SpoqaHansSansNeo(16, 24, 700)}
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        ${SpoqaHansSansNeo(14, 16, 400)}
    }
`