import {
    useRecoilValue,
    useRecoilValueLoadable,
    useSetRecoilState,
} from "recoil";
import { VPagination } from "src/ts/components/pagination/pagination";
import {
    BREAKPOINT_LARGE,
    BREAKPOINT_MIDDLE,
    BREAKPOINT_SMALL,
} from "src/ts/constants";
import { currentSizeSelector } from "src/ts/models";
import {
    artistPortFolioPageState,
    artistPortFolioSelector,
} from "src/ts/models/artistProfile/artistPortFolio";
import { makeCenter, SpoqaHansSansNeo } from "src/ts/styles/css";
import { Track } from "src/view/artistProfile/2.Portfolio/1.Track/track";
import styled from "styled-components";
import { isEmptyObject } from "../../lib/lib";
import { AddArtistProject } from "./addArtistProject";
import { ProjectColumn } from "./projectColumn";
import { Video } from "./video";

export function ArtistProjectsAndSongs(props) {
    const performances = useRecoilValueLoadable(
        artistPortFolioSelector("performances"),
    );
    const setPerformancesPage = useSetRecoilState(
        artistPortFolioPageState("performances"),
    );
    const etc = useRecoilValueLoadable(artistPortFolioSelector("etc"));
    const setEtc = useSetRecoilState(artistPortFolioPageState("etc"));
    const appearances = useRecoilValueLoadable(
        artistPortFolioSelector("appearances"),
    );
    const setAppearances = useSetRecoilState(
        artistPortFolioPageState("appearances"),
    );
    const records = useRecoilValueLoadable(artistPortFolioSelector("records"));
    const setRecords = useSetRecoilState(artistPortFolioPageState("records"));
    const currentSize = useRecoilValue(currentSizeSelector);

    if (
        records.state === "hasValue" &&
        appearances.state === "hasValue" &&
        etc.state === "hasValue" &&
        performances.state === "hasValue"
    ) {
        const recordsData = records.contents.data.data;
        const recordsPagenation = records.contents.data.pagination;
        const AppearancesData = appearances.contents.data.data;
        const AppearancesPagenation = appearances.contents.data.pagination;
        const etcData = etc.contents.data.data;
        const etcPagenation = etc.contents.data.pagination;
        const performancesData = performances.contents.data.data;
        const performancesPagenation = performances.contents.data.pagination;
        return (
            <Container>
                <InnerContainer>
                    <Track songs={props.data.songs} />
                    <Video
                        videos={props.data.videos}
                        currentSize={currentSize}
                    ></Video>
                    <InnerContainer2>
                        <TopRow>
                            <Text>프로젝트</Text>
                            <AddArtistProject />
                        </TopRow>
                    </InnerContainer2>
                    {!isEmptyObject(recordsData) && (
                        <>
                            <MajorHeading> · 음원</MajorHeading>
                            <ProjectList>
                                {recordsData.map((project, i) => {
                                    return (
                                        <ProjectColumn
                                            key={i}
                                            i={i}
                                            project={project}
                                            marginRight={undefined}
                                            marginTop={undefined}
                                        />
                                    );
                                })}
                            </ProjectList>
                            <VPagination
                                setPage={setRecords}
                                data={recordsPagenation}
                            />
                        </>
                    )}
                    {!isEmptyObject(performancesData) && (
                        <>
                            <MajorHeading> · 공연/페스티벌</MajorHeading>
                            <ProjectList>
                                {performancesData.map((project, i) => {
                                    return (
                                        <ProjectColumn
                                            key={i}
                                            i={i}
                                            project={project}
                                            marginRight={undefined}
                                            marginTop={undefined}
                                        />
                                    );
                                })}
                            </ProjectList>
                            <VPagination
                                setPage={setPerformancesPage}
                                data={performancesPagenation}
                            />
                        </>
                    )}
                    {!isEmptyObject(AppearancesData) && (
                        <>
                            <MajorHeading> · 방송</MajorHeading>
                            <ProjectList>
                                {AppearancesData.map((project, i) => {
                                    return (
                                        <ProjectColumn
                                            key={i}
                                            i={i}
                                            project={project}
                                            marginRight={undefined}
                                            marginTop={undefined}
                                        />
                                    );
                                })}
                            </ProjectList>
                            <VPagination
                                setPage={setAppearances}
                                data={AppearancesPagenation}
                            />
                        </>
                    )}
                    {!isEmptyObject(etcData) && (
                        <>
                            <MajorHeading> · 기타</MajorHeading>
                            <ProjectList>
                                {etcData.map((project, i) => {
                                    return (
                                        <ProjectColumn
                                            key={i}
                                            i={i}
                                            project={project}
                                            marginRight={undefined}
                                            marginTop={undefined}
                                        />
                                    );
                                })}
                            </ProjectList>
                            <VPagination
                                setPage={setEtc}
                                data={etcPagenation}
                            />
                        </>
                    )}
                </InnerContainer>
            </Container>
        );
    } else if (
        records.state === "hasError" ||
        appearances.state === "hasError" ||
        etc.state === "hasError" ||
        performances.state === "hasError"
    ) {
        return <></>;
    } else {
        return <></>;
    }
}

const MajorHeading = styled.div`
    margin-top: 20px;
    display: flex;
    ${SpoqaHansSansNeo(20, 24, 700)}
`;
const InnerContainer2 = styled.div`
    padding: 40px 20px;
`;

const InnerContainer = styled.div`
    @media screen and (min-width: ${BREAKPOINT_LARGE}px) {
        width: 1196px;
        padding: 40px 20px;
    }
    @media screen and (min-width: ${BREAKPOINT_MIDDLE}px) and (max-width: ${BREAKPOINT_LARGE}px) {
        width: 1140px;
        padding: 40px 20px;
    }
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) and (max-width: ${BREAKPOINT_MIDDLE}px) {
        width: 754px;
        padding: 40px 20px;
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        width: 100%;
        padding: 20px 0px;
    }
`;
const SendRequest = styled.div`
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
        ${makeCenter()}
        margin-left: auto;
        height: 40px;
        width: 231px;
    }
    display: flex;
    cursor: pointer;
    border-radius: 4px;
`;

const TopRow = styled.div`
    ${makeCenter(0, 1)}
    width: 100%;
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
        display: flex;
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        margin-left: 20px;
    }
`;
const ProjectList = styled.div`
    display: flex;
    margin-top: 20px;
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
        flex-wrap: wrap;
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        -ms-overflow-style: none;
        overflow-x: scroll;
        -ms-scroll-chaining: none;
        -ms-scroll-snap-type: mandatory;
        scroll-snap-type: mandatory;
        scrollbar-width: none; /* Firefox */
        &::-webkit-scrollbar {
            display: none; /* Chrome, Safari, Opera*/
        }
        -ms-scroll-snap-points-x: snapInterval(0%, 10%);
    }
`;
const Text = styled.div`
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
        ${SpoqaHansSansNeo(34, 40, 700)}
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        ${SpoqaHansSansNeo(20, 24, 700)}
    }
`;
const Container = styled.div`
    ${makeCenter(1, 0)};
`;
