import axios from "axios";
import React from "react";
import { useHistory } from "react-router";
import styled from "styled-components";
import { API_SERVER2 } from "../../../../constants/constants";
import { DisplayFlex } from "../../../../css/css";
import { useInputs } from "../../../../hook/useInputs";
import arrowLeft from "../../../../images/arrow-left.svg";
import x from "../../../../images/x.svg";

export function MobileSearchBar({ setMobileSearch, mobileSearch }) {
  const getArtistData = async () => {
    const url = API_SERVER2 + `/artists/text-search?keyword=${search}`;
    const response = await axios.get( url,{withCredentials:true} );

    return response.data.data.artists
  }
  const history = useHistory()
  const [{ search }, onChange, reset] = useInputs({
    search: ''
  });
  const getResults = () => {
    const res = getArtistData()
    res.then((data) => {
      if(data === undefined){
        history.push('/searchresults', {data : null, search:search})
        window.scrollTo(0,0);
      }
      else{
        history.push('/searchresults', {data : data, search:search})
        window.scrollTo(0,0);
      }
    })
  }
  const handleKeyPress = (e) => {
    if (e.key === "Enter") {
      getResults();
    }
  };
  
  return (
    <Container mobileSearch={mobileSearch}>
      <LeftArrow src={arrowLeft} onClick={()=>setMobileSearch(false)}/>
      <UnderLinedInput 
        value={search}
        placeholder='어떤 아티스트를 찾으시나요?'
        onChange={onChange}
        name='search'
        onKeyPress={(e) => handleKeyPress(e)}/>
      <Erase src={x} onClick={()=>{reset();}}/>
    </Container>
  );
}
const Erase = styled.img`
    cursor: pointer;
`
const UnderLinedInput = styled.input`
  width: 256px;
  margin: 0px 16px;
  border: 0px;
  border-bottom: 1px solid #DBDBDB;
  padding-bottom: 4px;
  color: white;
  box-sizing: border-box;
  &::-webkit-outer-spin-button{
      -webkit-appearance: none;
      margin: 0;
  };
  &::-webkit-inner-spin-button{
      -webkit-appearance: none;
      margin: 0;
  };
  &:hover{
      outline: none;
  };
  &:focus{
      outline: none;
  };
  background: #0E0E10;
`
const LeftArrow = styled.img`
  cursor: pointer;
`
const Container = styled.div`
  transition: all .5s;
  visibility: ${props=>!props.mobileSearch && 'hidden'};
  opacity: ${props=>props.mobileSearch ? 1 : 0};
  height: 100%;
  padding: 0px 20px;
  position: absolute;
  ${DisplayFlex('center','center')}
`