import styled from "styled-components";
import editButton from "../../images/editButton.svg";

export const DeleteTestimonial = ({ id }) => {
    const [fetchTestimonial] = useDeleteTestimonial();

    return (
        <>
            <Container
                onClick={() => {
                    fetchTestimonial(id);
                }}
            >
                <div
                    style={{
                        marginLeft: "auto",
                        display: "flex",
                        alignItems: "center",
                    }}
                >
                    <PlusImage src={editButton}></PlusImage>
                    <Text>삭제하기</Text>
                </div>
            </Container>
        </>
    );
};
const Text = styled.div`
    ${SpoqaHansSansNeo(16, 20, 700, "#BD03FA")}
    margin-left: 8px;
`;
const PlusImage = styled.img``;
const Container = styled.div`
    cursor: pointer;
    ${makeCenter(0, 1)};
    display: flex;
`;
