import React from "react";
import styled from "styled-components";
import { DisplayFlex, SpoqaHansSansNeo } from "../../css/css";
import check from '../../images/check.svg';

export function MiniCircle({ number, state, style, status }) {
    const colorConverter = () => {
        if (state === 'default') {
            return '#4A4A4A'
        } else if (state === 'current') {
            return '#F7F7F7'
        } else if (state === 'complete') {
            return '#F7F7F7'
        }
    }
    const backgroundcolorConverter = () => {
        if (state === 'default') {
            return '#0E0E10'
        } else if (state === 'current') {
            return '#BD03FA'
        } else if (state === 'complete') {
            return '#BD03FA'
        }
    }
    return (
        <Container style={style}>
            <Position
                state={state}
                color={colorConverter()}
                backgroundColor={backgroundcolorConverter()}>{(state !== 'complete') && <Num>{number}</Num>}</Position>
            {(state === 'complete') && <img src={check} style={{ position: 'absolute', top: 6}} />}
            <Text>{status}</Text>
        </Container>
    );
}
const Num = styled.div`
    position: relative;
    top:1px;
    left:1px;
`
const Position = styled.div`
    ${SpoqaHansSansNeo(12,14,700)};
    border: ${props => props.state && '1px solid #4A4A4A'};
    ${DisplayFlex('center', 'center')}
    color: ${props => props.color};
    background-color: ${props => props.backgroundColor};
    margin-bottom: 7px;
    width: 20px;
    height: 20px;
    border-radius: 50em;
`
const Text = styled.div`
    display: flex;
    position: relative;
    left:1px;
    ${SpoqaHansSansNeo(16,20,700)};
`
const Container = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
`