import React from "react";
import styled from "styled-components";
import { BREAKPOINT_SMALL } from "../../constants/constants";
import { DisplayFlex, SpoqaHansSansNeo } from "../../css/css";

export function TextButton({ style, text, activate, onClick }) {
    return (
        <Container style={style}  onClick={onClick} >
            <Text activate={(text === activate)}>{text}</Text>
            {(text === activate) && <UnderBar />}
        </Container>
    );
}
const UnderBar = styled.div`
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) { 
        width: 40px;
        height: 4px;
        margin-top: 8px;
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        width: 24px;
        height: 4px;
        margin-top: 4px;
    }
    background: #BD03FA;
`
const Text = styled.div`
@media screen and (min-width: ${BREAKPOINT_SMALL}px) { 
    ${SpoqaHansSansNeo(20,24,700,'#BEBEBE')}; 
    color: ${props=>props.activate && '#E0E0E0'};
}
@media screen and (max-width: ${BREAKPOINT_SMALL}px) {
    ${SpoqaHansSansNeo(14,18,700,'#BEBEBE','0em')};
    color: ${props=>props.activate && '#E0E0E0'};
}
    &:hover{
        color: ${props=>props.activate ? '#E0E0E0' : '#BD03FA'};
    }
    transition: all .3s;
`
const Container = styled.div`
    ${DisplayFlex('','center')}
    flex-direction:column;
    cursor: pointer;
`