import React from "react";
import styled from "styled-components";
import { BREAKPOINT_SMALL } from "../../../../constants/constants";
import { DisplayFlex, SpoqaHansSansNeo } from "../../../../css/css";

import like from '../../../../images/like.svg'
import view from '../../../../images/view.svg'

export function ArtistCardData(props) {
  const { followCount, viewCount, artistType } = props;

  return (
    <Container>
      {/* <Row>
        <Followers>
          <IMG src={like} />
          <Number>{followCount}</Number>
        </Followers>
        <Views>
          <IMG src={view} />
          <Number>{viewCount}</Number>
        </Views>
      </Row> */}
      <ArtistType>
        {artistType && artistType.map((data, i) => {
          if (data === 'producer')
            return <BadgeProducer key={i} data={data}>프로듀서</BadgeProducer>
          else if (data === 'performer')
            return <BadgePerformer key={i} data={data}>퍼포머</BadgePerformer>
        })}
      </ArtistType>
    </Container>
  );
}
export const BadgeProducer = styled.div`
  ${DisplayFlex('center', 'center')};
  ${SpoqaHansSansNeo(12, 16, 700, '#01010B')};
  background: #D0B1FF;
  width:60px;
  height:24px;
  border-radius: 4px;
`
export const BadgePerformer = styled.div`
  ${DisplayFlex('center', 'center')};
  ${SpoqaHansSansNeo(12, 16, 700, '#01010B')};
  background: #90CAF9;
  width:50px;
  height:24px;
  margin-right: 4px;
  border-radius: 4px;
`
const ArtistType = styled.div`
  display: flex;
`
const IMG = styled.img`
  @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
    width: 24px;
    height: 24px;
  }
  @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
    width: 16px;
    height: 16px;
  }
`
const Followers = styled.div`
  align-items: center;
  display: flex;
  height: 24px;
`
const Row = styled.div`
    ${DisplayFlex('', 'center')};
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
      margin-bottom: 8px;
  }
  @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
    margin-bottom: 4px;
  }
`
const Number = styled.div`
  margin-left: 4px;
  position: relative;
  @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
    ${SpoqaHansSansNeo(16, 20, 400, '#BEBEBE')}
  }
  @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
    ${SpoqaHansSansNeo(12, 14, 400, '#BEBEBE')}
  }
`
const Container = styled.div`

  @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
    margin-top: 8px;
  }
  @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
    margin-top: 4px;
  }
`

const Views = styled.div`
  ${DisplayFlex('', 'center')};
  margin-left: 14px;
`
