import { atom, selector } from "recoil";
import {
    BREAKPOINT_LARGE,
    BREAKPOINT_MIDDLE,
    BREAKPOINT_SMALL,
} from "../constants/common";
const paddingTop = atom<number>({
    key: "paddingTopState",
    default: 80,
});
const culPaddingTop = () => {
    if (window.innerWidth >= BREAKPOINT_LARGE) {
        return 80;
    } else if (window.innerWidth >= BREAKPOINT_MIDDLE) {
        return 80;
    } else if (window.innerWidth >= BREAKPOINT_SMALL) {
        return 56;
    } else {
        return 56;
    }
};
export const commonPaddingTopSelector = selector({
    key: "commonPaddingTopSelector",
    get: ({ get }) => get(paddingTop),
    set: ({ set }) => {
        set(paddingTop, culPaddingTop);
    },
});
