export function useFetchNameEN() {
    const setArtistTriger = useSetRecoilState(artistTriggerState);

    const nameForURI = getNameForURIFromURI();
    const updateSlogun = useCallback((nameEN: string) => {
        if (nameEN === null) return;

        const data = new FormData();
        data.append("nameEN", nameEN);

        const options: AxiosRequestConfig = {
            method: "PATCH",
            data: data,
            url: API_SERVER2 + `/artists/${nameForURI}/nameEN`,
            withCredentials: true,
        };
        axios(options).then(() => {
            const userData = JSON.parse(
                window.localStorage.getItem("userData") || "",
            );
            userData.nameForURI = nameEN.toLowerCase();
            window.localStorage.setItem("userData", JSON.stringify(userData));
            setArtistTriger(Date.now());
        });
    }, []);

    return [updateSlogun];
}
