export function usePatchSongs() {
    const setArtistTriger = useSetRecoilState(artistTriggerState);
    const nameForURI = getNameForURIFromURI();
    const updateSlogun = useCallback(
        (
            files: any,
            song: any,
            title: string,
            artistName: string,
            albumName: string,
            genre: any[],
        ) => {
            const data = new FormData();
            data.append("files", files);
            data.append("files", song[0]);
            data.append("title", title);
            data.append("artistName", artistName);
            data.append("albumName", albumName);
            data.append("genre", JSON.stringify(genre));

            const options: AxiosRequestConfig = {
                method: "PATCH",
                data: data,
                url: API_SERVER2 + `/artists/${nameForURI}/songs/add`,
                withCredentials: true,
                headers: { "Content-Type": "multipart/form-data" },
            };
            axios(options).then(() => setArtistTriger(Date.now()));
        },
        [],
    );

    return [updateSlogun];
}
export function useUpdateSongs() {
    const setArtistTriger = useSetRecoilState(artistTriggerState);
    const nameForURI = getNameForURIFromURI();
    const updateSlogun = useCallback(
        (
            files: any,
            song: any,
            title: string,
            artistName: string,
            albumName: string,
            genre: any[],
            id,
        ) => {
            const data = new FormData();
            data.append("files", files);
            data.append("files", song[0]);
            data.append("title", title);
            data.append("artistName", artistName);
            data.append("albumName", albumName);
            data.append("genre", JSON.stringify(genre));

            const options: AxiosRequestConfig = {
                method: "PATCH",
                data: data,
                url: API_SERVER2 + `/artists/${nameForURI}/songs/${id}/update`,
                withCredentials: true,
                headers: { "Content-Type": "multipart/form-data" },
            };
            axios(options).then(() => setArtistTriger(Date.now()));
        },
        [],
    );

    return [updateSlogun];
}
