import React from "react";
import { useHistory } from "react-router-dom";
import styled from "styled-components";
import { BREAKPOINT_MIDDLE, BREAKPOINT_SMALL } from "../../../../constants/constants";
import stage8Logo from '../../../../images/stage8Logo.svg';

export function Logo() {
  const history = useHistory();
  return (
    <Container src={stage8Logo} onClick={() => { window.scrollTo(0, 0); history.push('/') }}>

    </Container>
  );
}

const Container = styled.img`
  cursor: pointer;
  @media screen and (min-width: ${BREAKPOINT_MIDDLE}px) {
    width: 200px;
    height: 40px;
  }
  @media screen and (min-width: ${BREAKPOINT_SMALL}px) and (max-width: ${BREAKPOINT_MIDDLE}px) {
    width: 146px;
    height: 30px;
  }
  @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
    width: 92px;
    height: 20px;
    margin-right: 16px;
  }
`