import axios from "axios";
import React, { useEffect, useState } from "react";
import { useHistory, useParams } from "react-router";
import styled from "styled-components";
import { API_SERVER2, BREAKPOINT_LARGE, BREAKPOINT_MIDDLE, BREAKPOINT_SMALL } from "../../../../constants/constants";
import { DisplayFlex, SpoqaHansSansNeo } from "../../../../css/css";
import arrowRight from '../../../../images/arrow-right.svg';
import { ArtistPagination } from "../../../main/4.BrowseArtist/3.ArtistPagination/artistPagination";
import { ProjectColumn } from "./projectColumn";

export function ArtistProject(props) {
    const history = useHistory();
    const artistID = useParams().artistID;
    const [page, setPage] = useState(1);
    const culLimitNum = () => {
        if (props.currentSize === "LARGE" || props.currentSize === "MIDDLE") {
            return 8;
        } else if (props.currentSize === "SMALL") {
            return 9;
        } else {
            return 9;
        }
    }
    const getArtistData = async (kind, setter, setter2) => {
        let form = new FormData()
        form.append('nameForURI', artistID)
        const url = API_SERVER2 + `/portfolios/artist/by-name-uri?page=${page}&limit=${culLimitNum()}&kind=${kind}`;
        const options = {
            method: 'POST',
            url: url,
            withCredentials: true,
            data: form
        };
        axios(options).then(res => {
            setter(res.data.data)
            setter2(res.data.pagination)
        })
    }
    useEffect(() => {
        getArtistData('records', setrecords, setpagination);
        getArtistData('performances', setperformances, setpagination2);
        getArtistData('appearances', setappearances, setpagination3);
        getArtistData('etc', setetc, setpagination4);
    }, [page])
    const [currentSize, setCurrentSize] = useState(props.currentSize);
    useEffect(() => {
        if (props.currentSize !== currentSize) {
            setCurrentSize(props.currentSize)
            getArtistData()
        }
    });
    const [records, setrecords] = useState(null);
    const [pagination, setpagination] = useState(null);
    const [pagination2, setpagination2] = useState(null);
    const [pagination3, setpagination3] = useState(null);
    const [pagination4, setpagination4] = useState(null);

    const [performances, setperformances] = useState(null);
    const [appearances, setappearances] = useState(null);
    const [etc, setetc] = useState(null);


    return (
        (records !== null && performances !== null && appearances !== null && etc !== null) && (
            <Container>
                <InnerContainer>
                    <TopRow>
                        <Text>프로젝트</Text>
                        <SendRequest
                            onClick={() => {
                                if (window.localStorage.getItem('userData')) {
                                    history.push('/stage8/request');
                                    window.scrollTo(0, 0);
                                } else {
                                    history.push('/login');
                                }
                            }}>
                            <Text2>이런 작품 나도 받아보기</Text2>
                            <img style={{ width: 20, height: 20, position: 'relative', top: 1 }} src={arrowRight} />
                        </SendRequest>
                    </TopRow>
                    {records && (
                        <>
                            <MajorHeading> · 음원</MajorHeading>
                            <ProjectList>
                                {records.map((project, i) => {
                                    return <ProjectColumn
                                        key={i}
                                        i={i}
                                        project={project} />;
                                })}
                            </ProjectList></>)}
                    {pagination && (pagination.lastPage > 1) && (props.currentSize !== "MOBILE") && <ArtistPagination pagination={pagination} setPage={setPage} />}
                    {performances && (
                        <>
                            <MajorHeading> · 공연/페스티벌</MajorHeading>
                            <ProjectList>
                                {performances.map((project, i) => {
                                    return <ProjectColumn
                                        key={i}
                                        i={i}
                                        project={project} />;
                                })}
                            </ProjectList></>)}
                    {pagination2 && (pagination2.lastPage > 1) && (props.currentSize !== "MOBILE") && <ArtistPagination pagination={pagination2} setPage={setPage} />}
                    {appearances && (
                        <>
                            <MajorHeading> · 방송</MajorHeading>
                            <ProjectList>
                                {appearances.map((project, i) => {
                                    return <ProjectColumn
                                        key={i}
                                        i={i}
                                        project={project} />;
                                })}
                            </ProjectList></>)}
                    {pagination3 && (pagination3.lastPage > 1) && (props.currentSize !== "MOBILE") && <ArtistPagination pagination={pagination3} setPage={setPage} />}
                    {etc && (
                        <>
                            <MajorHeading> · 기타</MajorHeading>
                            <ProjectList>
                                {etc.map((project, i) => {
                                    return <ProjectColumn
                                        key={i}
                                        i={i}
                                        project={project} />;
                                })}
                            </ProjectList></>)}
                    {pagination4 && (pagination4.lastPage > 1) && (props.currentSize !== "MOBILE") && <ArtistPagination pagination={pagination4} setPage={setPage} />}
                </InnerContainer>
            </Container>
        )
    );
}

const MajorHeading = styled.div`
    margin-top: 20px;
    display: flex;
    ${SpoqaHansSansNeo(20, 24, 700)}
`

const InnerContainer = styled.div`
    @media screen and (min-width: ${BREAKPOINT_LARGE}px) {
        width: 1196px;
        padding: 40px 20px;
    }
    @media screen and (min-width: ${BREAKPOINT_MIDDLE}px) and (max-width: ${BREAKPOINT_LARGE}px) {
        width: 1140px;
        padding: 40px 20px;
    }
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) and (max-width: ${BREAKPOINT_MIDDLE}px){
        width: 754px;
        padding: 40px 20px;
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        width: 100%;
        padding: 20px 0px;
    }
`
const SendRequest = styled.div`
  @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
    ${DisplayFlex('center', 'center')}
    margin-left: auto;
    height: 40px;
    width: 231px;
  }
  display: flex;
  cursor: pointer;
  border-radius: 4px;
`
const Text2 = styled.div`
  margin-right: 4px;
  @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
    ${SpoqaHansSansNeo(16, 24, 700)};   
  } 
  @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
    ${SpoqaHansSansNeo(16, 24, 400)}; 
  }
`
const TopRow = styled.div`
  width: 100%;
  @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
    display: flex;
  }
  @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
    margin-left: 20px;
  }
`
const ProjectList = styled.div`
    display:flex;
    margin-top: 20px;
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) { 
        flex-wrap: wrap;
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        -ms-overflow-style: none;
        overflow-x: scroll;
        -ms-scroll-chaining: none;
        -ms-scroll-snap-type: mandatory;
        scroll-snap-type: mandatory;
        scrollbar-width: none; /* Firefox */
        &::-webkit-scrollbar {
            display: none; /* Chrome, Safari, Opera*/
        }
        -ms-scroll-snap-points-x: snapInterval(0%, 10%);
    }
`
const Text = styled.div`
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) { 
        ${SpoqaHansSansNeo(34, 40, 700)}
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        ${SpoqaHansSansNeo(20, 24, 700)}
    }
`
const Container = styled.div`
    ${DisplayFlex('center', '')};
`