import { useState } from "react";
import { useSetRecoilState } from "recoil";
import { Button } from "src/ts/components/button/button";
import {
    BREAKPOINT_LARGE,
    BREAKPOINT_MIDDLE,
    BREAKPOINT_SMALL,
} from "src/ts/constants";
import { ArtistInfo } from "src/ts/models/artistProfile/about";
import { currentModaldataState } from "src/ts/models/modal";
import {
    FontBig,
    FontNormal,
    makeCenter,
    SpoqaHansSansNeo,
} from "src/ts/styles/css";
import styled from "styled-components";
import camera from "../../images/camera.svg";
import { useSetArtistProfile } from "../../models/artistProfile/profileBanner";
import { AddTestimonial } from "./addTestimonial";
import { handleFileInput } from "./handleFileInput";
import { Testimonial } from "./testimonial";
import { UpdateIntroduction } from "./updateIntroduction";
import { UpdateSlogun } from "./updateSlogun";
import { VArtistDetail } from "./vArtistDetail";
export interface IInnerContainer {
    data: ArtistInfo;
}

export const AboutData = ({ data }: IInnerContainer) => {
    const setModal = useSetRecoilState(currentModaldataState);
    const [setArtistProfile] = useSetArtistProfile();
    const [selectedFile, setSelectedFile] = useState(null);
    return (
        <Container>
            <InnerContainer>
                <Row1>
                    <Column1>
                        {!data.slogun ? (
                            <EditRow2 style={{}}>
                                <EditRow3>
                                    <FontBig style={{ marginRight: 10 }}>
                                        슬로건
                                    </FontBig>
                                    <UpdateSlogun existedData={""} />
                                </EditRow3>
                                <Explanation>
                                    <FontNormal style={{ color: "#a0a0a0" }}>
                                        본인이 어떤 아티스트인지 1-2줄로 짧고
                                        임팩트있게 소개해주세요.
                                    </FontNormal>
                                </Explanation>
                            </EditRow2>
                        ) : (
                            <EditRow>
                                <Slogun>
                                    {data.slogun}
                                    <UpdateSlogun existedData={data.slogun} />
                                </Slogun>
                            </EditRow>
                        )}
                        {!data.introduction ? (
                            <Introduction>
                                <EditRow3>
                                    <FontBig style={{ marginRight: 10 }}>
                                        자기소개
                                    </FontBig>
                                    <UpdateIntroduction existedData={""} />
                                </EditRow3>
                                <Explanation>
                                    <FontNormal style={{ color: "#a0a0a0" }}>
                                        본인의 음악 경력, 아티스트 이미지 등을
                                        간략하게 소개해주세요.
                                    </FontNormal>
                                </Explanation>
                            </Introduction>
                        ) : (
                            <Introduction>
                                {data.introduction}
                                <UpdateIntroduction
                                    existedData={data.introduction}
                                />
                            </Introduction>
                        )}
                        <AddTestimonial />
                        <Testimonial />
                    </Column1>
                    <Column2>
                        <ProfileContainer>
                            <ProfileURI
                                src={selectedFile || data.profileURI}
                            ></ProfileURI>
                            <Button
                                round
                                label={true}
                                style={{ position: "absolute", bottom: 40 }}
                                width={148}
                                height={40}
                                onClick={() => {}}
                            >
                                <input
                                    style={{ display: "none" }}
                                    accept="image/*"
                                    type="file"
                                    name="file"
                                    onChange={e =>
                                        handleFileInput(
                                            e,
                                            setModal,
                                            setArtistProfile,
                                            setSelectedFile,
                                        )
                                    }
                                />
                                <img
                                    alt="camera"
                                    src={camera}
                                    style={{ marginRight: 5 }}
                                />
                                업데이트
                            </Button>
                        </ProfileContainer>
                        <VArtistDetail data={data} />
                    </Column2>
                </Row1>
            </InnerContainer>
        </Container>
    );
};

const EditRow3 = styled.div`
    display: flex;
`;
const Explanation = styled.div`
    margin-top: 20px;
`;
const InnerContainer = styled.div`
    padding: 0px 20px;
    padding-top: 20px;
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
        margin-top: 40px;
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        margin-top: 0px;
    }
`;

const ProfileContainer = styled.div`
    position: relative;
    ${makeCenter(1, 0)}
    @media screen and (min-width: ${BREAKPOINT_LARGE}px) {
        width: 548px;
        height: 548px;
    }
    @media screen and (min-width: ${BREAKPOINT_MIDDLE}px) and (max-width: ${BREAKPOINT_LARGE}px) {
        width: 540px;
        height: 540px;
    }
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) and (max-width: ${BREAKPOINT_MIDDLE}px) {
        width: 375px;
        height: 375px;
        margin-right: 20px;
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        width: 332px;
        height: 332px;
        margin-top: 8px;
    }
`;
const Container = styled.div`
    margin-bottom: 80px;
    width: 100%;
    ${makeCenter(0, 1)};
    flex-direction: column;
    background-color: #01010b;
    /* background-color: red; */
`;
const EditRow2 = styled.div`
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
        margin-bottom: 44px;
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        margin-bottom: 40px;
    }
`;
const EditRow = styled.div`
    ${makeCenter(0, 1)};

    @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
        margin-bottom: 24px;
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        margin-bottom: 20px;
    }
`;
const ProfileURI = styled.img`
    @media screen and (min-width: ${BREAKPOINT_LARGE}px) {
        width: 548px;
        height: 548px;
    }
    @media screen and (min-width: ${BREAKPOINT_MIDDLE}px) and (max-width: ${BREAKPOINT_LARGE}px) {
        width: 540px;
        height: 540px;
    }
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) and (max-width: ${BREAKPOINT_MIDDLE}px) {
        width: 375px;
        height: 375px;
        margin-right: 20px;
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        width: 332px;
        height: 332px;
        margin-top: 8px;
    }
`;

const Column1 = styled.div`
    @media screen and (min-width: ${BREAKPOINT_LARGE}px) {
        width: 548px;
        margin-right: 60px;
    }
    @media screen and (min-width: ${BREAKPOINT_MIDDLE}px) and (max-width: ${BREAKPOINT_LARGE}px) {
        width: 540px;
        margin-right: 20px;
    }
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) and (max-width: ${BREAKPOINT_MIDDLE}px) {
        width: 714px;
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        width: 335px;
    }
`;
const Column2 = styled.div`
    position: relative;
`;
const Row1 = styled.div`
    display: flex;
    @media screen and (min-width: ${BREAKPOINT_MIDDLE}px) {
        padding-bottom: 40px;
    }
    @media screen and (max-width: ${BREAKPOINT_MIDDLE}px) {
        padding-bottom: 0px;
    }
`;

const Introduction = styled.div`
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
        ${SpoqaHansSansNeo(16, 20, 400, "#BEBEBE")}
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        ${SpoqaHansSansNeo(14, 18, 400, "#BEBEBE")}
    }
    margin-bottom: 40px;
    white-space: break-spaces;
`;
const Slogun = styled.span`
    display: flex;
    @media screen and (min-width: ${BREAKPOINT_LARGE}px) {
        ${SpoqaHansSansNeo(34, 40, 700)}
    }
    @media screen and (min-width: ${BREAKPOINT_MIDDLE}px) and (max-width: ${BREAKPOINT_LARGE}px) {
        ${SpoqaHansSansNeo(34, 40, 700)}
    }
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) and (max-width: ${BREAKPOINT_MIDDLE}px) {
        ${SpoqaHansSansNeo(34, 40, 700)}
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        ${SpoqaHansSansNeo(20, 24, 700)}
        margin-bottom: 20px;
    }
`;
