import axios from "axios";
import { useState } from "react";
import { BlankButton } from "src/ts/components/button/blankButton";
import { Button } from "src/ts/components/button/button";
import { InputForm } from "src/ts/components/input/inputForm";
import {
    Modal,
    ModalFooter,
    ModalHeader,
    ModalMain,
} from "src/ts/components/modal/modal";
import { API_SERVER2 } from "src/ts/constants";
import { onChange } from "src/ts/lib/lib";
import { usePatchVideo } from "src/ts/models/artistProfile/profileBanner";
import { makeCenter, SpoqaHansSansNeo } from "src/ts/styles/css";
import styled from "styled-components";
import plus from "../../images/plus.svg";

export const AddVideo = () => {
    const [isOpen, setIsOpen] = useState(false);
    const [fetchVideo] = usePatchVideo();
    const [input, setInput] = useState("");
    const [activate, setActivate] = useState(true);
    const [videoData, setVideoData] = useState({
        pass: false,
        message: "",
        requirement: false,
    });
    const validateVideo = e => {
        const value = e.target.value;
        if (value === "") {
            setVideoData({
                pass: false,
                message: "",
                requirement: false,
            });
            return;
        }
        const duplicate = async () => {
            let form = new FormData();
            form.append("videoUrl", value);
            const url = API_SERVER2 + `/auth/check-videourl`;

            const response = await axios.post(url, form, {
                withCredentials: true,
            });
            return response;
        };
        duplicate()
            .then(function (data) {
                setVideoData({
                    pass: true,
                    message: "사용가능한 Youtube url입니다.",
                    requirement: false,
                });
            })
            .catch(er => {
                setVideoData({
                    pass: false,
                    message: "유효하지 않은 Youtube url입니다.",
                    requirement: true,
                });
            });
    };
    return (
        <>
            <AddButton
                onClick={() => {
                    setIsOpen(true);
                }}
            >
                <PlusImage src={plus}></PlusImage>
                <Text2>동영상 추가</Text2>
            </AddButton>
            <Modal isOpen={isOpen} empty={false}>
                {isOpen && (
                    <>
                        <ModalHeader>{"동영상 추가"}</ModalHeader>
                        <ModalMain>
                            <InputForm
                                marginBottom={48}
                                error={videoData.requirement}
                                errorMessage={videoData.message}
                                value={input}
                                onChange={onChange(setInput, validateVideo)}
                                explanation="동영상 링크를 입력해주세요."
                                placeholder="https://www.youtube.com/watch?v=y5pqNsGlEio"
                            />
                        </ModalMain>
                        <ModalFooter>
                            <BlankButton
                                height={40}
                                width={96}
                                onClick={() => {
                                    setIsOpen(false);
                                }}
                            >
                                취소
                            </BlankButton>
                            <Button
                                rightAlign={true}
                                height={40}
                                width={96}
                                activate={activate}
                                onClick={() => {
                                    fetchVideo(input);
                                    setActivate(false);
                                }}
                            >
                                {"확인"}
                            </Button>
                        </ModalFooter>
                    </>
                )}
            </Modal>
        </>
    );
};
const Text2 = styled.div`
    ${makeCenter(0, 1)}
    ${SpoqaHansSansNeo(16, 20, 700, "#BD03FA")}
margin-left: 8px;
`;
const PlusImage = styled.img``;
const AddButton = styled.div`
    height: 24px;
    cursor: pointer;
    margin-left: auto;
    display: flex;
`;
