import { IPagination } from "src/ts/interfaces/common";
import styled from "styled-components";
import { Chevron } from "../chevron/chevron";
import { VPaginationItem } from "./paginationItem";

interface IVPagination {
    data: IPagination;
    setPage: Function;
    style?: {};
}

export function VPagination({ data, setPage, style }: IVPagination) {
    const arr: any = [];
    const { currentPage, hasNext, hasBefore, lastPage } = data;

    const renderPagination = () => {
        for (let i = 0; i < lastPage; ++i) {
            arr.push(
                <VPaginationItem
                    setPage={setPage}
                    key={"pagination" + i}
                    isCurrent={i + 1 === currentPage}
                    number={i + 1}
                />,
            );
        }
        return arr;
    };

    return data.lastPage > 1 ? (
        <Container style={style}>
            <Chevron
                isLeft={true}
                activate={hasBefore}
                currentPage={currentPage}
                setPage={setPage}
            />
            {renderPagination()}
            <Chevron
                isLeft={false}
                activate={hasNext}
                currentPage={currentPage}
                setPage={setPage}
            />
        </Container>
    ) : (
        <></>
    );
}
const Container = styled.div`
    -ms-user-select: none;
    -moz-user-select: -moz-none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    user-select: none;
    align-items: center;
    display: flex;
    height: 32px;
    justify-content: center;
    margin-top: 20px;
    min-width: 176px;
    padding: 0 0px;
    position: relative;
`;
