import React, { useState } from "react";
import { Button } from "src/ts/components/button/button";
import { useDeleteTrack } from "src/ts/models/artistProfile/profileBanner";
import { AddTrack } from "src/ts/views/artistProfile/addTrack";
import { UpdateTrack } from "src/ts/views/artistProfile/updateTrack";
import styled from "styled-components";
import { BREAKPOINT_LARGE, BREAKPOINT_MIDDLE, BREAKPOINT_SMALL } from "../../../../constants/constants";
import { DisplayFlex, SpoqaHansSansNeo } from "../../../../css/css";
import { MusicPlayer } from "../musicPlayer";
import { TrackRow } from "./trackRow";

export function Track({ songs }) {

    const [isPlay, setIsPlay] = useState(false);
    const [selectedMusic, setSelectedMusic] = useState(null);
    const [fetch] = useDeleteTrack()
    const onClickMusic = (song) => () => {
        setSelectedMusic(song)

        if (selectedMusic === song)
            toggle()
        else
            setIsPlay(true)
    }
    const toggle = () => {
        setIsPlay(!isPlay)
    }
    return (
        <Container>
            <InnerContainer>
                <TopRow><Text>미발매 대표곡</Text><AddTrack></AddTrack></TopRow>
                <TrackList>
                    {songs.map((song, i) => {
                        return (<div style={{ position: 'relative' }} key={i}>
                            <TrackRow
                                isPlay={isPlay}
                                selectedMusic={selectedMusic}
                                key={i}
                                song={song}
                                onClickMusic={onClickMusic} />
                            <UpdateTrack key={i + 300} existedData={song} />
                            <Button
                                style={{ position: "absolute", right: 0, top: 20, background: "#ff4e45", }}
                                width={40}
                                height={40}
                                onClick={() => {
                                    fetch(songs[0]._id);
                                }}
                            >
                                삭제
                            </Button></div>);
                    })}
                </TrackList>
                {(selectedMusic !== null) &&
                    <MusicPlayer
                        isPlay={isPlay}
                        selectedMusic={selectedMusic}
                        toggle={toggle} />}
            </InnerContainer>
        </Container>
    );
}
const TrackList = styled.div`
    margin-top: 24px;
`
const Text = styled.div`
    ${SpoqaHansSansNeo(34, 40, 700)};
`
const Container = styled.div`
    ${DisplayFlex('center', '')}
`
const InnerContainer = styled.div`
    padding: 40px 20px;
    @media screen and (min-width: ${BREAKPOINT_LARGE}px) {
        width: 1196px;
    }
    @media screen and (min-width: ${BREAKPOINT_MIDDLE}px) and (max-width: ${BREAKPOINT_LARGE}px) {
        width: 1140px;
    }
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) and (max-width: ${BREAKPOINT_MIDDLE}px){
        width: 754px;
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        width: 375px;
    }
`
const TopRow = styled.div`

    display: flex;
`