import axios from "axios";
import React, { useState } from "react";
import styled from "styled-components";
import { BlankButton3 } from "../../components/0.Button/blankButton3";
import { Button } from "../../components/0.Button/button";
import { Modal2 } from "../../components/3.Modal/modal2";
import { API_SERVER2 } from "../../constants/constants";
import { DisplayFlex, SpoqaHansSansNeo } from "../../css/css";
import x from '../../images/x.svg'
export function ChooseArtistModal({ open, refetch2, projectID, artistID, estimateID, close, img, name, projectName }) {
    const [chosen, setChosen] = useState(false);
    const selectArtist = async () => {
        let form = new FormData()
        form.append('artistID', artistID);
        form.append('estimateID', estimateID);

        const url = API_SERVER2 + `/projects/${projectID}/select-artist`;
        const options = {
            method: 'PATCH',
            url: url,
            data: form,
            withCredentials: true
        };
        axios(options).then(res => {
            if (res.status === 200) {
                setChosen(true)
            }
        })
    }
    const setAllRefuse = async () => {
        let form = new FormData()
        form.append('projectID', projectID);

        const url = API_SERVER2 + `/estimates/set-lefts-selected-to-false`;
        const options = {
            method: 'PATCH',
            url: url,
            data: form,
            withCredentials: true
        };
        axios(options);
    }
    return (
        <Modal2 open={open} close={close} >
            <Container chosen={chosen}>
                {chosen ? (
                    <div>
                        <Row2>
                            <IMG src={img} chosen={chosen}/>
                            <Text1>{name}을(를) &quot;{projectName}&quot;의 아티스트로 <br></br>
                            선택하셨습니다!</Text1>
                        </Row2>
                        <Row4>
                        <Discription>이 프로젝트를 담당할 아티스트를 추가로 선택하시겠습니까? <br></br>
                     <Highlight>· [네]</Highlight>   : 아티스트 견적서 마감일까지 추가 견적서를 받고 선택하실 수 있습니다.<br></br>
                     <Highlight>· [아니요]</Highlight> : 다른 아티스트 견적서들은 모두 자동 거절 처리됩니다.</Discription>
                        </Row4>
                        <Row3>
                            <BlankButton3
                                onClick={() => {close();refetch2();}}
                                style={{ height: 40, width: 96 }}
                                text1={"네"} />
                            <Button
                                onClick={() => {
                                    setAllRefuse();
                                    close();
                                    refetch2();
                                }}
                                style={{ marginLeft: 'auto', height: 40, width: 96 }}>확인</Button>
                        </Row3></div>
                ) : (<div>
                    <Row1>
                        <img onClick={() => close()} src={x} style={{ marginLeft: 'auto', cursor: 'pointer' }} />
                    </Row1>
                    <Row2>
                        <IMG chosen={chosen} src={img} />
                        <Text1>{name}을(를) &quot;{projectName}&quot;의 아티스트로<br></br>선택하시겠습니까?</Text1>
                    </Row2>
                    <Row3>
                        <BlankButton3
                            onClick={() => close()}
                            style={{ height: 40, width: 96 }}
                            text1={"취소"} />
                        <Button
                            onClick={() => {
                                selectArtist();
                            }}
                            style={{ marginLeft: 'auto', height: 40, width: 96 }}>확인</Button>
                    </Row3>
                    </div>)}

            </Container>
        </Modal2>
    );
}
const Highlight = styled.span`
${SpoqaHansSansNeo(16,20,700)};

`
const Discription = styled.div`
//styleName: Body;
${SpoqaHansSansNeo(16,20)};
`
const Row4 = styled.div`
    flex-direction: column;
${DisplayFlex('','center')}
`
const Row3 = styled.div`
    border-top: 1px solid #F7F7F7;
    margin-top: 24px;
    padding:24px;
    display: flex;
`
const IMG = styled.img`
    margin-top: ${props=>props.chosen && '48px'};
    width: 240px;
    height: 240px;
    border-radius: 50em;
`
const Row2 = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
`
const Container = styled.div`
    width:688px;
    height: ${props=>props.chosen ? '548px' : '480px'};
    background: #0E0E10;
`
const Row1 = styled.div`
  padding-top: 24px;
  padding-right: 24px;
    display:flex;
    align-items: flex-end;
`
const Text1 = styled.div`
    ${SpoqaHansSansNeo(24,28,700)};
    margin-top:24px;
`