import React from "react";
import styled from "styled-components";
import { DisplayFlex, SpoqaHansSansNeo } from "../../css/css";

export function BlankButton({ width = 100, height = 100, onClick, text }) {
    return (
        <Container width={width} height={height} onClick={onClick}>
            <Text>
                {text}
            </Text>
        </Container>
    );
}

const Text = styled.div`
    position:relative;
    ${SpoqaHansSansNeo(16, 24, 700)};
`
const Container = styled.div`
    width: ${props => props.width}px;
    height: ${props => props.height}px;
    transition: all 0.3s;
    cursor: pointer;
    ${DisplayFlex('center', 'center')}
    border-radius: 4px;
    border: 1px solid;
    text-align: center;
    color: #F7F7F7;
    border-color: #F7F7F7;
    &:hover{
        background-color: #4A4A4A;
        border-color: #F7F7F7;
        color: ${props => props.activate ? '#F7F7F7' : '#F7F7F7'};
    }
`