import { useCallback, useEffect } from "react";
import { useRecoilState } from "recoil";
import { BlankButton } from "src/ts/components/button/blankButton";
import { Button } from "src/ts/components/button/button";
import { Modal } from "src/ts/components/modal/modal";
import { CModalData, currentModaldataState } from "src/ts/models/modal";
import { SpoqaHansSansNeo } from "src/ts/styles/css";
import styled from "styled-components";

export function ModalManager() {
    const [
        {
            isOpen,
            header,
            footerText,
            main,
            empty,
            form,
            onClick,
            handleClick,
            setIsOpen,
        },
        set,
    ] = useRecoilState(currentModaldataState);

    const fHandleClick = useCallback(
        (e: Event) => {
            if (handleClick && isOpen) {
                if (
                    !(
                        (e.target as HTMLInputElement).id === "picture" ||
                        (e.target as HTMLInputElement).id === "left" ||
                        (e.target as HTMLInputElement).id === "right"
                    )
                ) {
                    set(
                        new CModalData(
                            "",
                            <></>,
                            "",
                            () => {},
                            () => {},
                            false,
                            false,
                        ),
                    );
                }
            }
        },
        [handleClick, isOpen, set],
    );
    useEffect(() => {
        if (handleClick) window.addEventListener("click", fHandleClick);
        return () => {
            window.removeEventListener("click", fHandleClick);
        };
    }, [isOpen, fHandleClick, handleClick]);

    return (
        <Modal isOpen={isOpen} empty={empty}>
            {isOpen &&
                (!empty ? (
                    <>
                        <Header>{header}</Header>
                        <Main>{main}</Main>
                        <Footer>
                            <BlankButton
                                height={40}
                                width={96}
                                onClick={() => {
                                    setIsOpen(false);
                                    set(
                                        new CModalData(
                                            "",
                                            <></>,
                                            "",
                                            () => {},
                                            () => {},
                                            false,
                                            false,
                                        ),
                                    );
                                }}
                            >
                                취소
                            </BlankButton>
                            <Button
                                rightAlign={true}
                                height={40}
                                width={96}
                                onClick={() => onClick()}
                            >
                                {footerText}
                            </Button>
                        </Footer>
                    </>
                ) : (
                    <>{form}</>
                ))}
        </Modal>
    );
}

const Header = styled.div`
    position: relative;
    height: 72px;
    padding: 24px;
    ${SpoqaHansSansNeo(20, 24, 700)};
    display: flex;
    align-items: center;
`;

const Main = styled.div`
    ${SpoqaHansSansNeo(16, 20, 400)};
    padding: 24px;
    border-top: 1px solid #828282;
    min-height: 150px;
`;

const Footer = styled.div`
    width: 100%;
    margin-top: auto;
    border-top: 1px solid #828282;
    ${SpoqaHansSansNeo(16, 20, 400)};
    padding: 24px;
    display: flex;
`;
