import { ReactNode } from "react";
import { BREAKPOINT_SMALL } from "src/ts/constants/common";
import { makeCenter, SpoqaHansSansNeo } from "src/ts/styles/css";
import styled from "styled-components";

interface IButton {
    round?: boolean;
    width: number;
    height: number;
    onClick: Function;
    children?: ReactNode;
    style?: object;
    activate?: boolean;
    label?: boolean;
    marginLeft?: number;
    marginRight?: number;
    marginTop?: number;
    marginBottom?: number;
    rightAlign?: boolean;
}

interface IContainer {
    round: boolean;
    style?: object;
    width: number;
    height: number;
    children?: ReactNode;
    activate?: boolean;
    marginLeft?: number;
    marginRight?: number;
    marginTop?: number;
    marginBottom?: number;
    rightAlign: boolean;
}
export function Button({
    style,
    width = 166,
    height = 48,
    onClick,
    activate = true,
    children = <></>,
    label = false,
    marginLeft = 0,
    marginRight = 0,
    marginTop = 0,
    marginBottom = 0,
    rightAlign = false,
    round = false,
}: IButton) {
    if (label) {
        return (
            <LabelContainer
                round={round}
                rightAlign={rightAlign}
                width={width}
                height={height}
                style={style}
                onClick={() => (activate ? onClick() : null)}
                activate={activate}
            >
                <Text>{children}</Text>
            </LabelContainer>
        );
    } else {
        return (
            <Container
                round={round}
                rightAlign={rightAlign}
                marginLeft={marginLeft}
                marginRight={marginRight}
                marginTop={marginTop}
                marginBottom={marginBottom}
                width={width}
                height={height}
                style={style}
                onClick={() => (activate ? onClick() : null)}
                activate={activate}
            >
                <Text>{children}</Text>
            </Container>
        );
    }
}
const Text = styled.div`
    display: flex;
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
        ${SpoqaHansSansNeo(16, 16, 700)};
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        ${SpoqaHansSansNeo(14, 16, 700)};
    }
    ${makeCenter()}
`;
const Container = styled.div<IContainer>`
    width: ${props => props.width}px;
    height: ${props => props.height}px;
    margin-top: ${props => props.marginTop}px;
    margin-right: ${props => props.marginRight}px;
    margin-left: ${props => props.marginLeft}px;
    margin-left: ${props => props.rightAlign && "auto"};
    transition: all 0.3s;
    &:hover {
        background: ${props => props.activate && "#8700C5"};
    }
    cursor: ${props => props.activate && "pointer"};
    ${makeCenter()};
    background: ${props => (props.activate ? "#BD03FA" : "#828282")};
    border-radius: ${props => (props.round ? "50" : "4")}px;
`;
const LabelContainer = styled.label<IContainer>`
    width: ${props => props.width}px;
    height: ${props => props.height}px;
    transition: all 0.3s;
    &:hover {
        background: ${props => props.activate && "#8700C5"};
    }
    cursor: ${props => props.activate && "pointer"};
    ${makeCenter()};
    background: ${props => (props.activate ? "#BD03FA" : "#828282")};
    border-radius: ${props => (props.round ? "50" : "4")}px;
`;
