import React from "react";
import styled from "styled-components";
import { BREAKPOINT_SMALL } from "../../constants/constants";
import { DisplayFlex, SpoqaHansSansNeo } from "../../css/css";
import { CheckBox } from "../0.Button/checkBox";

export function DropDownListRow({ sibal, parentisOpen = true, isOpen, content, onCreate, onRemove, onToggle, addQuery, deleteQuery }) {
    const onClick = () => {
        if (parentisOpen && isOpen) {
            if (addQuery)
                addQuery(content.query)
            onToggle(content.id)
            if (content.activate) {
                if (deleteQuery)
                    deleteQuery(content.query)
                onRemove(content.id)
            } else {
                onCreate(content)
            }
        }
    }
    return (
        <Container sibal={sibal}isOpen={(parentisOpen && isOpen)}>
            <InnerContainer onClick={onClick} isOpen={(parentisOpen && isOpen)}>
                <CheckBox activate={content.activate} />
                <Text>{content.text}</Text>
            </InnerContainer>
        </Container>
    )
}
const Container = styled.div`
    transition: all 0.4s;
    display: ${props => (props.sibal ? 'none':'block')};
`
const InnerContainer = styled.div`
    box-sizing: border-box;
    cursor: ${props => (props.isOpen && 'pointer')};
    transition: all 0.4s;
    &:hover {
        opacity: 1;
        background: ${props => (props.activate ? '#BD03FA' : '#4A4A4A')};
    }
    background: ${props => (props.activate ? '#BD03FA' : '#0E0E10')};
    &:active{
        opacity: 1;
        background: #BD03FA;
    }
    ${DisplayFlex("", "center")}
    height:40px;
    padding: 8px 12px;
`;
const Text = styled.div`
box-sizing: border-box;
    z-index:2;
    -ms-user-select: none; 
    -moz-user-select: -moz-none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    user-select: none;
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
        ${SpoqaHansSansNeo(16, 24, 700)}
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        ${SpoqaHansSansNeo(14, 16, 400)}
    }
`