import React from "react";
import { useHistory } from "react-router";
import styled from "styled-components";
import { Input } from "../../../../components/4.Input/input";
import { BREAKPOINT_MIDDLE, BREAKPOINT_SMALL } from "../../../../constants/constants";
import { DisplayFlex } from "../../../../css/css";
import { useInputs } from "../../../../hook/useInputs";
import magnifyingGlass from "../../../../images/magnifyingGlass.svg";

export function SearchBar({currentSize,setMobileSearch}) {
  const history = useHistory()
  const [{ search }, onChange] = useInputs({
    search: ''
  });
  const getResults = () => {
    if(currentSize === 'MOBILE'){
      setMobileSearch(true);
      return;
    }
    history.push('/searchresults',{fromSearch : true, search:search})
    window.scrollTo(0,0);
  }
  const handleKeyPress = (e) => {
    if (e.key === "Enter") {
      getResults();
    }
  };
  
  return (
    <Container style={{ position: 'relative' }}>
      {
        (currentSize !== 'MOBILE') && <Input
          onKeyPress={(e) => handleKeyPress(e)}
          style={{
            marginLeft: 24,
            width: 320
          }}
          value={search}
          placeholder='어떤 아티스트를 찾으시나요?'
          onChange={onChange}
          name='search' />
      }
      <MagnifyingGlass
        src={magnifyingGlass} 
        onClick={() => {getResults();}}
        />
    </Container>
  );
}
const Container = styled.div`
  ${DisplayFlex('','center')}
`
const MagnifyingGlass = styled.img`
  cursor: pointer;
  width: 24px;
  height: 24px;
  position: absolute;
  @media screen and (min-width: ${BREAKPOINT_SMALL}px) and (max-width: ${BREAKPOINT_MIDDLE}px) {
    height: 18px;
  }
  @media screen and (min-width: ${BREAKPOINT_SMALL}px) { 
    left: 306px;
  }
`