import React from "react";
import { IDropdownState, IDropdownStateNode } from "src/ts/models";
import { FontHeader, FontRedStar, SpoqaHansSansNeo } from "src/ts/styles/css";
import styled from "styled-components";
import { DropDown } from "./dropDown";

interface IInputForm {
    keywords: IDropdownStateNode[];
    setKeywords: React.Dispatch<React.SetStateAction<IDropdownStateNode[]>>;
    checkBox?: boolean;
    contents: IDropdownState;
    setContents: React.Dispatch<React.SetStateAction<IDropdownState>>;
    explanation?: string;
    onBlur?: () => void;
    onFocus?: () => void;
    style?: object;
    marginBottom?: number;
    marginRight?: number;
    dropDownHeader: string;
    type?: string;
    textarea?: boolean;
    requirement?: boolean;
    pass?: boolean;
    width?: number;
    placeholder?: string;
    error?: boolean;
    onKeyPress?: React.KeyboardEventHandler<
        HTMLInputElement | HTMLTextAreaElement
    >;
    name?: string;
    header: string;
    errorMessage?: string;
    height?: number | string;
    setContainerOpen: Function;
}
export function DropDownForm({
    contents,
    keywords,
    setKeywords,
    setContents,
    checkBox,
    marginRight = 0,
    style,
    dropDownHeader,
    requirement = false,
    pass,
    setContainerOpen,
    error = false,
    header = "",
    height = "100%",
    width = 100,
    errorMessage,
    marginBottom = 0,
    explanation = "",
}: IInputForm) {
    const onRemove = id => {
        setKeywords(keywords.filter(keyword => keyword.id !== id));
        if (keywords.length - 1 > 0) {
            setContainerOpen(true);
        } else {
            setContainerOpen(false);
        }
    };
    const onToggle = id => {
        let newOptions: any;

        const newContents = contents.contents.map(content =>
            content.id === id
                ? { ...content, activate: !content.activate }
                : content,
        );

        newOptions = {
            header: contents.header,
            contents: newContents,
        };

        setContents(newOptions);
        setKeywords(
            keywords.map(keyword =>
                keyword.id === id
                    ? { ...keyword, activate: !keyword.activate }
                    : keyword,
            ),
        );
    };
    const onCreate = newKeyword => {
        let duplication = false;
        keywords.map(
            keyword =>
                (duplication = keyword.id === newKeyword.id ? true : false),
        );
        if (duplication) return;
        newKeyword.activate = true;
        if (newKeyword) setKeywords(keywords.concat(newKeyword));

        //아직 keyword set이 안되었기 때문에 +1
        if (keywords.length + 1 > 0) {
            setContainerOpen(true);
        } else {
            setContainerOpen(false);
        }
    };

    return (
        <Container
            style={style}
            marginBottom={marginBottom}
            marginRight={marginRight}
            width={width}
            height={height}
        >
            {header !== "" && (
                <FontHeader>
                    {header}
                    {requirement && <FontRedStar>*</FontRedStar>}
                </FontHeader>
            )}
            {explanation !== "" && <Explanation>{explanation}</Explanation>}
            <DropDown
                checkBox={checkBox}
                position="absolute"
                error={error}
                pass={false}
                header={dropDownHeader}
                contents={contents.contents}
                width={width}
                onCreate={onCreate}
                onRemove={onRemove}
                onToggle={onToggle}
            />
            {error && <Error>{errorMessage}</Error>}
            {pass && <Pass>{errorMessage}</Pass>}
        </Container>
    );
}
const Explanation = styled.div`
    ${SpoqaHansSansNeo(16, 20, 400)};
    margin-bottom: 16px;
`;
const Error = styled.div`
    margin-top: 4px;
    ${SpoqaHansSansNeo(14, 24, 400)};
    color: #ff4e45;
`;
const Pass = styled.div`
    margin-top: 4px;
    ${SpoqaHansSansNeo(14, 24, 400)};
    color: #06be34;
`;
interface IContainer {
    marginBottom: number;
    marginRight: number;
    width: number | string;
    height: number | string;
}
const Container = styled.div<IContainer>`
    height: ${props => props.height}px;
    width: ${props => props.width}px;
    margin-right: ${props => props.marginRight}px;
    margin-bottom: ${props => props.marginBottom}px;
`;
