import React from "react";
import { useHistory } from "react-router";
import styled from "styled-components";
import { DisplayFlex, SpoqaHansSansNeo } from "../../css/css";
import { Progress } from "./Progress";
import { Button } from "../../components/0.Button/button";
import { MiniArtistProfile } from "./miniArtistProfile";
import { isEmptyObject } from "../../lib/lib";
import { BREAKPOINT_LARGE, BREAKPOINT_MIDDLE, BREAKPOINT_SMALL } from "../../constants/constants";
import { Mobile, MobileOrSmallTablet, NotMobile, PCOrLargeTablet } from "../../lib/mediaQuery";

export function ProjectCard({ data,currentSize }) {
    const history = useHistory()
    const converter = () => {
        if (data.progress === '의뢰' ||
            data.progress === '견적 확인 및 선택') {
            return '오픈 준비'
        }
        else if (
            data.progress === '세부 사항 협의' ||
            data.progress === '계약' ||
            data.progress === '결제') {
            return '진행 중'
        }
        else if (data.progress === '결과물 수령') {
            return '완료'
        }
    }
    const printArtistProfile = () => {
        if (data.progress === '의뢰') {
            return "요청 아티스트:"
        } else if (data.progress === '견적 확인 및 선택') {
            return "지원 아티스트:"
        } else {
            return "담당 아티스트:"
        }
    }
    const printGenre = () => {
        const a = [];
        data.genre.map((v, i) => {

            a.push(v);
            if (data.genre.length - 1 !== i)
                a.push(" · ")
        })
        return a;
    }
    const printCategory = () => {
        const a = [];
        data.category.map((v, i) => {
            const index = v.indexOf('-');
            const newThing = v.slice(0, index).
                concat(' > ').concat(v.slice(index + 1, v.length));

            a.push(newThing);
            if (data.category.length - 1 !== i) {
                a.push(" ")
                a.push("  ·  ")
                a.push(" ")
            }

        })
        return a;
    }
    const printBudget = () => {
        let a;
        if (data.budget === '30-50') {
            a = '30-50'
            return '예산: ' + a + "만원"
        } else if (data.budget === '50-100') {
            a = '50-100'
            return '예산: ' + a + "만원"
        } else if (data.budget === '100-300') {
            a = '100-300'
            return '예산: ' + a + "만원"
        } else if (data.budget === '300-500') {
            a = '300-500'
            return '예산: ' + a + "만원"
        } else if (data.budget === 'gte500') {
            a = '500'
            return '예산: ' + a + "만원 이상"
        }
    }
    const printCreatedAt = () => {
        let date = data.createdAt
        const index = date.indexOf('T');
        let newDate = date.slice(0, index);

        return "등록일자 " + newDate;
    }
    const printDeadline = () => {
        let date = data.deadline
        const index = date.indexOf('T');
        let newDate = date.slice(0, index);

        return "아티스트 견적서: " + newDate;
    }
    const renderArtistProfile = () => {
        if (data.progress === '의뢰') {
            return <MiniArtistProfile star={true} src={
                (!isEmptyObject(data.requestedArtists)) &&
                data.requestedArtists[0].smallProfileURI} />
        } else if (data.progress === '견적 확인 및 선택') {
            const newArray = [];

            data.appliedArtists.map((data) => {
                if (data.checkedFromAdmin)
                    newArray.push(data)
            });
            return newArray.map((data, i) => {
                if (i < 3)
                    return (
                        <MiniArtistProfile
                            star={data.star}
                            key={i}
                            style={{ marginRight: 8 }}
                            src={data.smallProfileURI} />
                    )
            })
        } else {
            return <MiniArtistProfile star={data.selectedArtist[0]} src={data.selectedArtist[0].smallProfileURI} />
        }
    }
    const renderPlusArtist = () => {
        if (data.appliedArtists.length > 3) {
            return "+" + (data.appliedArtists.length - 3) + "명"
        }
    }
    return (
        <Container>
            <Row0>
                <State><Text12>{converter()}</Text12></State>
                <CreatedAt>{printCreatedAt()}</CreatedAt>
            </Row0>
            <div style={{ display: 'flex' }}>
                <Column1>
                    <ProjectName>{data.name}</ProjectName>
                    <NotMobile>
                        <Description>{data.description}</Description>
                    </NotMobile>
                    <Row1>
                        <Category>{printCategory()}</Category>
                        <Genre>{printGenre()}</Genre>
                    </Row1>
                    <Mobile>
                        <Budget>{printBudget()}</Budget>
                        <Artist>{printArtistProfile()}</Artist>
                        <ArtistProfile>
                            {renderArtistProfile()}
                            <Plus>{renderPlusArtist()}</Plus>
                        </ArtistProfile>
                    </Mobile>
                </Column1>
                <NotMobile>
                    <Column2>
                        <Budget>{printBudget()}</Budget>
                        <Artist>{printArtistProfile()}</Artist>
                        <ArtistProfile>
                            {renderArtistProfile()}
                            <Plus>{renderPlusArtist()}</Plus>
                        </ArtistProfile>
                    </Column2>
                </NotMobile>
            </div>
            <PCOrLargeTablet>
                <Status>프로젝트 진행 상황</Status>
                <Progress data={data.progress} />
                <div style={{ display: 'flex', justifyContent: 'center' }}>
                    <Button onClick={
                        () => {
                            history.push(`/projectdetail/${data._id}`);
                            window.scrollTo(0, 0);
                        }}
                        width={372}>자세히 보기</Button>
                </div>
            </PCOrLargeTablet>
            <MobileOrSmallTablet>
                <div style={{ display: 'flex', justifyContent: 'center' }}>
                    <Button
                        style={{ marginTop: 20 }}
                        onClick={() => { history.push(`/projectdetail/${data._id}`); 
                            window.scrollTo(0, 0); }} 
                            height={ (currentSize !== "MOBILE") ? 48 : 40}
                            width={372}>자세히 보기</Button>
                </div>
            </MobileOrSmallTablet>
            <DeadLine>{printDeadline()}</DeadLine>
        </Container>
    );
}
const Plus = styled.div`
    width: 48px;
    height: 48px;
    ${DisplayFlex("center", "center")};
    ${SpoqaHansSansNeo(16, 20)};
`
const DeadLine = styled.div`
    margin-top: 8px;
    ${DisplayFlex('center', '')};
    ${SpoqaHansSansNeo(12, 14, 400, '#A0A0A0', '0em')};
`
const Text12 = styled.div`

`
const Status = styled.div`
    ${SpoqaHansSansNeo(24, 28, 700)};
`
const CreatedAt = styled.div`
    margin-left: auto;
    ${SpoqaHansSansNeo(12, 14, 400, '#A0A0A0', '0em')};
`
const Row0 = styled.div`
    ${DisplayFlex('', 'center')};
`
const ArtistProfile = styled.div`
    display: flex;
    flex-wrap: wrap;
    margin-top: 8px;
`
const Artist = styled.div`
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
        margin-top: 16px;
        ${SpoqaHansSansNeo(20, 24, 700)};
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        margin-top: 8px;
        ${SpoqaHansSansNeo(14, 16, 700)};
    }
`
const Column2 = styled.div`
    width: 285px;
    padding: 20px 0px 20px 40px;
`
const Budget = styled.div`
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
        ${SpoqaHansSansNeo(20, 24, 700)};
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        ${SpoqaHansSansNeo(14, 16, 700)};
        margin-top: 20px;
    }
`
const Genre = styled.div`
    white-space: pre-wrap;
    width: fit-content;
    ${SpoqaHansSansNeo(12, 14, 700, '#D0B1FF', '0em')};
    padding-left: 8px;
`
const Category = styled.div`
    width: fit-content;
    white-space: pre-wrap;
    ${SpoqaHansSansNeo(12, 14, 700, '#D0B1FF', '0em')};
    padding-right: 8px;
    border-right: 1px solid #4A4A4A;
`
const Row1 = styled.div`
    display: flex;
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
        margin-top: 24px;
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        margin-top: 12px;
    }
`
const Column1 = styled.div`
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
        border-right: 1px solid #4A4A4A;
    }
    width: 811px;
`
const Description = styled.div`
    padding-right: 20px;
    margin-top: 16px;
    ${SpoqaHansSansNeo(16, 20)};
`
const ProjectName = styled.div`
    ${SpoqaHansSansNeo(24, 28, 700)};
    margin-top: 20px;
`
const State = styled.div`
    ${DisplayFlex('center', 'center')}
    background: #4A4A4A;
    border-radius: 50px;
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
        ${SpoqaHansSansNeo(16, 20, 700)};
        padding: 8px 20px;
        height: 40px;
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        ${SpoqaHansSansNeo(14, 16, 700)};
        padding: 4px 12px;
    }
`
const Container = styled.div`
    margin-bottom: 20px;
    background: #0E0E10;    
    @media screen and (min-width: ${BREAKPOINT_LARGE}px) {
        width: 1156px;
    }
    @media screen and (min-width: ${BREAKPOINT_MIDDLE}px) and (max-width: ${BREAKPOINT_LARGE}px) {
        width: 100%;
    }
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) and (max-width: ${BREAKPOINT_MIDDLE}px){
        width: 100%;
    }
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
        padding:40px;
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        padding:20px;
    }
`