import React, { useState } from "react";
import styled from "styled-components";
import { BREAKPOINT_SMALL } from "../../constants/constants";
import { DisplayFlex, SpoqaHansSansNeo } from "../../css/css";
import chevron from "../../images/chevron.svg"
import { DropDownListRow } from "./dropDownRow";

export function DropDown({ parentisOpen, option, onCreate, onRemove, onToggle, setCategory, deleteCategory }) {
    const [isOpen, setIsOpen] = useState(false);
    const { header, contents } = option;
    const [displayNone, setDisplayNone] = useState(false); 
    const [sibal, setSibal] = useState(true); 
    const toggling = () => {
        setIsOpen(!isOpen);
        setDisplayNone(false);
        setSibal(false)
    }
    const a = () => {
        if(!isOpen){
            setSibal(true)
        }
        else{
            setSibal(false)
        }
            
    }
    return (
        <Container isOpen={isOpen} contentsNum={contents.length}>
            <Header
                onClick={toggling}
                isOpen={isOpen}>
                <Text>{header}</Text>
                <IMG isOpen={isOpen} src={chevron} />
            </Header>
            <DropDownList
                sibal={sibal}
                onTransitionEnd={a}
                displayNone={displayNone}
                isOpen={isOpen} 
                contentsNum={contents.length} >
                <InnerContainer>
                {contents.map((content,i) => (
                    <DropDownListRow
                        sibal={sibal}
                        parentisOpen={parentisOpen}
                        isOpen={isOpen}
                        key={i}
                        content={content}
                        onCreate={onCreate}
                        onRemove={onRemove}
                        onToggle={onToggle}
                        addQuery={setCategory}
                        deleteQuery={deleteCategory}/>
                ))}
                </InnerContainer>
            </DropDownList>
        </Container>
    );
}
const InnerContainer = styled("div")``
const Container = styled("div")`
    box-sizing:border-box;
`
const Header = styled.div`
    z-index:3;
    &:hover {
        opacity: 1;
        background-color: #4A4A4A;
    }
    background: #0E0E10;
    transition: all 0.2s;
    position:relative;
    cursor: pointer;
    ${DisplayFlex("", "center")}
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
        height: 40px;
        padding: 8px 12px;
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        height: 32px;
        width: 100px;
        padding: 4px 12px;
    }
`;
const IMG = styled.img`
    z-index: 16;
    position:relative;
    -ms-user-select: none; 
    -moz-user-select: -moz-none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    user-select: none;
    margin-left: auto;
    transition: all ease 0.5s;
    transform: ${props => props.isOpen && 'rotate( -180deg )'};
`
const DropDownList = styled.div`
    box-sizing:border-box;
    z-index:2;
    position: relative;
    background: #0E0E10;
    transition: all 0.5s;
    height: ${props => (props.isOpen ? props => (props.contentsNum * 40)+'px' : "0px")};
    //opacity: ${props => (props.isOpen ? "1" : "0")};
`;

const Text = styled.div`
    z-index:2;
    -ms-user-select: none; 
    -moz-user-select: -moz-none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    user-select: none;
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
        ${SpoqaHansSansNeo(16, 24, 700)}
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        ${SpoqaHansSansNeo(14, 16)}
    }
`
