import React, { useRef } from "react";
import { Modal4 } from "../../../../components/3.Modal/modal4";

export function YoutubeModal({open,close,videoID,currentSize}) {
    const style = {
        width: (currentSize==='MOBILE') ? (window.innerWidth - 20): 1156,
        height:(currentSize==='MOBILE') ? ((window.innerWidth - 20) * 0.48) : 546,
    }
    const youtube = useRef();
    document.addEventListener('click',(e) =>{
        if(open){
            if(e.target.id == 'modalBG'){
                close();
            }
        }
    })
    return (
    <Modal4 open={open} close={close} style={style}>
        {open && <iframe ref={youtube}  style={style} src={`https://www.youtube.com/embed/${videoID}?autoplay=1&mute=0`} allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowFullScreen></iframe>}
    </Modal4>
    );
}