import React from "react";
import styled from "styled-components";
import { BREAKPOINT_SMALL } from "../../constants/constants";
import { DisplayFlex, SpoqaHansSansNeo } from "../../css/css";

export function Button({ style, width = 166, height = 48, onClick, 
    activate = true, children, label = false }) {
    if(label){
        return (
            <Container2 
                width={width}
                height={height}
                style={style} 
                onClick={activate ? onClick : null} 
                activate={activate}>
                <Text>{children}</Text>
            </Container2>
        );
    } else{
        return (
            <Container
                width={width}
                height={height}
                style={style} 
                onClick={activate ? onClick : null} 
                activate={activate}>
                <Text>{children}</Text>
            </Container>
        );
    }
}
const Text = styled.div`
    display: flex;
    ${props=> !props.isMobile ? SpoqaHansSansNeo(16,16,700) : SpoqaHansSansNeo(14,18,700)};
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) { 
        ${SpoqaHansSansNeo(16,16,700)};
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        ${SpoqaHansSansNeo(14,16,700)};
    }
`
const Container = styled.div`
    width: ${props=>props.width}px;
    height: ${props=>props.height}px;
    transition: all .3s;
    &:hover{
        background: ${props=>(props.activate) && '#8700C5'};
    }
    cursor: ${props=>(props.activate) && 'pointer'};
    ${DisplayFlex('center','center')};
    background : ${props=>(props.activate) ? '#BD03FA' : '#828282'};
    border-radius : 4px;
`
const Container2 = styled.label`
    width: ${props=>props.width}px;
    height: ${props=>props.height}px;
    transition: all .3s;
    &:hover{
        background: ${props=>(props.activate) && '#8700C5'};
    }
    cursor: ${props=>(props.activate) && 'pointer'};
    ${DisplayFlex('center','center')};
    background : ${props=>(props.activate) ? '#BD03FA' : '#828282'};
    border-radius : 4px;
`