import axios from "axios";
import React, { useEffect, useState } from "react";
import { useParams } from "react-router";
import styled from "styled-components";
import { Modal2 } from "../../components/3.Modal/modal2";
import { API_SERVER2, BREAKPOINT_LARGE, BREAKPOINT_MIDDLE, BREAKPOINT_SMALL } from "../../constants/constants";
import { DisplayFlex, SpoqaHansSansNeo } from "../../css/css";
import prev from '../../images/arrow-left-prev.svg';
import next from '../../images/arrow-right-next.svg';
import facebook from '../../images/facebook.svg';
import instagram from '../../images/instagram.svg';
import twitter from '../../images/twitter.svg';
import website from '../../images/website.svg';
import youtube from '../../images/youtube.svg';
import { isEmptyObject } from "../../lib/lib";
import { Mobile, PCOrLargeTablet, SmallTablet } from "../../lib/mediaQuery";
import { BadgePerformer, BadgeProducer } from "../main/4.BrowseArtist/2.ArtistCards/artistCardData";
import { ArtistPagination } from "../main/4.BrowseArtist/3.ArtistPagination/artistPagination";
import { BannerSection } from "./1.BannerSection/0.BannerSection/bannerSection";

export function About(props) {


    const artistID = useParams().artistID;
    const getTestimonials = async () => {
        let params = new URLSearchParams();
        params.append("page", page)
        const url = API_SERVER2 + `/artists/testimonials/by-name-uri/${artistID}`;
        const options = {
            method: 'GET',
            url: url,
            withCredentials: true,
            params: params
        };
        axios(options).then(res => {
            setData2(res.data)
        })
    }
    const culLimitNum = () => {
        if (props.currentSize === "LARGE" || props.currentSize === "MIDDLE") {
            return 8;
        } else if (props.currentSize === "SMALL") {
            return 6;
        } else {
            return 9;
        }
    }
    const getPhotos = async () => {
        let params = new URLSearchParams();
        params.append("page", page2)
        params.append("limit", culLimitNum())

        const url = API_SERVER2 + `/artists/${artistID}/photos/by-name-uri`;
        const options = {
            method: 'GET',
            url: url,
            withCredentials: true,
            params: params
        };
        axios(options).then(res => {
            setData3(res.data)
        })
    }
    const [currentSize, setCurrentSize] = useState(props.currentSize);
    const [page, setPage] = useState(1);
    const [page2, setPage2] = useState(1);
    useEffect(() => {
        if (props.currentSize !== currentSize) {
            setCurrentSize(props.currentSize)
            getPhotos()
        }
    })
    useEffect(() => {
        getTestimonials();
        getPhotos();
    }, [page, page2])
    const [data2, setData2] = useState(null);
    const [data3, setData3] = useState(null);
    let testimonials, pagination, photos, photoPagination = null;

    if (data2) {
        testimonials = data2.data;
        pagination = data2.pagination
    }
    if (data3) {
        photos = data3.data.photos;
        photoPagination = data3.pagination
    }
    const [modalOpen, setModalOpen] = useState(false);
    const [leftEnd, setLeftEnd] = useState(false);
    const [rightEnd, setRightEnd] = useState(false);
    useEffect(() => {
        window.addEventListener('click', (e) => {
            if (modalOpen) {
                if (!(e.target.id == 'picture' || e.target.id == 'left' ||
                    e.target.id == 'right')) {
                    closeModal();
                }
            }
        })
    }, [modalOpen])

    const goLeft = () => {
        if (pictureIndex > 0) {
            setPictureIndex(pictureIndex - 1);
            if (pictureIndex === 1) {
                setLeftEnd(true)
                setRightEnd(false)
            } else {
                setLeftEnd(false)
                setRightEnd(false)
            }
        }
    }
    const goRight = () => {
        if (pictureIndex < data.photos.length - 1) {
            setPictureIndex(pictureIndex + 1);
            if (pictureIndex === data.photos.length - 2) {
                setRightEnd(true)
                setLeftEnd(false)
            } else {
                setRightEnd(false)
                setLeftEnd(false)
            }
        }
    }
    const openModal = () => {
        setModalOpen(true);
    }
    const closeModal = () => {
        setModalOpen(false);
    }

    const data = props.data.artist;
    let awards, artistType = null;

    if (data.award !== undefined) {
        awards = data.award.split(",");
    }

    if (data) {
        artistType = data.artistType;
    }
    const [pictureIndex, setPictureIndex] = useState(data.photos.length);
    const clickPicture = (i) => {
        const index = i + ((photoPagination.currentPage - 1) * 8)
        setPictureIndex(index)
        openModal()
        if (i === 0) {
            setLeftEnd(true)
            setRightEnd(false)
        } else if (index === (data.photos.length - 1)) {
            setLeftEnd(false)
            setRightEnd(true)
        } else {
            setLeftEnd(false)
            setRightEnd(false)
        }
    }

    const renderArtistInfo = () => {
        return (
            <Row3>
                <ArtistType>
                    {artistType && artistType.map((data, i) => {
                        if (data === 'producer')
                            return <BadgeProducer key={i} data={data}>프로듀서</BadgeProducer>
                        else if (data === 'performer')
                            return <BadgePerformer key={i} data={data}>퍼포머</BadgePerformer>
                    })}
                </ArtistType>
                {data.activeYear[0] !== "" && (<Row5>
                    <Title>활동기간:</Title>
                    <Content2>
                        {data.activeYear[0] + " - " + data.activeYear[1]}</Content2>
                </Row5>)}
                {data.members !== "" && (
                    <Row5>
                        <Title>멤버:</Title>
                        <Content2>
                            {data.members}
                        </Content2>
                    </Row5>)
                }
                {
                    data.genre.length !== 0 && (<Row5>
                        <Title>장르:</Title>
                        <Content2>
                            {data.genre.map((value, i) => {
                                if (i < (data.genre.length - 1))
                                    return value + ", "
                                else
                                    return value
                            })}
                        </Content2>
                    </Row5>)
                }
                {
                    data.category.length !== 0 && (<Row5>
                        <Title>활동영역:</Title>
                        <Content2>
                            {data.category.map((value, i) => {
                                if (i < (data.category.length - 1))
                                    return value + ", "
                                else
                                    return value
                            })}
                        </Content2>
                    </Row5>)
                }
                {
                    data.discography !== "" && (<Row5>
                        <Title>디스코그래피:</Title>
                        <Content2>{data.discography}</Content2>
                    </Row5>)
                }

                {
                    (data.award !== "") && (data.award !== undefined) &&
                    (
                        <>
                            <Row5>
                                <Title>대표 수상이력:</Title>
                            </Row5>
                            <Content2>{awards.map((data, i) => {
                                return <Text999 key={i}>{' · ' + data}</Text999>
                            })}</Content2>
                        </>
                    )
                }
                {data.SNSChannel && (<Row4>
                    {!isEmptyObject(data.SNSChannel.youtube) &&
                        <a style={{ width: 24, height: 24, display: 'inline-block', marginRight: 8 }} rel="noreferrer" target='_blank' href={data.SNSChannel.youtube}>
                            <IMG src={youtube}></IMG></a>}
                    {!isEmptyObject(data.SNSChannel.facebook) &&
                        <a style={{ width: 24, height: 24, display: 'inline-block', marginRight: 8 }} rel="noreferrer" target='_blank' href={data.SNSChannel.facebook}><IMG src={facebook}></IMG></a>}
                    {!isEmptyObject(data.SNSChannel.instagram) &&
                        <a style={{ width: 24, height: 24, display: 'inline-block', marginRight: 10 }} rel="noreferrer" target='_blank' href={data.SNSChannel.instagram}><IMG src={instagram}></IMG></a>}
                    {!isEmptyObject(data.SNSChannel.twitter) &&
                        <a style={{ width: 24, height: 24, display: 'inline-block', marginRight: 8 }} rel="noreferrer" target='_blank' href={data.SNSChannel.twitter}><IMG src={twitter}></IMG></a>}
                    {!isEmptyObject(data.SNSChannel.website) &&
                        <a style={{ width: 24, height: 24, display: 'inline-block', marginRight: 8 }} rel="noreferrer" target='_blank' href={data.SNSChannel.website}><IMG src={website}></IMG></a>}
                </Row4>)}

            </Row3>
        );
    }
    const renderTM = () => {
        return (
            <>
                <TMContainer>
                    <Testimonial>
                        <Content>{testimonials.content}</Content>
                        <TestimonialRow1>
                            <Writer>{testimonials.writer}</Writer>
                            <Field>{testimonials.field}</Field>
                        </TestimonialRow1>
                    </Testimonial>
                </TMContainer>
                {(pagination.lastPage > 1) &&
                    <ArtistPagination style={{ marginTop: 8 }} pagination={pagination} setPage={setPage} />}
            </>
        );
    }

    return (
        <Container>
            <BannerSection
                refetch={props.refetch}
                currentSize={props.currentSize}
                id={data._id}
                src={data.bannerURI}
                nameEN={data.nameEN}
                followCount={data.followCount}
                followed={props.data.followed}
                onRequest={props.data.onRequest} />
            {(data && data3) && (
                <InnerContainer>
                    <PCOrLargeTablet>
                        <Row1>
                            <Column1>
                                <Slogun>{data.slogun}</Slogun>
                                <Bio>{data.introduction}</Bio>
                                {data2 && renderTM()}
                            </Column1>
                            <Column2>
                                <ProfileURI src={data.profileURI}></ProfileURI>
                                {renderArtistInfo()}
                            </Column2>
                        </Row1>
                        {!isEmptyObject(data.photos) &&
                            <Row2>
                                <Slogun>포토</Slogun>
                                <PhotoContainer>
                                    {photos.map((value, i) => {
                                        return (
                                            <PictureContainer key={i} i={i}>
                                                <Picture
                                                    src={value}
                                                    onClick={() => clickPicture(i)}
                                                    key={i}
                                                    style={{ cursor: 'pointer' }} />
                                            </PictureContainer>
                                        );
                                    })}
                                </PhotoContainer>
                                {(photoPagination.lastPage > 1) &&
                                    <ArtistPagination pagination={photoPagination} setPage={setPage2} />
                                }
                            </Row2>}
                    </PCOrLargeTablet>
                    <SmallTablet>
                        <Row1>
                            <Column1>
                                <Slogun>{data.slogun}</Slogun>
                                <Bio>{data.introduction}</Bio>
                                <div style={{ display: 'flex', marginTop: 24 }}>
                                    <ProfileURI src={data.profileURI}></ProfileURI>
                                    {renderArtistInfo()}
                                </div>
                                {data2 && renderTM()}
                            </Column1>
                        </Row1>
                        {!isEmptyObject(data.photos) &&
                            <Row2>
                                <Slogun>포토</Slogun>
                                <PhotoContainer>
                                    {photos.map((value, i) => {
                                        return (
                                            <PictureContainer key={i} i={i}>
                                                <Picture
                                                    src={value}
                                                    onClick={() => clickPicture(i)}
                                                    key={i}
                                                    style={{ cursor: 'pointer' }} />
                                            </PictureContainer>
                                        );
                                    })}
                                </PhotoContainer>
                                {(photoPagination.lastPage > 1) &&
                                    <ArtistPagination style={{ marginTop: 24 }} pagination={photoPagination} setPage={setPage2} />
                                }
                            </Row2>}
                    </SmallTablet>
                    <Mobile>
                        <Row1>
                            <Column1>
                                <Slogun>{data.slogun}</Slogun>
                                <Bio>{data.introduction}</Bio>
                                <div style={{ marginTop: 24 }}>
                                    <ProfileURI src={data.profileURI}></ProfileURI>
                                    {renderArtistInfo()}
                                </div>
                                {data2 && renderTM()}
                            </Column1>
                        </Row1>
                        {!isEmptyObject(data.photos) &&
                            <Row2>
                                <Slogun>포토</Slogun>
                                <PhotoContainer>
                                    {photos.map((value, i) => {
                                        return (
                                            <PictureContainer key={i} i={i}>
                                                <Picture
                                                    src={value}
                                                    onClick={() => clickPicture(i)}
                                                    key={i}
                                                    style={{ cursor: 'pointer' }} />
                                            </PictureContainer>
                                        );
                                    })}
                                </PhotoContainer>
                                {(photoPagination.lastPage > 1) &&
                                    <ArtistPagination style={{ marginTop: 24 }} pagination={photoPagination} setPage={setPage2} />
                                }
                            </Row2>}
                    </Mobile>
                </InnerContainer>
            )
            }
            <Modal2 open={modalOpen} close={closeModal}>
                <div style={{ display: 'flex', alignItems: 'center' }}>
                    <Left
                        id="left"
                        isEnd={leftEnd}
                        onClick={goLeft} src={prev} style={{
                            transition: 'all 0.3s',
                            width: 70, height: 70, marginRight: 10
                        }} />
                    <img id="picture" src={data.photos[pictureIndex]} style={{ objectFit: 'contain', maxWidth: 700, maxHeight: 700 }} />
                    <Right
                        id="right"
                        isEnd={rightEnd}
                        onClick={goRight} src={next} style={{ transition: 'all 0.3s', width: 70, height: 70, marginLeft: 10 }} />
                </div>
            </Modal2>
        </Container>
    );
}
const Text999 = styled.div`
    ${SpoqaHansSansNeo(16, 20, 400, '#BEBEBE')};
`
const PictureContainer = styled.div`
    margin-bottom: 20px;
    @media screen and (min-width: ${BREAKPOINT_LARGE}px) {
        width: 274px;
        height: 274px;
        margin-right: ${props => (props.i % 4 !== 3) ? '20px' : '0px'};
    }
    @media screen and (min-width: ${BREAKPOINT_MIDDLE}px) and (max-width: ${BREAKPOINT_LARGE}px) {
        width: 260px;
        height: 260px;
        margin-right: ${props => (props.i % 4 !== 3) ? '20px' : '0px'};
    }
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) and (max-width: ${BREAKPOINT_MIDDLE}px){
        width: 224px;
        height:224px;
        margin-right: ${props => (props.i % 3 !== 2) ? '20px' : '0px'};
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        width: 105px;
        height: 105px;
        margin-right: ${props => (props.i % 3 !== 2) ? '8px' : '0px'};
    }
    overflow: hidden;
`
const TMContainer = styled.div`
    width: 100%;
    ${DisplayFlex('center', '')};
`
const PhotoContainer = styled.div`
    display: flex;
    @media screen and (min-width: ${BREAKPOINT_LARGE}px) {
        width: 1156px;
    }
    @media screen and (min-width: ${BREAKPOINT_MIDDLE}px) and (max-width: ${BREAKPOINT_LARGE}px) {
        width: 1100px;
    }
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) and (max-width: ${BREAKPOINT_MIDDLE}px){
        width: 712px;
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        width: 331px;
    }
    flex-wrap: wrap;
`
const Left = styled.img`
    cursor: ${props => !props.isEnd && 'pointer'};
    &:hover{
        transform: ${props => !props.isEnd && 'scale( 1.5 )'};
    }
    opacity: ${props => props.isEnd ? 0.2 : 1};
`
const Right = styled.img`
    cursor: ${props => !props.isEnd && 'pointer'};
    &:hover{
        transform: ${props => !props.isEnd && 'scale( 1.5 )'};
    }
    opacity: ${props => props.isEnd ? 0.2 : 1};
`
const Picture = styled.img`
    transition: all .3s;
    &:hover{
        transform: scale(1.1);
    }
    object-fit: cover;
    @media screen and (min-width: ${BREAKPOINT_LARGE}px) {
        width: 274px;
        height: 274px;
    }
    @media screen and (min-width: ${BREAKPOINT_MIDDLE}px) and (max-width: ${BREAKPOINT_LARGE}px) {
        width: 260px;
        height: 260px;
    }
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) and (max-width: ${BREAKPOINT_MIDDLE}px){
        width: 224px;
        height: 224px;
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        width: 105px;
        height: 105px;
    }
`
const IMG = styled.img`
    display:inline-block;
    width: ${24};
    height: ${24};
`
const ArtistType = styled.div`
  display: flex;
  margin-bottom: 14px;
`
const Title = styled.div`
    min-width: fit-content;
    ${SpoqaHansSansNeo(16, 20, 700)}
`
const Row5 = styled.div`
    margin-top: 8px;
    display: flex;
`
const Row3 = styled.div`
    margin-top: 14px;
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) and (max-width: ${BREAKPOINT_MIDDLE}px){
        margin-top: 0px;
    }
    @media screen and (min-width: ${BREAKPOINT_LARGE}px) {
    width: 548px;
    
  }
  @media screen and (min-width: ${BREAKPOINT_MIDDLE}px) and (max-width: ${BREAKPOINT_LARGE}px) {
    width: 540px;
    
  }
  @media screen and (min-width: ${BREAKPOINT_SMALL}px) and (max-width: ${BREAKPOINT_MIDDLE}px){
    width: 375px;
  }
  @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
    width: 332px;
  }
`
const Row4 = styled.div`
    margin-top: 20px;
`
const ProfileURI = styled.img`
  @media screen and (min-width: ${BREAKPOINT_LARGE}px) {
    width: 548px;
    height:548px;
  }
  @media screen and (min-width: ${BREAKPOINT_MIDDLE}px) and (max-width: ${BREAKPOINT_LARGE}px) {
    width: 540px;
    height:540px;
  }
  @media screen and (min-width: ${BREAKPOINT_SMALL}px) and (max-width: ${BREAKPOINT_MIDDLE}px){
    width: 375px;
    height:375px;
    margin-right: 20px;
  }
  @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
    width: 332px;
    height: 332px;
    margin-top: 8px;
  }
`
const TestimonialRow1 = styled.div`
    margin-top: 20px;
    display: flex;
`
const Content = styled.div`
    padding-bottom: 20px;
    border-bottom: 1px solid;
`
const Content2 = styled.div`
    ${SpoqaHansSansNeo(16, 20, 400, '#BEBEBE')};
    margin-left: 4px;
`
const Writer = styled.div`
    margin-right: 8px;
    ${SpoqaHansSansNeo(16, 20, 700)}
`
const Field = styled.div`

`
const Testimonial = styled.div`
    @media screen and (min-width: ${BREAKPOINT_LARGE}px) {
        width: 480px;
    }
    @media screen and (min-width: ${BREAKPOINT_MIDDLE}px) and (max-width: ${BREAKPOINT_LARGE}px) {
        width: 480px;
    }
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) and (max-width: ${BREAKPOINT_MIDDLE}px){
        width: 654px;
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        width: 335px;
    }
    margin-top: 40px;
    padding: 40px;
    ${SpoqaHansSansNeo(16, 20)}
    background: #310FAF;
`
const Column1 = styled.div`
    @media screen and (min-width: ${BREAKPOINT_LARGE}px) {
        width: 548px;
        margin-right: 60px;
    }
    @media screen and (min-width: ${BREAKPOINT_MIDDLE}px) and (max-width: ${BREAKPOINT_LARGE}px) {
        width: 540px;
        margin-right: 20px;
    }
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) and (max-width: ${BREAKPOINT_MIDDLE}px){
        width: 714px;
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        width: 335px;
    }  
`
const Column2 = styled.div``
const Row1 = styled.div`
    display:flex;
    @media screen and (min-width: ${BREAKPOINT_MIDDLE}px) {
        padding-bottom: 40px;
    }
    @media screen and (max-width: ${BREAKPOINT_MIDDLE}px) {
        padding-bottom: 0px;
    }  
`
const Row2 = styled.div`
    padding-top:40px;
`
const Bio = styled.div`
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) { 
        ${SpoqaHansSansNeo(16, 20, 400, '#BEBEBE')}
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        ${SpoqaHansSansNeo(14, 18, 400, '#BEBEBE')}
    }
    white-space: break-spaces;
`
const Slogun = styled.div`
    @media screen and (min-width: ${BREAKPOINT_LARGE}px) {
        ${SpoqaHansSansNeo(34, 40, 700)}
        margin-bottom: 24px;
    }
    @media screen and (min-width: ${BREAKPOINT_MIDDLE}px) and (max-width: ${BREAKPOINT_LARGE}px) {
        ${SpoqaHansSansNeo(34, 40, 700)}
        margin-bottom: 24px;
    }
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) and (max-width: ${BREAKPOINT_MIDDLE}px){
        ${SpoqaHansSansNeo(34, 40, 700)}
        margin-bottom: 24px;
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        ${SpoqaHansSansNeo(20, 24, 700)}
        margin-bottom: 20px;
    }
`
const InnerContainer = styled.div`
    padding: 0px 20px;
    padding-top: 20px;
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
        margin-top: 40px;
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        margin-top: 0px;
    }
`
const Container = styled.div`
    margin-bottom: 80px;
    width: 100%;
    ${DisplayFlex("", "center")};
    flex-direction: column;
    background-color: #01010B;
`