import axios from "axios";
import React, { useState } from "react";
import { useHistory } from "react-router";
import styled from "styled-components";
import { Button } from "../../../components/0.Button/button";
import { PurpleTextLink } from "../../../components/1.PurpleTextLink/purpleTextLink";
import { API_SERVER2 } from "../../../constants/constants";
import { DisplayFlex, SpoqaHansSansNeo } from "../../../css/css";
import { useInputs } from "../../../hook/useInputs";
import { InputForm } from "../../signUp/3.SignUpSection/1.InputForm/inputForm";
import { LoginLink } from "../../signUp/3.SignUpSection/3.LoginLink/loginLink";

export const signIn = async (history, email, password, emailError, passwordError) => {
    let params = new URLSearchParams();
    const url = API_SERVER2 + `/auth/signin/local`;
    params.append('email', email)
    params.append('password', password)
    const options = {
        method: 'POST',
        data: params,
        url: url,
        withCredentials: true
    };

    axios(options)
        .then(res => {
            if (res.status === 200) {
                window.localStorage.setItem("isLogin", true);
                window.localStorage.setItem("userData", JSON.stringify(res.data));
                history.push("/")
            }
        })
        .catch(error => {
            const errorCode = error.response.data.code;

            if (errorCode === 100006) {
                emailError()
            }
            else if (errorCode === 100005) {
                passwordError()
            }
        })
}

export function LoginSection() {
    const history = useHistory();
    const [requirements, setRequirements] = useState([
        {
            id: 'email',
            activate: false,
        },
        {
            id: 'password',
            activate: false,
        },
    ]);
    const [{ password, email }, onChange] = useInputs({
        password: '',
        email: ''
    });

    const setRequirement = (id, activate) => {
        setRequirements(
            requirements.map(requirement =>
                requirement.id === id ? { ...requirement, activate: activate } : requirement
            )
        );
    }
    const handleKeyPress = (e) => {
        if (e.key === "Enter") {
            signIn(
                history,
                email,
                password,
                () => setRequirement('email', true),
                () => setRequirement('password', true))
        }
    };
    return (
        <Container>
            <InnerContainer>
                <InputForm
                    onKeyPress={(e) => handleKeyPress(e)}
                    style={{ marginBottom: 48 }}
                    onChange={onChange}
                    requirement={requirements[0].activate}
                    message={'*등록되지않은 이메일입니다.'}
                    name="email"
                    value={email}
                    placeholder="example@example.com">Email Address</InputForm>
                <InputForm
                    onKeyPress={(e) => handleKeyPress(e)}
                    style={{ marginBottom: 24 }}
                    onChange={onChange}
                    requirement={requirements[1].activate}
                    message={'*비밀번호를 다시 확인해주세요.'}
                    name="password"
                    value={password}
                    placeholder="영문, 숫자, 특수문자 포함 8자 이상">Password</InputForm>
                <Text><PurpleTextLink style={{ marginLeft: 'auto' }} to={'/pwrecovery'} linkTag={true}>비밀번호 찾기</PurpleTextLink></Text>
                <Button
                    onClick={() => signIn(
                        history,
                        email,
                        password,
                        () => setRequirement('email', true),
                        () => setRequirement('password', true))}
                    activate={true}
                    style={{ width: 372, height: 48 }}>
                    LOG IN
                </Button>
                <LoginLink>STAGE8 회원이 아니신가요? <PurpleTextLink to={'/usertypeselect'} linkTag={true}>회원가입</PurpleTextLink></LoginLink>
            </InnerContainer>
        </Container >
    );
}
const Text = styled.div`
    display:flex;
    ${SpoqaHansSansNeo(12, 14, 700)};
    margin-bottom:40px;
`
const Container = styled.div`
    margin-top:80px;
    ${DisplayFlex("center", "")}
`
const InnerContainer = styled.div`
    padding: 80px 195px;
    width: 764px;
    border: 1px solid #F7F7F7;
    border-radius: 8px;
`