import React from "react";
import styled from "styled-components";
import { DisplayFlex, SpoqaHansSansNeo } from "../../../../../css/css";

export function Pagination(props) {
  const { number, isCurrent, setPage } = props;
  return (
    <Container isCurrent={isCurrent} onClick={() => setPage(number)}>
        <Number number={number} isCurrent={isCurrent}>{number}</Number>
    </Container>
  );
}
const Container = styled.div`
  transition: all 0.3s;
  cursor:${props => !props.isCurrent && 'pointer'};
  ${DisplayFlex('center','center')}
  height: 32px;
  margin: 0px 4px;
  width: 32px;
  background: ${props => props.isCurrent && '#4A4A4A'};
  &:hover {
    background: ${props => !props.isCurrent && '#4A4A4A'};
  }
  border-radius: 50em;
`
const Number = styled.div`
  ${SpoqaHansSansNeo(16, 24, 700,'#F7F7F7')};
  position: relative;
  left: 1px;
  top: 1px;
`


