import { useState } from "react";
import { Button } from "src/ts/components/button/button";
import { EditButton } from "src/ts/components/button/editButton";
import { onChange } from "src/ts/lib/lib";
import { usePatchDisco } from "src/ts/models/artistProfile/profileBanner";
import { FontNormal, makeCenter } from "src/ts/styles/css";
import styled from "styled-components";
import { BlankButton } from "../../components/button/blankButton";
import { InputForm } from "../../components/input/inputForm";
import {
    Modal,
    ModalFooter,
    ModalHeader,
    ModalMain,
} from "../../components/modal/modal";

export const UpdateDisco = () => {
    const [isOpen, setIsOpen] = useState(false);
    const [patch] = usePatchDisco();
    const [members, setMembers] = useState("");

    return (
        <>
            <EditButton
                mini={true}
                rightAlign={true}
                onClick={() => setIsOpen(true)}
            />
            <Modal isOpen={isOpen} empty={false}>
                {isOpen && (
                    <>
                        <ModalHeader>{"앨범정보 수정"}</ModalHeader>
                        <ModalMain>
                            <FontNormal style={{ marginBottom: 20 }}>
                                입력값이 없을 경우 항목이 보이지 않습니다.
                            </FontNormal>
                            <InputForm
                                onKeyPress={() => {}}
                                onChange={onChange(setMembers)}
                                errorMessage={"*등록되지않은 이메일입니다."}
                                value={members}
                                placeholder="앨범정보를 입력해 주세요."
                            ></InputForm>
                        </ModalMain>
                        <ModalFooter>
                            <BlankButton
                                height={40}
                                width={96}
                                onClick={() => {
                                    setIsOpen(false);
                                }}
                            >
                                취소
                            </BlankButton>
                            <Button
                                rightAlign={true}
                                height={40}
                                width={96}
                                onClick={() => patch(members)}
                            >
                                {"확인"}
                            </Button>
                        </ModalFooter>
                    </>
                )}
            </Modal>
        </>
    );
};

const InputFormRow = styled.div`
    display: flex;
`;
const Text = styled.div`
    width: 30px;
    ${makeCenter()}
`;
