import React from "react";
import styled from "styled-components";
import { SpoqaHansSansNeo } from "../../css/css";
import download from '../../images/download.svg'

export function UpdateContent({data, src, name, first, last}) {
    const devideURL = (url) => {
        var arSplitUrl   = url.split("/");    //   "/" 로 전체 url 을 나눈다
        var nArLength     = arSplitUrl.length;
        var arFileName         = arSplitUrl[nArLength-1];   // 나누어진 배열의 맨 끝이 파일명이다
        return arFileName;
    }

    const printDate = (date) => {
        let replaced_str = date.replaceAll('-', '.');
        replaced_str = replaced_str.replaceAll('T', ' · ');
        let index = replaced_str.indexOf(':',16);
        let result = replaced_str.slice(0,index)
        
        return result
    }

    return (
        <Container first={first} last={last}>
            <Column1>
                <Profile src={src}/>
            </Column1>
            <Column2>
                <div style={{display:'flex',width:'100%'}}>
                    <Name>{name}</Name>
                    <CreatedAt>{printDate(data.createdAt)}</CreatedAt>
                </div>
                    
                    <Text>{data.description}</Text>
                    {
                    data.fileURI.map((url, i) => {
                        return (
                            <Data key={i}>
                                <div style={{ display: 'flex', alignItems: 'center' }}>
                                    <Text >{devideURL(unescape(url))}</Text>
                                    <a href={url} download>
                                        <img src={download} style={{width:24,height:24,marginLeft:8}}/>
                                    </a>
                                </div>
                            </Data>);

                        })
                    }
            </Column2>
        </Container>
    );
}
const CreatedAt = styled.div`
    margin-left: auto;
    ${SpoqaHansSansNeo(16,20,400,'#828282')};
    text-align: right;
`
const Profile = styled.img`
    width: 48px;
    height: 48px;
    border-radius: 50em;
`
const Name = styled.div`
    margin-bottom: 8px;
    ${SpoqaHansSansNeo(16,20,700)};
`
const Text = styled.div`
    ${SpoqaHansSansNeo(16,20)};
`
const Data = styled.div`
    margin-top: 20px;
`
const Column1 = styled.div`
    margin-top: 8px;
    margin-right: 16px;
`
const Column2 = styled.div`
    margin-top: 8px;
    width: 100%;
`
const Container = styled.div`
    border-top: ${props=>!props.first && '1px solid #DBDBDB'};
    margin-top: ${props=>!props.first && '20px'};
    margin-bottom: ${props=>!props.last && '20px'};
    display: flex;
`