import axios from "axios";
import React, { useState } from "react";
import { useParams } from "react-router";
import styled from "styled-components";
import { API_SERVER2, BREAKPOINT_MIDDLE, BREAKPOINT_SMALL } from "../../constants/constants";
import { SpoqaHansSansNeo } from "../../css/css";
import useAsync from "../../hook/useAsync";
import { VideoColumn } from "../artistProfile/2.Portfolio/2.Video/videoColumn";
import { YoutubeModal } from "../artistProfile/2.Portfolio/2.Video/youtubeModal";
import { NavBar } from "../main/1.NavBar/0.NavBar/navBar";
import { Footer } from "../main/6.Footer/0.Footer/footer";

export function PortfolioDetail({ currentSize }) {
    const portfolioID = useParams().portfolioID;

    const getPortflio = async () => {
        const url = API_SERVER2 + `/portfolios/${portfolioID}`;
        const response = await axios.get(
            url, { withCredentials: true });

        return response.data;
    }
    const [state] = useAsync(getPortflio, []);
    const [modalOpen, setModalOpen] = useState(false);
    const [videoID, setVideoID] = useState(null);
    const openModal = (videoID) => {
        setVideoID(videoID)
        setModalOpen(true);
    }
    const closeModal = () => {
        setModalOpen(false);
    }

    const { data } = state;
    let portfolioImageURI, description,
        category, name, fromYear, toYear, year, client, genre, videos, id = null;

    if (data) {
        videos = data.portfolio.videos;
        portfolioImageURI = data.portfolio.portfolioImageURI;
        const printCategory = () => {
            const a = [];
            data.portfolio.category.map((v, i) => {

                a.push(v);
                if (data.portfolio.category.length - 1 !== i)
                    a.push(" · ")
            })
            return a;
        }
        category = printCategory();
        name = data.portfolio.name;

        fromYear = data.portfolio.year[0];
        toYear = data.portfolio.year[1];
        if (fromYear === toYear) {
            year = fromYear;
        } else {
            year = fromYear + " - " + toYear;
        }
        const printGenre = () => {
            const a = [];
            data.portfolio.genre.map((v, i) => {

                a.push(v);
                if (data.portfolio.genre.length - 1 !== i)
                    a.push(" · ")
            })
            return a;
        }
        genre = printGenre();
        client = data.portfolio.client;
        description = data.portfolio.description;
    }

    return (
        <Container >
            {data && (<>            <NavBar currentSize={currentSize} />
                <InnerContainer2>
                    <InnerContainer>
                        <Row1>
                            <IMG src={portfolioImageURI} />
                            <Info>
                                <Category>{category}</Category>
                                <Name>{name}</Name>
                                <Row3>
                                    <Row2>
                                        <Title>수행연도</Title>
                                        <Content>{year}</Content>
                                    </Row2>
                                    <Row2>
                                        <Title>의뢰자</Title>
                                        <Content>{client}</Content>
                                    </Row2>
                                    <Row2>
                                        <Title>장르</Title>
                                        <Content>{genre}</Content>
                                    </Row2>
                                    <Row2>
                                        <Title>프로젝트 소개</Title>
                                        <Content>{description}</Content>
                                    </Row2>
                                </Row3>
                            </Info>
                        </Row1>
                        {videos[0] !== null &&
                            videos.length !== 0 &&
                            <>
                                <Text777>관련 비디오</Text777>
                                <VideoList>
                                    {videos.map((video, i) => {
                                        return (<VideoColumn
                                            forPortfolio={true}
                                            openModal={openModal}
                                            key={i}
                                            video={video}
                                            marginBottom={20}
                                            marginRight={(i % 4 !== 3) && 20} />);
                                    })}
                                </VideoList></>
                        }
                    </InnerContainer>

                </InnerContainer2>
                <Footer />
                <YoutubeModal videoID={videoID} open={modalOpen} close={closeModal} />
            </>)}

        </Container>
    );
}
const VideoList = styled.div`
    flex-wrap: wrap;
    display:flex;
    margin-top:24px;
`
const Text777 = styled.div`
    ${SpoqaHansSansNeo(24, 28, 700)}
    margin-top: 80px;
`
const InnerContainer2 = styled.div`
    @media screen and (min-width: ${BREAKPOINT_MIDDLE}px) { 
        padding-top: 80px;
    }
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) and (max-width: ${BREAKPOINT_MIDDLE}px) {
        padding-top: 56px;
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        padding-top: 56px;
    }
`
const Row3 = styled.div`
    margin-top: 24px;
`
const Row2 = styled.div`
    margin-top: 8px;
    display: flex;
`
const Title = styled.div`
    width: 120px;
    ${SpoqaHansSansNeo(16, 20, 700, '#BEBEBE')}
`
const Content = styled.div`
    width: 604px;
    ${SpoqaHansSansNeo(16, 20, 400, '#BEBEBE')}
`
const Participant = styled.div`
    ${SpoqaHansSansNeo(16, 20)}
    width: fit-content;
    cursor: pointer;
    text-decoration: none;
    &:hover { text-decoration: underline;}
    margin-top: 8px;
`
const Name = styled.div`
    margin-top: 8px;
    ${SpoqaHansSansNeo(34, 70, 700)}
`
const Category = styled.div`
    ${SpoqaHansSansNeo(16, 20, 400, '#D0B1FF')}
`
const Info = styled.div`
    margin-left: 40px;
`
const IMG = styled.img`
    width: 372px;
    height: 372px;
`
const Row1 = styled.div`
    display: flex;
`
const InnerContainer = styled.div`
    width: 1440px;
    min-height: 700px;
    padding: 80px 142px;
`
const Container = styled.div`
    display: flex;
    align-items: center;
    flex-direction: column;
    background-color:#01010B;
`