import React, { useState } from "react";
import { Button } from "src/ts/components/button/button";
import { BREAKPOINT_LARGE, BREAKPOINT_MIDDLE, BREAKPOINT_SMALL } from "src/ts/constants";
import { useDeleteVideo } from "src/ts/models/artistProfile/profileBanner";
import { SpoqaHansSansNeo } from "src/ts/styles/css";
import styled from "styled-components";
import { AddVideo } from "./addVideo";
import { VideoColumn } from "./videoColumn";
import { YoutubeModal } from "./youtubeModal";
export function Video({ videos, currentSize }) {
    const [modalOpen, setModalOpen] = useState(false);
    const [fetch] = useDeleteVideo()
    const [videoID, setVideoID] = useState(null);
    const openModal = (videoID) => {
        setVideoID(videoID)
        setModalOpen(true);
    }
    const closeModal = () => {
        setModalOpen(false);
    }

    return (
        <Container>
            <InnerContainer>
                <TopRow>
                    <Text>동영상</Text>
                    <AddVideo />
                </TopRow>
                <VideoList>
                    {videos.map((video, i) => {

                        return (<div > <Button
                            style={{
                                background:
                                    "#ff4e45",
                            }}
                            width={40}
                            height={40}
                            onClick={() => {
                                fetch(video._id);
                            }}
                        >
                            삭제
                        </Button><VideoColumn
                                openModal={openModal}
                                key={i}
                                video={video}
                                i={i} /></div>);
                    })}
                </VideoList>
                <YoutubeModal currentSize={currentSize} videoID={videoID} open={modalOpen} close={closeModal} />
            </InnerContainer>
        </Container>
    );
}

const TopRow = styled.div`
    display: flex;
`
const VideoList = styled.div`
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) { 
        flex-wrap: wrap;
        display:flex;
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        -ms-overflow-style: none;
        overflow-x: scroll;
        -ms-scroll-chaining: none;
        -ms-scroll-snap-type: mandatory;
        scroll-snap-type: mandatory;
        scrollbar-width: none; /* Firefox */
        &::-webkit-scrollbar {
            display: none; /* Chrome, Safari, Opera*/
        }
        -ms-scroll-snap-points-x: snapInterval(0%, 10%);
        display:flex;
    }
    margin-top:24px;
`
const Text = styled.div`
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) { 
        ${SpoqaHansSansNeo(34, 40, 700)}
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        ${SpoqaHansSansNeo(20, 24, 700)}
        margin-bottom: 20px;
        margin-left: 20px;
    }
`
const Container = styled.div`
width: 100%;
    
`

const InnerContainer = styled.div`
    @media screen and (min-width: ${BREAKPOINT_LARGE}px) {
        
        padding: 40px 20px;
    }
    @media screen and (min-width: ${BREAKPOINT_MIDDLE}px) and (max-width: ${BREAKPOINT_LARGE}px) {
        
        padding: 40px 20px;
    }
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) and (max-width: ${BREAKPOINT_MIDDLE}px){
        
        padding: 40px 20px;
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        width: 100%;
        padding: 20px 0px;
    }
`
const AddButton = styled.div`
    cursor: pointer;
    margin-left: auto;
    display: flex;
`;
const PlusImage = styled.img``;