import React, { useState } from "react";
import styled from "styled-components";
import { File } from "./file";
import { Button } from "../../components/0.Button/button";
import { SpoqaHansSansNeo } from "../../css/css";
import { BREAKPOINT_SMALL } from "../../constants/constants";

export function AttachFiles({
    files,
    addFile,
    deleteFile,
    setOverSizeModalOpen,
    setOverSize,
    currentSize,
}) {
    const [totalSize, setTotalSize] = useState(0);
    const [frontNum, setFrontNum] = useState(0);
    const [backNum, setBackNum] = useState(0);
    const handleFileInput = e => {
        if (e.target.files.length === 0) {
            return;
        } else if (totalSize + e.target.files[0].size > 1024 * 1024 * 10) {
            setOverSizeModalOpen(true);
            setOverSize(
                Math.round((e.target.files[0].size / 1024 / 1024) * 100) / 100 +
                "MB",
            );
            return;
        }
        addFile(files.concat(e.target.files[0]));
        setTotalSize(totalSize + e.target.files[0].size);
        const sizeString = (totalSize + e.target.files[0].size).toString();
        setFrontNum(sizeString.substr(0, sizeString.length - 3));
        setBackNum(sizeString.substr(sizeString.length - 3, 2));
    };
    const deleteFileAndResizing = (name, size) => {
        deleteFile(name);
        setTotalSize(totalSize - size);
        const sizeString = (totalSize - size).toString();
        setFrontNum(sizeString.substr(0, sizeString.length - 3));
        setBackNum(sizeString.substr(sizeString.length - 3, 2));
    };
    const printFrontNum = () => {
        if (frontNum.length === 0) {
            return "00";
        } else if (frontNum.length === 1) {
            return "0" + frontNum;
        } else {
            return frontNum;
        }
    };
    return (
        <Container>
            <SmallTItle>레퍼런스 및 관련 파일</SmallTItle>
            <Discription>
                프로젝트에 관련된 파일이 있으면 첨부해주세요. 프로젝트 기획서,
                음원 소스, 스토리보드 등 무엇이든 괜찮습니다.
            </Discription>
            <Button
                label={true}
                round={false}
                width={currentSize !== "MOBILE" ? 166 : 104}
                height={currentSize !== "MOBILE" ? 48 : 32}
            >
                파일 첨부
                <input
                    style={{ display: "none" }}
                    type="file"
                    name="file"
                    onChange={e => handleFileInput(e)}
                />
            </Button>
            <DataLimit>
                {totalSize == 0
                    ? "(00.00 KB / 10.00 MB 사용)"
                    : "( " +
                    printFrontNum() +
                    "." +
                    backNum +
                    "KB / 10.00 MB 사용)"}
            </DataLimit>
            <List file={files.length > 0}>
                {files.map((file, i) => (
                    <File
                        deleteFile={deleteFileAndResizing}
                        key={i}
                        name={file.name}
                        size={file.size}
                    />
                ))}
            </List>
        </Container>
    );
}
const List = styled.div`
    margin-top: ${props => props.file && "16px"};
`;
const DataLimit = styled.div`
    margin-left: 4px;
    margin-top: 4px;
    ${SpoqaHansSansNeo(12, 14, 400, "#F7F7F7", "0em")};
`;
const Container = styled.div``;
const SmallTItle = styled.div`
    margin-top: 40px;
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
        ${SpoqaHansSansNeo(24, 28, 700)};
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        ${SpoqaHansSansNeo(16, 20, 700)};
    }
`;
const Discription = styled.div`
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
        margin: 16px 0px;
        ${SpoqaHansSansNeo(16, 20)};
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        margin: 8px 0px;
        ${SpoqaHansSansNeo(14, 16, 400, "#F7F7F7", "0em")};
    }
`;
