import React from "react";
import styled from "styled-components";
import activeImage from '../../images/activate.svg'
import inactivateImage from '../../images/inactivate.svg'
export function CheckBox({ activate, onClick, style, requirement, hover=false, inactivate = false }) {
    return (
        <Container
            inactivate={inactivate}
            hover={hover}
            requirement={requirement}
            style={style} 
            activate={activate} 
            onClick={onClick}>
            { activate && <IMG src={inactivate ? inactivateImage : activeImage} /> }
        </Container>
    );
}

const Container = styled.div`
    transition: all .5s;
    cursor: ${props=>!props.inactivate && 'pointer'};
    border: ${props => !props.activate ? props.requirement ? '1px solid #FF4E45' : '1px solid #828282' : null};
    &:hover{
        border-color: ${props=>props.hover && '#8700C5'}
    }
    border-color: ${props=>props.inactivate && '#4A4A4A'};
    box-sizing: ${props => !props.activate && 'border-box'};
    border-radius: ${props => !props.activate && '4px'};
    height: 20px;
    width: 20px;
    margin-right:8px;
    -ms-user-select: none; 
    -moz-user-select: -moz-none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    user-select: none;
`
const IMG = styled.img`
    height: 20px;
    width: 20px;
`