import { useState } from "react";
import { Button } from "src/ts/components/button/button";
import { EditButton } from "src/ts/components/button/editButton";
import { onChange } from "src/ts/lib/lib";
import { usePatchSNS } from "src/ts/models/artistProfile/profileBanner";
import { FontNormal } from "src/ts/styles/css";
import { BlankButton } from "../../components/button/blankButton";
import { InputForm } from "../../components/input/inputForm";
import {
    Modal,
    ModalFooter,
    ModalHeader,
    ModalMain,
} from "../../components/modal/modal";

export const UpdateFacebook = () => {
    const [isOpen, setIsOpen] = useState(false);
    const [patch] = usePatchSNS();
    const [facebook, setFacebook] = useState("");
    const [insta, setInsta] = useState("");
    const [twitter, setTwitter] = useState("");
    const [youtube, setYoutube] = useState("");
    const [website, setWebsite] = useState("");

    return (
        <>
            <EditButton
                mini={true}
                rightAlign={true}
                onClick={() => setIsOpen(true)}
            />
            <Modal isOpen={isOpen} empty={false}>
                {isOpen && (
                    <>
                        <ModalHeader>{"SNS 링크 수정"}</ModalHeader>
                        <ModalMain>
                            <FontNormal style={{ marginBottom: 20 }}>
                                입력값이 없을 경우 항목이 보이지 않습니다.
                            </FontNormal>
                            <InputForm
                                marginBottom={8}
                                onKeyPress={() => {}}
                                onChange={onChange(setFacebook)}
                                errorMessage={"*등록되지않은 이메일입니다."}
                                value={facebook}
                                placeholder="Facebook URL을 입력해 주세요."
                            ></InputForm>
                            <InputForm
                                marginBottom={8}
                                onKeyPress={() => {}}
                                onChange={onChange(setInsta)}
                                errorMessage={"*등록되지않은 이메일입니다."}
                                value={insta}
                                placeholder="Instagram URL을 입력해 주세요."
                            ></InputForm>
                            <InputForm
                                marginBottom={8}
                                onKeyPress={() => {}}
                                onChange={onChange(setTwitter)}
                                errorMessage={"*등록되지않은 이메일입니다."}
                                value={twitter}
                                placeholder="Twitter URL을 입력해 주세요."
                            ></InputForm>
                            <InputForm
                                marginBottom={8}
                                onKeyPress={() => {}}
                                onChange={onChange(setYoutube)}
                                errorMessage={"*등록되지않은 이메일입니다."}
                                value={youtube}
                                placeholder="Youtube URL을 입력해 주세요."
                            ></InputForm>
                            <InputForm
                                marginBottom={8}
                                onKeyPress={() => {}}
                                onChange={onChange(setWebsite)}
                                errorMessage={"*등록되지않은 이메일입니다."}
                                value={website}
                                placeholder="Website URL을 입력해 주세요."
                            ></InputForm>
                        </ModalMain>
                        <ModalFooter>
                            <BlankButton
                                height={40}
                                width={96}
                                onClick={() => {
                                    setIsOpen(false);
                                }}
                            >
                                취소
                            </BlankButton>
                            <Button
                                rightAlign={true}
                                height={40}
                                width={96}
                                onClick={() =>
                                    patch(
                                        facebook,
                                        insta,
                                        twitter,
                                        youtube,
                                        website,
                                    )
                                }
                            >
                                {"확인"}
                            </Button>
                        </ModalFooter>
                    </>
                )}
            </Modal>
        </>
    );
};
