import axios, { AxiosRequestConfig } from "axios";
import { useCallback } from "react";
import { atom, atomFamily, selectorFamily, useSetRecoilState } from "recoil";
import { API_SERVER2 } from "src/ts/constants";
import { CPageAPIResponse } from "src/ts/types";
import { getNameForURIFromURI } from "../../lib/lib";
import { currentSizeSelector } from "../currentSize";
import { artistTriggerState } from "./profileBanner";
interface IArtistPortfolio {
    files: File;
    name: string;
    withStage8: boolean;
    participants: string[];
    category: string[];
    year: string[];
    client: string;
    genre: string[];
    description: string;
    videos: string[];
}
interface IParticipant {
    artistID: string;
    nameEN: string;
    nameForURI: string;
    nameKO: string;
}

interface IPortfolio {
    category: string[];
    name: string;
    participants: IParticipant[];
    portfolioImageURI: string;
    videos: string[];
    withStage8: boolean;
    year: string[];
}

interface IArtistPortFolioData extends CPageAPIResponse {
    data: IPortfolio[];
}
export const artistPortFolioPageState = atomFamily({
    key: "artistPortFolioPageState",
    default: 1,
});
export const artistPortFolioProfileState = atom<any>({
    key: "artistPortFolioProfileState",
    default: null,
});
export const artistPortFolioProfileSelector = selectorFamily({
    key: "artistPortFolioProfileSelector",
    get: profile => () => {},
    set:
        profile =>
        ({ set }) => {
            set(artistPortFolioProfileState, profile);
        },
});
export const artistPortFolioSelector = selectorFamily({
    key: "artistPortFolioSelector",
    get:
        (kind: string) =>
        async ({ get }) => {
            get(artistTriggerState);
            const page = get(artistPortFolioPageState(kind));
            const nameForURI = getNameForURIFromURI();
            const currentSize = get(currentSizeSelector);

            const culLimitNum = () => {
                if (currentSize === "LARGE" || currentSize === "MIDDLE") {
                    return 4;
                } else if (currentSize === "SMALL") {
                    return 3;
                } else {
                    return 3;
                }
            };

            const res = await axios.post<IArtistPortFolioData>(
                API_SERVER2 +
                    `/portfolios/artist/by-name-uri?kind=${kind}&page=${page}&limit=${culLimitNum()}`,
                {
                    nameForURI: nameForURI,
                },
                { withCredentials: true },
            );

            return res;
        },
});

export function usePostArtistPortfolio() {
    const setArtistTriger = useSetRecoilState(artistTriggerState);
    const update = useCallback((data: IArtistPortfolio) => {
        if (data === null) return;

        const options: AxiosRequestConfig = {
            method: "POST",
            data: data,
            url: API_SERVER2 + `/artists/portfolios`,
            withCredentials: true,
        };
        axios(options).then(() => setArtistTriger(Date.now()));
    }, []);

    return [update];
}
