import axios from "axios";
import React, { useState } from "react";
import styled from "styled-components";
import { API_SERVER2, BREAKPOINT_SMALL } from "../../../../../constants/constants";
import { SpoqaHansSansNeo } from "../../../../../css/css";
import useAsync from "../../../../../hook/useAsync";
import noti from '../../../../../images/noti.svg'
import { Mobile, NotMobile } from "../../../../../lib/mediaQuery";
import { MobileNoti } from "./mobileNoti";

export function Noti({ isOpen, setIsOpen }) {
  const [mobileNoti, setMobileNoti] = useState(false);
  const userType = JSON.parse(window.localStorage.getItem('userData')).userType;
  const [sibal, setSibal] = useState(true);

  const toggling = () => {
    setIsOpen(!isOpen);
    setMobileNoti(true);
    setSibal(false);
  }
  const gotoEstimate = (id, projectID, estimateID) => {
    setCheckNoti(id);
    window.location.replace(`/estimatedetail/${projectID}/${estimateID}/${userType}`);
    window.scrollTo(0, 0);
  }
  const gotoProject = (id) => {
    setCheckNoti(id);
    window.location.replace(`/mypage/myprojects`);
    window.scrollTo(0, 0);
  }
  const onTransitionEnd = () => {
    if (!isOpen) {
      setSibal(true)
    }
    else {
      setSibal(false)
    }

  }
  const setCheckNoti = async (notiID) => {
    const url = API_SERVER2 + `/notifications/${notiID}/set-checked`;
    const response = await axios.get(
      url, { withCredentials: true });

    return response.data;
  }
  const getNoti = async () => {
    const url = API_SERVER2 + `/notifications/my`;
    const response = await axios.get(
      url, { withCredentials: true });

    return response.data;
  }
  const [state] = useAsync(getNoti, []);
  const { data } = state;
  let notifications = null;
  if (data) {
    notifications = data.notifications;
  }
  const printCount = (num) => {
    if (num < 10) {
      return num;
    }
    else if (num > 9 && num < 101) {
      return 10 + '+';
    }
    else {
      return 100 + '+';
    }
  }

  return (
    <Container >
      <IMG src={noti} onClick={toggling} style={{ cursor: 'pointer' }} />
      {(data && (notifications.length > 0)) && <Count style={{ cursor: 'pointer' }} onClick={toggling}><Text120>{printCount(notifications.length)}</Text120></Count>}
      <NotMobile>
        {(data && (notifications.length > 0)) ? (
          <NotiBox
            sibal={sibal}
            onTransitionEnd={onTransitionEnd}
            isOpen={isOpen}>
            <Inner>
              {notifications.map((data, i) => {
                if (data.message === "new estimate from artist") {
                  return (
                    <NotiOne
                      sibal={sibal} key={i} last={i === (notifications.length - 1)} isOpen={isOpen}>
                      <Name
                        onClick={() => {
                          setCheckNoti(data._id);
                          window.location.replace(`/${data.nameForURI}/`);
                          window.scrollTo(0, 0);
                        }}>{data.artistName}
                      </Name>
                      <Text1>의 <Name onClick={() => gotoEstimate(data._id, data.projectID, data.estimateID)}>견적서</Name>가 도착했습니다.</Text1>
                      <CreatedAt>{" " + data.createdAt}</CreatedAt>
                    </NotiOne>
                  )
                } else if (data.message === "new update") {
                  return (
                    <NotiOne
                      sibal={sibal} key={i} last={i === (notifications.length - 1)} isOpen={isOpen}>
                      <Name onClick={() => gotoEstimate(data._id, data.projectID, data.estimateID)}>{'"' + data.projectName + '"'}</Name><Text1> 이 업데이트 되었습니다.</Text1>
                      <CreatedAt>{" " + data.createdAt}</CreatedAt>
                    </NotiOne>
                  )
                } else if (data.message === "progress updated") {
                  return (
                    <NotiOne
                      sibal={sibal} key={i} last={i === (notifications.length - 1)} isOpen={isOpen}>
                      <Name onClick={() => gotoProject(data._id, data.projectID)}>{'"' + data.projectName + '"'}</Name><Text1> 이 업데이트 되었습니다.</Text1>
                      <CreatedAt>{" " + data.createdAt}</CreatedAt>
                    </NotiOne>
                  )
                } else if (data.message === "progress updated/toMyProject") {
                  return (
                    <NotiOne
                      sibal={sibal} key={i} last={i === (notifications.length - 1)} isOpen={isOpen}>
                      <Name onClick={() => gotoProject(data._id, data.projectID)}>{'"' + data.projectName + '"'}</Name><Text1> 이 업데이트 되었습니다.</Text1>
                      <CreatedAt>{" " + data.createdAt}</CreatedAt>
                    </NotiOne>
                  )
                }
              })
              }</Inner>
          </NotiBox>
        )
          :
          <NotiBox
            sibal={sibal}
            onTransitionEnd={onTransitionEnd}
            isOpen={isOpen}>
            <Text1010 isOpen={isOpen}>받은 알림이 없습니다.</Text1010>
          </NotiBox>
        }
      </NotMobile>
      <Mobile>
        {data &&         <MobileNoti 
          gotoProject={gotoProject}
          gotoEstimate={gotoEstimate} 
          setCheckNoti={setCheckNoti} 
          sibal={sibal} 
          notifications={notifications} 
          mobileNoti={mobileNoti} 
          setMobileNoti={setMobileNoti}>
        </MobileNoti>}

      </Mobile>
    </Container>
  );
}
const Text1010 = styled.div`
  transition: all .3s;
  opacity: ${props => props.isOpen ? 1 : 0};
  ${SpoqaHansSansNeo(16, 20, 400, 'rgba(255, 255, 255, 0.87)')};
`
const Text120 = styled.div`
  position: relative;
  ${SpoqaHansSansNeo(12, 14, 700, '#F7F7F7', '0em')};
`
const Count = styled.div`
  padding: 2px 6px;
  position: absolute;
  left: 25px;
  @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
    top: 1px;
  }
  @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
    top: -8px;
  }
  height: 16px;
  border-radius: 50em;
  background: #BD03FA;
`
const CreatedAt = styled.span`
  position: relative;
  top: 1px;
  ${SpoqaHansSansNeo(16, 20, 400, '#4A4A4A')};
`
const Text1 = styled.span`
  ${SpoqaHansSansNeo(16, 20, 400)};
`
const Inner = styled.div`
`
const Name = styled.span`
  cursor: pointer;
  &:hover { text-decoration: underline; }
  ${SpoqaHansSansNeo(16, 20, 700)};
`
const NotiOne = styled.div`
  display: ${props => (props.sibal ? 'none' : 'block')};
  padding: ${props => props.isOpen ? '8px 8px' : '0px 8px'};
  border-bottom: ${props => !props.last && '1px solid #F7F7F7'};
  transition: all .3s;
  opacity: ${props => props.isOpen ? 1 : 0};
`
const NotiBox = styled.div`
  overflow-y: auto;
  &::-webkit-scrollbar {
      display: none; 
  }
  -ms-overflow-style: none;
  scrollbar-width: none; 
  padding: ${props => props.isOpen ? '16px 16px' : '0px 16px'};
  transition: all 0.3s;
  max-height:${props => props.isOpen ? '240px' : '0px'};
  @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
    top:40px;
  }
  @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
    top:30px;
  }
  left:-245px;
  position: absolute;
  width: 288px;
  background: #0E0E10;
  border:${props => props.isOpen ? '1px solid #4A4A4A' : '1px solid #0E0E10'};
  border-radius: 4px;
`
const Container = styled.div`
  box-sizing:border-box;
  position: relative;
  justify-content: center;
  align-items: center;
  display: flex;
  @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
    height: 40px;
    width: 48px;
    margin-right:4px;
  }
  @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
    width: 52px;
    height: 16px;
    padding: 0px 4px;
  }
`
const IMG = styled.img`

`