/* eslint-disable no-unused-vars */
import axios from "axios";
import React, { useEffect, useState } from "react";
import styled from "styled-components";
import { Button } from "../../components/0.Button/button";
import { CheckBox } from "../../components/0.Button/checkBox";
import { RoundButton } from "../../components/0.Button/roundButton";
import { Toggle } from "../../components/0.Button/toggle";
import { Input } from "../../components/4.Input/input";
import { Tooltip } from "../../components/5.tooltip/tooltip";
import {
    API_SERVER2,
    BREAKPOINT_LARGE,
    BREAKPOINT_MIDDLE,
    BREAKPOINT_SMALL
} from "../../constants/constants";
import { DisplayFlex, SpoqaHansSansNeo } from "../../css/css";
import useAsync from "../../hook/useAsync";
import { useInputs } from "../../hook/useInputs";
import camera from "../../images/camera.svg";
import chevronDown from "../../images/chevron-down.svg";
import confirmed from "../../images/confirmed.svg";
import profile from "../../images/profile.svg";
import { SecessionModal } from "./secessionModal";
import { SettingSuccessModal } from "./settingSuccessModal";

export function Settings({ setSmallProfileURI, currentSize }) {
    const setUserSettings = async (name, value) => {
        let form = new FormData();
        form.append(name, value);
        const url = API_SERVER2 + `/users/my-settings`;
        axios
            .patch(url, form, { withCredentials: true })
            .then(function (response) {
                setSettingModalOpen(true);
                let newUserData = JSON.parse(
                    window.localStorage.getItem("userData"),
                );
                newUserData.nickname = response.data.data.user.nickname;
                if (response.data.data.user.smallProfileURI !== undefined)
                    newUserData.smallProfileURI =
                        response.data.data.user.smallProfileURI;
                setSmallProfileURI(newUserData.smallProfileURI);
                window.localStorage.setItem(
                    "userData",
                    JSON.stringify(newUserData),
                );
                return response.data;
            });
    };
    const getUserSettings = async () => {
        const url = API_SERVER2 + `/users/my-settings`;
        const response = await axios.get(url, { withCredentials: true });

        return response.data;
    };
    const [state, refetch] = useAsync(getUserSettings, []);
    const { data } = state;

    const validateEmail = e => {
        const value = e.target.value;
        var regExp =
            /^[0-9a-zA-Z]([-_]?[0-9a-zA-Z])*@[0-9a-zA-Z]([-_]?[0-9a-zA-Z])*\.[a-zA-Z]{2,3}$/i;
        if (regExp.test(value)) {
            const duplicate = async () => {
                let form = new FormData();
                form.append("email", value);
                const url = API_SERVER2 + `/auth/check-email-duplicate`;

                const response = await axios.post(url, form, {
                    withCredentials: true,
                });
                return response;
            };
            duplicate().then(function (data) {
                const duplicated = data.data.data.duplicated;
                if (duplicated) {
                    setEmailData({
                        pass: true,
                        message: "이미 사용중인 이메일입니다.",
                        requirement: false,
                    });
                } else {
                    setEmailData({
                        pass: true,
                        message: "사용가능한 이메일입니다.",
                        requirement: false,
                    });
                }
            });
        } else {
            setEmailData({
                pass: false,
                message: "*규칙에 맞는 이메일 주소를 입력해주세요.",
                requirement: true,
            });
        }
    };
    const validatePassword = e => {
        const value = e.target.value;
        var re = {
            lower: /[a-z]/g,
            upper: /[A-Z]/g,
            alpha: /[A-Z]/gi,
            numeric: /[0-9]/g,
            special: /[\W_]/g,
        };
        var alpha = (value.match(re["alpha"]) || []).length > 0 ? true : false;
        var numeric =
            (value.match(re["numeric"]) || []).length > 0 ? true : false;
        var special =
            (value.match(re["special"]) || []).length > 0 ? true : false;
        var length = value.length > 7 ? true : false;

        if (!length) {
            setPasswordData({
                pass: false,
                message: "*비밀번호가 너무 짧습니다(8자 이상).",
                requirement: true,
            });
        } else if (!alpha) {
            setPasswordData({
                pass: false,
                message: "*비밀번호에 문자가 포함되어있지 않습니다.",
                requirement: true,
            });
        } else if (!numeric) {
            setPasswordData({
                pass: false,
                message: "*비밀번호에 숫자가 포함되어있지 않습니다.",
                requirement: true,
            });
        } else if (!special) {
            setPasswordData({
                pass: false,
                message: "*비밀번호에 특수문자가 포함되어있지 않습니다.",
                requirement: true,
            });
        } else {
            setPasswordData({
                pass: true,
                message: "사용 가능한 비밀번호 입니다.",
                requirement: false,
            });
        }
    };
    const validatePassword2 = e => {
        const value = e.target.value;
        var match = password === value;

        if (!match) {
            setPassword2Data({
                pass: false,
                message: "*비밀번호가 일치하지 않습니다.",
                requirement: true,
            });
        } else {
            setPassword2Data({
                pass: true,
                message: "사용 가능한 비밀번호 입니다.",
                requirement: false,
            });
        }
    };
    const sendEmailCode = () => {
        let form = new FormData();
        form.append("email", email);
        const url = API_SERVER2 + `/auth/email/verify`;
        const options = {
            method: "POST",
            url: url,
            withCredentials: true,
            data: form,
        };
        axios(options)
            .then(() => { })
            .catch(error => {
                if (error.response.data.code === 100002) {
                    setEmailData({
                        pass: false,
                        message: "*이미 등록된 이메일입니다.",
                        requirement: true,
                    });
                }
            });
    };
    const confirmEmail = () => {
        let form = new FormData();
        form.append("email", email);
        form.append("verifyCode", verificationCode);
        const url = API_SERVER2 + `/auth/email/confirm`;
        const options = {
            method: "POST",
            url: url,
            withCredentials: true,
            data: form,
        };

        axios(options).then(() => {
            setUserSettings("email", email);
        });
    };
    const sendSMS = () => {
        let form = new FormData();
        form.append("phoneNumber", phone);

        const url = API_SERVER2 + `/auth/sms/verify`;
        const getNickData = async () => {
            return await axios.post(url, form);
        };
        getNickData().then(() => {
            let prevData = JSON.parse(window.localStorage.getItem("userData"));
            prevData.phoneVerified = true;
            window.localStorage.setItem("userData", JSON.stringify(prevData));
        });
    };
    const confirmSMS = () => {
        let form = new FormData();
        form.append("phoneNumber", phone);
        form.append("verifyCode", SMSverificationCode);
        const url = API_SERVER2 + `/auth/sms/confirm`;
        const options = {
            method: "POST",
            url: url,
            withCredentials: true,
            data: form,
        };
        axios(options).then(() => {
            setUserSettings("phoneNumber", phone);
        });
    };
    const closeAll = () => {
        reset();
        refetch();
        setChangeNickname(false);
        setChangeName(false);
        setChangeEmail(false);
        setChangePW(false);
        setChangePhone(false);
        setPasswordData({
            pass: false,
            message: "",
            requirement: false,
        });
        setPassword2Data({
            pass: false,
            message: "",
            requirement: false,
        });
        setPrevPWData({
            pass: false,
            message: "",
            requirement: false,
        });
    };
    const [
        {
            prevPassword,
            password,
            password2,
            email,
            nickName,
            name,
            verificationCode,
            phone,
            SMSverificationCode,
        },
        onChange,
        reset,
    ] = useInputs({
        password: "",
        password2: "",
        email: "",
        nickName: "",
        name: "",
        verificationCode: "",
        phone: "",
        SMSverificationCode: "",
    });
    useEffect(() => {
        if (data) {
            setPrevNickname(data.data.user.nickname);
            setPrevName(data.data.user.realName);
            setAgreeEmail(data.data.user.emailPermit);
            setAgreeText(data.data.user.textPermit);
            setPrevEmail(data.data.user.email);
            setPrevPhone(data.data.user.phoneNumber);
            setEmailConfirmed(data.data.user.emailVerified);
            setPhoneConfirmed(data.data.user.phoneVerified);
            setPrevProfile(data.data.user.smallProfileURI);
            if (data.data.user.userType === "client") {
                setUserType("client");
            } else {
                setUserType("artist");
                setArtistType(data.data.user.artistType);
            }
        }
    }, [data]);
    const checkPassword = () => {
        let params = new URLSearchParams();
        const url = API_SERVER2 + `/auth/signin/local`;
        params.append("email", prevEmail);
        params.append("password", prevPassword);
        const options = {
            method: "POST",
            data: params,
            url: url,
            withCredentials: true,
        };

        axios(options).then(res => {
            if (res.status === 200) {
                setPrevPWData({
                    pass: true,
                    message: "",
                    requirement: false,
                });
            }
        });
    };
    const [selectedFile, setSelectedFile] = useState(null);
    const [userType, setUserType] = useState(null);
    const [artistType, setArtistType] = useState(null);
    const [changeEmail, setChangeEmail] = useState(false);
    const [changeNickname, setChangeNickname] = useState(false);
    const [changeName, setChangeName] = useState(false);
    const [agreeEmail, setAgreeEmail] = useState(false);
    const [agreeText, setAgreeText] = useState(false);
    const [prevEmail, setPrevEmail] = useState("");
    const [prevName, setPrevName] = useState("");
    const [prevNickname, setPrevNickname] = useState("");
    const [prevPhone, setPrevPhone] = useState("");
    const [prevProfile, setPrevProfile] = useState("");
    const [prevPWData, setPrevPWData] = useState({ pass: false, message: "" });
    const [emailData, setEmailData] = useState({ pass: false, message: "" });
    const [passwordData, setPasswordData] = useState({
        pass: false,
        message: "",
    });
    const [password2Data, setPassword2Data] = useState({
        pass: false,
        message: "",
    });
    const [changePhone, setChangePhone] = useState(false);
    const [changePW, setChangePW] = useState(false);
    const [changePW2, setChangePW2] = useState(false);
    const [modalOpen, setModalOpen] = useState(false);
    const [settingModalOpen, setSettingModalOpen] = useState(false);
    const [phoneModalOpen, setPhoneModalOpen] = useState(false);
    const [passwordModalOpen, setPasswordModalOpen] = useState(false);
    const [overSizeModalOpen, setOverSizeModalOpen] = useState(false);
    const [emailConfirmed, setEmailConfirmed] = useState(false);
    const [phoneConfirmed, setPhoneConfirmed] = useState(false);
    const [overSize, setOverSize] = useState(false);
    const changeArtistType = type => {
        const newArray = [];
        if (type === "performer") {
            if (artistType.indexOf("performer") === -1) {
                newArray.push('"performer"');
            }
            if (artistType.indexOf("producer") !== -1) {
                newArray.push('"producer"');
            }
        }
        if (type === "producer") {
            if (artistType.indexOf("producer") === -1) {
                newArray.push('"producer"');
            }
            if (artistType.indexOf("performer") !== -1) {
                newArray.push('"performer"');
            }
        }

        return "[" + newArray + "]";
    };

    const handleFileInput = e => {
        var fileReader = new FileReader();
        fileReader.readAsDataURL(e.target.files[0]);
        if (e.target.files[0].size > 1024 * 1024 * 10) {
            setOverSizeModalOpen(true);
            setOverSize(
                Math.round((e.target.files[0].size / 1024 / 1024) * 100) / 100 +
                "MB",
            );
            return;
        } else {
            fileReader.onload = function (e) {
                setSelectedFile(e.target.result);
            };
            setUserSettings("files", e.target.files[0]);
        }
    };
    const closeModal = () => {
        setModalOpen(false);
    };

    return (
        <Container>
            <InnerContainer>
                <Title>기본 정보</Title>
                <Section1>
                    <InputRow
                        style={{
                            marginTop: 20,
                            display: currentSize === "MOBILE" && "block",
                        }}
                    >
                        <Column1>프로필 사진</Column1>
                        <Column2>
                            <StateRow>
                                <IMG
                                    src={selectedFile || prevProfile || profile}
                                />
                                <RoundButton
                                    style={{
                                        width:
                                            currentSize !== "MOBILE"
                                                ? 244
                                                : 200,
                                        height: 40,
                                        marginLeft:
                                            currentSize !== "MOBILE" ? 32 : 20,
                                    }}
                                >
                                    <input
                                        style={{ display: "none" }}
                                        accept="image/*"
                                        type="file"
                                        name="file"
                                        onChange={e => handleFileInput(e)}
                                    />
                                    <Camera src={camera} />
                                    <Text2>프로필 사진 업데이트</Text2>
                                </RoundButton>
                            </StateRow>
                            <Row2>
                                <Text3>
                                    유저 프로필 사진을 업로드 해주세요.
                                    <br />
                                    (JPEF,PNG,GIF; 10 MB 제한)
                                </Text3>
                            </Row2>
                        </Column2>
                    </InputRow>
                    <InputRow isOpen={changeNickname}>
                        <StateRow>
                            <Column1>
                                닉네임
                                <Tooltip left={"-60px"} width={"242px"}>
                                    &lsquo;닉네임&lsquo;은 다른 유저들이 볼 수
                                    있는 이름입니다.
                                    <br />
                                    불리고 싶은 닉네임을 설정해 보세요.
                                </Tooltip>
                            </Column1>
                            <Status>{prevNickname || ""}</Status>
                            <Chevron
                                isOpen={changeNickname}
                                onClick={() => {
                                    setChangeNickname(!changeNickname);
                                }}
                                src={chevronDown}
                            />
                        </StateRow>
                    </InputRow>
                    <HiddenSection2 isOpen={changeNickname}>
                        <Row4>
                            <Input
                                value={nickName}
                                style={{ width: 395 }}
                                name="nickName"
                                onChange={e => {
                                    onChange(e);
                                }}
                                placeholder={
                                    "불리고 싶은 닉네임(영문)을 입력해주세요"
                                }
                            />
                            <Button
                                style={{
                                    zIndex: 1,
                                    height: currentSize !== "MOBILE" ? 40 : 32,
                                    width: currentSize !== "MOBILE" ? 104 : 100,
                                    marginLeft: 16,
                                }}
                                isOpen={changeNickname}
                                onClick={() => {
                                    setUserSettings("nickname", nickName);
                                }}
                            >
                                저장
                            </Button>
                        </Row4>
                    </HiddenSection2>
                    <InputRow isOpen={changeName}>
                        <StateRow>
                            <Column1>
                                이름
                                <Tooltip left={"-40px"} width={"245px"}>
                                    본인 인증된 이름을 사용하시면 더욱 더
                                    안전하게 <br />
                                    프로젝트를 진행할 수 있습니다.
                                </Tooltip>
                            </Column1>
                            <Status>{prevName || ""}</Status>
                            <Chevron
                                isOpen={changeName}
                                onClick={() => {
                                    setChangeName(!changeName);
                                }}
                                src={chevronDown}
                            />
                        </StateRow>
                    </InputRow>
                    <HiddenSection2 isOpen={changeName}>
                        <Row4>
                            <Input
                                value={name}
                                style={{ width: 395 }}
                                name="name"
                                onChange={e => {
                                    onChange(e);
                                }}
                                placeholder={"본인 실명을 입력해주세요"}
                            />
                            <Button
                                style={{
                                    height: currentSize !== "MOBILE" ? 40 : 32,
                                    width: currentSize !== "MOBILE" ? 104 : 100,
                                    marginLeft: 16,
                                }}
                                isOpen={changeName}
                                onClick={() => {
                                    setUserSettings("realName", name);
                                }}
                            >
                                저장
                            </Button>
                        </Row4>
                    </HiddenSection2>
                    <InputRow isOpen={changeEmail}>
                        <StateRow>
                            <Column1>
                                이메일<Red>*</Red>
                            </Column1>
                            <Status>
                                {prevEmail || ""}
                                {emailConfirmed && (
                                    <img
                                        style={{ marginLeft: 8 }}
                                        src={confirmed}
                                    />
                                )}
                            </Status>
                            <Chevron
                                isOpen={changeEmail}
                                onClick={() => {
                                    setChangeEmail(!changeEmail);
                                }}
                                src={chevronDown}
                            />
                        </StateRow>
                    </InputRow>
                    <HiddenSection isOpen={changeEmail}>
                        <Row4 isOpen={changeEmail}>
                            <Input
                                style={{
                                    width: currentSize !== "MOBILE" ? 326 : 198,
                                }}
                                requirement={emailData.requirement}
                                pass={emailData.pass}
                                name={"email"}
                                placeholder={"새로운 이메일을 입력해 주세요"}
                                onChange={e => {
                                    validateEmail(e);
                                    onChange(e);
                                }}
                            />
                            <Button
                                activate={emailData.pass}
                                onClick={sendEmailCode}
                                style={{
                                    height: currentSize !== "MOBILE" ? 40 : 32,
                                    width: currentSize !== "MOBILE" ? 142 : 129,
                                    marginLeft: 16,
                                }}
                            >
                                인증메일 전송
                            </Button>
                        </Row4>
                        <NickNameErrorMessage pass={emailData.pass}>
                            {emailData.message}
                        </NickNameErrorMessage>
                        <Row4
                            style={{
                                margin:
                                    currentSize !== "MOBILE"
                                        ? "40px 0px"
                                        : "20px 0px",
                            }}
                            isOpen={changeEmail}
                        >
                            <Input
                                style={{
                                    width: currentSize !== "MOBILE" ? 364 : 227,
                                }}
                                value={verificationCode}
                                name={"verificationCode"}
                                placeholder={"인증번호 6자리를 입력해 주세요"}
                                onChange={e => {
                                    onChange(e);
                                }}
                            />
                            <Button
                                activate={
                                    emailData.pass &&
                                    verificationCode !== "" &&
                                    verificationCode.length === 6
                                }
                                style={{
                                    marginLeft: 16,
                                    height: currentSize !== "MOBILE" ? 40 : 32,
                                    width: currentSize !== "MOBILE" ? 104 : 100,
                                }}
                                isOpen={changeEmail}
                                onClick={() => {
                                    confirmEmail();
                                }}
                            >
                                인증하기
                            </Button>
                        </Row4>
                    </HiddenSection>
                    <InputRow isOpen={changePhone}>
                        <StateRow>
                            <Column1>휴대전화</Column1>
                            <Status>
                                {prevPhone || ""}
                                {phoneConfirmed && (
                                    <img
                                        style={{ marginLeft: 8 }}
                                        src={confirmed}
                                    />
                                )}
                            </Status>
                            <Chevron
                                isOpen={changePhone}
                                onClick={() => {
                                    setChangePhone(!changePhone);
                                }}
                                src={chevronDown}
                            />
                        </StateRow>
                    </InputRow>
                    <HiddenSection isOpen={changePhone}>
                        <Row4 isOpen={changePhone}>
                            <Input
                                value={phone}
                                style={{
                                    width: currentSize !== "MOBILE" ? 326 : 227,
                                }}
                                type="number"
                                name={"phone"}
                                placeholder={"새로운 번호를 입력해 주세요"}
                                onChange={e => {
                                    onChange(e);
                                }}
                            />
                            <Button
                                onClick={sendSMS}
                                style={{
                                    height: currentSize !== "MOBILE" ? 40 : 32,
                                    width: 142,
                                    marginLeft: 16,
                                    marginBottom:
                                        currentSize !== "MOBILE" ? 40 : 20,
                                }}
                            >
                                인증문자 전송
                            </Button>
                        </Row4>
                        <Row4 isOpen={changePhone}>
                            <Input
                                style={{
                                    width: currentSize !== "MOBILE" ? 364 : 227,
                                }}
                                value={SMSverificationCode}
                                type="number"
                                name={"SMSverificationCode"}
                                placeholder={"인증번호 6자리를 입력해 주세요"}
                                onChange={e => {
                                    onChange(e);
                                }}
                            />
                            <Button
                                activate={SMSverificationCode.length === 6}
                                style={{
                                    marginLeft: 16,
                                    height: currentSize !== "MOBILE" ? 40 : 32,
                                    width: currentSize !== "MOBILE" ? 104 : 100,
                                }}
                                isOpen={changePhone}
                                onClick={() => {
                                    confirmSMS(SMSverificationCode);
                                }}
                            >
                                인증하기
                            </Button>
                        </Row4>
                    </HiddenSection>
                    <InputRow isOpen={changePW}>
                        <StateRow>
                            <Column1>
                                비밀번호<Red>*</Red>
                            </Column1>
                            <Status style={{ top: 2, position: "relative" }}>
                                {"***********"}
                            </Status>
                            <Chevron
                                isOpen={changePW}
                                onClick={() => {
                                    setChangePW(!changePW);
                                    setChangePW2(!changePW2);
                                }}
                                src={chevronDown}
                            />
                        </StateRow>
                    </InputRow>
                    <HiddenSection2 isOpen={changePW}>
                        <div style={{ display: "flex", marginBottom: 40 }}>
                            <Input
                                type="password"
                                style={{ width: 395 }}
                                value={prevPassword}
                                name={"prevPassword"}
                                placeholder={"기존 비밀번호를 입력해 주세요."}
                                onChange={e => {
                                    onChange(e);
                                }}
                            />
                            <Button
                                onClick={() => checkPassword()}
                                style={{
                                    marginLeft: 16,
                                    height: currentSize !== "MOBILE" ? 40 : 32,
                                    width: currentSize !== "MOBILE" ? 104 : 100,
                                }}
                            >
                                확인
                            </Button>
                        </div>
                    </HiddenSection2>
                    <HiddenSection4 isOpen={prevPWData.pass && changePW2}>
                        <Row4>
                            <Input
                                style={{ width: 395 }}
                                value={password}
                                pass={passwordData.pass}
                                requirement={passwordData.requirement}
                                name={"password"}
                                placeholder={"새로운 비밀번호를 입력해주세요."}
                                onChange={e => {
                                    validatePassword(e);
                                    onChange(e);
                                }}
                            />
                        </Row4>
                        <NickNameErrorMessage pass={passwordData.pass}>
                            {passwordData.message}
                        </NickNameErrorMessage>
                        <div style={{ display: "flex", marginTop: 40 }}>
                            <Input
                                style={{ width: 395 }}
                                value={password2}
                                pass={password2Data.pass}
                                requirement={password2Data.requirement}
                                name={"password2"}
                                placeholder={
                                    "새로운 비밀번호를 다시 입력해주세요."
                                }
                                onChange={e => {
                                    validatePassword2(e);
                                    onChange(e);
                                }}
                            />
                            <Button
                                activate={password2Data.pass}
                                style={{
                                    marginLeft: 16,
                                    height: 40,
                                    width: 72,
                                }}
                                isOpen={changePW}
                                onClick={() => {
                                    setUserSettings("password", password2);
                                }}
                            >
                                확인
                            </Button>
                        </div>
                        <NickNameErrorMessage pass={password2Data.pass}>
                            {password2Data.message}
                        </NickNameErrorMessage>
                    </HiddenSection4>
                    <InputRow>
                        <StateRow
                            style={{
                                display: "flex",
                                alignItems: "flex-start",
                            }}
                        >
                            <Column1>유저 타입</Column1>
                            <Column2>
                                <CheckBoxRow style={{ marginBottom: 20 }}>
                                    <CheckBox
                                        activate={userType === "client"}
                                        inactivate={true}
                                    />
                                    <CheckBoxText
                                        activate={userType === "client"}
                                    >
                                        일반 유저
                                    </CheckBoxText>
                                </CheckBoxRow>
                                <CheckBoxRow>
                                    <CheckBox
                                        activate={userType === "artist"}
                                        inactivate={true}
                                    />
                                    <CheckBoxText
                                        activate={userType === "artist"}
                                    >
                                        아티스트
                                    </CheckBoxText>
                                </CheckBoxRow>
                            </Column2>
                        </StateRow>
                    </InputRow>
                    {userType === "artist" && (
                        <InputRow>
                            <StateRow
                                style={{
                                    display: "flex",
                                    alignItems: "flex-start",
                                }}
                            >
                                <Column1>상세 유저 타입</Column1>
                                <Column2>
                                    <CheckBoxRow style={{ marginBottom: 20 }}>
                                        <CheckBox
                                            onClick={() =>
                                                setUserSettings(
                                                    "artistType",
                                                    changeArtistType(
                                                        "performer",
                                                    ),
                                                )
                                            }
                                            activate={
                                                artistType &&
                                                artistType.indexOf(
                                                    "performer",
                                                ) !== -1
                                            }
                                        />
                                        <Text4>퍼포머</Text4>
                                    </CheckBoxRow>
                                    <CheckBoxRow>
                                        <CheckBox
                                            onClick={() =>
                                                setUserSettings(
                                                    "artistType",
                                                    changeArtistType(
                                                        "producer",
                                                    ),
                                                )
                                            }
                                            activate={
                                                artistType &&
                                                artistType.indexOf(
                                                    "producer",
                                                ) !== -1
                                            }
                                        />
                                        <Text4>프로듀서</Text4>
                                    </CheckBoxRow>
                                </Column2>
                            </StateRow>
                        </InputRow>
                    )}
                    <InputRow></InputRow>
                </Section1>
                <Section1>
                    <Title>알림 설정</Title>
                    <InputRow style={{ marginTop: "20px" }}>
                        <StateRow
                            style={{
                                display: "flex",
                                alignItems: "flex-start",
                            }}
                        >
                            <Column1>마케팅 수신</Column1>
                            <Column2>
                                <CheckBoxRow style={{ marginBottom: 20 }}>
                                    <Toggle
                                        hover={true}
                                        onClick={() => {
                                            setUserSettings(
                                                "emailPermit",
                                                !agreeEmail,
                                            );
                                        }}
                                        activate={agreeEmail}
                                    />
                                    <Text4>이메일</Text4>
                                </CheckBoxRow>
                                <CheckBoxRow>
                                    <Toggle
                                        hover={true}
                                        onClick={() => {
                                            setUserSettings(
                                                "textPermit",
                                                !agreeText,
                                            );
                                        }}
                                        activate={agreeText}
                                    />
                                    <Text4>문자</Text4>
                                </CheckBoxRow>
                            </Column2>
                        </StateRow>
                    </InputRow>
                    <InputRow></InputRow>
                </Section1>
                <Text000
                    onClick={() => {
                        setModalOpen(true);
                    }}
                >
                    회원탈퇴
                </Text000>
            </InnerContainer>
            <SecessionModal
                nickName={
                    JSON.parse(window.localStorage.getItem("userData")).nickname
                }
                open={modalOpen}
                close={closeModal}
            />
            <SettingSuccessModal
                open={settingModalOpen}
                close={() => {
                    setSettingModalOpen(false);
                    closeAll();
                }}
                text={"설정 변경이 완료되었습니다."}
            />
            <SettingSuccessModal
                open={overSizeModalOpen}
                close={() => setOverSizeModalOpen(false)}
                text={
                    "최대 업로드 가능한 파일 사이즈는 10 MB입니다." +
                    "\n" +
                    "현재파일 용량: " +
                    overSize
                }
            />
            <SettingSuccessModal
                open={phoneModalOpen}
                close={() => setPhoneModalOpen(false)}
                text={"휴대전화 인증이 완료되었습니다."}
            />
            <SettingSuccessModal
                open={passwordModalOpen}
                close={() => setPasswordModalOpen(false)}
                text={"비밀번호 변경이 완료되었습니다."}
            />
        </Container>
    );
}
