import axios from "axios";
import React, { useState } from "react";
import { useHistory, useLocation, useParams } from "react-router";
import styled from "styled-components";
import { BlankButton4 } from "../../../../components/0.Button/blankButton4";
import { Button } from "../../../../components/0.Button/button";
import { TextButton } from "../../../../components/0.Button/textButton";
import { API_SERVER2, BREAKPOINT_LARGE, BREAKPOINT_MIDDLE, BREAKPOINT_SMALL } from "../../../../constants/constants";
import { DisplayFlex, SpoqaHansSansNeo } from "../../../../css/css";
import { isMobile } from "../../../../lib/lib";
import { RequireVeriModal } from "./requireVeriModal";
import { RequireVeriModal2 } from "./requireVeriModal2";

export function ArtistDataContainer({ refetch, nameEN, currentSize, followCount, onRequest, followed, id }) {
    const [settingModalOpen, setSettingModalOpen] = useState(false);
    const [settingModalOpen2, setSettingModalOpen2] = useState(false);
    const history = useHistory();
    let tab = 'ABOUT'
    if (useLocation().pathname.toString().indexOf('port') !== -1) {
        tab = 'PORTFOLIO';
    }
    const [tabActive, setTabActive] = useState(tab);
    const changeActive = (name) => {
        setTabActive(name)
    }
    const artistID = useParams().artistID;

    const followArtist = async () => {
        if (!window.localStorage.getItem("userData")) {
            history.push('/login')
            return;
        }
        const url = API_SERVER2 + `/artists/${id}/follow`;
        const options = {
            method: 'PATCH',
            url: url,
            withCredentials: true
        };
        axios(options).then(res => {
            if (res.status === 200) {
                refetch()
            }
        })
    }
    const unfollowArtist = async () => {
        const url = API_SERVER2 + `/artists/${id}/unfollow`;
        const options = {
            method: 'PATCH',
            url: url,
            withCredentials: true
        };
        axios(options).then(res => {
            if (res.status === 200) {
                refetch()
            }
        })
    }

    return (
        <Container>
            <InnerContainer>
                <Text>
                    {nameEN}
                </Text>
                <ButtonRow>
                    {onRequest ? (<OnRequest>{currentSize === "MOBILE" ? "의뢰 중" : "의뢰 진행 중"}</OnRequest>) : (<Button
                        onClick={() => {
                            const userData = JSON.parse(window.localStorage.getItem('userData'));
                            if (window.localStorage.getItem('isLogin')) {
                                if (userData.phoneVerified) {
                                    history.push(`/${artistID}/request`);
                                    window.scrollTo(0, 0);
                                } else {
                                    setSettingModalOpen2(true)
                                }
                            } else {
                                setSettingModalOpen(true)
                            }
                        }}
                        isMobile={isMobile(currentSize)}
                        style={{
                            height: isMobile(currentSize) ? 32 : 40,
                            width: isMobile(currentSize) ? 84 : 112,
                            marginRight: 8,
                        }}>
                        의뢰하기
                    </Button>)}
                    <BlankButton4
                        isMobile={true}
                        followed={followed}
                        onClick={followArtist}
                        onClick2={unfollowArtist}
                        style={{
                            height: isMobile(currentSize) ? 32 : 40,
                            width: isMobile(currentSize) ? 84 : 112,
                            marginRight: 8,
                        }}
                        text1={"팔로우 " + ((followCount === undefined) ? 0 : followCount)}
                        text2={"팔로잉 " + ((followCount === undefined) ? 0 : followCount)} />
                </ButtonRow>
                <TabRow>
                    <TextButton
                        onClick={() => {
                            changeActive('ABOUT');
                            history.push(`/${artistID}/`)
                        }}
                        activate={tabActive}
                        text='ABOUT'
                        style={{ marginRight: isMobile(currentSize) ? 8 : 16 }} />
                    <TextButton
                        onClick={() => {
                            changeActive('PORTFOLIO');
                            history.push(`/${artistID}/portfolio`)
                        }}
                        activate={tabActive}
                        text='PORTFOLIO' />
                </TabRow>
            </InnerContainer>
            <RequireVeriModal
                open={settingModalOpen}
                close={() => setSettingModalOpen(false)} />
            <RequireVeriModal2
                open={settingModalOpen2}
                close={() => setSettingModalOpen2(false)} />
        </Container>
    );
}
const OnRequest = styled.div`
    ${DisplayFlex('center', 'center')}
    ${SpoqaHansSansNeo(14, 18, 700, '#BD03FA')};
    border-radius: 4px;
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) { 
        width: 112px;
        height: 40px;
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        width: 84px;
        height: 32px;
    }
    background: rgba(1, 1, 11, 0.1);
    border: 1px solid #F7F7F7;
    margin-right: 8px;
`
const TabRow = styled.div`
    @media screen and (min-width: ${BREAKPOINT_MIDDLE}px) { 
        margin-top: 16px;
    }
    @media screen and (max-width: ${BREAKPOINT_MIDDLE}px) {
        margin-top: 12px;
    }
    display:flex;
`
const ButtonRow = styled.div`
    margin-top: 8px;
    display:flex;
`
const Text = styled.div`
    @media screen and (min-width: ${BREAKPOINT_LARGE}px) {
        ${SpoqaHansSansNeo(72, 90, 700)}; 
    }
    @media screen and (min-width: ${BREAKPOINT_MIDDLE}px) and (max-width: ${BREAKPOINT_LARGE}px) {
        ${SpoqaHansSansNeo(48, 60, 700)};
    }
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) and (max-width: ${BREAKPOINT_MIDDLE}px){
        ${SpoqaHansSansNeo(34, 40, 700)};
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        ${SpoqaHansSansNeo(24, 30, 700)};
    }
`
const InnerContainer = styled.div`
    padding: 0px 20px;
    @media screen and (min-width: ${BREAKPOINT_LARGE}px) {
        width: 1196px;
    }
    @media screen and (min-width: ${BREAKPOINT_MIDDLE}px) and (max-width: ${BREAKPOINT_LARGE}px) {
        width: 1140px;
    }
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) and (max-width: ${BREAKPOINT_MIDDLE}px){
        width: 754px;
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        width: 100%;
    }
`
const Container = styled.div`
    ${DisplayFlex('center', '')};
    width:100%;
    position:relative;
    @media screen and (min-width: ${BREAKPOINT_LARGE}px) {
        ${SpoqaHansSansNeo(72, 84, 700)}; 
        top: 374px;
    }
    @media screen and (min-width: ${BREAKPOINT_MIDDLE}px) and (max-width: ${BREAKPOINT_LARGE}px) {
        ${SpoqaHansSansNeo(72, 84, 700)}; 
        top: 256px;
    }
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) and (max-width: ${BREAKPOINT_MIDDLE}px){
        ${SpoqaHansSansNeo(72, 84, 700)}; 
        top: 146px;
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        ${SpoqaHansSansNeo(34, 40, 700)};
        top: 146px;
    }
  `