import axios from "axios";
import React from "react";
import { useParams } from "react-router";
import styled from "styled-components";
import { API_SERVER2, BREAKPOINT_LARGE, BREAKPOINT_MIDDLE, BREAKPOINT_SMALL } from "../../constants/constants";
import { DisplayFlex, SpoqaHansSansNeo } from "../../css/css";
import useAsync from "../../hook/useAsync";
import grayRadioButtonNO from '../../images/grayRadioButtonNO.svg';
import grayRadioButtonOK from '../../images/grayRadioButtonOK.svg';
import { isEmptyObject } from "../../lib/lib";
import { NavBar } from "../main/1.NavBar/0.NavBar/navBar";
import { Footer } from "../main/6.Footer/0.Footer/footer";
import { MiniArtistProfile } from "../myPage/miniArtistProfile";
import { File2 } from "./file2";
import { Link2 } from "./link2";

export function RequestDetail({ currentSize }) {
    const printCreatedAt = () => {
        let date = project.createdAt
        const index = date.indexOf('T');
        let newDate = date.slice(0, index);

        return "등록일자 " + newDate;
    }
    const projectID = useParams().projectID
    const getEstimates = async () => {
        const url = API_SERVER2 + `/projects/${projectID}`;
        const response = await axios.get(
            url, { withCredentials: true });

        return response.data;
    }
    const [state] = useAsync(getEstimates, []);
    const { loading, data } = state;
    let project = null;
    if (data) {
        project = data.data.project
    }
    const converter = () => {
        if (project.progress === '의뢰' ||
            project.progress === '견적 확인 및 선택') {
            return '오픈 준비'
        }
        else if (
            project.progress === '세부 사항 협의' ||
            project.progress === '계약' ||
            project.progress === '결제') {
            return '진행 중'
        }
        else if (project.progress === '결과물 수령') {
            return '완료'
        }
    }
    const printGenre = () => {
        const a = [];
        project.genre.map((v, i) => {

            a.push(v);
            if (project.genre.length - 1 !== i)
                a.push(" · ")
        })
        return a;
    }
    const printCategory = () => {
        const a = [];
        project.category.map((v, i) => {
            const index = v.indexOf('-');
            const newThing = v.slice(0, index).
                concat(' > ').concat(v.slice(index + 1, v.length));

            a.push(newThing);
            if (project.category.length - 1 !== i) {
                a.push(" ")
                a.push("  ·  ")
                a.push(" ")
            }

        })
        return a;
    }
    const printBudget = () => {
        let a;
        if (project.budget === '30-50') {
            a = '30-50'
            return '예산: ' + a + "만원"
        } else if (project.budget === '50-100') {
            a = '50-100'
            return '예산: ' + a + "만원"
        } else if (project.budget === '100-300') {
            a = '100-300'
            return '예산: ' + a + "만원"
        } else if (project.budget === '300-500') {
            a = '300-500'
            return '예산: ' + a + "만원"
        } else if (project.budget === 'gte500') {
            a = '500'
            return '예산: ' + a + "만원 이상"
        }
    }
    const printDeadline = () => {
        const t = new Date();
        let diffDate = new Date(project.deadline) - t
        var result = Math.ceil(diffDate / (60 * 1000 * 60 * 24));
        if (result === 0) {
            return "오늘";
        } else if (result > 0) {
            return result + "일 전";
        } else {
            return "마감"
        }

    }
    const printDeadline2 = () => {
        let date = project.deadline
        const index = date.indexOf('T');
        let newDate = date.slice(0, index);
        const index2 = date.indexOf('-');
        newDate = newDate.slice(index2 + 1, newDate.length);
        newDate = newDate.replace('-', '/')
        return "( ~" + newDate + " )";
    }

    return (
        <Container>
            <NavBar currentSize={currentSize} />
            {data ?
                <InnerContainer>
                    <InnerContainer2>
                        {
                            (!loading && data) && <>
                                <Row0>
                                    <State><Text12>{converter()}</Text12></State>
                                    <CreatedAt>{printCreatedAt()}</CreatedAt>
                                </Row0>
                                <Name>{project.name}</Name>
                                <Row1>
                                    <Category>{printCategory()}</Category>
                                    <Genre>{printGenre()}</Genre>
                                </Row1>
                                <InnerContainer3>
                                    <Column>
                                        <Title>의뢰자(단체)명</Title>
                                        <Text11>{project.clientName}</Text11>
                                        <Title>프로젝트 설명</Title>
                                        <Text11>{project.description}</Text11>
                                        <Title>일정</Title>
                                        <Text11>{project.duration}</Text11>
                                        {
                                            (project.referenceFileURI !== undefined) && (project.referenceFileURI.length !== 0) &&
                                            <>
                                                <Title>레퍼런스 및 관련 파일</Title>
                                                {project.referenceFileURI.map((file, i) => (
                                                    <File2
                                                        key={i}
                                                        data={file} />
                                                ))}
                                            </>
                                        }
                                        {
                                            (project.referenceWebsite.length !== 0) &&
                                            <>
                                                <Title2>관련 링크</Title2>
                                                {project.referenceWebsite.map((url, i) => (
                                                    <Link2
                                                        link={url}
                                                        key={i}
                                                        name={url} />
                                                ))}
                                            </>
                                        }
                                        <Title>STAGE8이 이 프로젝트에 적합한 다른 아티스트를 추천해도 되나요?</Title>
                                        <div style={{ marginTop: 18 }}>
                                            <Row1>
                                                <img style={{ width: 20, height: 20, marginRight: 10, marginLeft: 10 }} src={project.canApply ? grayRadioButtonOK : grayRadioButtonNO} />
                                                <Text13>
                                                    네, 다른 아티스트의 견적서도 받아보고 싶어요
                                                </Text13>
                                            </Row1>
                                            <Row1 style={{ marginTop: 12 }}>
                                                <img style={{ width: 20, height: 20, marginRight: 10, marginLeft: 10 }} src={!project.canApply ? grayRadioButtonOK : grayRadioButtonNO} />
                                                <Text13 >
                                                    아니요, 이 아티스트에게만 프로젝트를 맡기고 싶어요
                                                </Text13>
                                            </Row1>
                                        </div>
                                    </Column>
                                    <Column2>
                                        <Budget>{printBudget()}</Budget>
                                        <Artist>{"요청 아티스트:"}</Artist>
                                        <ArtistProfile>
                                            <MiniArtistProfile star={true} src={(!isEmptyObject(project.requestedArtists)) && project.requestedArtists[0].profileURI} />
                                        </ArtistProfile>
                                        <Text88>지원 마감:</Text88>
                                        <div style={{ display: 'flex', marginTop: 8, alignItems: 'center' }}>
                                            <Red>{printDeadline()}</Red>
                                            <Gray>{printDeadline2()}</Gray>
                                        </div>
                                    </Column2>
                                </InnerContainer3>
                            </>}
                    </InnerContainer2>
                </InnerContainer>
                :
                <EmptySpace></EmptySpace>}
            <Footer />
        </Container>
    );
}
