import React from "react";
import styled from "styled-components";
import { BREAKPOINT_SMALL } from "../../../../../constants/constants";
import { SpoqaHansSansNeo } from "../../../../../css/css";

export function LinkButton({children, link}) {
  return (
    <Container href={link} target='_blank'>
      <Text>{children}</Text>
    </Container>
  );
}
const Container = styled.a`
  @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
    margin-bottom: 20px;
  }
  text-decoration:none;
  align-items: center;
  display: flex;
  margin-right: 28px;
`
const Text = styled.div`
  @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
    ${SpoqaHansSansNeo(16, 24, 700,'#BEBEBE')}
  }
  @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
    ${SpoqaHansSansNeo(14, 18, 700,'#BEBEBE')}
  }
`
