import React, { useState } from "react";
import { useHistory } from "react-router";
import signatureImg from "src/images/signature.svg";
import { makeCenter } from "src/ts/styles/css";
import styled, { css } from "styled-components";
import { BREAKPOINT_LARGE, BREAKPOINT_MIDDLE, BREAKPOINT_SMALL } from "../../../../constants/constants";
import { SpoqaHansSansNeo } from "../../../../css/css";
import { ArtistCardData } from "./artistCardData";

export function ArtistCard({ i, data }) {
  const history = useHistory();
  const { viewCount, profileURI, nameKO, followCount, nameEN, nameForURI, artistType, signature } = data;
  const [toolTipOn, setToolTipOn] = useState()
  const click = () => {
    history.push(`/${nameForURI}/`)
    window.scrollTo(0, 0);
  }

  return (
    <Container i={i} onClick={click}>
      <IMGContainer>
        <IMG src={profileURI} />
      </IMGContainer>
      <NameRow>
        <Text >{nameKO + '(' + nameEN + ')'}</Text>
        {signature && (<Signature onMouseOver={() => { setToolTipOn(true) }}
          onMouseOut={() => { setToolTipOn(false) }} src={signatureImg}>
          {toolTipOn && <Message top={"-10px"} left={"30px"} ><div><div>스테이지에이트</div><div>시그니처아티스트</div></div></Message>}
        </Signature>)}
      </NameRow>
      <ArtistCardData
        followCount={followCount}
        viewCount={viewCount}
        artistType={artistType} />
    </Container>
  );
}
const NameRow = styled.div`
  ${makeCenter(0, 1)}
  @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
    margin-top: 16px;
    
  }
  @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
    margin-top: 8px;
    
  }
`
const Signature = styled.div`
  background: url(${props => props.src});
  cursor: pointer;
  width:20px;
  height:20px;
  position: relative;
`
const IMGContainer = styled.div`
  overflow: hidden;
  @media screen and (min-width: ${BREAKPOINT_LARGE}px) {
    width: 274px;
    height: 274px;
  }
  @media screen and (min-width: ${BREAKPOINT_MIDDLE}px) and (max-width: ${BREAKPOINT_LARGE}px) {
    width: 260px;
    height: 260px;
  }
  @media screen and (min-width: ${BREAKPOINT_SMALL}px) and (max-width: ${BREAKPOINT_MIDDLE}px){
    width: 224px;
    height: 224px;
  }
  @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
    width: 156px;
    height: 156px;
  }
`
const Container = styled.div`
  cursor: pointer;
  @media screen and (min-width: ${BREAKPOINT_LARGE}px) {
    width: 274px;
    height: (382-24)px;
    ${props => {
    if (props.i % 4 === 3) {
      return css`
              margin: 20px 0px;
            `;
    } else {
      return css`
              margin: 20px 20px 20px 0px;
            `;
    }
  }}
  }
  @media screen and (min-width: ${BREAKPOINT_MIDDLE}px) and (max-width: ${BREAKPOINT_LARGE}px) {
    width: 260px;
    height: (368-24)px;
    ${props => {
    if (props.i % 4 === 3) {
      return css`
              margin: 20px 0px;
            `;
    } else {
      return css`
              margin: 20px 20px 20px 0px;
            `;
    }
  }}
  }
  @media screen and (min-width: ${BREAKPOINT_SMALL}px) and (max-width: ${BREAKPOINT_MIDDLE}px){
    width: 224px;
    height: (332-24)px;
    ${props => {
    if (props.i % 3 === 2) {
      return css`
              margin: 20px 0px 20px 0px;
            `;
    } else {
      return css`
              margin: 20px 20px 20px 0px;
            `;
    }
  }}
  }
  @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
    width: 156px;
    height: 222px;
    ${props => {
    if (props.i % 2 === 1) {
      return css`
              margin: 12px 0px 12px 0px;
            `;
    } else {
      return css`
              margin: 12px 20px 12px 0px;
            `;
    }
  }}
    }
  position: relative;
`
const Message = styled.div`
    z-index:2;
    left:${props => props.left || '0px'};
    width:110px;
    height:44px;
    background-color:#878787;
    position:absolute;
    border-radius: 4px;
    padding: 8px;
    ${SpoqaHansSansNeo(12, 14, 400, '#F7F7F7', '0em')};
    display: flex;
    align-items: center;
    top:${props => props.top || '0px'};
`
const IMG = styled.img`
  z-index: 9999;
  transition: all .3s;
  &:hover{
    transform: scale(1.2);
  }
  @media screen and (min-width: ${BREAKPOINT_LARGE}px) {
    width: 274px;
    height: 274px;
  }
  @media screen and (min-width: ${BREAKPOINT_MIDDLE}px) and (max-width: ${BREAKPOINT_LARGE}px) {
    width: 260px;
    height: 260px;
  }
  @media screen and (min-width: ${BREAKPOINT_SMALL}px) and (max-width: ${BREAKPOINT_MIDDLE}px) {
    width: 224px;
    height: 224px;
  }
  @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
    width: 156px;
    height: 156px;
  }
`
const Text = styled.div`
  max-width: 92%;
    overflow: hidden;
	  text-overflow: ellipsis;
    white-space: nowrap;
  @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
    
    ${SpoqaHansSansNeo(20, 24, 700)}
  }
  @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
    
    ${SpoqaHansSansNeo(14, 16, 700)}
  }
`
