import { useHistory } from "react-router";
import styled from "styled-components";
import {
    BREAKPOINT_LARGE,
    BREAKPOINT_MIDDLE,
    BREAKPOINT_SMALL
} from "../../../../constants/constants";
import { SpoqaHansSansNeo } from "../../../../css/css";
import haveVideo from "../../../../images/haveVideo.svg";
import { Badge } from "../../../main/5.Portfolio/01.PortfolioCards/1.PortfolioCard/1.Badge/badge";
export function ProjectColumn({ project, marginRight, marginTop, i }) {
    const history = useHistory();

    return (
        <Container
            i={i}
            style={{ marginRight: marginRight, marginTop: marginTop }}
        >
            <ThumbnailContainer>
                <Thumbnail
                    src={project.portfolioImageURI}
                    onClick={() => {
                        history.push(`/portfoliodetail/${project._id}`);
                        window.scrollTo(0, 0);
                    }}
                />
            </ThumbnailContainer>
            <VideoData>
                <div style={{ display: "flex" }}>
                    <Name>{project.name}</Name>
                    <HaveVideo src={haveVideo}></HaveVideo>
                </div>
                <Category>{project.category[0]}</Category>
                {project.withStage8 ? <Badge /> : <Blank />}
            </VideoData>
        </Container>
    );
}
const HaveVideo = styled.img`
    height: 24px;
`;
const Blank = styled.div`
    height: 24px;
`;
const Category = styled.div`
    margin-top: 4px;
    ${SpoqaHansSansNeo(16, 20, 400, "#D0B1FF")}
`;
const ThumbnailContainer = styled.div`
    @media screen and (min-width: ${BREAKPOINT_LARGE}px) {
        width: 274px;
        height: 274px;
    }
    @media screen and (min-width: ${BREAKPOINT_MIDDLE}px) and (max-width: ${BREAKPOINT_LARGE}px) {
        width: 260px;
        height: 260px;
    }
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) and (max-width: ${BREAKPOINT_MIDDLE}px) {
        width: 224px;
        height: 224px;
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        width: 274px;
        height: 274px;
    }
    overflow: hidden;
`;
const Participants = styled.div`
    margin-top: 4px;
    ${SpoqaHansSansNeo(16, 20)}
`;
const Name = styled.div`
    margin-right:10px;
    width: 100%;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    ${SpoqaHansSansNeo(20, 24, 700)};
`;
const Thumbnail = styled.img`
    cursor: pointer;
    transition: all 0.3s;
    @media screen and (min-width: ${BREAKPOINT_LARGE}px) {
        width: 274px;
        height: 274px;
    }
    @media screen and (min-width: ${BREAKPOINT_MIDDLE}px) and (max-width: ${BREAKPOINT_LARGE}px) {
        width: 260px;
        height: 260px;
    }
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) and (max-width: ${BREAKPOINT_MIDDLE}px) {
        width: 224px;
        height: 224px;
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        width: 274px;
        height: 274px;
    }
    &:hover {
        transform: scale(1.1);
    }
`;
const VideoData = styled.div`
    margin-top: 16px;
`;
const Container = styled.div`
    margin-bottom: 20px;
    @media screen and (min-width: ${BREAKPOINT_LARGE}px) {
        width: 274px;
        margin-right: ${props => (props.i % 4 !== 3 ? "20px" : "0px")};
    }
    @media screen and (min-width: ${BREAKPOINT_MIDDLE}px) and (max-width: ${BREAKPOINT_LARGE}px) {
        width: 260px;
        margin-right: ${props => (props.i % 4 !== 3 ? "20px" : "0px")};
    }
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) and (max-width: ${BREAKPOINT_MIDDLE}px) {
        width: 224px;
        margin-right: ${props => (props.i % 3 !== 2 ? "20px" : "0px")};
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        width: 274px;
        margin-left: ${props => props.i === 0 && "20px"};
        margin-right: 20px;
    }
`;
