import React from "react";
import styled from "styled-components";
import starImage from '../../images/star.svg'

export function MiniArtistProfile({ src, style, star }) {
    return (
        <Container style={style}>
            <Circle src={src} />
            {star && <Star src={starImage}></Star>}
        </Container>
    );
}
const Container = styled.div`
    position: relative;
`
const Circle = styled.img`
    border-radius: 50em;
    width: 48px;
    height: 48px;
`
const Star = styled.img`
    position: absolute;
    left: 32px;
    top:30px;
`