import { css } from "styled-components";

export const SpoqaHansSansNeo = (size, lineHeight, weight = 400, color = '#F7F7F7', spacing = '0.1em') => css`
    color: ${color};
    font-family: "Spoqa Han Sans Neo";
    top: ${(navigator.userAgent.split('(')[1].split(';')[0] === 'Macintosh') && "2px"};
    font-size: ${size}px;
    font-style: normal;
    font-weight: ${weight};
    letter-spacing: ${spacing};
    line-height: ${lineHeight}px;
`;
export const SpoqaHansSansNeo_PX = (size, lineHeight, weight = 400, color = '#F7F7F7', spacing = '0.1em') => css`
    color: ${color};
    font-family: "Spoqa Han Sans Neo", "Arial", "Helvetica", "sans-serif";
    font-size: ${size}px;
    font-style: normal;
    font-weight: ${weight};
    letter-spacing: ${spacing};
    line-height: ${lineHeight}px;
`;
export const DisplayFlex = (justify_content, align_items) => css`
    display:flex;
    justify-content:${justify_content};
    align-items:${align_items};
`;
export const DragNone = css`
    -ms-user-select: none; 
    -moz-user-select: -moz-none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    user-select: none;
`;
export const VW = (num) => {
    return (num * 0.05215) + 'vw';
}