import React, { useReducer } from "react";
import { withRouter } from "react-router";
import styled from "styled-components";
import { useInputs } from "../../../hook/useInputs";
import { Header } from "../1.Header/header";
import { SignUpSection } from "../3.SignUpSection/0.SignUpSection/signUpSection";

const initialState = {
    agreeMarketing: false,
    agreeTerms: false,
    requirements: [
        {
            id: 'email',
            activate: false,
            message: '',
            pass: false
        },
        {
            id: 'password',
            activate: false,
            message: '',
            pass: false
        }
    ]
}

function reducer(state, action) {
    switch (action.type) {
        case 'CHANGE_REQUIREMENT':
            return {
                ...state,
                requirements: state.requirements.map(requirement =>
                    requirement.id === action.id ? { 
                        ...requirement,
                        activate: action.activate,
                        pass: action.pass,
                        message: action.message } : requirement
                )
            };
        case 'TOGGLE_AGREEMARKETING':
            return {
                ...state,
                agreeMarketing: !state.agreeMarketing
            };
        case 'TOGGLE_AGREETERMS':
            return {
                ...state,
                agreeTerms: !state.agreeTerms
            };
        default:
            return state;
    }
}

export const SignUpDispatch = React.createContext(null);
export const SignUp = withRouter(SignUpFunction);

function SignUpFunction() {
    const [state, dispatch] = useReducer(reducer, initialState);
    const { agreeMarketing, agreeTerms, requirements } = state;
    const [{ password, email }, onChange] = useInputs({
        password: '',
        email: ''
    });

    return (
        <SignUpDispatch.Provider value={dispatch}>
            <Container>
                <Header />
                <SignUpSection
                    requirements={requirements}
                    password={password}
                    email={email}
                    agreeMarketing={agreeMarketing}
                    agreeTerms={agreeTerms}
                    onChange={onChange} />
            </Container>
        </SignUpDispatch.Provider>
    );
}

const Container = styled.div`
    min-height: 946px;
    display: flex;
    flex-direction: column;
    background-color:#01010B;
    -ms-user-select: none; 
    -moz-user-select: -moz-none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    user-select: none;
`