import React, { useContext } from "react";
import styled from "styled-components";
import { RadioButton } from "../../../../components/0.Button/radioButton";
import { SpoqaHansSansNeo } from "../../../../css/css";
import { SelectUserTypeDispatch } from "../../0.UserTypeSelect/UserTypeSelect";

export function Select({ header, exp, userType, producer, performer, requirement1, requirement2 }) {
    const dispatch = useContext(SelectUserTypeDispatch)
    const isExtra = () => {
        return (userType === '아티스트' && header === '아티스트')
    }

    return (
        <Container requirement={requirement1 || (requirement2 && header === '아티스트')} extra={isExtra()}>
            <Row1>
                <RadioButton
                    requirement={requirement1}
                    style={{ marginRight: 20 }}
                    activate={userType === header}
                    onClick={() => dispatch({ type: 'CHOOSE_USERTYPE', userType: header, })} />
                <Header>{header}</Header>
            </Row1>
            <Explanation>{exp}</Explanation>
        </Container>
    );
}
const Container = styled.div`
    position: relative;
    height: 184px;
    padding: 40px;
    width: 280px;
    border: 1px solid;
    border-color: ${props => props.requirement ? '#FF4E45' : '#F7F7F7'};
    border-radius: 4px;
    transition: all .5s;
`
const Row1 = styled.div`
    display:flex;
    width: 200px;
    margin-bottom:16px;
`
const Header = styled.div`
    ${SpoqaHansSansNeo(20, 24, 700)};
`
const Explanation = styled.div`
    width: 190px;
    ${SpoqaHansSansNeo(16, 20, 400, '#BEBEBE')};
`