import React from "react";
import styled from "styled-components";
import { BREAKPOINT_SMALL } from "../../../constants/constants";
import { DisplayFlex } from "../../../css/css";
import { isEmptyObject } from "../../../lib/lib";
import { BannerSection } from "../1.BannerSection/0.BannerSection/bannerSection";
import { Track } from "./1.Track/track";
import { Video } from "./2.Video/video";
import { ArtistProject } from "./3.ArtistProject/artistProject";

export function ArtistPortfolio(props) {
    return (
        <Container>
            <BannerSection
                refetch={props.refetch}
                currentSize={props.currentSize}
                id={props.data.artist._id}
                src={props.data.artist.bannerURI}
                nameEN={props.data.artist.nameEN}
                followCount={props.data.artist.followCount}
                followed={props.data.followed}
                onRequest={props.data.onRequest} />
            <InnerContainer>
                {!isEmptyObject(props.songs) && <Track songs={props.songs} />}
                <ArtistProject currentSize={props.currentSize} />
                {!isEmptyObject(props.videos) && <Video videos={props.videos} />}

            </InnerContainer>
        </Container>
    );
}

const InnerContainer = styled.div`
    padding-bottom: 40px;
`
const Container = styled.div`
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
        width:100%;
        ${DisplayFlex("", "center")}
    }
    flex-direction: column;
    background-color: #01010B;
    -ms-user-select: none; 
    -moz-user-select: -moz-none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    user-select: none;
`