import React from "react";
import { Link } from "react-router-dom";
import styled from "styled-components";
import { SpoqaHansSansNeo } from "../../css/css";

export function PurpleTextLink({ style, href, children, onClick, linkTag, to }) {
    if (linkTag) {
        return (<Link style={{...style, textDecoration:'none'}} to={to} onClick={onClick}><Text>{children}</Text></Link>)
    } else {
        return (<A style={style} onClick={onClick} target="_blank" href={href}>{children}</A>)
    }
}

const A = styled.a`
    ${SpoqaHansSansNeo(12,14,700,'#D0B1FF')};
    cursor: pointer;
    text-decoration: none;
    &:hover { text-decoration: underline;}
`
const Text = styled.span`
    ${SpoqaHansSansNeo(12,14,700,'#D0B1FF')};
    cursor: pointer;
    &:hover { text-decoration: underline;}
`