import React from "react";
import styled from "styled-components";
import { BREAKPOINT_LARGE, BREAKPOINT_MIDDLE, BREAKPOINT_SMALL } from "../../constants/constants";
import { RequestForm } from "./requestForm";

export function Request(props) {
    const data = props.data;

    let artistData = null;
    let src, nameKO, nameForURI, profileURI = null;
    if (data) {
        artistData = data.artist
        src = artistData.bannerURI
        nameKO = artistData.nameKO
        nameForURI = artistData.nameForURI
        profileURI = artistData.profileURI
    }
    return (
        <Container>
            <InnerContainer>
                {
                    data ?
                        <>
                            <Banner src={src}></Banner>
                            <RequestForm
                                setNoScroll={props.setNoScroll}
                                currentSize={props.currentSize}
                                profileURI={profileURI}
                                nameForURI={nameForURI}
                                nameKO={nameKO} />
                        </>
                        :
                        <EmptySpace />
                }
            </InnerContainer>
        </Container>
    );
}
const EmptySpace = styled.div`
    height:900px;
    width: 100%;
`
const InnerContainer = styled.div`
    
`
const Banner = styled.div`
    background: linear-gradient(rgba(0, 0, 0, 0) 25%, 75%, #000000), url(${props => props.src});
    background-repeat: no-repeat;
    background-position: top;
    background-size: cover;
    width:100%;
    display:flex;
    flex-direction:column;
    @media screen and (min-width: ${BREAKPOINT_LARGE}px) {
        height: 572px;
    }
    @media screen and (min-width: ${BREAKPOINT_MIDDLE}px) and (max-width: ${BREAKPOINT_LARGE}px) {
        height: 416px;
    }
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) and (max-width: ${BREAKPOINT_MIDDLE}px){
        height: 278px;
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        height: 258px;
    }
`
const Container = styled.div`
    border: none !important;
    display: flex;
    flex-direction: column;
    background-color:#01010B;
    -ms-user-select: none; 
    -moz-user-select: -moz-none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    user-select: none;
`