import { useState } from "react";
import { Button } from "src/ts/components/button/button";
import { EditButton } from "src/ts/components/button/editButton";
import { GENRES } from "src/ts/constants";
import { cloneObj } from "src/ts/lib/lib";
import { IDropdownState, IDropdownStateNode } from "src/ts/models";
import { usePatchArtistGenre } from "src/ts/models/artistProfile/profileBanner";
import { FontNormal, makeCenter } from "src/ts/styles/css";
import styled from "styled-components";
import { BlankButton } from "../../components/button/blankButton";
import {
    Modal,
    ModalFooter,
    ModalHeader,
    ModalMain,
} from "../../components/modal/modal";
import { ChooseGenre } from "./chooseGenre";

export const UpdateGenre = () => {
    const [isOpen, setIsOpen] = useState(false);
    const [patch] = usePatchArtistGenre();
    const [genreKeywords, setGenreKeywords] = useState<IDropdownStateNode[]>(
        new Array<IDropdownStateNode>(),
    );
    const [genres, setGenres] = useState<IDropdownState>(cloneObj(GENRES));
    const convert = (genres: IDropdownStateNode[]) => {
        const genre: string[] = [];
        genres.forEach(item => {
            genre.push(item.text);
        });

        return genre;
    };
    return (
        <>
            <EditButton
                mini={true}
                rightAlign={true}
                onClick={() => setIsOpen(true)}
            />
            <Modal isOpen={isOpen} empty={false}>
                {isOpen && (
                    <>
                        <ModalHeader>{"장르 수정"}</ModalHeader>
                        <ModalMain>
                            <FontNormal style={{ marginBottom: 20 }}>
                                입력값이 없을 경우 항목이 보이지 않습니다.
                            </FontNormal>
                            <ChooseGenre
                                genreKeywords={genreKeywords}
                                setGenreKeywords={setGenreKeywords}
                                genres={genres}
                                setGenres={setGenres}
                            />
                        </ModalMain>
                        <ModalFooter>
                            <BlankButton
                                height={40}
                                width={96}
                                onClick={() => {
                                    setIsOpen(false);
                                }}
                            >
                                취소
                            </BlankButton>
                            <Button
                                rightAlign={true}
                                height={40}
                                width={96}
                                onClick={() => patch(convert(genreKeywords))}
                            >
                                {"확인"}
                            </Button>
                        </ModalFooter>
                    </>
                )}
            </Modal>
        </>
    );
};

const InputFormRow = styled.div`
    display: flex;
`;
const Text = styled.div`
    width: 30px;
    ${makeCenter()}
`;
