import { useState } from "react";
import { Button } from "src/ts/components/button/button";
import { EditButton } from "src/ts/components/button/editButton";
import { InputForm } from "src/ts/components/input/inputForm";
import { SpoqaHansSansNeo } from "src/ts/styles/css";
import styled from "styled-components";
import { BlankButton } from "../../components/button/blankButton";
import { Modal } from "../../components/modal/modal";
import { useFetchIntroduction } from "../../models/artistProfile/profileBanner";

export const UpdateIntroduction = ({ existedData }) => {
    const [isOpen, setIsOpen] = useState(false);
    const [fetch] = useFetchIntroduction();
    const [input, setInput] = useState(existedData);
    const onChange = (e: React.ChangeEvent<HTMLTextAreaElement>) => {
        setInput(e.target.value);
    };

    return (
        <>
            <EditButton rightAlign={false} onClick={() => setIsOpen(true)} />
            <Modal isOpen={isOpen} empty={false}>
                {isOpen && (
                    <>
                        <Header>{"자기소개 수정"}</Header>
                        <Main>
                            <InputForm
                                onKeyPress={() => {}}
                                textarea={true}
                                style={{
                                    width: "100%",
                                }}
                                height={200}
                                onChange={onChange}
                                value={input}
                                placeholder="본인의 음악 경력, 아티스트 이미지 등을 간략하게 소개해주세요. "
                            ></InputForm>
                        </Main>
                        <Footer>
                            <BlankButton
                                height={40}
                                width={96}
                                onClick={() => {
                                    setIsOpen(false);
                                }}
                            >
                                취소
                            </BlankButton>
                            <Button
                                rightAlign={true}
                                height={40}
                                width={96}
                                onClick={() => fetch(input)}
                            >
                                {"확인"}
                            </Button>
                        </Footer>
                    </>
                )}
            </Modal>
        </>
    );
};

const Header = styled.div`
    position: relative;
    height: 72px;
    padding: 24px;
    ${SpoqaHansSansNeo(20, 24, 700)};
    display: flex;
    align-items: center;
`;

const Main = styled.div`
    ${SpoqaHansSansNeo(16, 20, 400)};
    padding: 24px;
    border-top: 1px solid #828282;
    min-height: 150px;
`;

const Footer = styled.div`
    width: 100%;
    margin-top: auto;
    border-top: 1px solid #828282;
    ${SpoqaHansSansNeo(16, 20, 400)};
    padding: 24px;
    display: flex;
`;
