import React from "react";
import styled from "styled-components";
import { Input } from "../../components/4.Input/input";
import { BlankButton3 } from "../../components/0.Button/blankButton3";
import { useInputs } from "../../hook/useInputs";
import { Link } from "./link";
import { SpoqaHansSansNeo } from "../../css/css";
import { BREAKPOINT_SMALL } from "../../constants/constants";
import { Mobile, NotMobile } from "../../lib/mediaQuery";

export function RelatedLinks({URLs, setURLs, currentSize}) {
    const [{ URL }, onChange, reset] = useInputs({
        URL: ''
    });
    const onClick = () =>{
        setURLs(URLs.concat(URL))
        reset();
    }
    const onRemove = (id) => {
        setURLs(URLs.filter(url => url !== id));
    }
    return (
        <Container>
            <SmallTItle>관련 링크</SmallTItle>
            <Discription>
                <NotMobile>
                    아티스트가 참고할 자료가 웹사이트에 올려져 있나요? 해당 URL을 아래 기입해주세요.
                </NotMobile>
                <Mobile>
                    아티스트가 참고할 자료가 웹사이트에 올려져 있나요?<br></br> 해당 URL을 아래 기입해주세요.
                </Mobile>
            </Discription>
            <Row1>
                <Input
                    name='URL'
                    value={URL}
                    style={{ width: currentSize !== "MOBILE" ? 626 : 223, marginRight:16, marginBottom:12 }}
                    placeholder='https://www.naver.com'
                    onChange={(e) => {
                        onChange(e);
                    }}/>
                <BlankButton3 
                    onClick={onClick}
                    width={currentSize !== "MOBILE" ? 96 : 104} 
                    height={currentSize !== "MOBILE" ? 40 : 32}  
                    text1={"링크 추가"}/>
            </Row1>
            {URLs.map((url, i) => (
                <Link
                    onRemove={onRemove}
                    key={i}
                    name={url}/>
            ))}
        </Container>
    );
}
const Row1 = styled.div`
display:flex;
`
const Container = styled.div`

`
const SmallTItle = styled.div`
    margin-top:40px;
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) { 
        ${SpoqaHansSansNeo(24,28,700)};
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        ${SpoqaHansSansNeo(16,20,700)};
    }
`
const Discription = styled.div`
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) { 
        margin: 16px 0px;
        ${SpoqaHansSansNeo(16,20)};
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        margin: 8px 0px;
        ${SpoqaHansSansNeo(14,16,400,'#F7F7F7','0em')};
    }
`