import styled, { css } from "styled-components";

export const SpoqaHansSansNeo = (
    size = 16,
    lineHeight = 20,
    weight = 400,
    color = "#F7F7F7",
    spacing = "0.1em",
) => css`
    color: ${color};
    font-size: ${size}px;
    font-weight: ${weight};
    letter-spacing: ${spacing};
    line-height: ${lineHeight}px;
`;
export const makeCenter = (
    horizontal: number = 1,
    verticality: number = 1,
) => css`
    display: flex;
    justify-content: ${horizontal === 1 ? "center" : ""};
    align-items: ${verticality === 1 ? "center" : ""};
`;
