import { useRecoilValueLoadable } from "recoil";
import { artistInfoSelector } from "src/ts/models/artistProfile/about";
import styled from "styled-components";
import { ArtistProjectsAndSongs } from "./artistProjectsAndSongs";
import { ProfileBanner } from "./profileBanner";

export function ArtistPortfolio() {
    const artistInfo = useRecoilValueLoadable(artistInfoSelector);

    switch (artistInfo.state) {
        case "hasValue":
            const { artist } = artistInfo.contents;
            return (
                <>
                    <ProfileBanner
                        button
                        bannerURI={artist.bannerURI}
                        nameEN={artist.nameEN}
                    />
                    <InnerContainer>
                        <ArtistProjectsAndSongs data={artist} />
                    </InnerContainer>
                </>
            );
        case "loading":
            return <></>;
        case "hasError":
            throw artistInfo.contents;
    }
}
const InnerContainer = styled.div`
    padding-bottom: 40px;
`;
