import { ReactNode } from "react";
import { atom } from "recoil";

export class CModalData {
    isOpen: boolean;
    header: string;
    main: ReactNode;
    footerText: string;
    empty: boolean;
    form: ReactNode;
    onClick: Function;
    handleClick: boolean;
    setIsOpen: Function;
    constructor(
        header: string = "타이틀",
        main: ReactNode = <></>,
        footerText: string = "제출",
        onClick: Function = () => {},
        setIsOpen: Function = () => {},
        empty: boolean = false,
        isOpen: boolean = true,
        form: ReactNode = <></>,
        handleClick = false,
    ) {
        this.setIsOpen = setIsOpen;
        this.isOpen = isOpen;
        this.header = header;
        this.main = main;
        this.footerText = footerText;
        this.onClick = onClick;
        this.empty = empty;
        this.form = form;
        this.handleClick = handleClick;
    }
}

export const currentModaldataState = atom<CModalData>({
    key: "currentModaldataState",
    default: new CModalData(
        "타이틀",
        <></>,
        "제출",
        () => {},
        () => {},
        false,
        false,
    ),
});

export const modalScrollState = atom({
    key: "modalScrollState",
    default: false,
});
