import React from "react";
import styled from "styled-components";
import { BREAKPOINT_SMALL } from "../../constants/constants";
import { DisplayFlex, SpoqaHansSansNeo } from "../../css/css";

export function DropDownListRow2({ isOpen, onClick, content }) {
    const open = () => {
        if(isOpen)onClick(content);
    }
    return (
        <Container onClick={() => open()} isOpen={isOpen}>
            <Text>{content.text}</Text>
        </Container>
    )
}
const Container = styled.div`
    transition: all .3s;
    &:hover {
        opacity: 1;
        background: ${props => (props.activate ? '#BD03FA' : '#4A4A4A')};
    }
    //background: ${props => (props.activate ? '#BD03FA' : '#0E0E10')};
    &:active{
        opacity: 1;
        background: #BD03FA;
    }
    opacity: ${props => (props.isOpen ? "1" : "0")};

    ${DisplayFlex("", "center")}
    display: ${props => (!props.isOpen ? "flex" : 'flex')};
    cursor: ${props => (props.isOpen && 'pointer')};
    height:40px;
    padding: 8px 12px;
`;
const Text = styled.div`
    z-index:2;
    -ms-user-select: none; 
    -moz-user-select: -moz-none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    user-select: none;
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
        ${SpoqaHansSansNeo(16, 24, 700)}
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        ${SpoqaHansSansNeo(14, 16, 400)}
    }
`