import { ReactNode } from "react";
import { useRecoilValue } from "recoil";
import { modalScrollState } from "src/ts/models/modal";
import { commonPaddingTopSelector } from "src/ts/models/paddingTop";
import { SpoqaHansSansNeo } from "src/ts/styles/css";
import styled, { css } from "styled-components";

interface IModal {
    isOpen: boolean;
    children: ReactNode;
    empty: boolean;
    fullScreen?: boolean;
    style?: Object;
}

interface IBG {
    isOpen: boolean;
}

export const Modal = function ({
    isOpen,
    children,
    empty,
    fullScreen = false,
    style = {},
}: IModal) {
    const paddingTop = useRecoilValue(commonPaddingTopSelector);
    const scrollY = useRecoilValue(modalScrollState);
    return (
        <BG isOpen={isOpen}>
            {empty ? (
                children
            ) : (
                <Form
                    style={style}
                    scroll={scrollY}
                    fullScreen={fullScreen}
                    paddingTop={paddingTop}
                >
                    {children}
                </Form>
            )}
        </BG>
    );
};
interface IForm {
    paddingTop: number;
    fullScreen: boolean;
    scroll: boolean;
}
const Form = styled.div<IForm>`
    width: ${props => (props.fullScreen ? "100vw" : "688px")};
    height: ${props => props.fullScreen && "100vh"};
    padding-top: ${props => props.fullScreen && props.paddingTop}px;
    border-radius: 8px;
    background: #01010b;
    position: relative;
    overflow-y: ${props => props.scroll && "scroll"};
`;

const BG_CLOSE = css`
    position: fixed;
    display: flex;
    opacity: 0;
    z-index: -1;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
`;

const BG_OPEN = css`
    position: fixed;
    display: flex;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 99;
    opacity: 1;
    background-color: rgba(0, 0, 0, 0.6);
`;

const BG = styled.div<IBG>`
    ${props => (props.isOpen ? BG_OPEN : BG_CLOSE)}
    align-items: center;
    justify-content: center;
    transition: all 0.3s;
`;
export const ModalHeader = styled.div`
    position: relative;
    height: 72px;
    padding: 24px;
    ${SpoqaHansSansNeo(20, 24, 700)};
    display: flex;
    align-items: center;
`;

export const ModalMain = styled.div`
    ${SpoqaHansSansNeo(16, 20, 400)};
    padding: 24px;
    border-top: 1px solid #828282;
    min-height: 150px;
`;

export const ModalFooter = styled.div`
    width: 100%;
    margin-top: auto;
    border-top: 1px solid #828282;
    ${SpoqaHansSansNeo(16, 20, 400)};
    padding: 24px;
    display: flex;
`;
