import editButton from "src/ts/images/editButton.svg";
import { makeCenter } from "src/ts/styles/css";
import styled from "styled-components";

interface IBlankButton {
    onClick: () => void;
    style?: object;
    children?: React.ReactNode;
    leftMargin?: number;
    rightAlign?: boolean;
    mini?: boolean;
}

interface IContainer {
    style?: object;
    leftMargin?: number;
    rightAlign?: boolean;
    mini?: boolean;
}

export function EditButton({
    style,
    onClick,
    children,
    leftMargin,
    rightAlign,
    mini = false,
}: IBlankButton) {
    return (
        <Circle
            mini={mini}
            style={style}
            onClick={onClick}
            leftMargin={leftMargin}
            rightAlign={rightAlign}
        >
            {children}
            <Edit src={editButton}></Edit>
        </Circle>
    );
}
const Edit = styled.img`
    width: 18px;
    height: 18px;
`;
const Circle = styled.label<IContainer>`
    margin-left: ${props => props.leftMargin}px;
    margin-left: ${props => props.rightAlign && "auto"};
    background-color: ${props => !props.mini && "white"};
    border-radius: ${props => !props.mini && "50em"};
    width: ${props => (props.mini ? "20" : "32")}px;
    height: ${props => (props.mini ? "20" : "32")}px;
    ${makeCenter()}
    cursor: pointer;
`;
