import React from 'react';
import styled from 'styled-components';
import { BREAKPOINT_LARGE, BREAKPOINT_MIDDLE, BREAKPOINT_SMALL } from '../../constants/constants';
import { SpoqaHansSansNeo } from '../../css/css';

export function Input({ onBlur, onFocus, ref1, style, type, textarea, autoComplete, pass, onClick, placeholder, value, name, onChange, requirement, onKeyPress }) {
    if (textarea)
        return (
            <Container2
                onBlur={onBlur}
                onFocus={onFocus}
                ref={ref1}
                autoComplete={!autoComplete && "new-password"}
                onKeyPress={onKeyPress}
                style={style}
                pass={pass}
                onClick={onClick}
                requirement={requirement}
                onChange={onChange}
                name={name}
                placeholder={placeholder}
                value={value}
                type={
                    (name === 'password' || name === 'password2') ? 'password' :
                        type || 'text'}
            />
        );
    else
        return (
            <Container
                autoComplete={!autoComplete && "new-password"}
                onKeyPress={onKeyPress}
                style={style}
                pass={pass}
                onClick={onClick}
                requirement={requirement}
                onChange={onChange}
                name={name}
                placeholder={placeholder}
                value={value}
                type={
                    (name === 'password' || name === 'password2') ? 'password' :
                        type || 'text'}
            />
        );
}
const Container = styled.input`
    &::-webkit-outer-spin-button{
        -webkit-appearance: none;
        margin: 0;
    }
    &::-webkit-inner-spin-button{
        -webkit-appearance: none;
        margin: 0;
    }

    &:hover{
        outline: none;
        border-color:${props => props.requirement ? '#FF4E45' : props.pass ? '#06BE34' : '#BD03FA'};
    }
    &:focus{
        outline: none;
        border-color:${props => props.requirement ? '#FF4E45' : '#BD03FA'};
    }
    width: 372px;
    background: #0E0E10;
    border: 1px solid;
    box-sizing: border-box;
    border-radius: 4px;
    @media screen and (min-width: ${BREAKPOINT_LARGE}px) {
        ${SpoqaHansSansNeo(16, 24, 400)};
        height: 40px;
        padding-left:20px;
    }
    @media screen and (min-width: ${BREAKPOINT_MIDDLE}px) and (max-width: ${BREAKPOINT_LARGE}px) {
        ${SpoqaHansSansNeo(16, 24, 400)};
        height: 40px;
        padding-left:20px;
    }
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) and (max-width: ${BREAKPOINT_MIDDLE}px){
        ${SpoqaHansSansNeo(14, 16, 400)};
        height: 32px;
        padding-left:20px;
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        ${SpoqaHansSansNeo(14, 16, 400)};
        height: 32px;
        padding-left:8px;
    }
    &::placeholder {
        @media screen and (min-width: ${BREAKPOINT_SMALL}px) { 
            ${SpoqaHansSansNeo(16, 24, 400, '#828282')};
        }
        @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
            ${SpoqaHansSansNeo(14, 16, 400, '#828282', '0em')};
        }
    }
    
    border-color:${props => props.requirement ? '#FF4E45' : props.pass ? '#06BE34' : '#828282'};
`
const Container2 = styled.textarea`
    ${SpoqaHansSansNeo(16, 24, 400)};

    &::placeholder {
        @media screen and (min-width: ${BREAKPOINT_SMALL}px) { 
            ${SpoqaHansSansNeo(16, 24, 400, '#828282')};
        }
        @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
            ${SpoqaHansSansNeo(14, 16, 400, '#828282', '0em')};
        }
    }
    &::-webkit-scrollbar { display: none; };
    resize:none;
    &:hover{
        outline: none;
        border-color:${props => props.requirement ? '#FF4E45' : props.pass ? '#06BE34' : '#BD03FA'};
    }
    &:focus{
        outline: none;
        border-color:${props => props.requirement ? '#FF4E45' : '#BD03FA'};
    }
    height: 40px;
    width: 372px;
    background: #0E0E10;
    border: 1px solid;
    box-sizing: border-box;
    border-radius: 4px;
    padding-left:20px;
    border-color:${props => props.requirement ? '#FF4E45' : props.pass ? '#06BE34' : '#828282'};
`