import axios from "axios";
import React, { useState } from "react";
import styled from "styled-components";
import { Button } from "../../components/0.Button/button";
import { PurpleTextLink } from "../../components/1.PurpleTextLink/purpleTextLink";
import { Input } from "../../components/4.Input/input";
import { API_SERVER2 } from "../../constants/constants";
import { SpoqaHansSansNeo } from "../../css/css";
import { useInputs } from "../../hook/useInputs";
import { SettingSuccessModal } from "../myPage/settingSuccessModal";
import { Header } from "../signUp/1.Header/header";

export function PwRecovery() {
    const [settingModalOpen, setSettingModalOpen] = useState(false);
    const [errorMessage, setErrorMessage] = useState('링크가 발송되었습니다. 이메일을 확인해 주세요.');
    const [{ email }, onChange] = useInputs({
        email: ''
    });
    const send = async () => {
        let form = new FormData();
        form.append("email", email)

        const url = API_SERVER2 + `/auth/forgot-password`;
        const options = {
            method: 'POST',
            url: url,
            withCredentials: true,
            data: form
        };
        axios(options).then(() => {
            setSettingModalOpen(true);
            setErrorMessage('링크가 발송되었습니다. 이메일을 확인해 주세요.')
        }).catch(error => {
            if(error.response.status === 404){
                setSettingModalOpen(true);
                setErrorMessage('등록된 이메일이 아닙니다.')
            } else if(error.response.status === 400){
                setSettingModalOpen(true);
                setErrorMessage('잠시 후에 시도해 주십시오.')
            }
        })
    }
    return (
        <Container>
            <Header />
            <InnerContainer>
                <InnerContainer2>
                    <Title>비밀번호 찾기</Title>
                    <Text1>STAGE8과 등록된 이메일 주소를 입력해주세요.<br></br>
                        담당자의 확인 후 비밀번호를 새로 설정할 수 있는 링크를 이메일로 보내드리겠습니다.</Text1>
                    <EmailAddress>Email Address</EmailAddress>
                    <Input
                        onChange={onChange}
                        name="email"
                        value={email}
                        placeholder="example@example.com" />
                    <Button 
                        onClick={() => send()}
                        style={{ 
                            width: 200, 
                            height: 48, 
                            marginTop: 40, 
                            marginBottom: 8 }}>
                        링크 발송
                    </Button>
                    <PurpleTextLink
                        to={'/recoveryhelp'}
                        linkTag={true}>
                        이메일 주소가 떠오르지 않으시나요?
                    </PurpleTextLink>
                </InnerContainer2>
            </InnerContainer>
            <SettingSuccessModal
                open={settingModalOpen}
                close={() => {setSettingModalOpen(false);}}
                text={errorMessage} />
        </Container>
    );
}

const EmailAddress = styled.div`
    margin-bottom:16px;
    margin-top:40px;
    ${SpoqaHansSansNeo(24,28,700)};
`
const Text1 = styled.div`
    ${SpoqaHansSansNeo(16,20)};
    margin-top:16px;
`
const InnerContainer2 = styled.div`
    width:1440px;
    padding: 80px 142px 0px 142px;
`
const InnerContainer = styled.div`
    display:flex;
    justify-content:center;
`
const Title = styled.div`
    ${SpoqaHansSansNeo(34,40,700)};
`

const Container = styled.div`
    min-height: 946px;
    display: flex;
    flex-direction: column;
    background-color:#01010B;
    padding-bottom:200px;
    -ms-user-select: none; 
    -moz-user-select: -moz-none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    user-select: none;
`