import { useState } from "react";
import { useRecoilState, useSetRecoilState } from "recoil";
import albumArt from "src/images/albumArt.svg";
import { BlankButton } from "src/ts/components/button/blankButton";
import { Button } from "src/ts/components/button/button";
import { EditButton } from "src/ts/components/button/editButton";
import { InputForm } from "src/ts/components/input/inputForm";
import {
    Modal,
    ModalFooter,
    ModalHeader,
    ModalMain,
} from "src/ts/components/modal/modal";
import { GENRES } from "src/ts/constants";
import { cloneObj, onChange } from "src/ts/lib/lib";
import {
    currentSizeSelector,
    IDropdownState,
    IDropdownStateNode,
} from "src/ts/models";
import { useUpdateSongs } from "src/ts/models/artistProfile/profileBanner";
import { currentModaldataState } from "src/ts/models/modal";
import { makeCenter, SpoqaHansSansNeo } from "src/ts/styles/css";
import { AttachFiles2 } from "src/view/request/attachFiles2";
import styled from "styled-components";
import camera from "../../images/camera.svg";
import { ChooseGenre } from "./chooseGenre";
import { handleFileInput } from "./handleFileInput";

export const UpdateTrack = ({ existedData }) => {
    const [fileUploaded, setFileUploaded] = useState([]);
    const deleteFile = text => {
        setFileUploaded(
            fileUploaded.filter((keyword: any) => keyword.name !== text),
        );
    };

    const [genres, setGenres] = useState<IDropdownState>(cloneObj(GENRES));
    const fileUpload = v => {
        setFileUploaded(v);
    };
    const convert = (genres: IDropdownStateNode[]) => {
        const genre: string[] = [];
        genres.forEach(item => {
            genre.push(item.query);
        });

        return genre;
    };
    const makeGenre = data => {
        let _genre = new Array<IDropdownStateNode>();
        data.map(singleCategory => {
            GENRES.contents.map(item => {
                if (item.query === singleCategory) {
                    item.activate = true;
                    _genre.push(item);
                }
            });
        });

        return _genre;
    };
    const [genreKeywords, setGenreKeywords] = useState<IDropdownStateNode[]>(
        makeGenre(existedData.genre),
    );
    console.log(existedData);
    const [overSizeModalOpen, setOverSizeModalOpen] = useState(false);
    const [overSize, setOverSize] = useState(false);
    const setModal = useSetRecoilState(currentModaldataState);
    const [fetchImage, setFetchImage] = useState(existedData.albumArt);
    const [selectedFile, setSelectedFile] = useState(existedData.albumArt);
    const [isOpen, setIsOpen] = useState(false);
    const [fetchSongs] = useUpdateSongs();
    const [trackName, setTrackName] = useState(existedData.title);
    const [artistName, setArtistName] = useState(existedData.artistName);
    const [albumName, setAlbumName] = useState(existedData.albumName);
    const [currentSize, setCurrentSize] = useRecoilState(currentSizeSelector);

    return (
        <div style={{ position: "relative" }}>
            <EditButton
                style={{
                    position: "absolute",
                    right: 40,
                    width: 140,
                    top: -54,
                }}
                mini={true}
                rightAlign={true}
                onClick={() => setIsOpen(true)}
            ></EditButton>
            <Modal isOpen={isOpen} empty={false} style={{ width: 752 }}>
                {isOpen && (
                    <>
                        <ModalHeader>{"대표곡 수정"}</ModalHeader>
                        <ModalMain style={{ display: "flex" }}>
                            <LeftColumn>
                                <ProfileContainer>
                                    <ProfileURI
                                        src={selectedFile || albumArt}
                                    ></ProfileURI>
                                    <Button
                                        round
                                        label={true}
                                        style={{
                                            position: "absolute",
                                            bottom: 40,
                                        }}
                                        width={148}
                                        height={40}
                                        onClick={() => {}}
                                    >
                                        <input
                                            accept="image/*"
                                            style={{ display: "none" }}
                                            type="file"
                                            name="file"
                                            onChange={e =>
                                                handleFileInput(
                                                    e,
                                                    setModal,
                                                    setFetchImage,
                                                    setSelectedFile,
                                                )
                                            }
                                        />
                                        <img
                                            alt="camera"
                                            src={camera}
                                            style={{ marginRight: 5 }}
                                        />
                                        업데이트
                                    </Button>
                                </ProfileContainer>
                                <AttachFiles2
                                    currentSize={currentSize}
                                    setOverSize={setOverSize}
                                    setOverSizeModalOpen={setOverSizeModalOpen}
                                    files={fileUploaded}
                                    addFile={fileUpload}
                                    deleteFile={deleteFile}
                                />
                            </LeftColumn>
                            <RightColumn>
                                <InputForm
                                    requirement
                                    onKeyPress={() => {}}
                                    style={{ marginBottom: 20 }}
                                    onChange={onChange(setTrackName)}
                                    errorMessage={"*등록되지않은 이메일입니다."}
                                    value={trackName}
                                    placeholder="트랙명"
                                    header="트랙명"
                                    width={406}
                                ></InputForm>
                                <InputForm
                                    requirement
                                    onKeyPress={() => {}}
                                    style={{ marginBottom: 20 }}
                                    onChange={onChange(setArtistName)}
                                    errorMessage={"*등록되지않은 이메일입니다."}
                                    value={artistName}
                                    placeholder="아티스트명"
                                    header="아티스트명"
                                    width={406}
                                ></InputForm>
                                <InputForm
                                    requirement
                                    onKeyPress={() => {}}
                                    style={{ marginBottom: 20 }}
                                    onChange={onChange(setAlbumName)}
                                    errorMessage={"*등록되지않은 이메일입니다."}
                                    value={albumName}
                                    placeholder="앨범명"
                                    header="앨범명"
                                    width={406}
                                ></InputForm>
                                <ChooseGenre
                                    width={406}
                                    genreKeywords={genreKeywords}
                                    setGenreKeywords={setGenreKeywords}
                                    genres={genres}
                                    setGenres={setGenres}
                                />
                            </RightColumn>
                        </ModalMain>
                        <ModalFooter>
                            <BlankButton
                                height={40}
                                width={96}
                                onClick={() => {
                                    setIsOpen(false);
                                }}
                            >
                                취소
                            </BlankButton>
                            <Button
                                rightAlign={true}
                                height={40}
                                width={96}
                                onClick={() =>
                                    fetchSongs(
                                        fetchImage,
                                        fileUploaded,
                                        trackName,
                                        artistName,
                                        albumName,
                                        convert(genreKeywords),
                                        existedData._id,
                                    )
                                }
                            >
                                {"확인"}
                            </Button>
                        </ModalFooter>
                    </>
                )}
            </Modal>
        </div>
    );
};
const LeftColumn = styled.div`
    margin-right: 20px;
`;
const RightColumn = styled.div``;
const Text2 = styled.div`
    ${makeCenter(0, 1)}
    ${SpoqaHansSansNeo(16, 20, 700, "#BD03FA")}
margin-left: 8px;
`;
const PlusImage = styled.img``;
const AddButton = styled.div`
    height: 24px;
    cursor: pointer;
    margin-left: auto;
    display: flex;
`;
const ProfileContainer = styled.div`
    position: relative;
    ${makeCenter(1, 0)}
    margin-bottom: 20px;
`;
const ProfileURI = styled.img`
    width: 274px;
    height: 274px;
`;
