import { useState } from "react";
import { BlankButton } from "src/ts/components/button/blankButton";
import { Button } from "src/ts/components/button/button";
import { InputForm } from "src/ts/components/input/inputForm";
import {
    Modal,
    ModalFooter,
    ModalHeader,
    ModalMain,
} from "src/ts/components/modal/modal";
import { onChange } from "src/ts/lib/lib";
import { usePatchTestimonial } from "src/ts/models/artistProfile/aboutdata";
import { makeCenter, SpoqaHansSansNeo } from "src/ts/styles/css";
import styled from "styled-components";
import plus from "../../images/plus.svg";

export const AddTestimonial = () => {
    const [isOpen, setIsOpen] = useState(false);
    const [fetchTestimonial] = usePatchTestimonial();
    const [inputContent, setInputContent] = useState("");
    const [inputWriter, setInputWriter] = useState("");
    const [inputField, setInputField] = useState("");

    return (
        <>
            <Container onClick={() => setIsOpen(true)}>
                <PlusImage src={plus}></PlusImage>
                <Text>추천사 추가하기</Text>
            </Container>
            <Modal isOpen={isOpen} empty={false}>
                {isOpen && (
                    <>
                        <ModalHeader>{"추천사 추가"}</ModalHeader>
                        <ModalMain>
                            <InputForm
                                textarea={true}
                                marginBottom={40}
                                height={100}
                                onChange={onChange(setInputContent)}
                                errorMessage={"*등록되지않은 이메일입니다."}
                                value={inputContent}
                                placeholder="대중음악평론가, 선배/동료 아티스트, 이전 클라이언트 등으로 부터 받은 추천사를 입력하세요."
                            />
                            <InputForm
                                marginBottom={8}
                                onChange={onChange(setInputWriter)}
                                errorMessage={"*등록되지않은 이메일입니다."}
                                value={inputWriter}
                                placeholder="추천인 이름"
                            />
                            <InputForm
                                marginBottom={16}
                                onChange={onChange(setInputField)}
                                errorMessage={"*등록되지않은 이메일입니다."}
                                value={inputField}
                                placeholder="소속기관"
                            />
                        </ModalMain>
                        <ModalFooter>
                            <BlankButton
                                height={40}
                                width={96}
                                onClick={() => {
                                    setIsOpen(false);
                                }}
                            >
                                취소
                            </BlankButton>
                            <Button
                                rightAlign={true}
                                height={40}
                                width={96}
                                onClick={() => {
                                    setIsOpen(false);
                                    fetchTestimonial({
                                        writer: inputWriter,
                                        field: inputField,
                                        content: inputContent,
                                    });
                                }}
                            >
                                {"확인"}
                            </Button>
                        </ModalFooter>
                    </>
                )}
            </Modal>
        </>
    );
};
const Text = styled.div`
    ${SpoqaHansSansNeo(16, 20, 700, "#BD03FA")}
    margin-left: 8px;
`;
const PlusImage = styled.img``;
const Container = styled.div`
    cursor: pointer;
    ${makeCenter(0, 1)}
    display: flex;
`;
