import { useSetRecoilState } from "recoil";
import { useAddPhoto } from "src/ts/models/artistProfile/profileBanner";
import { currentModaldataState } from "src/ts/models/modal";
import { makeCenter, SpoqaHansSansNeo } from "src/ts/styles/css";
import styled from "styled-components";
import plus from "../../images/plus.svg";
import { handleFileInput } from "./handleFileInput";

export const AddPhoto = () => {
    const setModal = useSetRecoilState(currentModaldataState);
    const [addPhoto] = useAddPhoto();
    return (
        <>
            <Container>
                <PlusImage src={plus}></PlusImage>
                <Text>
                    사진 추가하기
                    <input
                        style={{ display: "none" }}
                        accept="image/*"
                        type="file"
                        name="file"
                        onChange={e =>
                            handleFileInput(e, setModal, addPhoto, () => {})
                        }
                    />
                </Text>
            </Container>
        </>
    );
};
const Text = styled.label`
    ${SpoqaHansSansNeo(16, 20, 700, "#BD03FA")}
    margin-left: 8px;
    cursor: pointer;
`;
const PlusImage = styled.img``;
const Container = styled.div`
    cursor: pointer;
    ${makeCenter(0, 1)}
    display: flex;
    margin-left: auto;
`;
