import React, { useState } from "react";
import { useHistory } from "react-router";
import { Button } from "src/ts/components/button/button";
import { Modal, ModalFooter, ModalHeader, ModalMain } from "src/ts/components/modal/modal";
import { FontNormal } from "src/ts/styles/css";
import styled from "styled-components";
import { NewBlankButton } from "../../../components/0.Button/newBlankButton";
import {
  BREAKPOINT_LARGE,
  BREAKPOINT_MIDDLE,
  BREAKPOINT_SMALL
} from "../../../constants/constants";
import { DisplayFlex, SpoqaHansSansNeo } from "../../../css/css";
import filledCheck from '../../../images/filledCheck.svg';
import speechBubble from '../../../images/speechBubble2.svg';
import { RequireVeriModal } from "../../artistProfile/1.BannerSection/1.ArtistDataContainer/requireVeriModal";
import { RequireVeriModal2 } from "../../artistProfile/1.BannerSection/1.ArtistDataContainer/requireVeriModal2";
import { RequireVeriModal3 } from "../../artistProfile/1.BannerSection/1.ArtistDataContainer/requireVeriModal3";
export function TextSection({ currentSize }) {
  const history = useHistory()
  const [settingModalOpen, setSettingModalOpen] = useState(false);
  const [settingModalOpen2, setSettingModalOpen2] = useState(false);
  const [settingModalOpen3, setSettingModalOpen3] = useState(false);
  const [isOpen, setIsOpen] = useState(false);
  return (
    <Container>
      <InnerContainer>
        <Flexcol>
          <Text8>
            뮤지션과 연결되는 가장 빠른 길,{" "}
            {currentSize === "MOBILE" && <br />}STAGE8
          </Text8>
          <Text9>
            <Span0>STAGE8</Span0>
            <Span1>
              는 국내 최초의{currentSize === "MOBILE" && <br />}{" "}
              전문 뮤지션/프로듀서 플랫폼입니다.
            </Span1>
            <br></br>
            <Span1>
              최고의 아티스트들과 함께
              {currentSize === "MOBILE" && <br />} 여러분의
              콘텐츠를 완성해 보세요.
            </Span1>
          </Text9>
        </Flexcol>
        <ButtonSection>
          <NewBlankButton
            style={{ marginRight: 22 }}


            onClick={() => {
              const userData = JSON.parse(window.localStorage.getItem('userData'));
              if (window.localStorage.getItem('isLogin')) {
                if (userData.phoneVerified) {
                  history.push(`/stage8/request`);
                  window.scrollTo(0, 0);
                } else {
                  setSettingModalOpen2(true)
                }
              } else {
                setSettingModalOpen(true)
              }
            }}
          >
            문의 / 상담하기
            <img src={speechBubble} style={{ marginLeft: 15, top: -1, position: 'relative' }}></img>
          </NewBlankButton>
          <NewBlankButton onClick={() => {
            if (window.localStorage.getItem('isLogin')) {
              setIsOpen(true)
            } else {
              history.push('/signup/artist')
            }

          }}>
            아티스트 등록
            <img src={filledCheck} style={{ marginLeft: 15, top: -1, position: 'relative' }}></img>
          </NewBlankButton>
        </ButtonSection>
      </InnerContainer>
      <RequireVeriModal
        open={settingModalOpen}
        close={() => setSettingModalOpen(false)} />
      <RequireVeriModal2
        open={settingModalOpen2}
        close={() => setSettingModalOpen2(false)} />
      <RequireVeriModal3
        open={settingModalOpen3}
        close={() => setSettingModalOpen3(false)} />
      <Modal isOpen={isOpen} empty={false}>
        {isOpen && (
          <>
            <ModalHeader>{"아티스트 등록"}</ModalHeader>
            <ModalMain>
              <FontNormal>
                로그인 한 상태에서는 아티스트 등록을 할 수 없습니다.
              </FontNormal>
            </ModalMain>
            <ModalFooter>
              <Button
                rightAlign={true}
                height={40}
                width={96}
                onClick={() => {
                  setIsOpen(false);
                  history.push("/");
                }}
              >
                {"확인"}
              </Button>
            </ModalFooter>
          </>
        )}
      </Modal>
    </Container>
  );
}
const ButtonSection = styled.div`
padding: 20px 0px;
    @media screen and (min-width: ${BREAKPOINT_MIDDLE}px) {
      padding: 0px 0px 20px 0px;
    }
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) and (max-width: ${BREAKPOINT_MIDDLE}px) {
      padding: 16px 0px 16px 0px;
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
      padding: 0px 0px 20px 0px;
    }
    
    width: 100%;
    ${DisplayFlex("center", "")};
`;
const Flexcol = styled.div`
    justify-content: center;
    display: flex;
    flex-direction: column;
    @media screen and (min-width: ${BREAKPOINT_MIDDLE}px) {
      padding: 20px 0px;
    }
    
`;
const Container = styled.div`
    justify-content: center;
    background-color: #01010b;
    align-items: center;
    width: 100%;
    display: flex;
`;
const InnerContainer = styled.div`
    ${DisplayFlex("center", "center")};
    text-align: center;
    flex-direction: column;
    padding: 40px 20px;
`;
const Span0 = styled.span`
    font-weight: 700;
`;
const Span1 = styled.span`
    font-weight: 400;
`;
const Text8 = styled.div`
    @media screen and (min-width: ${BREAKPOINT_LARGE}px) {
        ${SpoqaHansSansNeo(48, 60, 700)}
    }
    @media screen and (min-width: ${BREAKPOINT_MIDDLE}px) and (max-width: ${BREAKPOINT_LARGE}px) {
        ${SpoqaHansSansNeo(34, 40, 700)}
    }
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) and (max-width: ${BREAKPOINT_MIDDLE}px) {
        ${SpoqaHansSansNeo(34, 40, 700)}
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        ${SpoqaHansSansNeo(20, 24, 700)}
    }
`;
const Text9 = styled.div`
    margin-top: 16px;
    @media screen and (min-width: ${BREAKPOINT_LARGE}px) {
        ${SpoqaHansSansNeo(20, 24)};
    }
    @media screen and (min-width: ${BREAKPOINT_MIDDLE}px) and (max-width: ${BREAKPOINT_LARGE}px) {
        ${SpoqaHansSansNeo(20, 24, 700)}
    }
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) and (max-width: ${BREAKPOINT_MIDDLE}px) {
        ${SpoqaHansSansNeo(16, 20, 700)}
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        ${SpoqaHansSansNeo(14, 18)};
    }
`;
