import { useState } from "react";
import { Button } from "src/ts/components/button/button";
import { EditButton } from "src/ts/components/button/editButton";
import { onChange } from "src/ts/lib/lib";
import { usePatchActiveYears } from "src/ts/models/artistProfile/profileBanner";
import { makeCenter } from "src/ts/styles/css";
import styled from "styled-components";
import { BlankButton } from "../../components/button/blankButton";
import { InputForm } from "../../components/input/inputForm";
import {
    Modal,
    ModalFooter,
    ModalHeader,
    ModalMain,
} from "../../components/modal/modal";
import { FontNormal } from "../../styles/css";

export const UpdateActiveYear = () => {
    const [isOpen, setIsOpen] = useState(false);
    const [patch] = usePatchActiveYears();
    const [year1, setYear1] = useState("");
    const [year2, setYear2] = useState("");

    return (
        <>
            <EditButton
                mini={true}
                rightAlign={true}
                onClick={() => setIsOpen(true)}
            />
            <Modal isOpen={isOpen} empty={false}>
                {isOpen && (
                    <>
                        <ModalHeader>{"활동기간 수정"}</ModalHeader>
                        <ModalMain>
                            <FontNormal style={{ marginBottom: 20 }}>
                                입력값이 없을 경우 항목이 보이지 않습니다.
                            </FontNormal>
                            <InputFormRow>
                                <InputForm
                                    onKeyPress={() => {}}
                                    width={200}
                                    onChange={onChange(setYear1)}
                                    errorMessage={"*등록되지않은 이메일입니다."}
                                    value={year1}
                                    placeholder="YYYY"
                                ></InputForm>
                                <Text>~</Text>
                                <InputForm
                                    onKeyPress={() => {}}
                                    width={200}
                                    onChange={onChange(setYear2)}
                                    errorMessage={"*등록되지않은 이메일입니다."}
                                    value={year2}
                                    placeholder="YYYY"
                                ></InputForm>
                            </InputFormRow>
                        </ModalMain>
                        <ModalFooter>
                            <BlankButton
                                height={40}
                                width={96}
                                onClick={() => {
                                    setIsOpen(false);
                                }}
                            >
                                취소
                            </BlankButton>
                            <Button
                                rightAlign={true}
                                height={40}
                                width={96}
                                onClick={() => patch(year1, year2)}
                            >
                                {"확인"}
                            </Button>
                        </ModalFooter>
                    </>
                )}
            </Modal>
        </>
    );
};

const InputFormRow = styled.div`
    display: flex;
`;
const Text = styled.div`
    width: 30px;
    ${makeCenter()}
`;
