import {
    BREAKPOINT_LARGE,
    BREAKPOINT_MIDDLE,
    BREAKPOINT_SMALL,
} from "src/ts/constants/common";
import styled from "styled-components";
import { SpoqaHansSansNeo } from "../../styles/css";
interface IInput {
    onBlur?: () => void;
    onFocus?: () => void;
    name?: string;
    height?: number;
    style?: object;
    type: string;
    textarea?: boolean;
    pass?: boolean;
    placeholder?: string;
    value: string;
    error?: boolean;
    autoComplete?: boolean;
    onKeyPress: React.KeyboardEventHandler<
        HTMLInputElement | HTMLTextAreaElement
    >;
    onChange: React.ChangeEventHandler<HTMLTextAreaElement | HTMLInputElement>;
}
interface IContainer {
    pass?: boolean;
    error?: boolean;
    type?: string;
    height?: number;
}
export function Input({
    height,
    onBlur,
    onFocus,
    style,
    type = "text",
    textarea,
    pass,
    name,
    placeholder,
    value,
    error,
    onKeyPress,
    onChange,
    autoComplete = false,
}: IInput) {
    if (textarea)
        return (
            <Textarea
                height={height}
                name={name}
                onChange={e => onChange(e)}
                onBlur={onBlur}
                onFocus={onFocus}
                onKeyPress={e => onKeyPress(e)}
                style={style}
                pass={pass}
                error={error}
                placeholder={placeholder}
                value={value}
                type="text"
            />
        );
    else
        return (
            <SInput
                autoComplete={!autoComplete ? "new-password" : ""}
                height={height}
                name={name}
                onChange={e => onChange(e)}
                onBlur={onBlur}
                onFocus={onFocus}
                onKeyPress={e => onKeyPress(e)}
                style={style}
                pass={pass}
                error={error}
                placeholder={placeholder}
                value={value}
                type={type}
            />
        );
}
const SInput = styled.input<IContainer>`
    &::-webkit-outer-spin-button {
        -webkit-appearance: none;
        margin: 0;
    }
    &::-webkit-inner-spin-button {
        -webkit-appearance: none;
        margin: 0;
    }

    &:hover {
        outline: none;
        border-color: ${props =>
            props.error ? "#FF4E45" : props.pass ? "#06BE34" : "#BD03FA"};
    }
    &:focus {
        outline: none;
        border-color: ${props => (props.error ? "#FF4E45" : "#BD03FA")};
    }
    width: 100%;
    background: #0e0e10;
    border: 1px solid;
    box-sizing: border-box;
    border-radius: 4px;
    @media screen and (min-width: ${BREAKPOINT_LARGE}px) {
        ${SpoqaHansSansNeo(16, 24, 400)};
        height: 40px;
        padding-left: 20px;
    }
    @media screen and (min-width: ${BREAKPOINT_MIDDLE}px) and (max-width: ${BREAKPOINT_LARGE}px) {
        ${SpoqaHansSansNeo(16, 24, 400)};
        height: 40px;
        padding-left: 20px;
    }
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) and (max-width: ${BREAKPOINT_MIDDLE}px) {
        ${SpoqaHansSansNeo(14, 16, 400)};
        height: 32px;
        padding-left: 20px;
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        ${SpoqaHansSansNeo(14, 16, 400)};
        height: 32px;
        padding-left: 8px;
    }
    &::placeholder {
        @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
            ${SpoqaHansSansNeo(16, 24, 400, "#828282")};
        }
        @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
            ${SpoqaHansSansNeo(14, 16, 400, "#828282", "0em")};
        }
    }

    border-color: ${props =>
        props.error ? "#FF4E45" : props.pass ? "#06BE34" : "#828282"};
`;
const Textarea = styled.textarea<IContainer>`
    ${SpoqaHansSansNeo(16, 24, 400)};

    width: 100%;
    &::placeholder {
        @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
            ${SpoqaHansSansNeo(16, 24, 400, "#828282")};
        }
        @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
            ${SpoqaHansSansNeo(14, 16, 400, "#828282", "0em")};
        }
    }
    &::-webkit-scrollbar {
        display: none;
    }
    resize: none;
    &:hover {
        outline: none;
        border-color: ${props =>
            props.error ? "#FF4E45" : props.pass ? "#06BE34" : "#BD03FA"};
    }
    &:focus {
        outline: none;
        border-color: ${props => (props.error ? "#FF4E45" : "#BD03FA")};
    }
    background: #0e0e10;
    border: 1px solid;
    box-sizing: border-box;
    border-radius: 4px;
    padding-left: 20px;
    border-color: ${props =>
        props.error ? "#FF4E45" : props.pass ? "#06BE34" : "#828282"};
    height: ${props => props.height}px;
`;
