import React, { useEffect, useState } from "react";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import { useRecoilState } from "recoil";
import {
    BREAKPOINT_LARGE,
    BREAKPOINT_MIDDLE,
    BREAKPOINT_SMALL
} from "./constants/constants";
import { ModalManager } from "./ts/lib/modalManager";
import { currentSizeSelector } from "./ts/models";
import { NewArtist } from "./ts/views/newArtist";
import { ArtistProfile } from "./view/artistProfile/0.ArtistProfile/artistProfile";
import { Login } from "./view/login/0.Login/login";
import { Main } from "./view/main/0.Main/main";
import { MyPage } from "./view/myPage/myPage";
import { PortfolioDetail } from "./view/portfolioDetail/portfolioDetail";
import { PwRecovery } from "./view/pwRecovery/pwRecovery";
import { ResetConfirmed } from "./view/pwRecovery/resetConfirmed";
import { ResetPW } from "./view/pwRecovery/resetPW";
import { Recoveryhelp } from "./view/recoveryhelp/recoveryhelp";
import { EstimateDetail } from "./view/request/estimateDetail";
import { ProjectDetail } from "./view/request/projectDetail";
import { RequestDetail } from "./view/request/requestDetail";
import { RequestToStage8 } from "./view/request/requestToStage8";
import { SearchResults } from "./view/searchResults/searchResults";
import { SignUp } from "./view/signUp/0.signUp/signUp";
import { UserTypeSelect } from "./view/userTypeSelect/0.UserTypeSelect/UserTypeSelect";

function App() {
    const culSize = () => {
        if (window.innerWidth >= BREAKPOINT_LARGE) {
            return "LARGE";
        } else if (window.innerWidth >= BREAKPOINT_MIDDLE) {
            return "MIDDLE";
        } else if (window.innerWidth >= BREAKPOINT_SMALL) {
            return "SMALL";
        } else {
            return "MOBILE";
        }
    };
    useEffect(() => {
        setCurrentSize2(culSize());
    }, []);
    const [currentSize, setCurrentSize] = useState(culSize());
    const [currentSize2, setCurrentSize2] = useRecoilState(currentSizeSelector);
    const checkWidth = () => {
        setCurrentSize(culSize());
        setCurrentSize2(culSize())
    };

    useEffect(() => {
        window.addEventListener("resize", checkWidth);
        return () => {
            window.removeEventListener("resize", checkWidth);
        };
    }, [currentSize, currentSize2]);
    return (<>
        <Router>
            <Switch>
                <Route
                    exact
                    path="/"
                    render={() => <Main currentSize={currentSize} />}
                />
                <Route
                    exact
                    path="/usertypeselect"
                    render={() => <UserTypeSelect currentSize={currentSize} />}
                />
                <Route
                    exact
                    path="/signup/client"
                    render={() => <SignUp currentSize={currentSize} />}
                />
                <Route
                    exact
                    path="/signup/artist"
                    render={() => <NewArtist currentSize={currentSize} />}
                />
                <Route
                    exact
                    path="/login"
                    render={() => <Login currentSize={currentSize} />}
                />
                <Route
                    exact
                    path="/pwrecovery"
                    render={() => <PwRecovery currentSize={currentSize} />}
                />
                <Route
                    path="/findpw/reset"
                    render={() => <ResetPW currentSize={currentSize} />}
                />
                <Route
                    exact
                    path="/resetconfirmed"
                    render={() => <ResetConfirmed currentSize={currentSize} />}
                />
                <Route
                    exact
                    path="/recoveryhelp"
                    render={() => <Recoveryhelp currentSize={currentSize} />}
                />
                <Route

                    path="/mypage/:tab"
                    render={() => <MyPage currentSize={currentSize} />}
                />
                <Route
                    exact
                    path="/projectdetail/:projectID"
                    render={() => <ProjectDetail currentSize={currentSize} />}
                />
                <Route
                    exact
                    path="/requestdetail/:projectID"
                    render={() => <RequestDetail currentSize={currentSize} />}
                />
                <Route
                    exact
                    path="/estimatedetail/:projectID/:estimateID/client"
                    render={() => <EstimateDetail currentSize={currentSize} />}
                />
                <Route
                    exact
                    path="/portfoliodetail/:portfolioID"
                    render={() => <PortfolioDetail currentSize={currentSize} />}
                />
                <Route
                    exact
                    path="/searchresults"
                    render={() => <SearchResults currentSize={currentSize} />}
                />
                <Route
                    exact
                    path="/stage8/request"
                    render={() => <RequestToStage8 currentSize={currentSize} />}
                />
                <Route
                    path="/:artistID/"
                    render={() => <ArtistProfile currentSize={currentSize} />}
                />
            </Switch>
        </Router>
        <ModalManager /></>
    );
}

export default App;
