import { CModalData } from "src/ts/models/modal";

export const handleFileInput = (
    e: React.ChangeEvent<HTMLInputElement>,
    setModal: Function,
    fetchImage: any,
    setSelectedFile?: any,
) => {
    var fileReader = new FileReader();
    fileReader.readAsDataURL(e.target.files![0]);
    if (e.target.files![0].size > 1024 * 1024 * 10) {
        const overSize =
            Math.round((e.target.files![0].size / 1024 / 1024) * 100) / 100 +
            "MB";
        setModal(
            new CModalData(
                "용량 초과",
                (
                    <>
                        "최대 업로드 가능한 파일 사이즈는 10 MB입니다." + "\n" +
                        "현재파일 용량: " +{overSize}
                    </>
                ),
                "확인",
                () => {},
                () => {},
                true,
            ),
        );

        return;
    } else {
        fileReader.onload = function (e) {
            if (e.target !== null) setSelectedFile(e.target.result);
        };
        fetchImage(e.target.files?.[0]);
    }
};
