import React, { useState } from "react";
import styled from "styled-components";
import { BREAKPOINT_SMALL } from "../../constants/constants";
import { DisplayFlex } from "../../css/css";

export function RadioButton({ requirement, activate, onClick, style }) {
    const [isHover, SetIsHover] = useState(false);
    return (
        <Container
            activate={activate}
            isHover={isHover}
            requirement={requirement}
            onMouseEnter={() => SetIsHover(true)}
            onMouseLeave={() => SetIsHover(false)}
            onClick={onClick} style={style}>
            <InnerContainer isHover={isHover}/>
            <InnerCircle activate={activate} isHover={isHover} />
        </Container>
    );
}

const InnerCircle = styled.div`
    width: 12px;
    height: 12px;
    background-color: ${props => props.activate ? '#BD03FA' : props => props.isHover ? '#BD03FA' : ''};
    border-radius: 50em;
    transition: all .3s;
`
const InnerContainer = styled.div`
    border-radius: 50em;
    background-color: ${props=>props.isHover && 'rgba(202, 53, 251, 0.24)'};
    position: absolute;
    transition: all .9s;
`
const Container = styled.div`
    ${DisplayFlex('center', 'center')}
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) { 
        width: 24px;
        height: 24px;
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        width: 20px;
        height: 20px;
    }
    border: 2px solid;
    border-color: ${props => props.activate ? '#BD03FA' : props => props.isHover ? '#BD03FA' : props => props.requirement ? '#FF453A' : '#BEBEBE'};
    border-radius: 50em;
    cursor: pointer;
    transition: all .6s;
`;