import React from 'react';
import styled, { css } from 'styled-components';

export const Modal4 = function ({ open, children, style }) {
    return (
        <BG open={open} id="modalBG">
            <Move style={style} open={open}>{children}</Move>
        </BG>
    )
}
const Move = styled.div`
    width: 544px;
    border-radius: 8px;
    background: #0E0E10;
    transition: all .3s;
    position: relative;
    top: ${props => props.open ? '0px' : '-50px'}
`
const BG_CLOSE = css`
    position: fixed;
    display:flex;
    opacity:0;
    z-index: -1;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
`
const BG_OPEN = css`
    position: fixed;
    display:flex;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 9999;
    opacity:1;
    background-color: rgba(0, 0, 0, 0.6);
`
const BG = styled.div`
    ${props => props.open ? BG_OPEN : BG_CLOSE}
    align-items: center;
    justify-content:center;
    transition: all .3s;
`;