import React from "react";
import styled from "styled-components";
import { DisplayFlex, SpoqaHansSansNeo } from "../../../../../css/css";
import x from '../../../../../images/x.svg'

export function MobileNoti({ mobileNoti, gotoProject, setMobileNoti, notifications, gotoEstimate, isOpen, sibal, setCheckNoti }) {
  return (
    <Container mobileNoti={mobileNoti}>
      <Row1>
        <Text111>알림</Text111>
        <XButton src={x} onClick={() => { setMobileNoti(false) }}></XButton>
      </Row1>
      {((notifications.length > 0)) ? (
        <Inner>
          {notifications.map((data, i) => {
            if (data.message === "new estimate from artist") {
              return (
                <NotiOne
                  sibal={sibal} key={i} last={i === (notifications.length - 1)} isOpen={isOpen}>
                  <Name
                    onClick={() => {
                      setCheckNoti(data._id);
                      window.location.replace(`/${data.nameForURI}/`);
                      window.scrollTo(0, 0);
                    }}>{data.artistName}
                  </Name>
                  <Text1>의 <Name onClick={() => gotoEstimate(data._id, data.projectID, data.estimateID)}>견적서</Name>가 도착했습니다. </Text1>
                  <CreatedAt>{" " + data.createdAt}</CreatedAt>
                </NotiOne>
              )
            } else if (data.message === "new update") {
              return (
                <NotiOne
                  sibal={sibal} key={i} last={i === (notifications.length - 1)} isOpen={isOpen}>
                  <Name onClick={() => gotoEstimate(data._id, data.projectID, data.estimateID)}>{'"' + data.projectName + '"'}</Name><Text1> 이 업데이트 되었습니다. </Text1>
                  <CreatedAt>{" " + data.createdAt}</CreatedAt>
                </NotiOne>
              )
            } else if (data.message === "progress updated") {
              return (
                <NotiOne
                  sibal={sibal} key={i} last={i === (notifications.length - 1)} isOpen={isOpen}>
                  <Name onClick={() => gotoProject(data._id, data.projectID)}>{'"' + data.projectName + '"'}</Name><Text1> 이 업데이트 되었습니다. </Text1>
                  <CreatedAt>{" " + data.createdAt}</CreatedAt>
                </NotiOne>
              )
            } else if (data.message === "progress updated/toMyProject") {
              return (
                <NotiOne
                  sibal={sibal} key={i} last={i === (notifications.length - 1)} isOpen={isOpen}>
                  <Name onClick={() => gotoProject(data._id, data.projectID)}>{'"' + data.projectName + '"'}</Name><Text1> 이 업데이트 되었습니다. </Text1>
                  <CreatedAt>{" " + data.createdAt}</CreatedAt>
                </NotiOne>
              )
            }
          })
          }</Inner>
      ) : <Text1010 isOpen={isOpen}>받은 알림이 없습니다.</Text1010>}
    </Container>
  );
}
const Text1010 = styled.div`
  transition: all .3s;
  opacity: ${props => props.isOpen ? 1 : 0};
  ${SpoqaHansSansNeo(14, 16, 400, 'rgba(255, 255, 255, 0.87)')};
`
const XButton = styled.img`
  cursor: pointer;
  position: absolute;
  right:20px;
`
const Row1 = styled.div`
  padding-top: 20px;
  padding-bottom: 20px;
  ${DisplayFlex('center', '')};
`
const Text1 = styled.div`
  ${SpoqaHansSansNeo(14, 16, 700)};
`
const Text111 = styled.div`
  ${SpoqaHansSansNeo(20, 24, 700)};
`
const Container = styled.div`
  padding: 0px 20px;
  opacity: ${props => props.mobileNoti ? 1 : 0};
  visibility: ${props => !props.mobileNoti && 'hidden'};
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 9998;
  background: #01010B;
  transition: all .3s;
`

const CreatedAt = styled.span`
  position: relative;
  margin-left: 4px;
  top: 1px;
  ${SpoqaHansSansNeo(14, 16, 400, '#828282')};
`
const Inner = styled.div`

`
const Name = styled.span`
  cursor: pointer;
  &:hover { text-decoration: underline; }
  ${SpoqaHansSansNeo(14, 16, 700)};
`
const NotiOne = styled.div`
  display: flex;
  padding: 20px 0px;
  transition: all .3s;
`