import axios from "axios";
import React, { useState } from "react";
import { Route, useParams } from "react-router";
import styled from "styled-components";
import { API_SERVER2, BREAKPOINT_MIDDLE, BREAKPOINT_SMALL } from "../../../constants/constants";
import useAsync from "../../../hook/useAsync";
import { NavBar } from "../../main/1.NavBar/0.NavBar/navBar";
import { Footer } from "../../main/6.Footer/0.Footer/footer";
import { Request } from "../../request/request";
import { ArtistPortfolio } from "../2.Portfolio/artistPortfolio";
import { About } from "../about";

export function ArtistProfile({ currentSize }) {
    const artistID = useParams().artistID;
    const getArtistData = async () => {
        const url = API_SERVER2 + `/artists/by-name-uri/${artistID}`;
        const response = await axios.get(
            url, { withCredentials: true }
        );

        return response.data;
    }
    const [noScroll, setNoScroll] = useState(false);
    const [state, refetch] = useAsync(getArtistData, []);
    const { data } = state;
    let artistData, songs, videos = null;

    if (data) {
        artistData = data.artist;
        songs = artistData.songs;
        videos = artistData.videos;
    }
    return (
        <Container>
            <NavBar currentSize={currentSize} />
            <InnerContainer noScroll={noScroll}>
                {data ?
                    <>
                        <Route
                            exact
                            path="/:artistID/"
                            render={() =>
                                <About
                                    refetch={() => refetch()}
                                    currentSize={currentSize}
                                    data={data} />} />
                        <Route
                            exact
                            path="/:artistID/portfolio"
                            render={() =>
                                <ArtistPortfolio
                                    refetch={() => refetch()}
                                    currentSize={currentSize}
                                    data={data}
                                    songs={songs}
                                    videos={videos} />} />
                        <Route
                            exact
                            path="/:artistID/request"
                            render={() => <Request
                                setNoScroll={setNoScroll}
                                currentSize={currentSize}
                                data={data} />} />
                    </>
                    :
                    <EmptySpace />}
                <Footer currentSize={currentSize} />
            </InnerContainer>
            {/* <SelectionModal isOpen={isOpen} setNoScroll={setNoScroll} setIsOpen={setIsOpen} options={options} title={"서비스 선택"} /> */}
        </Container>
    );
}
const EmptySpace = styled.div`
    height: 900px;
    width: 100%;
`
const InnerContainer = styled.div`
    height: ${props => props.noScroll && '100vh'};
    overflow: ${props => props.noScroll && 'hidden'};
    @media screen and (min-width: ${BREAKPOINT_MIDDLE}px) { 
        padding-top: 80px;
    }
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) and (max-width: ${BREAKPOINT_MIDDLE}px) {
        padding-top: 56px;
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        padding-top: 56px;
    }
`
const Container = styled.div`
    display: flex;
    flex-direction: column;
    background-color:#01010B;
`