import React from "react";
import styled from "styled-components";
import { DisplayFlex, SpoqaHansSansNeo } from "../../css/css";
import chevron from '../../images/chevron.svg'
import { BREAKPOINT_SMALL } from "../../constants/constants";

export function SelectServiceButton({ requirement, pass, toggling }) {
    return (
        <Container>
            <Header requirement={requirement} pass={pass} onClick={toggling}>
                <Text>서비스 선택</Text>
                <IMG isOpen={true} src={chevron} />
            </Header>
        </Container>
    );
}
const Container = styled.div`
    margin-top:16px;
    height:40px;
`
const Header = styled.div`
    z-index:5;
    &:hover{
        outline: none;
        border-color:${props => props.requirement ? '#FF4E45' : '#BD03FA'};
    }
    background-color: #01010B;
    position:relative;
    cursor: pointer;
    border: 1px solid ;
    border-radius: 4px;
    ${DisplayFlex("", "center")}
    border-color: ${props => props.requirement ? '#FF4E45' : props => props.isOpen ? '#BD03FA' : props.pass ? '#06BE34' : '#828282'};
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
        width:626px;
        height:40px;
        padding: 8px 12px;
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        height: 32px;
        width: 335px;
        padding: 4px 12px;
    }
`;
const IMG = styled.img`
    -ms-user-select: none; 
    -moz-user-select: -moz-none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    user-select: none;
    margin-left: auto;
    transition: all ease 0.5s;
    transform: ${props => props.isOpen && 'rotate( -180deg )'};
`
const Text = styled.div`
    z-index:2;
    -ms-user-select: none; 
    -moz-user-select: -moz-none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    user-select: none;
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
        ${SpoqaHansSansNeo(16, 24, 700)}
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        ${SpoqaHansSansNeo(14, 16, 400)}
    }
`