import React from "react";
import styled from "styled-components";
import { DisplayFlex } from "../../css/css";
export function Toggle({ activate, onClick, style, requirement, hover=false }) {
    return (
        <Container
            hover={hover}
            requirement={requirement}
            style={style} 
            activate={activate} 
            onClick={onClick}>
                <Circle activate={activate} ></Circle>
        </Container>
    );
}
const Circle = styled.div`
    height: 20px;
    width: 20px;
    border-radius: 50em;
    background: ${props => props.activate ? '#BD03FA' : '#A0A0A0'};;
    position: relative;
    transition: all .2s;
    left: ${props => props.activate ? '14px' : '0px'};
`
const Container = styled.div`
    ${props => props.activate ? DisplayFlex('','center') :
        DisplayFlex('','center')}
    z-index: 1;
    transition: all .4s;
    cursor: pointer;
    box-sizing: ${props => !props.activate && 'border-box'};
    border-radius: 7px;
    background: ${props => props.activate ? '#D0B1FF' : '#4A4A4A'};
    height: 14px;
    width: 34px;
    margin-right:8px;
    -ms-user-select: none; 
    -moz-user-select: -moz-none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    user-select: none;
`