import React, { useState } from "react";
import styled from "styled-components";
import chevronRight from '../../../images/chevron-right.svg'
import chevronLeft from '../../../images/chevron-left.svg'
import pause3 from '../../../images/pause3.svg'
import play2 from '../../../images/play2.svg'
import TimeProgressBar from "./timeProgressBar";
import { useInterval } from "../../../hook/useInterval";
import { DisplayFlex, SpoqaHansSansNeo } from "../../../css/css";
import { BREAKPOINT_LARGE, BREAKPOINT_MIDDLE, BREAKPOINT_SMALL } from "../../../constants/constants";

export default function SlideNavBar({ currentSize, sliderRef, totalSlideNum, currentSlide }) {
    const culCurrentSlide = () => {
        if (currentSlide < 10)
            return "0" + currentSlide
        else
            return currentSlide
    }
    const culCurrentSlide2 = () => {
        if (totalSlideNum < 10)
            return "0" + totalSlideNum
        else
            return totalSlideNum
    }
    
    const [isPlay, setIsPlay] = useState(true);
    const onNext = () => { sliderRef.slickNext(); }
    const onPrev = () => { sliderRef.slickPrev(); }
    const onGoTo = () => { sliderRef.slickGoTo(currentSlide); }
    const [currentTimeMs, setCurrentTimeMs] = useState(0);

    useInterval(() => {
        if (currentTimeMs < 3000) {
            setCurrentTimeMs(currentTimeMs + 10);
        }
        else {
            onGoTo()
            setCurrentTimeMs(0);
        }
    }, isPlay ? 10 : null);
    
    return (
        <Container>
            <Index>
                <Numerator>{culCurrentSlide()}</Numerator>
                <Denominator>{'|'}</Denominator>
                <Denominator>{culCurrentSlide2()}</Denominator>
            </Index>
            {(currentSize !== 'MOBILE') && <TimeProgressBar width={currentTimeMs} />}
            {(currentSize !== 'MOBILE') && <>
                <RightBox>
                    <IMG src={chevronLeft} onClick={() => { onPrev(); setCurrentTimeMs(0); }} />
                    {isPlay ?
                        (
                            <IMG onClick={() => {
                                setIsPlay(!isPlay);
                            }}
                                src={pause3} />)
                        :
                        (
                            <PlayButtonContainer >
                                <PlayButton onClick={() => {
                                    setIsPlay(!isPlay);
                                }}
                                    src={play2} /></PlayButtonContainer>)}
                    <IMG src={chevronRight} style={{ marginLeft: `8px` }}
                        onClick={() => { onNext(); setCurrentTimeMs(0); }} />
                </RightBox></>}
        </Container>
    );
}
const PlayButtonContainer = styled.div`
    margin-left: 8px;
    width: 32px;
    height: 32px;
    ${DisplayFlex('center', 'center')};
`
const RightBox = styled.div`
    display: flex;
`
const PlayButton = styled.img`
    cursor: pointer;
    @media screen and (min-width: ${BREAKPOINT_MIDDLE}px) {
        width: 16px;
        height: 16px;
    }
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) and (max-width: ${BREAKPOINT_MIDDLE}px) {
        width: 16px;
        height: 16px;
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        width: 16px;
        height: 16px;
    }
`
const IMG = styled.img`
    cursor: pointer;
    @media screen and (min-width: ${BREAKPOINT_MIDDLE}px) {
        width: 32px;
        height: 32px;
        margin-left: 8px;
    }
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) and (max-width: ${BREAKPOINT_MIDDLE}px) {
        width: 32px;
        height: 32px;
        margin-left: 8px;
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        width: 32px;
        height: 32px;
        margin-left: 8px;
    }
`
const Container = styled.div`
    position: absolute;
    @media screen and (min-width: ${BREAKPOINT_LARGE}px) {
        bottom: 60px;
        right: 142px;
    }
    @media screen and (min-width: ${BREAKPOINT_MIDDLE}px) and (max-width: ${BREAKPOINT_LARGE}px) {
        bottom: 32px;
        right: 26px;
    }
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) and (max-width: ${BREAKPOINT_MIDDLE}px) {
        bottom: 24px;
        right: 28px;
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        bottom: 20px;
        right: 20px;
    }
    
    ${DisplayFlex('','center')};
    @media screen and (min-width: ${BREAKPOINT_MIDDLE}px) {
        height: 32px;
    }
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) and (max-width: ${BREAKPOINT_MIDDLE}px) {
        height: 32px;
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        height: 18px;
    }
`
const Index = styled.div`
    display: flex;
    align-items: center;
`;
const Denominator = styled.span`
    margin-left:16px;
    @media screen and (min-width: ${BREAKPOINT_MIDDLE}px) {
        ${SpoqaHansSansNeo(20,24,700,'#828282')}
    }
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) and (max-width: ${BREAKPOINT_MIDDLE}px) {
        ${SpoqaHansSansNeo(20,24,700,'#828282')}
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        ${SpoqaHansSansNeo(14,18,700,'#828282')}
    }
`;
const Numerator = styled.span`
    @media screen and (min-width: ${BREAKPOINT_MIDDLE}px) {
        ${SpoqaHansSansNeo(20,24,700)}
    }
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) and (max-width: ${BREAKPOINT_MIDDLE}px) {
        ${SpoqaHansSansNeo(20,24,700)}
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        ${SpoqaHansSansNeo(14,18,700)}
    }
`;
