import React, { useEffect, useState } from "react";
import { useHistory, useParams } from "react-router";
import { useSetRecoilState } from "recoil";
import { artistProfileStateTrigger } from "src/ts/models/artistProfile/about";
import styled from "styled-components";
import { TextButton } from "../../components/0.Button/textButton";
import {
    BREAKPOINT_LARGE,
    BREAKPOINT_MIDDLE,
    BREAKPOINT_SMALL
} from "../../constants/constants";
import { DisplayFlex, SpoqaHansSansNeo } from "../../css/css";
import { SortMyArtists } from "./sortMyArtists";

export function UpperSection({ setSort, currentSize }) {
    const setter = useSetRecoilState(artistProfileStateTrigger);
    const converter = text => {
        if (text === "myartists") {
            return "MY ARTISTS";
        } else if (text === "myprojects") {
            return "MY PROJECTS";
        } else if (text === "myprofile") {
            return "MY PROFILE";
        }
        else if (text === "settings") {
            return "SETTINGS";
        }
    };
    const history = useHistory();
    const tab = useParams().tab;

    const [tabActive, setTabActive] = useState(converter(tab));
    useEffect(() => {
        setTabActive(converter(tab));
    });
    const changeActive = name => {
        setTabActive(name);
    };

    return (
        <Container>
            <InnerContainer>
                <Title>My Page</Title>
                <TabRow>
                    <TextButton
                        onClick={() => {
                            changeActive("MY ARTISTS");
                            history.push("/mypage/myartists");
                        }}
                        activate={tabActive}
                        text="MY ARTISTS"
                        style={{
                            marginRight: currentSize !== "MOBILE" ? 32 : 12,
                        }}
                    />
                    <TextButton
                        onClick={() => {
                            changeActive("MY PROJECTS");
                            history.push("/mypage/myprojects");
                        }}
                        activate={tabActive}
                        style={{
                            marginRight: currentSize !== "MOBILE" ? 32 : 12,
                        }}
                        text="MY PROJECTS"
                    />
                    {
                        (JSON.parse(window.localStorage.getItem('userData')).userType === "artist") && (<TextButton
                            onClick={() => {
                                setter(Date.now())
                                changeActive("MY PROFILE");
                                history.push("/mypage/myprofile");
                            }}
                            activate={tabActive}
                            style={{
                                marginRight: currentSize !== "MOBILE" ? 32 : 12,
                            }}
                            text="MY PROFILE"
                        />)
                    }

                    <TextButton
                        onClick={() => {
                            changeActive("SETTINGS");
                            history.push("/mypage/settings");
                        }}
                        activate={tabActive}
                        text="SETTINGS"
                    />
                </TabRow>
                {tabActive === "MY ARTISTS" && (
                    <SortMyArtists setSort={setSort} />
                )}
            </InnerContainer>
        </Container>
    );
}
const InnerContainer = styled.div`
    @media screen and (min-width: ${BREAKPOINT_LARGE}px) {
        width: 1196px;
        padding: 80px 20px 40px 20px;
    }
    @media screen and (min-width: ${BREAKPOINT_MIDDLE}px) and (max-width: ${BREAKPOINT_LARGE}px) {
        width: 1140px;
        padding: 80px 20px 40px 20px;
    }
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) and (max-width: ${BREAKPOINT_MIDDLE}px) {
        width: 100%;
        padding: 80px 20px 40px 20px;
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        width: 375px;
        padding: 20px;
    }
`;
const Container = styled.div`
    ${DisplayFlex("center", "")}
`;
const TabRow = styled.div`
    display: flex;
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
        height: 40px;
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        height: 28px;
    }
`;
const Title = styled.div`
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
        margin-bottom: 32px;
        ${SpoqaHansSansNeo(48, 88, 700, "#F7F7F7")};
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        margin-bottom: 20px;
        ${SpoqaHansSansNeo(24, 28, 700, "#F7F7F7")};
    }
`;
