import axios, { AxiosRequestConfig } from "axios";
import { useCallback } from "react";
import { useSetRecoilState } from "recoil";
import { API_SERVER2 } from "src/ts/constants";
import { getNameForURIFromURI } from "src/ts/lib/lib";
import { TestimonialTriggerState } from "./about";
interface IPatchTestimonialData {
    content: string;
    writer: string;
    field: string;
}
export function usePatchTestimonial() {
    const setTestimonialTrigger = useSetRecoilState(TestimonialTriggerState);
    const nameForURI = getNameForURIFromURI();
    const update = useCallback((data: IPatchTestimonialData) => {
        if (data === null) return;

        const options: AxiosRequestConfig = {
            method: "PATCH",
            data: data,
            url: API_SERVER2 + `/artists/${nameForURI}/testimonials/add`,
            withCredentials: true,
        };
        axios(options).then(() => setTestimonialTrigger(Date.now()));
    }, []);

    return [update];
}
export function useDeleteTestimonial() {
    const setTestimonialTrigger = useSetRecoilState(TestimonialTriggerState);
    const nameForURI = getNameForURIFromURI();
    const update = useCallback((testimonialID: string) => {
        if (testimonialID === null) return;

        const options: AxiosRequestConfig = {
            method: "PATCH",
            url:
                API_SERVER2 +
                `/artists/${nameForURI}/testimonials/${testimonialID}/delete`,
            withCredentials: true,
        };
        axios(options).then(() => setTestimonialTrigger(Date.now()));
    }, []);

    return [update];
}
