import React, { useState } from "react";
import { Login } from "../1.Login/logIn";
import SignUp from "../2.SignUp/signUp";
import styled from "styled-components";
import { Noti } from '../3.Noti/noti'
import { Profile } from '../4.Profile/profile'

export function RightBox({ smallProfileURI }) {
  const isLoing = window.localStorage.getItem('isLogin');
  const [isOpen, setIsOpen] = useState(false);
  const [isOpen2, setIsOpen2] = useState(false);
  const toggle1 = (value) => {
    setIsOpen(value)
    if (value === true)
      setIsOpen2(false)
  }
  const toggle2 = (value) => {
    setIsOpen2(value)
    if (value === true)
      setIsOpen(false)
  }
  return (
    <Container >
      {isLoing ?
        <>
          <Noti isOpen={isOpen} setIsOpen={toggle1} />
          <Profile smallProfileURI={smallProfileURI} isOpen={isOpen2} setIsOpen={toggle2} />
        </> :
        <>
          <Login>LOG IN</Login>
          <SignUp>SIGN UP</SignUp>
        </>
      }

    </Container>
  );
}

const Container = styled.div`
  display: flex;
  align-items: center;
  margin-left: auto;
`