import React, { useState } from "react";
import styled from "styled-components";
import { BREAKPOINT_LARGE, BREAKPOINT_MIDDLE, BREAKPOINT_SMALL } from "../../../../constants/constants";
import { SpoqaHansSansNeo } from "../../../../css/css";
import { VideoColumn } from "./videoColumn";
import { YoutubeModal } from "./youtubeModal";

export function Video({ videos, currentSize }) {
    const [modalOpen, setModalOpen] = useState(false);
    const [videoID, setVideoID] = useState(null);
    const openModal = (videoID) => {
        setVideoID(videoID)
        setModalOpen(true);
    }
    const closeModal = () => {
        setModalOpen(false);
    }

    return (
        <Container>
            <InnerContainer>
                <Text>동영상</Text>
                <VideoList>
                    {videos.map((video, i) => {
                        return (<VideoColumn
                            openModal={openModal}
                            key={i}
                            video={video}
                            i={i} />);
                    })}
                </VideoList>
                <YoutubeModal currentSize={currentSize} videoID={videoID} open={modalOpen} close={closeModal} />
            </InnerContainer>
        </Container>
    );
}
const VideoList = styled.div`
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) { 
        flex-wrap: wrap;
        display:flex;
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        -ms-overflow-style: none;
        overflow-x: scroll;
        -ms-scroll-chaining: none;
        -ms-scroll-snap-type: mandatory;
        scroll-snap-type: mandatory;
        scrollbar-width: none; /* Firefox */
        &::-webkit-scrollbar {
            display: none; /* Chrome, Safari, Opera*/
        }
        -ms-scroll-snap-points-x: snapInterval(0%, 10%);
        display:flex;
    }
    margin-top:24px;
`
const Text = styled.div`
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) { 
        ${SpoqaHansSansNeo(34, 40, 700)}
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        ${SpoqaHansSansNeo(20, 24, 700)}
        margin-bottom: 20px;
        margin-left: 20px;
    }
`
const Container = styled.div`
width: 100%;
    
`
const InnerContainer = styled.div`
    @media screen and (min-width: ${BREAKPOINT_LARGE}px) {
        width: 1196px;
        padding: 40px 20px;
    }
    @media screen and (min-width: ${BREAKPOINT_MIDDLE}px) and (max-width: ${BREAKPOINT_LARGE}px) {
        width: 1140px;
        padding: 40px 20px;
    }
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) and (max-width: ${BREAKPOINT_MIDDLE}px){
        width: 754px;
        padding: 40px 20px;
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        width: 100%;
        padding: 20px 0px;
    }
`