import React, { useEffect, useState } from "react";
import styled from "styled-components";
import { BREAKPOINT_SMALL, CATEGORIES, GENRES } from "../../../../../constants/constants";
import { Sort } from "../1.Sort/sort";
import { Filter } from "../2.Filter/filter";
import { ArtistType } from '../3.ArtistType/artistType';
import { Category } from '../4.Category/category';
import { Genre } from "../5.Genre/genre";
import { Keyword } from "./keyword";
export const cloneObj = obj => JSON.parse(JSON.stringify(obj))

export function SearchOption({ setSort, setArtistType, setCategory, deleteCategory, addGenre, deleteGenre }) {
  const [containerIsOpen, setContainerOpen] = useState(false);
  const [container2IsOpen, setContainer2Open] = useState(false);
  const [options, setOptions] = useState(cloneObj(CATEGORIES));
  const [options2, setOptions2] = useState(cloneObj(GENRES));
  const [keywords, setKeywords] = useState([]);
  const deleteCategoryAndGenre = (query) => {
    deleteCategory(query)
    deleteGenre(query)
  }
  useEffect(() => {
    return () => {
      setOptions(cloneObj(CATEGORIES))
      setOptions2(cloneObj(GENRES))
      setKeywords([])
    }
  }, [])

  const onCreate = (newKeyword) => {
    let duplication = false;
    keywords.map(keyword => duplication = (keyword.id === newKeyword.id) ? true : false)
    if (duplication) return;
    newKeyword.activate = true;
    if (newKeyword)
      setKeywords(keywords.concat(newKeyword))
    //아직 keyword set이 안되었기 때문에 +1
    if (keywords.length + 1 > 0) {
      setContainer2Open(true)
    } else {
      setContainer2Open(false)
    }
  }
  const onRemove = (id) => {
    setKeywords(keywords.filter(keyword => keyword.id !== id));
    if (keywords.length - 1 > 0) {
      setContainer2Open(true)
    } else {
      setContainer2Open(false)
    }
  }
  const onToggle = id => {
    let newOptions = [];
    let newOptions2 = [];
    options.map(option => {
      const newContents = option.contents.map(content => (content.id === id) ?
        { ...content, activate: !content.activate } : content)

      newOptions.push({
        header: option.header,
        contents: newContents
      })
    })

    newOptions2 = {
      header: options2.header,
      contents: options2.contents.map(option =>
        option.id === id ? { ...option, activate: !option.activate } : option)
    }
    setOptions(newOptions)
    setOptions2(newOptions2)
    setKeywords(
      keywords.map(keyword => (keyword.id === id) ?
        { ...keyword, activate: !keyword.activate } : keyword)
    )

  };

  const FilterOnClick = () => {
    setContainerOpen(!containerIsOpen)
    if (containerIsOpen) {
      setContainer2Open(false)
    } else {
      if (keywords.length > 1) {
        setContainer2Open(true)
      }
    }

  }
  return (
    <Container>
      <InnerContainer>
        <Sort setSort={setSort} />
        <Filter
          onClick={FilterOnClick}
          containerIsOpen={containerIsOpen} />
      </InnerContainer>
      <HiddenContainer1 containerIsOpen={containerIsOpen}>
        <ArtistType setArtistType={setArtistType} />
        <Category setCategory={setCategory} deleteCategory={deleteCategory} options={options} onCreate={onCreate} onRemove={onRemove} onToggle={onToggle} />
        <Genre addGenre={addGenre} deleteGenre={deleteGenre} options={options2} onCreate={onCreate} onRemove={onRemove} onToggle={onToggle} />
      </HiddenContainer1>
      <HiddenContainer2 containerIsOpen2={container2IsOpen}>
        <HiddenContainer3>
          {keywords.map((keyword, i) => (
            <Keyword key={i} deleteQuery={deleteCategoryAndGenre} keyword={keyword} onRemove={onRemove} onToggle={onToggle} />
          ))}
        </HiddenContainer3>
      </HiddenContainer2>
    </Container>
  );
}

const Container = styled.div`
  
`
const InnerContainer = styled.div`
  display: flex;
  width: 100%;
  @media screen and (min-width: ${BREAKPOINT_SMALL}px) { 
    height: 40px;
  }
  @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
    height: 32px;
  }
`
const HiddenContainer1 = styled.div`
  transition: all 0.4s;
  opacity: ${props => (props.containerIsOpen ? "1" : "0")};

  width: 100%;
  @media screen and (min-width: ${BREAKPOINT_SMALL}px) { 
    display: flex;
    padding-top: ${props => (props.containerIsOpen ? `20px` : "0px")};
    height: ${props => (props.containerIsOpen ? '60px' : "0px")};
  }
  @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
    padding-top: ${props => (props.containerIsOpen ? `8px` : "0px")};
    height: ${props => (props.containerIsOpen ? '120px' : "0px")};
  }
`
const HiddenContainer2 = styled.div`
  transition: all 0.4s;
  opacity: ${props => (props.containerIsOpen2 ? "1" : "0")};
  height: ${props => (props.containerIsOpen2 ? '60px' : "0px")};
  padding-top: ${props => (props.containerIsOpen2 ? `20px` : "0px")};
  width: 100%;
`
const HiddenContainer3 = styled.div`
  transition: all 0.4s;
  display: flex;
  width:fit-content;
  flex-wrap: wrap;
`