import React from "react";
import { Badge } from "../1.Badge/badge";
import styled, { css } from "styled-components";
import { useHistory } from "react-router";
import { SpoqaHansSansNeo } from "../../../../../../css/css";
import { BREAKPOINT_LARGE, BREAKPOINT_MIDDLE, BREAKPOINT_SMALL } from "../../../../../../constants/constants";

export function PortfolioCard({ data, i }) {
  const history = useHistory();
    return (
      <Container i={i}>
        <IMGContainer>
          <IMG
            src={data.portfolioImageURI}
            onClick={() => {
              history.push(`/portfoliodetail/${data._id}`);
              window.scrollTo(0, 0);
            }} />
        </IMGContainer>
        <DataSection>
          <FistLow>
            <Text>{data.name}</Text>
          </FistLow>
          <Text2 onClick={()=>{
            history.push(`${data.participants[0].nameForURI}/`); 
            window.scrollTo(0,0);
          }}>{data.participants[0].nameKO}</Text2>
          <Text3>{data.category[0]}</Text3>
          <Badge />
        </DataSection>
      </Container>
    );
}
const Container = styled.div`
  cursor: pointer;
  @media screen and (min-width: ${BREAKPOINT_LARGE}px) {
    width: 274px;
    height: 390px;
    ${props => {
      if (props.i % 4 === 3) {
        return css`
              margin: 20px 0px;
            `;
      } else {
        return css`
              margin: 20px 20px 20px 0px;
            `;
      }
    }}
  }
  @media screen and (min-width: ${BREAKPOINT_MIDDLE}px) and (max-width: ${BREAKPOINT_LARGE}px) {
    width: 260px;
    height: 376px;
    ${props => {
      if (props.i % 4 === 3) {
        return css`
              margin: 20px 0px;
            `;
      } else {
        return css`
              margin: 20px 20px 20px 0px;
            `;
      }
    }}
  }
  @media screen and (min-width: ${BREAKPOINT_SMALL}px) and (max-width: ${BREAKPOINT_MIDDLE}px){
    width: 224px;
    height: 364px;
    ${props => {
      if (props.i % 3 === 2) {
        return css`
              margin: 20px 0px 20px 0px;
            `;
      } else {
        return css`
              margin: 20px 20px 20px 0px;
            `;
      }
    }}
  }
  @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
    width: 156px;
    height: 242px;
    ${props => {
      if (props.i % 2 === 1) {
        return css`
              margin: 12px 0px 12px 0px;
            `;
      } else {
        return css`
              margin: 12px 20px 12px 0px;
            `;
      }
    }}
    }
  position: relative;
`

const IMG = styled.img`
z-index: 9999;
transition: all .3s;
&:hover{
  transform: scale(1.2);
}
@media screen and (min-width: ${BREAKPOINT_LARGE}px) {
  width: 274px;
  height: 274px;
}
@media screen and (min-width: ${BREAKPOINT_MIDDLE}px) and (max-width: ${BREAKPOINT_LARGE}px) {
  width: 260px;
  height: 260px;
}
@media screen and (min-width: ${BREAKPOINT_SMALL}px) and (max-width: ${BREAKPOINT_MIDDLE}px) {
  width: 224px;
  height: 224px;
}
@media screen and (max-width: ${BREAKPOINT_SMALL}px) {
  width: 156px;
  height: 156px;
}
`
const FistLow = styled.div`
    align-items: center;
    display: flex;
    width: 100%;
    position: relative;
  `
const Text3 = styled.div`
  margin-top: 4px;
  @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
    ${SpoqaHansSansNeo(16,20,400,'#D0B1FF')}
  }
  @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
    ${SpoqaHansSansNeo(12,14,400,'#D0B1FF')}
  }
`
const Text2 = styled.div`
  &:hover { text-decoration: underline;}
  width: fit-content;
  cursor: pointer;
  margin-top: 4px;
  @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
    ${SpoqaHansSansNeo(16,20)}
  }
  @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
    ${SpoqaHansSansNeo(14,18)}
  }
`
const DataSection = styled.div`
  width: 100%;
  @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
    margin-top: 16px;
  }
  @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
    margin-top: 8px;
  }
`
const IMGContainer = styled.div`
  overflow: hidden;
  @media screen and (min-width: ${BREAKPOINT_LARGE}px) {
    width: 274px;
    height: 274px;
  }
  @media screen and (min-width: ${BREAKPOINT_MIDDLE}px) and (max-width: ${BREAKPOINT_LARGE}px) {
    width: 260px;
    height: 260px;
  }
  @media screen and (min-width: ${BREAKPOINT_SMALL}px) and (max-width: ${BREAKPOINT_MIDDLE}px){
    width: 224px;
    height: 224px;
  }
  @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
    width: 156px;
    height: 156px;
  }
`
const Text = styled.div`
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
    ${SpoqaHansSansNeo(20,24,700)}
  }
  @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
    ${SpoqaHansSansNeo(14,18,700)}
  }
`