import React from "react";
import styled from "styled-components";

export function Chevron({isLeft, activate, currentPage, setPage}) {
  return (
    <Container 
      style={{cursor:activate ? 'pointer' : null}} 
      onClick={() => {if(activate)setPage(isLeft ? currentPage - 1 : currentPage + 1)}}>
      <IMG 
        activate={activate} 
        isLeft={isLeft} 
        src={activate ? activated : disabled} />
    </Container>
  );
}
const disabled = "https://anima-uploads.s3.amazonaws.com/projects/605994b38d323a9859a00b7f/releases/605996135433a9ddabee4916/img/vector-254@2x.svg";
const activated = "https://anima-uploads.s3.amazonaws.com/projects/605994b38d323a9859a00b7f/releases/605996135433a9ddabee4916/img/vector-253@2x.svg";
const Container = styled.div`
    -ms-user-select: none; 
    -moz-user-select: -moz-none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    user-select: none;
  align-items: center;
  display: flex;
  height: 24px;
  width: 24px;
  padding: 0 8px;
`
const IMG = styled.img`
  height: 15px;
  transform: ${ props => props.isLeft ? props.activate ? null : 'rotate(180.00deg)' : props.activate ? 'rotate(180.00deg)': null};
  width: 8px;
`
