import React, { useState } from "react";
import styled from "styled-components";
import { DropDownListRow } from "../../../../../components/2.DropDown/dropDownRow";
import { BREAKPOINT_SMALL } from "../../../../../constants/constants";
import { DisplayFlex, SpoqaHansSansNeo } from "../../../../../css/css";
import chevron from '../../../../../images/chevron.svg'

export function Genre({ options, onCreate, onRemove, onToggle, addGenre, deleteGenre }) {
    const [isOpen, setIsOpen] = useState(false);
    const toggling = () => setIsOpen(!isOpen);
    return (
        <Container>
            <Header onClick={toggling} isOpen={isOpen}>
                <Text>{options.header}</Text>
                <IMG isOpen={isOpen} src={chevron} />
            </Header>
            <DropDownList isOpen={isOpen} >
                <Inner>
                    {options.contents.map((option, i) => (
                        <DropDownListRow
                            key={i}
                            isOpen={isOpen}
                            content={option}
                            onCreate={onCreate}
                            onRemove={onRemove}
                            onToggle={onToggle}
                            addQuery={addGenre}
                            deleteQuery={deleteGenre}
                        />
                    ))}
                </Inner>
            </DropDownList>
        </Container>
    );
}
const Container = styled.div`
    margin-right: 20px;
`
const Header = styled.div`
    z-index:3;
    &:hover {
        opacity: 1;
        border-color: #BD03FA;
    }
    transition: all 0.2s;
    background-color: #01010B;
    position:relative;
    cursor: pointer;
    border: 1px solid ;
    border-radius: 4px;
    ${DisplayFlex("", "center")}
    border-color: ${props => props.isOpen ? '#BD03FA' : 'var(--gravel)'};
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
        height: 40px;
        width: 248px;
        padding: 8px 12px;
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        height: 32px;
        width: 333px;
        padding: 4px 12px;
    }
`;
const IMG = styled.img`
    -ms-user-select: none; 
    -moz-user-select: -moz-none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    user-select: none;
    margin-left: auto;
    transition: all ease 0.5s;
    transform: ${props => props.isOpen && 'rotate( -180deg )'};
`
const Inner = styled.div`
    z-index:2;
    overflow-y: auto;
    max-height: 384px;
    &::-webkit-scrollbar {
        display: none; 
    }
    -ms-overflow-style: none;
    scrollbar-width: none;
`
const DropDownList = styled.div`
    max-height: 384px;
    border-radius: 4px;
    position: relative;
    visibility: ${props => (!props.isOpen && "hidden")};
    opacity: ${props => (props.isOpen ? "1" : "0")};
    top: ${props => (props.isOpen ? `8px` : `-40px`)};
    background-color:#0E0E10;
    padding: 8px 0;
    z-index:2;
    transition: all 0.4s;
`;
const Text = styled.div`
    z-index:2;
    -ms-user-select: none; 
    -moz-user-select: -moz-none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    user-select: none;
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
        ${SpoqaHansSansNeo(16, 24, 700)}
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        ${SpoqaHansSansNeo(14, 16, 400)}
    }
`