import axios from "axios";
import { useState } from "react";
import { Button } from "src/ts/components/button/button";
import { EditButton } from "src/ts/components/button/editButton";
import { API_SERVER2 } from "src/ts/constants";
import { onChange } from "src/ts/lib/lib";
import { SpoqaHansSansNeo } from "src/ts/styles/css";
import styled from "styled-components";
import { BlankButton } from "../../components/button/blankButton";
import { InputForm } from "../../components/input/inputForm";
import { Modal } from "../../components/modal/modal";
import { useFetchNameEN } from "../../models/artistProfile/profileBanner";

export const UpdateNameEN = ({ existedData }) => {
    const [isOpen, setIsOpen] = useState(false);
    const [fetchNameEN] = useFetchNameEN();
    const [input, setInput] = useState(existedData);
    const [nameENData, setNameENData] = useState({
        pass: false,
        message: "",
        requirement: false,
    });
    const validateNameEN = e => {
        const value = e.target.value;
        const duplicate = async () => {
            let form = new FormData();
            form.append("nameEN", value);
            const url = API_SERVER2 + `/auth/check-nameen-duplicate`;

            const response = await axios.post(url, form, {
                withCredentials: true,
            });
            return response;
        };
        duplicate().then(function (data) {
            const duplicated = data.data.data.duplicated;

            if (duplicated) {
                setNameENData({
                    pass: false,
                    message: "이미 사용중인 영어활동명입니다.",
                    requirement: true,
                });
            } else {
                setNameENData({
                    pass: true,
                    message: "사용가능한 활동명입니다.",
                    requirement: false,
                });
            }
        });
    };
    return (
        <>
            <EditButton leftMargin={10} onClick={() => setIsOpen(true)} />
            <Modal isOpen={isOpen} empty={false}>
                {isOpen && (
                    <>
                        <Header>{"영문활동명 수정"}</Header>
                        <Main>
                            <InputForm
                                error={nameENData.requirement}
                                errorMessage={nameENData.message}
                                onKeyPress={() => {}}
                                style={{ marginBottom: 48 }}
                                onChange={onChange(setInput, validateNameEN)}
                                value={input}
                                placeholder="mimisisters"
                                header="영문활동명"
                            ></InputForm>
                        </Main>
                        <Footer>
                            <BlankButton
                                height={40}
                                width={96}
                                onClick={() => {
                                    setIsOpen(false);
                                }}
                            >
                                취소
                            </BlankButton>
                            <Button
                                rightAlign={true}
                                height={40}
                                width={96}
                                onClick={() => fetchNameEN(input)}
                            >
                                {"확인"}
                            </Button>
                        </Footer>
                    </>
                )}
            </Modal>
        </>
    );
};

const Header = styled.div`
    position: relative;
    height: 72px;
    padding: 24px;
    ${SpoqaHansSansNeo(20, 24, 700)};
    display: flex;
    align-items: center;
`;

const Main = styled.div`
    ${SpoqaHansSansNeo(16, 20, 400)};
    padding: 24px;
    border-top: 1px solid #828282;
    min-height: 150px;
`;

const Footer = styled.div`
    width: 100%;
    margin-top: auto;
    border-top: 1px solid #828282;
    ${SpoqaHansSansNeo(16, 20, 400)};
    padding: 24px;
    display: flex;
`;
