import { useState } from "react";
import { Button } from "src/ts/components/button/button";
import { EditButton } from "src/ts/components/button/editButton";
import { usePatchSlogun } from "src/ts/models/artistProfile/profileBanner";
import { BlankButton } from "../../components/button/blankButton";
import { InputForm } from "../../components/input/inputForm";
import {
    Modal,
    ModalFooter,
    ModalHeader,
    ModalMain,
} from "../../components/modal/modal";

export const UpdateSlogun = ({ existedData }) => {
    const [isOpen, setIsOpen] = useState(false);
    const [fetchSlogun] = usePatchSlogun();
    const [slogunInput, setSlogunInput] = useState(existedData);
    const onChange = (e: React.ChangeEvent<HTMLTextAreaElement>) => {
        setSlogunInput(e.target.value);
    };

    return (
        <>
            <EditButton onClick={() => setIsOpen(true)} />
            <Modal isOpen={isOpen} empty={false}>
                {isOpen && (
                    <>
                        <ModalHeader>{"슬로건 수정"}</ModalHeader>
                        <ModalMain>
                            <InputForm
                                onKeyPress={() => {}}
                                style={{ marginBottom: 48 }}
                                onChange={onChange}
                                errorMessage={"*등록되지않은 이메일입니다."}
                                value={slogunInput}
                                placeholder="본인이 어떤 아티스트인지 1-2줄로 짧고 임팩트있게 소개해주세요. "
                                header="슬로건"
                            ></InputForm>
                        </ModalMain>
                        <ModalFooter>
                            <BlankButton
                                height={40}
                                width={96}
                                onClick={() => {
                                    setIsOpen(false);
                                }}
                            >
                                취소
                            </BlankButton>
                            <Button
                                rightAlign={true}
                                height={40}
                                width={96}
                                onClick={() => fetchSlogun(slogunInput)}
                            >
                                {"확인"}
                            </Button>
                        </ModalFooter>
                    </>
                )}
            </Modal>
        </>
    );
};
