import React, { useReducer } from "react";
import { withRouter } from "react-router";
import styled from "styled-components";
import { Button } from "../../../components/0.Button/button";
import { DisplayFlex, SpoqaHansSansNeo } from "../../../css/css";
import arrow from '../../../images/arrow-right.svg';
import { Header } from "../../signUp/1.Header/header";
import { TypeSelect } from "../1.TypeSelect/0.TypeSelect/typeSelect";
const initialState = {
    userType: '',
    performer: false,
    producer: false,
    requirement1: false,
    requirement2: false
}
function reducer(state, action) {
    switch (action.type) {
        case 'CHOOSE_USERTYPE':
            return {
                ...state,
                userType: action.userType,
                requirement1: false
            };
        case 'TOGGLE_PERFORMER':
            return {
                ...state,
                performer: !state.performer,
                requirement2: false
            };
        case 'TOGGLE_PRODUCER':
            return {
                ...state,
                producer: !state.producer,
                requirement2: false
            };
        case 'TRUE_REQ1':
            return {
                ...state,
                requirement1: true
            };
        case 'TRUE_REQ2':
            return {
                ...state,
                requirement2: true
            };
        default:
            return state;
    }
}

export const SelectUserTypeDispatch = React.createContext(null);
export const UserTypeSelect = withRouter(UserTypeSelectFunction);

function UserTypeSelectFunction({ history }) {
    const [state, dispatch] = useReducer(reducer, initialState);
    const { userType, performer, producer, requirement1, requirement2 } = state;
    return (
        <SelectUserTypeDispatch.Provider value={dispatch}>
            <Container>
                <Header />
                <TypeSelect
                    userType={userType}
                    performer={performer}
                    producer={producer}
                    requirement1={requirement1}
                    requirement2={requirement2} />
                <Row>
                    <Button
                        style={{ height: 40, width: 225, marginLeft: 'auto' }}
                        onClick={() => {
                            if (userType === '') {
                                dispatch({ type: 'TRUE_REQ1' })
                                return;
                            }
                            if (userType === '일반유저') {
                                history.push('/signup/client', {
                                    userType: 'client'
                                })
                            } else {
                                history.push('/signup/artist')
                            }

                        }}>
                        <Text>추가 정보를 알려주세요</Text><img style={{
                            height: 20, width: 20,
                            position: 'relative', top: -1
                        }} src={arrow} />
                    </Button>
                </Row>
            </Container>
        </SelectUserTypeDispatch.Provider>
    );
}
const Text = styled.div`
    ${SpoqaHansSansNeo(16, 20, 700)}
`
const Row = styled.div`
    width: 764px;
`
const Container = styled.div`
    min-height: 946px;
    ${DisplayFlex('', 'center')}
    flex-direction: column;
    background-color:#01010B;
    -ms-user-select: none; 
    -moz-user-select: -moz-none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    user-select: none;
`