import React from "react";
import { Link } from "react-router-dom";
import styled from "styled-components";
import { BREAKPOINT_SMALL } from "../../../../../constants/constants";
import { DisplayFlex, SpoqaHansSansNeo } from "../../../../../css/css";

function SignUp(props) {
  const { children } = props;

  return (
    <Link to={'/usertypeselect'} style={{ textDecoration: 'none' }}>
      <Container>
        <Text>{children}</Text>
      </Container>
    </Link>
  );
}

export default SignUp;

const Container = styled.div`
  transition: all .3s;
  background-color: #AA03E2;
  &:hover{
    background-color: #8402AF;
  }
  border-radius: 50px;
  ${DisplayFlex('center', 'center')};
  @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
    height: 40px;
    width: 120px;
  }
  @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
    height: 26px;
    width: 81px;
  }
`

const Text = styled.h1`
  @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
    ${SpoqaHansSansNeo(16, 16, 700)};
    position:relative;
    /* top: 2px; */
  }
  @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
    ${SpoqaHansSansNeo(14, 18, 700)};
  }
`
