import { makeCenter, SpoqaHansSansNeo } from "src/ts/styles/css";
import styled from "styled-components";
interface IPaginationItem {
    number: number;
    isCurrent: boolean;
    setPage: Function;
}
interface IContainer {
    isCurrent: boolean;
}
interface INumber {
    isCurrent: boolean;
    number: number;
}
export function VPaginationItem({
    number,
    isCurrent,
    setPage,
}: IPaginationItem) {
    return (
        <Container isCurrent={isCurrent} onClick={() => setPage(number)}>
            <Number number={number} isCurrent={isCurrent}>
                {number}
            </Number>
        </Container>
    );
}
const Container = styled.div<IContainer>`
    transition: all 0.3s;
    cursor: ${props => !props.isCurrent && "pointer"};
    ${makeCenter()};
    height: 32px;
    margin: 0px 4px;
    width: 32px;
    background: ${props => props.isCurrent && "#4A4A4A"};
    &:hover {
        background: ${props => !props.isCurrent && "#4A4A4A"};
    }
    border-radius: 50em;
`;
const Number = styled.div<INumber>`
    ${SpoqaHansSansNeo(16, 24, 700, "#F7F7F7")};
    position: relative;
    left: 1px;
    top: 1px;
`;
