import { useEffect, useState } from "react";
import { useRecoilState, useSetRecoilState } from "recoil";
import { Button } from "src/ts/components/button/button";
import emptyImage from "src/ts/images/emptyImage.svg";
import { artistPortFolioProfileState } from "src/ts/models/artistProfile/artistPortFolio";
import { currentModaldataState } from "src/ts/models/modal";
import { makeCenter } from "src/ts/styles/css";
import styled from "styled-components";
import camera from "../../images/camera.svg";
import { artistPortFolioProfileSelector } from "../../models/artistProfile/artistPortFolio";
import { handleFileInput } from "./handleFileInput";
export const ProjectProfile = ({ existedProfile }) => {
    const [artistPortFolioProfile, setArtistPortFolioProfile] = useRecoilState(
        artistPortFolioProfileState,
    );
    const [selectedFile, setSelectedFile] = useState(null);
    const setModal = useSetRecoilState(currentModaldataState);

    const setArtistPortFolioProfile2 = useSetRecoilState<any>(
        artistPortFolioProfileSelector(existedProfile),
    );

    useEffect(() => {
        setArtistPortFolioProfile2(existedProfile);
    }, []);
    return (
        <ProfileContainer>
            <Profile
                src={selectedFile || existedProfile || emptyImage}
            ></Profile>
            <Button
                round
                label={true}
                style={{ position: "absolute", bottom: 40 }}
                width={148}
                height={40}
                onClick={() => {}}
            >
                <input
                    style={{ display: "none" }}
                    accept="image/*"
                    type="file"
                    name="file"
                    onChange={e =>
                        handleFileInput(
                            e,
                            setModal,
                            setArtistPortFolioProfile,
                            setSelectedFile,
                        )
                    }
                />
                <img alt="camera" src={camera} style={{ marginRight: 5 }} />
                업데이트
            </Button>
        </ProfileContainer>
    );
};
const Profile = styled.img`
    width: 372px;
    height: 372px;
`;

const ProfileContainer = styled.div`
    position: relative;
    width: 372px;
    height: 372px;
    ${makeCenter(1, 0)}
    margin-right: 40px;
`;
