import React from "react";
import { Link } from "react-router-dom";
import styled from "styled-components";
import { BREAKPOINT_SMALL } from "../../../../../constants/constants";
import { DisplayFlex, SpoqaHansSansNeo } from "../../../../../css/css";

export function Login(props) {
  const { children } = props;

  return (
    <Container>
      <Link to={'/login'} style={{ textDecoration: 'none' }}>
        <Text>{children}</Text>
      </Link>
    </Container>
  );
}

const Container = styled.div`
  ${DisplayFlex('center', 'center')};
  @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
    height: 40px;
    width: 91px;
  }
  @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
    height: 26px;
    width: 68px;
  }
`

const Text = styled.div`
  &:hover{
    color: #BD03FA;
  }
  transition: all .3s;
  position: relative;
  @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
    ${SpoqaHansSansNeo(16, 24, 700)}
    
  }
  @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
    ${SpoqaHansSansNeo(14, 18, 700)}
  }
`