import React from "react";
import { FontHeader, FontRedStar, SpoqaHansSansNeo } from "src/ts/styles/css";
import styled from "styled-components";
import { Input } from "./input";

interface IInputForm {
    explanation?: string;
    onBlur?: () => void;
    onFocus?: () => void;
    style?: object;
    marginBottom?: number;
    marginRight?: number;
    type?: string;
    textarea?: boolean;
    requirement?: boolean;
    pass?: boolean;
    width?: number | string;
    placeholder?: string;
    value: string;
    error?: boolean;
    onKeyPress?: React.KeyboardEventHandler<
        HTMLInputElement | HTMLTextAreaElement
    >;
    name?: string;
    header?: string;
    onChange: React.ChangeEventHandler<HTMLTextAreaElement | HTMLInputElement>;
    errorMessage?: string;
    height?: number;
    marginTop?: number;
}
export function InputForm({
    onKeyPress = () => {},
    marginRight = 0,
    style,
    marginTop = 0,
    textarea,
    requirement = false,
    pass,
    type = "text",
    error,
    header = "",
    height = 40,
    placeholder,
    value,
    width = "100%",
    onChange,
    errorMessage,
    marginBottom = 0,
    explanation = "",
}: IInputForm) {
    return (
        <Container
            style={style}
            marginTop={marginTop}
            marginBottom={marginBottom}
            marginRight={marginRight}
            width={width}
        >
            {header !== "" && (
                <FontHeader>
                    {header}
                    {requirement && <FontRedStar>*</FontRedStar>}
                </FontHeader>
            )}
            {explanation !== "" && <Explanation>{explanation}</Explanation>}
            <Input
                height={height}
                type={type}
                textarea={textarea}
                onKeyPress={onKeyPress}
                pass={pass}
                error={error}
                value={value}
                placeholder={placeholder}
                onChange={onChange}
            />
            {error && <Error>{errorMessage}</Error>}
            {pass && <Pass>{errorMessage}</Pass>}
        </Container>
    );
}
const Explanation = styled.div`
    ${SpoqaHansSansNeo(16, 20, 400)};
    margin-bottom: 16px;
`;
const Error = styled.div`
    margin-top: 4px;
    ${SpoqaHansSansNeo(14, 24, 400)};
    color: #ff4e45;
`;
const Pass = styled.div`
    margin-top: 4px;
    ${SpoqaHansSansNeo(14, 24, 400)};
    color: #06be34;
`;
interface IContainer {
    marginBottom: number;
    marginRight: number;
    marginTop: number;
    width: number | string;
}
const Container = styled.div<IContainer>`
    margin-top: ${props => props.marginTop}px;
    height: fit-content;
    width: ${props => props.width}px;
    margin-right: ${props => props.marginRight}px;
    margin-bottom: ${props => props.marginBottom}px;
`;
