import star from '../../images/star.svg'
import { InqueryToArtist } from "./inqueryToArtist";
import { UpdateContent } from "./updateContent";
export function EstimateDetail({ currentSize }) {
    const projectID = useParams().projectID;
    const [estimateID, setEstimateID] = useState(useParams().estimateID);

    const setSelectedFalse = async () => {
        const url = API_SERVER2 + `/estimates/${data._id}/set-selected-to-false`;
        const options = {
            method: 'PATCH',
            url: url,
            withCredentials: true
        };
        axios(options).then(res => {
            if (res.status === 200) {
                refetch2();
            }
        })
    }

    const userType = JSON.parse(window.localStorage.getItem('userData')).userType;
    const history = useHistory();
    const [modalOpen, setModalOpen] = useState(false);

    const openModal = () => {
        setModalOpen(true);
    }
    const closeModal = () => {
        setModalOpen(false);
    }
    const [modal2Open, setModal2Open] = useState(false);
    const openModal2 = () => {
        setModal2Open(true);
    }
    const closeModal2 = () => {
        setModal2Open(false);
    }
    const printCreatedAt = () => {
        let date = project.createdAt
        const index = date.indexOf('T');
        let newDate = date.slice(0, index);

        return "지원일자 " + newDate;
    }

    const getProject = async () => {
        const url = API_SERVER2 + `/projects/${projectID}`;
        const response = await axios.get(
            url, { withCredentials: true });

        return response.data;
    }

    const getOtherEstimates = async () => {
        let params = new URLSearchParams();
        const url = API_SERVER2 + `/estimates/others`;
        params.append('projectID', projectID)
        params.append('currentEstimateID', estimateID)

        const response = await axios.get(
            url, { withCredentials: true, params: params });
        return response.data;
    }
    const getEstimates = async () => {
        const url = API_SERVER2 + `/estimates/${estimateID}/${userType}`;
        const response = await axios.get(
            url, { withCredentials: true });

        return response.data;
    }
    const [state] = useAsync(getProject, []);
    const [state2, refetch2] = useAsync(getEstimates, []);
    const [state3, refetch3] = useAsync(getOtherEstimates, []);
    useEffect(() => {
        window.scrollTo(0, 0);
        refetch2();
        refetch3();
    }, [estimateID])
    const data2 = state2.data;
    const data3 = state3.data;
    const { data } = state;
    let project, estimate, otherEstimates = null;
    if (data) {
        project = data.data.project
    }
    if (data2) {
        estimate = data2.data.estimate
    }

    if (data3) {
        otherEstimates = data3.data.estimates
    }
    const converter = () => {
        if (project.progress === '의뢰' ||
            project.progress === '견적 확인 및 선택') {
            return '오픈 준비'
        }
        else if (
            project.progress === '세부 사항 협의' ||
            project.progress === '계약' ||
            project.progress === '결제') {
            return '진행 중'
        }
        else if (project.progress === '결과물 수령') {
            return '완료'
        }
    }
    const printGenre = () => {
        const a = [];
        project.genre.map((v, i) => {

            a.push(v);
            if (project.genre.length - 1 !== i)
                a.push(" · ")
        })
        return a;
    }
    const printCategory = () => {
        const a = [];
        project.category.map((v, i) => {
            const index = v.indexOf('-');
            const newThing = v.slice(0, index).
                concat(' > ').concat(v.slice(index + 1, v.length));

            a.push(newThing);
            if (project.category.length - 1 !== i) {
                a.push(" ")
                a.push("  ·  ")
                a.push(" ")
            }

        })
        return a;
    }
    return (
        <Container>
            <NavBar currentSize={currentSize} />
            <InnerContainer>
                <InnerContainer2 className="샹">
                    {(data && data2) ? (
                        <>
                            <Row0>
                                <State><Text12>{converter()}</Text12></State>
                                <CreatedAt>{printCreatedAt()}</CreatedAt>
                            </Row0>
                            <Name>{project.name}</Name>
                            <Row1>
                                <Category>{printCategory()}</Category>
                                <Genre>{printGenre()}</Genre>
                            </Row1>
                            <BlankButton3
                                onClick={() => history.push(`/requestdetail/${project._id}`)}
                                style={
                                    {
                                        marginTop: 20,
                                        borderRadius: 0,
                                        marginBottm: 20
                                    }
                                }
                                width={182}
                                height={48}
                                text1={"의뢰서 보기"}></BlankButton3>
                            <Line />
                            <Estimate>
                                <Column1>
                                    <Box1>
                                        <Text11>아티스트 지원 내용</Text11>
                                        <Description>{estimate.description}</Description>
                                        <Text13>아티스트 요청/문의 사항</Text13>
                                        <Description>{estimate.artistRequest}</Description>
                                    </Box1>
                                    {
                                        (estimate.updates.length === 0) ?
                                            (<Updates>
                                                <Text11>프로젝트 업데이트 내용</Text11>
                                                <Description>{"아직 프로젝트 업데이트 내용이 없습니다."}</Description>
                                            </Updates>)
                                            :
                                            (<Updates>
                                                <Text11>프로젝트 업데이트 내용</Text11>
                                                {
                                                    estimate.updates.map((data, i) => {
                                                        return <UpdateContent
                                                            first={(i === 0)}
                                                            last={i === (estimate.updates.length - 1)}
                                                            name={estimate.artist.nameKO}
                                                            src={estimate.artist.smallProfileURI}
                                                            key={i} data={data} />
                                                    })
                                                }

                                            </Updates>)
                                    }
                                </Column1>
                                <Column2>
                                    {

                                        (estimate.selected === true) ?
                                            //최종선택됨
                                            (
                                                <ArtistMiniCard2>
                                                    <Row1>
                                                        <Profile src={estimate.artist.smallProfileURI} />
                                                        {
                                                            (estimate.artist.star !== undefined) &&
                                                            <img src={star} style={{
                                                                position: 'absolute', left: 73, top: 73
                                                            }} />
                                                        }
                                                        <Column3>
                                                            <Card><Text44>최종 선택</Text44></Card>
                                                            <ArtistName>{estimate.artist.nameKO}</ArtistName>
                                                            <Budget>{"예산: " + estimate.budget + "만원"}</Budget>
                                                            <Duration>{"일정: " + estimate.schedule + "일"}</Duration>
                                                        </Column3>
                                                    </Row1>
                                                    <Row3>
                                                        <BlankButton3
                                                            onClick={openModal2}
                                                            style={{ borderRadius: 0, marginBottom: 16 }}
                                                            width={160}
                                                            height={48}
                                                            text1={"문의하기"} />
                                                    </Row3>
                                                </ArtistMiniCard2>
                                            ) :

                                            (estimate.selected === false) ?
                                                //탈락됨
                                                (<ArtistMiniCard height={198}>
                                                    <Row1>
                                                        <Profile src={estimate.artist.smallProfileURI} />
                                                        {
                                                            (estimate.artist.star !== undefined) &&
                                                            <img src={star} style={{
                                                                position: 'absolute', left: 73, top: 73
                                                            }} />
                                                        }
                                                        <Column3>
                                                            <Card2><Text777>거절함</Text777></Card2>
                                                            <ArtistName>{estimate.artist.nameKO}</ArtistName>
                                                            <Budget>{"예산: " + estimate.budget + " 만원"}</Budget>
                                                            <Duration>{"일정: " + estimate.schedule + " 일"}</Duration>
                                                        </Column3>
                                                    </Row1>
                                                </ArtistMiniCard>)
                                                :
                                                //선택되기전
                                                (
                                                    <ArtistMiniCard height={330}>
                                                        <Row1>
                                                            <Profile src={estimate.artist.smallProfileURI} />
                                                            {
                                                                (estimate.artist.star !== undefined) &&
                                                                <img src={star} style={{
                                                                    position: 'absolute', left: 73, top: 73
                                                                }} />
                                                            }
                                                            <Column3>
                                                                <ArtistName>{estimate.artist.nameKO}</ArtistName>
                                                                <Budget>{"예산: " + estimate.budget + "만원"}</Budget>
                                                                <Duration>{"일정: " + estimate.schedule + "일"}</Duration>
                                                            </Column3>
                                                        </Row1>
                                                        <Row3>
                                                            <Button
                                                                onClick={() => { openModal(); }}
                                                                style={{
                                                                    marginBottom: 8,
                                                                    width: 160,
                                                                    height: 48,
                                                                    borderRadius: 0
                                                                }}>선택하기</Button>
                                                            <BlankButton3
                                                                onClick={openModal2}
                                                                style={{ borderRadius: 0, marginBottom: 16 }}
                                                                width={160}
                                                                height={48}
                                                                text1={"문의하기"} />
                                                            <Sorry onClick={
                                                                () => { setSelectedFalse(); }} >죄송합니다, 다음에 함께해요</Sorry>
                                                        </Row3>
                                                    </ArtistMiniCard>)}
                                    {
                                        (data3 && (otherEstimates !== undefined)) &&
                                        <>
                                            <Title2>다른 견적서 보기</Title2>
                                            <OtherEstimateList>
                                                {
                                                    otherEstimates.map((data, i) => {
                                                        return (
                                                            <OtherEstimates key={i}
                                                                onClick={() => { history.push(`/estimatedetail/${projectID}/${data._id}/${userType}`); setEstimateID(data._id) }}>
                                                                <div style={{ display: 'flex', marginBottom: 20 }}>
                                                                    <OthersProfile src={data.artist.smallProfileURI} />
                                                                    <Column3>
                                                                        <ArtistName>{data.artist.nameKO}</ArtistName>
                                                                        <Budget>{"예산: " + data.budget + "만원"}</Budget>
                                                                        <Duration>{"일정: " + data.schedule + "일"}</Duration>
                                                                    </Column3>
                                                                </div>
                                                                <Description2>{data.description}</Description2>
                                                            </OtherEstimates>
                                                        );
                                                    })
                                                }
                                            </OtherEstimateList>
                                        </>
                                    }
                                </Column2>
                            </Estimate>
                        </>
                    ) :
                        (
                            <EmptySpace />
                        )
                    }
                </InnerContainer2>
            </InnerContainer>
            <Footer />
            {(data && data2) && <ChooseArtistModal
                refetch2={refetch2}
                projectID={projectID}
                artistID={estimate.artist._id}
                estimateID={estimateID}
                projectName={project.name}
                name={estimate.artist.nameKO}
                img={estimate.artist.smallProfileURI}
                open={modalOpen}
                close={closeModal} />}
            {(data && data2) &&
                <InqueryToArtist
                    projectID={projectID}
                    estimateID={estimateID}
                    questionTo={estimate.artist._id}
                    name={estimate.artist.nameKO}
                    src={estimate.artist.smallProfileURI}
                    open={modal2Open}
                    close={closeModal2} />}
        </Container>
    );
}
