import axios from "axios";
import React, { useState } from "react";
import { useHistory, useParams } from "react-router";
import styled from "styled-components";
import { Button } from "../../components/0.Button/button";
import {
    API_SERVER2,
    BREAKPOINT_LARGE,
    BREAKPOINT_MIDDLE,
    BREAKPOINT_SMALL,
} from "../../constants/constants";
import { SpoqaHansSansNeo } from "../../css/css";
import { ChooseArtistModal } from "./chooseArtistModal";

export function EstimateCard({ data, style, refetch2 }) {
    const projectID = useParams().projectID;
    const history = useHistory();
    const userType = JSON.parse(
        window.localStorage.getItem("userData"),
    ).userType;
    const [notSelected] = useState(data.selected === undefined ? true : false);
    const setSelectedFalse = async () => {
        const url =
            API_SERVER2 + `/estimates/${data._id}/set-selected-to-false`;
        const options = {
            method: "PATCH",
            url: url,
            withCredentials: true,
        };
        axios(options).then(res => {
            if (res.status === 200) {
                refetch2();
            }
        });
    };
    return (
        <Container style={style} notSelected={notSelected}>
            <InnerContainer selected={data.selected === false}>
                <Profile src={data.artist.profileURI} />
                <Column1>
                    <MiniCardContainer>
                        {data.selected === false && (
                            <NotSelected>
                                <Text4>거절함</Text4>
                            </NotSelected>
                        )}
                        {data.artist.star && (
                            <MiniCard>
                                <Text1>의뢰인 요청</Text1>
                            </MiniCard>
                        )}
                    </MiniCardContainer>
                    <Name>{data.artist.nameKO}</Name>
                    <Description>{data.description}</Description>
                    <Text2>아티스트 요청/문의 사항:</Text2>
                    <Request>{data.artistRequest}</Request>
                </Column1>
                <Column2>
                    <Budget>예산: {data.budget} 만원</Budget>
                    <Budget style={{ marginTop: 16 }}>
                        일정: {data.schedule} 일
                    </Budget>
                    <Button
                        onClick={() => {
                            history.push(
                                `/estimatedetail/${projectID}/${data._id}/${userType}`,
                            );
                            window.scrollTo(0, 0);
                        }}
                        style={{ top: 186, left: 929, marginTop: 40 }}
                    >
                        상세보기
                    </Button>
                    {data.selected !== false && (
                        <Sorry
                            onClick={() => {
                                setSelectedFalse();
                            }}
                        >
                            죄송합니다, 다음에 함께해요
                        </Sorry>
                    )}
                </Column2>
            </InnerContainer>
            <ChooseArtistModal />
        </Container>
    );
}
const InnerContainer = styled.div`
    display: flex;
    opacity: ${props => (props.selected ? 0.4 : 1)};
    width: 100%;
`;
const Text4 = styled.div`
    position: relative;
    top: 1px;
    padding: 4px 8px;
    ${SpoqaHansSansNeo(12, 14, 700)};
`;
const NotSelected = styled.div`
    border-radius: 4px;
    margin-right: 8px;
    background: #4a4a4a;
    width: fit-content;
`;
const Sorry = styled.div`
    &:hover {
        text-decoration: underline;
    }
    cursor: pointer;
    margin-top: 8px;
    ${SpoqaHansSansNeo(12, 14, 700, "#D0B1FF")};
`;
const Budget = styled.div`
    ${SpoqaHansSansNeo(20, 24, 700)};
`;
const Column2 = styled.div`
    @media screen and (min-width: ${BREAKPOINT_LARGE}px) {
        padding: 32px 0px 32px 40px;
    }
    @media screen and (min-width: ${BREAKPOINT_MIDDLE}px) and (max-width: ${BREAKPOINT_LARGE}px) {
        padding: 32px 0px 32px 40px;
    }
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) and (max-width: ${BREAKPOINT_MIDDLE}px) {
        padding: 32px 0px 32px 40px;
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        padding: 32px 0px 32px 40px;
    }
    margin-left: auto;
    border-left: 1px solid #dbdbdb;
`;
const Text2 = styled.div`
    ${SpoqaHansSansNeo(20, 24, 700)};
    margin-top: 20px;
`;
const Request = styled.div`
    ${SpoqaHansSansNeo(16, 20)};
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    margin-top: 8px;
`;
const Description = styled.div`
    word-break: break-all;
    margin-top: 20px;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2; /* 라인수 */
    -webkit-box-orient: vertical;
    word-wrap: break-word;
    ${SpoqaHansSansNeo(16, 20)};
    height: 40px;
`;
const Name = styled.div`
    margin-top: 10px;
    ${SpoqaHansSansNeo(34, 40, 700)};
`;
const Text1 = styled.div`
    position: relative;
    top: 1px;
    padding: 4px 8px;
    ${SpoqaHansSansNeo(12, 14, 700, "#01010B", "0em")};
`;
const MiniCard = styled.div`
    border-radius: 4px;
    margin-right: 8px;
    background: #fce850;
    width: fit-content;
`;
const MiniCardContainer = styled.div`
    display: flex;
    height: 22px;
`;
const Column1 = styled.div`
    box-sizing: content-box;
    width: 40%;
    padding: 0px 40px;
`;
const Profile = styled.img`
    @media screen and (min-width: ${BREAKPOINT_LARGE}px) {
        width: 240px;
        height: 240px;
    }
    @media screen and (min-width: ${BREAKPOINT_MIDDLE}px) and (max-width: ${BREAKPOINT_LARGE}px) {
        width: 194px;
        height: 194px;
    }
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) and (max-width: ${BREAKPOINT_MIDDLE}px) {
        width: 140px;
        height: 140px;
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        width: 140px;
        height: 140px;
    }
    border-radius: 50em;
`;
const Container = styled.div`
    position: relative;
    display: flex;
    background: #0e0e10;
    @media screen and (min-width: ${BREAKPOINT_LARGE}px) {
        width: 100%;
        padding: 40px;
    }
    @media screen and (min-width: ${BREAKPOINT_MIDDLE}px) and (max-width: ${BREAKPOINT_LARGE}px) {
        width: 100%;
        padding: 40px;
    }
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) and (max-width: ${BREAKPOINT_MIDDLE}px) {
        width: 100%;
        padding: 40px;
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        width: 375px;
        padding: 20px;
    }
    height: 320px;
`;
