import React from "react";
import { BREAKPOINT_LARGE, BREAKPOINT_MIDDLE, BREAKPOINT_SMALL } from "src/ts/constants";
import { SpoqaHansSansNeo } from "src/ts/styles/css";
import styled from "styled-components";



export function VideoColumn({ video, marginRight, marginBottom, openModal, forPortfolio = false, i }) {
    const print = () => {
        if (!forPortfolio) {
            return video.publishedAt + '전'
        }
        else {
            return ''
        }
    }
    return (
        <Container i={i} onClick={() => openModal(video.videoID)} style={{ marginBottom: marginBottom, marginRight: marginRight }}>
            <ThumbnailContainer>
                <Thumbnail src={`https://img.youtube.com/vi/${video.videoID}/mqdefault.jpg`} />
            </ThumbnailContainer>
            <VideoData>
                <Title><Text1>{video.title}</Text1></Title>
                <ChannelTitle>{video.channelTitle}</ChannelTitle>
                <ViewCount>{'조회수 ' + video.viewCount + '회 · ' + print()}</ViewCount>
            </VideoData>
        </Container>
    );
}
const ThumbnailContainer = styled.div`
    @media screen and (min-width: ${BREAKPOINT_LARGE}px) {
        width: 274px;
        height: 153px;
    }
    @media screen and (min-width: ${BREAKPOINT_MIDDLE}px) and (max-width: ${BREAKPOINT_LARGE}px) {
        width: 260px;
        height: 143px;
    }
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) and (max-width: ${BREAKPOINT_MIDDLE}px){
        width: 224px;
        height: 123px;
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        width: 274px;
        height: 153px;
    }
    overflow: hidden;
`
const Text1 = styled.div`
    overflow: hidden;
	text-overflow: ellipsis;
    white-space: nowrap;
    ${SpoqaHansSansNeo(16, 20, 700)};
`
const Thumbnail = styled.img`
    @media screen and (min-width: ${BREAKPOINT_LARGE}px) {
        width: 274px;
        height: 153px;
    }
    @media screen and (min-width: ${BREAKPOINT_MIDDLE}px) and (max-width: ${BREAKPOINT_LARGE}px) {
        width: 260px;
        height: 143px;
    }
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) and (max-width: ${BREAKPOINT_MIDDLE}px){
        width: 224px;
        height: 123px;
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        width: 274px;
        height: 153px;
    }
    transition: all .3s;
    &:hover{
        transform: scale(1.1);
    }
`
const VideoData = styled.div`
    margin-top: 8px;
`
const ViewCount = styled.div`
    margin-top: 4px;
    ${SpoqaHansSansNeo(12, 14, 400, '#DBDBDB', 0)};
`
const ChannelTitle = styled.div`
    ${SpoqaHansSansNeo(12, 14, 400, '#DBDBDB', 0)};
    margin-top: 4px;
`
const Title = styled.div`
	overflow: hidden;
	text-overflow: ellipsis;
	word-wrap: break-word;
	display: -webkit-box;
	-webkit-line-clamp: 2;
	-webkit-box-orient: vertical;
    text-align: left;
    color: #F7F7F7;
`
const Container = styled.div`
    cursor: pointer;
    margin-bottom: 20px;
    @media screen and (min-width: ${BREAKPOINT_LARGE}px) {
        width: 274px;
        margin-right: ${props => (props.i % 4 !== 3) ? '20px' : '0px'};
    }
    @media screen and (min-width: ${BREAKPOINT_MIDDLE}px) and (max-width: ${BREAKPOINT_LARGE}px) {
        width: 260px;
        margin-right: ${props => (props.i % 4 !== 3) ? '20px' : '0px'};
    }
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) and (max-width: ${BREAKPOINT_MIDDLE}px){
        width: 224px;
        margin-right: ${props => (props.i % 3 !== 2) ? '20px' : '0px'};
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        width: 274px;
        margin-right: 20px;
        margin-left: ${props => (props.i === 0) && '20px'};
    }
`