import React from "react";
import { useHistory } from "react-router";
import styled from "styled-components";
import { RoundButton3 } from "../../components/0.Button/roundButton3";
import { BREAKPOINT_LARGE, BREAKPOINT_MIDDLE, BREAKPOINT_SMALL } from "../../constants/constants";
import { DisplayFlex, SpoqaHansSansNeo } from "../../css/css";
import emptyArtists from '../../images/emptyArtists.svg'
export function MyArtists({ data, artists }) {
    const history = useHistory()
    const onClick = (artist) => history.push(`/${artist.nameForURI}`)
    return (
        <Container>
            <InnerContainer>
                { 
                    data &&
                    (
                        (artists!== undefined) ? 
                            (artists.map((artist, i) => {
                                return (
                                    <ArtistCard
                                        key={i}
                                        i={i}
                                        onClick={() => onClick(artist)}>
                                        <Profile src={artist.profileURI} />
                                        <ArtistName>{artist.nameKO}</ArtistName>
                                    </ArtistCard>);
                            })) 
                        :
                            (<EmptyArtists>
                                <img src={emptyArtists} style={{ width: 136, height: 98, marginTop: 296 }} />
                                <Text1>팔로우하는 아티스트가 없습니다.</Text1>
                                <div style={{ display: 'flex', marginTop: 4, marginBottom: 32 }}>
                                    <Text2>STAGE8 홈페이지</Text2><Text3>에서 그동안 꿈꿔 왔던 당신의 아티스트를 검색해 보세요.</Text3>
                                </div>
                                <RoundButton3 onClick={() => {
                                    history.push('/');
                                    window.scrollTo(0, 768);
                                }}>아티스트 검색하기</RoundButton3>
                            </EmptyArtists>)
                    )
                }
            </InnerContainer>
        </Container>
    );
}
const Text3 = styled.div`
    ${SpoqaHansSansNeo(16,20,400,'#DBDBDB')};
`
const Text2 = styled.div`
    ${SpoqaHansSansNeo(16,20,700,'#DBDBDB')};
`
const Text1 = styled.div`
    ${SpoqaHansSansNeo(34,40,700,'#DBDBDB')};
`
const EmptyArtists = styled.div`
    width: 100%;
    height: 970px;
    display: flex;
    flex-direction: column;
    align-items: center;
`
const ArtistName = styled.div`
    ${DisplayFlex('center','')};
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
        ${SpoqaHansSansNeo(20,24,700)};
        width:176px;
        height:48px;
        margin-top:16px;
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        ${SpoqaHansSansNeo(14,16,700)};
        margin-left: 20px;
    }
`
const InnerContainer = styled.div`
    flex-wrap: wrap;
    padding: 0px 20px;
    @media screen and (min-width: ${BREAKPOINT_LARGE}px) {
        width: 1196px;
    }
    @media screen and (min-width: ${BREAKPOINT_MIDDLE}px) and (max-width: ${BREAKPOINT_LARGE}px) {
        width: 1140px;
    }
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) and (max-width: ${BREAKPOINT_MIDDLE}px){
        width: 100%;
    }
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
        min-height: 970px;
        display:flex;
        padding-bottom: 200px;
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        width: 375px;
        padding-bottom: 100px;
    }
    
`
const Container = styled.div`
    ${DisplayFlex("center", "")}
`
const Profile = styled.img`
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
        width:144px;
        height:144px;
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        width: 48px;
        height: 48px;
    }
    border-radius: 50em;
`
const ArtistCard = styled.div`
    cursor: pointer;
    ${DisplayFlex("", "center")}
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
        width:176px;
        height:208px;
        flex-direction:column;
        margin-right: ${props => (props.i + 1) % 5 !== 0 && '20px'};
        margin-bottom: 32px;
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        height:48px;
        margin-bottom:20px;
    }
`