import React, { useState } from "react";
import styled from "styled-components";
import { DropDown } from "../2.DropDown/dropDown";
import { DisplayFlex, SpoqaHansSansNeo } from "../../css/css";
import chevron from "../../images/chevron.svg";
import { BREAKPOINT_SMALL } from "../../constants/constants";

export function Accordion({
    requirement,
    pass,
    options,
    header,
    onCreate,
    onRemove,
    onToggle,
    setCategory,
    deleteCategory,
}) {
    const [isOpen, setIsOpen] = useState(false);
    const toggling = () => setIsOpen(!isOpen);
    return (
        <Container>
            <Header
                requirement={requirement}
                pass={pass}
                onClick={toggling}
                isOpen={isOpen}
            >
                <Text>{header}</Text>
                <IMG isOpen={isOpen} src={chevron} />
            </Header>
            <DropDownList isOpen={isOpen}>
                <Inner>
                    {options.map((option, i) => (
                        <DropDown
                            parentisOpen={isOpen}
                            key={i}
                            option={option}
                            onCreate={onCreate}
                            onRemove={onRemove}
                            onToggle={onToggle}
                            setCategory={setCategory}
                            deleteCategory={deleteCategory}
                        />
                    ))}
                </Inner>
            </DropDownList>
        </Container>
    );
}
const Container = styled.div`
    margin-top: 16px;
    height: 40px;
`;
const Header = styled.div`
    z-index: 5;
    &:hover {
        outline: none;
        border-color: ${props => (props.requirement ? "#FF4E45" : "#BD03FA")};
    }
    background-color: #01010b;
    position: relative;
    cursor: pointer;
    border: 1px solid;
    border-radius: 4px;
    ${DisplayFlex("", "center")}
    border-color: ${props =>
        props.requirement
            ? "#FF4E45"
            : props =>
                props.isOpen
                    ? "#BD03FA"
                    : props.pass
                        ? "#06BE34"
                        : "#828282"};
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
        width: 626px;
        height: 40px;
        padding: 8px 12px;
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        height: 32px;
        width: 100px;
        padding: 4px 12px;
    }
`;
const IMG = styled.img`
    -ms-user-select: none;
    -moz-user-select: -moz-none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    user-select: none;
    margin-left: auto;
    transition: all ease 0.5s;
    transform: ${props => props.isOpen && "rotate( -180deg )"};
`;
const Inner = styled.div`
    z-index: 4;
    overflow-y: auto;
    vertical-align: top;
    max-height: 384px;
    &::-webkit-scrollbar {
        display: none;
    }
    -ms-overflow-style: none; /* IE and Edge */
    scrollbar-width: none; /* Firefox */
`;
const DropDownList = styled.div`
    width: 626px;
    max-height: 384px;
    box-shadow: 0px 4px 4px rgba(1, 1, 11, 0.25);
    border-radius: 4px;
    position: relative;
    visibility: ${props => !props.isOpen && "hidden"};
    opacity: ${props => (props.isOpen ? "1" : "0")};
    top: ${props => (props.isOpen ? "8px" : "-40px")};
    background-color: #0e0e10;
    padding: 8px 0px;
    z-index: 4;
    transition: all 0.4s;
`;
const Text = styled.div`
    z-index: 2;
    -ms-user-select: none;
    -moz-user-select: -moz-none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    user-select: none;
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
        ${SpoqaHansSansNeo(16, 24, 700)}
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        ${SpoqaHansSansNeo(14, 16, 400)}
    }
`;
