import React from "react";
import { useMediaQuery } from "react-responsive";
import { BREAKPOINT_LARGE, BREAKPOINT_MIDDLE, BREAKPOINT_SMALL } from "../constants/constants";

const Mobile = ({ children }) => {
    const isMobile = useMediaQuery({
        query: `(max-width:${BREAKPOINT_SMALL}px)`
    });
    return <React.Fragment>{isMobile && children}</React.Fragment>
}
const NotMobile = ({ children }) => {
    const isMobile = useMediaQuery({
        query: `(min-width:${BREAKPOINT_SMALL}px)`
    });
    return <React.Fragment>{isMobile && children}</React.Fragment>
}
const SmallTablet = ({ children }) => {
    const isMobile = useMediaQuery({
        query: `(max-width:${BREAKPOINT_MIDDLE}px) and (min-width:${BREAKPOINT_SMALL}px)`
    });
    return <React.Fragment>{isMobile && children}</React.Fragment>
}
const LargeTablet = ({ children }) => {
    const isMobile = useMediaQuery({
        query: `(max-width:${BREAKPOINT_MIDDLE}px) and (min-width:${BREAKPOINT_LARGE}px)`
    });
    return <React.Fragment>{isMobile && children}</React.Fragment>
}
const PC = ({ children }) => {
    const isPc = useMediaQuery({
        query: `(min-width:${BREAKPOINT_LARGE}px)`
    });
    return <React.Fragment>{isPc && children}</React.Fragment>
}
const PCOrLargeTablet = ({ children }) => {
    const isPc = useMediaQuery({
        query: `(min-width:${BREAKPOINT_MIDDLE}px)`
    });
    return <React.Fragment>{isPc && children}</React.Fragment>
}
const MobileOrSmallTablet = ({ children }) => {
    const isPc = useMediaQuery({
        query: `(max-width:${BREAKPOINT_MIDDLE}px)`
    });
    return <React.Fragment>{isPc && children}</React.Fragment>
}
export { Mobile, PC, SmallTablet, LargeTablet, PCOrLargeTablet, NotMobile, MobileOrSmallTablet };
