import React from "react";
import { Chevron } from "./2.Chevron/chevron";
import styled from "styled-components";
import { Pagination } from "./1.Pagination/pagination";

export function ArtistPagination({ pagination, setPage, style }) {
  const { currentPage, hasNext, hasBefore, lastPage } = pagination;
  const arr = [];
  const renderPagination = () => {
    for (let i = 0; i < lastPage; ++i) {
      arr.push(<Pagination setPage={setPage} key={'pagination' + i} isCurrent={((i + 1) === currentPage)} number={i + 1}/>);
    }
    return arr;
  }

  return (
    <Container style={style}>
      <Chevron isLeft={true} activate={hasBefore} currentPage={currentPage} setPage={setPage}/>
      {renderPagination()}
      <Chevron isLeft={false} activate={hasNext} currentPage={currentPage} setPage={setPage}/>
    </Container>
  );
}

const Container = styled.div`
    -ms-user-select: none; 
    -moz-user-select: -moz-none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    user-select: none;
  align-items: center;
  display: flex;
  height: 32px;
  justify-content: center;
  margin-top: 20px;
  min-width: 176px;
  padding: 0 0.0px;
  position: relative;
`
