import { ReactElement } from "react";
import { Loadable } from "recoil";

export const getNameForURIFromURI = () => {
    return JSON.parse(window.localStorage.getItem("userData") || "").nameForURI;
};

export function isEmptyObject(param: any) {
    if (param == null) return true;
    return Object.keys(param).length === 0;
}
export const cloneObj = obj => JSON.parse(JSON.stringify(obj));
export const isMobile = (currentSize: string) => {
    return currentSize === "MOBILE";
};
export const Switch = (
    loadable: Loadable<any>,
    hasValue: ReactElement,
    loading: ReactElement = <></>,
) => {
    switch (loadable.state) {
        case "hasValue":
            return hasValue;

        case "loading":
            return loading;

        case "hasError":
            throw loadable.contents;
    }
};
export const onChange =
    (
        setter: React.Dispatch<React.SetStateAction<string>>,
        setter2?: Function,
    ) =>
    (e: React.ChangeEvent<HTMLTextAreaElement>) => {
        setter(e.target.value);
        if (setter2) setter2(e);
    };
