import axios, { AxiosPromise, AxiosRequestConfig } from "axios";
import { selectorFamily } from "recoil";
import { IAPIParams } from "src/ts/interfaces/common";
import { API_SERVER2 } from "../constants/common";

export async function GET<ReturnType>(url: string) {
    const res = await axios.get<ReturnType>(API_SERVER2 + url, {
        withCredentials: true,
    });
    return res.data;
}

export const PATCH = selectorFamily<AxiosPromise, IAPIParams>({
    key: "PATCH",
    get:
        (params: IAPIParams) =>
        async ({ get }) => {
            try {
                const form = new FormData();
                params.data?.forEach(element => {
                    form.append(element.key, element.value);
                });
                const options: AxiosRequestConfig = {
                    method: "PATCH",
                    data: form,
                    url: API_SERVER2 + String(params.url),
                    withCredentials: true,
                };
                const data = await axios(options);

                return data;
            } catch (err) {
                throw err;
            }
        },
});
