import axios from "axios";
import React from "react";
import { useHistory, useLocation } from "react-router";
import styled from "styled-components";
import { Button } from "../../components/0.Button/button";
import { Input } from "../../components/4.Input/input";
import { API_SERVER2 } from "../../constants/constants";
import { SpoqaHansSansNeo } from "../../css/css";
import { useInputs } from "../../hook/useInputs";
import { Header } from "../signUp/1.Header/header";

export function ResetPW() {
    const qeury = useLocation().search.toString();
    const token = qeury.substring(qeury.indexOf('=') + 1,qeury.length);
    const history = useHistory();
    const [{ password }, onChange] = useInputs({
        password: ''
    });
    const resetPW = async () => {
        const form = new FormData();
        form.append('newPassword',password)
        const url = API_SERVER2 + `/auth/reset-password/${token}`;
        const options = {
            method: 'PATCH',
            url: url,
            data: form,
            withCredentials: true,
        };
        axios(options).then(() => {
            history.push('/resetconfirmed')
        })
    }
    return (
        <Container>
            <Header />
            <InnerContainer>
                <InnerContainer2>
                    <Title>비밀번호 변경</Title>
                    <Text1>새로운 비밀번호를 등록해주세요.<br></br>
                        영문, 숫자, 특수문자 포함, 8자 이상이여야 됩니다.</Text1>
                    <EmailAddress>New Password</EmailAddress>
                    <Input
                        onChange={onChange}
                        name="password"
                        value={password}
                        placeholder="영문, 숫자, 특수문자 포함 8자 이상" />
                    <Button 
                        onClick={() => resetPW()}
                        style={{ 
                            width: 200, 
                            height: 48, 
                            marginTop: 40, 
                            marginBottom: 8 }}>
                        비밀번호 변경
                    </Button>
                </InnerContainer2>
            </InnerContainer>
        </Container>
    );
}
const EmailAddress = styled.div`
    margin-bottom:16px;
    margin-top:40px;
    ${SpoqaHansSansNeo(24,28,700)};
`
const Text1 = styled.div`
    ${SpoqaHansSansNeo(16,20)};
    margin-top:16px;
`
const InnerContainer2 = styled.div`
    width:1440px;
    padding: 80px 142px 0px 142px;
`
const InnerContainer = styled.div`
    display:flex;
    justify-content:center;
`
const Title = styled.div`
    ${SpoqaHansSansNeo(34,40,700)};
`

const Container = styled.div`
    min-height: 946px;
    display: flex;
    flex-direction: column;
    background-color:#01010B;
    padding-bottom:200px;
    -ms-user-select: none; 
    -moz-user-select: -moz-none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    user-select: none;
`