import {
    BREAKPOINT_LARGE,
    BREAKPOINT_MIDDLE,
    BREAKPOINT_SMALL,
} from "src/ts/constants";
import { IImage } from "src/ts/interfaces/common";
import { makeCenter, SpoqaHansSansNeo } from "src/ts/styles/css";
import styled from "styled-components";
import facebook from "../../images/facebook.svg";
import instagram from "../../images/instagram.svg";
import twitter from "../../images/twitter.svg";
import website from "../../images/website.svg";
import youtube from "../../images/youtube.svg";
import { isEmptyObject } from "../../lib/lib";
import { IInnerContainer } from "./aboutData";
import { UpdateActiveYear } from "./updateActiveYear";
import { UpdateAward } from "./updateAward";
import { UpdateCategory } from "./updateCategory";
import { UpdateDisco } from "./updateDisco";
import { UpdateFacebook } from "./updateFacebook";
import { UpdateGenre } from "./updateGenre";
import { UpdateMembers } from "./updateMembers";

export const VArtistDetail = ({ data }: IInnerContainer) => {
    return (
        <Row3>
            <SArtistType>
                {data.artistType &&
                    data.artistType.map((typeName, i) => {
                        if (typeName === "producer")
                            return (
                                <BadgeProducer key={i}>프로듀서</BadgeProducer>
                            );
                        else if (typeName === "performer")
                            return (
                                <BadgePerformer key={i}>퍼포머</BadgePerformer>
                            );
                    })}
            </SArtistType>
            <Row5>
                <Title>활동기간:</Title>
                {!isEmptyObject(data.activeYear) && (
                    <Content2>
                        {data.activeYear[0] + " - " + data.activeYear[1]}
                    </Content2>
                )}
                <UpdateActiveYear />
            </Row5>
            <Row5>
                <Title>멤버:</Title>
                {!isEmptyObject(data.members) && (
                    <Content2>{data.members}</Content2>
                )}
                <UpdateMembers />
            </Row5>
            <Row5>
                <Title>장르:</Title>
                {!isEmptyObject(data.genre) && (
                    <Content2>
                        {data.genre.map((value, i) => {
                            if (i < data.genre.length - 1) return value + ", ";
                            else return value;
                        })}
                    </Content2>
                )}
                <UpdateGenre />
            </Row5>
            <Row5>
                <Title>활동영역:</Title>
                {!isEmptyObject(data.category) && (
                    <Content2>
                        {data.category.map((value, i) => {
                            if (i < data.category.length - 1)
                                return value + ", ";
                            else return value;
                        })}
                    </Content2>
                )}
                <UpdateCategory />
            </Row5>
            <Row5>
                <Title>디스코그래피:</Title>
                {!isEmptyObject(data.discography) && (
                    <Content2>{data.discography}</Content2>
                )}
                <UpdateDisco />
            </Row5>
            <Row5>
                <Title style={{ marginBottom: 6 }}>대표 수상이력:</Title>
                <UpdateAward />
            </Row5>
            {!isEmptyObject(data.award) && (
                <Content2>
                    {data.award.split(",").map((data, i) => {
                        return <Text999 key={i}>{" · " + data}</Text999>;
                    })}
                </Content2>
            )}
            <div style={{ display: "flex" }}>
                {" "}
                <Title>SNS:</Title>
                <UpdateFacebook />
            </div>
            {data.SNSChannel && (
                <Row4>
                    {data.SNSChannel.youtube && (
                        <a
                            style={{
                                width: 24,
                                height: 24,
                                display: "inline-block",
                                marginRight: 8,
                            }}
                            rel="noreferrer"
                            target="_blank"
                            href={data.SNSChannel.youtube}
                        >
                            <IMG src={youtube}></IMG>
                        </a>
                    )}
                    {data.SNSChannel.facebook && (
                        <a
                            style={{
                                width: 24,
                                height: 24,
                                display: "inline-block",
                                marginRight: 8,
                            }}
                            rel="noreferrer"
                            target="_blank"
                            href={data.SNSChannel.facebook}
                        >
                            <IMG src={facebook}></IMG>
                        </a>
                    )}
                    {data.SNSChannel.instagram && (
                        <a
                            style={{
                                width: 24,
                                height: 24,
                                display: "inline-block",
                                marginRight: 10,
                            }}
                            rel="noreferrer"
                            target="_blank"
                            href={data.SNSChannel.instagram}
                        >
                            <IMG src={instagram}></IMG>
                        </a>
                    )}
                    {data.SNSChannel.twitter && (
                        <a
                            style={{
                                width: 24,
                                height: 24,
                                display: "inline-block",
                                marginRight: 8,
                            }}
                            rel="noreferrer"
                            target="_blank"
                            href={data.SNSChannel.twitter}
                        >
                            <IMG src={twitter}></IMG>
                        </a>
                    )}
                    {data.SNSChannel.website && (
                        <a
                            style={{
                                width: 24,
                                height: 24,
                                display: "inline-block",
                                marginRight: 8,
                            }}
                            rel="noreferrer"
                            target="_blank"
                            href={data.SNSChannel.website}
                        >
                            <IMG src={website}></IMG>
                        </a>
                    )}
                </Row4>
            )}
        </Row3>
    );
};
const Row4 = styled.div`
    display: flex;
    margin-top: 20px;
`;
const Title = styled.span`
    min-width: fit-content;
    ${SpoqaHansSansNeo(16, 20, 700)}
`;
const Row5 = styled.div`
    margin-top: 8px;
    display: flex;
`;
const Text999 = styled.div`
    ${SpoqaHansSansNeo(16, 20, 400, "#BEBEBE")};
`;

const IMG = styled.img<IImage>`
    z-index: 2;
    display: inline-block;
    width: ${24};
    height: ${24};
`;
const Row3 = styled.div`
    margin-top: 14px;
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) and (max-width: ${BREAKPOINT_MIDDLE}px) {
        margin-top: 0px;
    }
    @media screen and (min-width: ${BREAKPOINT_LARGE}px) {
        width: 548px;
    }
    @media screen and (min-width: ${BREAKPOINT_MIDDLE}px) and (max-width: ${BREAKPOINT_LARGE}px) {
        width: 540px;
    }
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) and (max-width: ${BREAKPOINT_MIDDLE}px) {
        width: 375px;
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        width: 332px;
    }
`;
const Content2 = styled.div`
    ${SpoqaHansSansNeo(16, 20, 400, "#BEBEBE")};
    margin-left: 4px;
`;

const SArtistType = styled.div`
    display: flex;
    margin-bottom: 14px;
`;
export const BadgeProducer = styled.div`
    ${makeCenter()};
    ${SpoqaHansSansNeo(12, 16, 700, "#01010B")};
    background: #d0b1ff;
    width: 60px;
    height: 24px;
    border-radius: 4px;
    margin-right: 4px;
`;
export const BadgePerformer = styled.div`
    ${makeCenter()};
    ${SpoqaHansSansNeo(12, 16, 700, "#01010B")};
    background: #90caf9;
    width: 50px;
    height: 24px;
    margin-right: 4px;
    border-radius: 4px;
`;
