import styled from "styled-components";
import React from "react";
import './slide.css'
import { useHistory } from "react-router";
import { SpoqaHansSansNeo } from "../../../../css/css";
import { BREAKPOINT_LARGE, BREAKPOINT_MIDDLE, BREAKPOINT_SMALL } from "../../../../constants/constants";

export function Slide({ bannerURI, name, nameForURI }) {
  const history = useHistory();
  const click = () => {
    history.push(`${nameForURI}/`)
    window.scrollTo(0, 0);
  }
  return (
    <Container >
      <IMG src={bannerURI}>
        <InnerContainer>
          <ArtistName onClick={click}>{name}</ArtistName>
        </InnerContainer>
      </IMG>
      <ClickableLayer onClick={click}/>
    </Container>
  );
}
const ClickableLayer = styled.div`
  position: absolute;
  top: 0px;
  right: 0px;
  width: 100%;
  @media screen and (min-width: ${BREAKPOINT_LARGE}px) {
    height: 450px;
  }
  @media screen and (min-width: ${BREAKPOINT_MIDDLE}px) and (max-width: ${BREAKPOINT_LARGE}px) {
    height: 330px;
  }
  @media screen and (min-width: ${BREAKPOINT_SMALL}px) and (max-width: ${BREAKPOINT_MIDDLE}px) {
    height: 200px;
  }
  @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
    height: 0px;
  }
  cursor: pointer;
  z-index: 9999;
`
const InnerContainer = styled.div`
  @media screen and (min-width: ${BREAKPOINT_LARGE}px) {
    padding: 148px 142px;
  }
  @media screen and (min-width: ${BREAKPOINT_MIDDLE}px) and (max-width: ${BREAKPOINT_LARGE}px) {
    padding: 80px 26px;
  }
  @media screen and (min-width: ${BREAKPOINT_SMALL}px) and (max-width: ${BREAKPOINT_MIDDLE}px) {
    padding: 64px 28px;
  }
  @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
    padding: 40px 20px;
  }
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: flex-end;
`
const IMG = styled.div`
  width: auto;
  position: relative;
  height: 100%;
  background: linear-gradient(rgba(0, 0, 0, 0) 25%, 75%, #000000), url(${props => props.src});
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
`
const Container = styled.div`
  background-color: #01010B;
  position: relative;
  width: 100%;
  @media screen and (min-width: ${BREAKPOINT_LARGE}px) {
    height: 572px;
  }
  @media screen and (min-width: ${BREAKPOINT_MIDDLE}px) and (max-width: ${BREAKPOINT_LARGE}px) {
    height: 416px;
  }
  @media screen and (min-width: ${BREAKPOINT_SMALL}px) and (max-width: ${BREAKPOINT_MIDDLE}px) {
    height: 278px;
  }
  @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
    height: 278px;
  }
`;
const ArtistName = styled.div`
  z-index: 9998;
  cursor: pointer;
  position: relative;
  @media screen and (min-width: ${BREAKPOINT_MIDDLE}px) {
    ${SpoqaHansSansNeo(72,84,700)}
    }
  @media screen and (min-width: ${BREAKPOINT_SMALL}px) and (max-width: ${BREAKPOINT_MIDDLE}px) {
    ${SpoqaHansSansNeo(48,60,700)}
  }
  @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
    ${SpoqaHansSansNeo(24,30,700)}
  }
`